import { IVector3, IVector2 } from "@hc/core";
import { Vector2, Vector3 } from "../babylon";
import { equaln } from ".";

export const MaxVector = new Vector3(Infinity, Infinity, Infinity);
export const MinVector = new Vector3(-Infinity, -Infinity, -Infinity);

export const TempVector = new Vector3();

export function iVector2Vector(v: IVector3) {
  return Vector3.FromArray([v.x ?? 0, v.y ?? 0, v.z ?? 0]);
}

export function setVecLike2Vector(
  sourc: Vector3 | Vector2,
  target: IVector3 | IVector2
) {
  sourc.x = target.x ?? sourc.x;
  sourc.y = target.y ?? sourc.y;
  if ("z" in sourc && "z" in target) sourc.z = target.z ?? sourc.z;
}

export function equlnVector3(v1: IVector3, v2: IVector3) {
  return equaln(v1.x, v2.x) && equaln(v1.y, v2.y) && equaln(v1.z, v2.z);
}

export function vec2Vec3Like(pt: Vector3) {
  return { x: pt.x, y: pt.y, z: pt.z } as IVector3;
}
export function vec2Vec2Like(pt: Vector2) {
  return { x: pt.x, y: pt.y } as IVector2;
}
