import type { ParticleSystem } from "./ParticleSystem";
import { Particle } from "./Particle";
import { SubEmitter as BSubEmitter, SubEmitterType } from "../babylon";
import { Factory, HCFiler } from "@hc/core";

@Factory
export class SubEmitter extends Particle {
  static type = "SubEmitter";
  protected _name: string = "子粒子特效触发器";
  private type = SubEmitterType.END;
  private _intance: BSubEmitter;
  constructor(public particleSystem: ParticleSystem) {
    super();
  }
  get Intance() {
    if (!this._intance) {
      this._intance = this.initParticle();
    }
    return this._intance;
  }
  initParticle() {
    const child = this.particleSystem.Intance;
    child.disposeOnStop = true;
    const sub = new BSubEmitter(child);
    sub.type = this.type;
    return sub;
  }
  updateParticle(sub: BSubEmitter) {
    sub.type = this.type;
    // sub.inheritDirection = true;
    // sub.inheritedVelocityAmount = 2;
    this.particleSystem.updateParticle(sub.particleSystem);
  }
  destory(): void {
    super.destory();
    if (this._intance) {
      this._intance.dispose();
      this._intance = null;
    }
  }
  fromJSON(json: Record<string, any>) {
    if (!json) return;
    if (json.type) this.type = json.type;
    this.particleSystem.fromJSON(json as any, true);
    return this;
  }

  readFile(filer: HCFiler): void {
    super.readFile(filer);
    this.particleSystem = filer.readObject();
  }
  writeFile(filer: HCFiler): void {
    super.writeFile(filer);
    filer.writeObject(this.particleSystem);
  }
}
