import { HistoryRecord } from "./HistoryRecord";
import { AllObjectData } from "./AllObjectData";
import { ObjectId } from "../ObjectId";
import { HCFiler } from "../filer/HCFiler";

/**对象所有数据记录 */
export class ObjectAllDataHistoryRecord extends HistoryRecord {
  constructor(private id: ObjectId) {
    super();
    this.writeUndo();
  }
  writeUndo() {
    this.undoData = new AllObjectData(this.id.Object);
  }
  writeRedo() {
    this.redoData = new AllObjectData(this.id.Object);
  }
  writeFile(file: HCFiler) {
    file.write(1);
    file.writeObjectId(this.id);
  }
  readFile(file: HCFiler) {
    file.read();
    this.id = file.readObjectId();
  }
}
