import { ActionManagerType } from "./../types/event";
import { AppStore } from "../app/AppStore";
import { Factory, HCFiler, ObjectId, BaseObject, IEvent } from "@hc/core";
import { BaseWidget } from "../core/BaseWidget";
import { Entity } from "../entitys/Entity";
import { Wait } from ".";
import { getEventName } from "../utils/interactionUtils";
import { Database } from "../database";

export type TriggerType =
  | "Click"
  | "DoubleClick"
  | "RightClick"
  | "KeyDown"
  | "KeyUp"
  | "Load"
  | "SelfEvent"
  | string;

/**
 * 交互流程
 *
 * @export
 * @class Interaction
 * @extends {BaseObject}
 */
@Factory
export class Interaction extends BaseObject {
  static type = "Interaction";
  private list: BaseWidget[] = [];
  private _cleaners: (() => void)[] = [];
  constructor(
    private _triggerType: TriggerType,
    private target: ObjectId<Entity> = null
  ) {
    super();
    this.watch();
  }
  get DB(): Database {
    return this._db as Database;
  }
  /**触发事件类型 */
  get TriggerType() {
    return this._triggerType;
  }
  /**
   * 动作列表
   * @memberof Interaction
   */
  get List() {
    return [...this.list];
  }
  set List(v: BaseWidget[]) {
    this.list = v;
  }
  /**
   * 触发事件的实体
   *
   * @readonly
   * @memberof Interaction
   */
  get Target() {
    return this.target;
  }
  /**
   * 执行动作列表
   * @returns
   */
  async do() {
    const promises: Promise<unknown>[] = [];
    let startTime = 0;

    for (const rpa of this.list) {
      if (this.DB.InteractionTable.Pause) {
        await this.DB.InteractionTable.wait();
        await Promise.all(promises).then((res) => {
          promises.length = 0;
        });
      }

      //如果等待元件，先执行完毕
      if (rpa.Type === "Wait") {
        await rpa.do();
        startTime += (rpa as Wait)._time / 1000;
      } else {
        if (rpa.IsContainer) {
          const status = await rpa.do();
          if (status) {
            return;
          }
        } else {
          rpa.startTime = startTime;
          promises.push(rpa.do() as Promise<unknown>);
        }
      }
    }

    return Promise.all(promises).then((res) => {
      console.log("res: ", res);
    });
  }
  /**添加元件 */
  add(act: BaseWidget) {
    this.list.push(act);
  }
  destory(): void {
    super.destory();
    this.clearWatch();
    for (const o of this.list) {
      o.destory();
    }
  }
  readFile(filer: HCFiler): void {
    super.readFile(filer);
    filer.read();
    this._triggerType = filer.read();
    filer.readAnyArrayAndPush(this.list, filer.readObject);
    this.target = filer.readObjectId() as ObjectId<Entity>;

    //延迟监听，因为实体需要等文件加载好 才添加到场景
    setTimeout(() => {
      this.watch();
    }, 0);
  }
  writeFile(filer: HCFiler): void {
    super.writeFile(filer);
    filer.write(1);
    filer.write(this._triggerType);
    filer.writeAnyArray(this.list, filer.writeObject);
    filer.writeObjectId(this.target);
  }
  private clearWatch() {
    this._cleaners.forEach((f) => f());
    this._cleaners.length = 0;
  }
  private watch() {
    this.clearWatch();
    if (this.target?.Object && AppStore.PreviewMode) {
      let eventName = getEventName(this._triggerType);

      if (ActionManagerType[eventName]) {
        this._cleaners.push(
          this.target.Object.registerAction(eventName, async (event) => {
            if (!this.check(event.sourceEvent)) return;
            if (this.DB.InteractionTable.doing) return;
            await this.doList();
          })
        );
        return;
      }

      this._cleaners.push(
        this.target.Object.on(eventName, async (event) => {
          if (this.DB.InteractionTable.doing && event.type !== "load") {
            if (!event.isChild) return;
          }
          if (!this.check(event)) return;
          await this.doList();
        })
      );
    }
  }
  private check(event: IEvent) {
    if (!event) return true;

    if (this._triggerType === "RightClick") {
      return event.event.button === 2;
    }

    return event.event?.button !== undefined ? event.event.button <= 0 : true;
  }
  private async doList() {
    if (this.DB.InteractionTable.Pause) {
      this.DB.InteractionTable.queues.push(async () => {
        this.DB.InteractionTable.doing = true;
        await this.do();
        this.DB.InteractionTable.doing = false;
      });
    } else {
      this.DB.InteractionTable.doing = true;
      await this.do();
      this.DB.InteractionTable.doing = false;
    }
  }
}
