import { HCFiler, IVector3 } from "@hc/core";

import {
  Animation,
  AnimationGroup,
  ArcRotateCamera,
  ArcRotateCameraMouseWheelInput,
  Camera,
  IWheelEvent,
  Mesh,
  Observable,
  Scalar,
  Scene,
  Vector3,
} from "../../babylon";
import { DEFAULT_BETA, DEFAULT_RADIUS } from "../../contants";
import {
  DEFAULT_ALPHA,
  ORTH_SIZE,
  SKYBOX_SIZE,
} from "../../contants/constants";
import { AppEvent } from "../../core/AppEvent";
import { Entity } from "../../entitys";
import {
  IAnimateReturn,
  ICameraRotationAnimation,
} from "../../types/animation.types";
import { CameraEnable, CameraViewMode } from "../../types/camera.types";
import { UndefinedType } from "../../types/types";
import { isMobile } from "../../utils/os";
import {
  iVector2Vector,
  setVecLike2Vector,
  vec2Vec3Like,
} from "../../utils/vector";
import { BaseCamera } from "./BaseCamera";
import { CustomArcRotateCameraPointersInput } from "./CustomArcRotateCameraPointersInput";
import { ArcRotateCameraTrackpadInput } from "./trackpadInput";

export class ArcRotateCameraControl extends BaseCamera {
  protected _camera: ArcRotateCamera;
  protected _pointerInput: CustomArcRotateCameraPointersInput;
  private _radius = DEFAULT_RADIUS;
  private _alpha = DEFAULT_ALPHA;
  private _beta = DEFAULT_BETA;
  protected _lowerRadiusLimit = 0.01;
  protected _upperRadiusLimit: number;
  private _lowerAlphaLimit: number;
  private _upperAlphaLimit: number;
  private _lowerBetaLimit: number;
  private _upperBetaLimit: number;
  private _panningAxis = new Vector3(1, 1, 0);
  private _originPanTarget = Vector3.Zero();
  private _panDistance: number = null;
  private _minZ = 1;
  private _maxZ = SKYBOX_SIZE;
  public onRadiusChangeObserver = new Observable<ArcRotateCamera>();
  constructor(protected _scene: Scene, protected _canvas: HTMLCanvasElement) {
    super(_scene, _canvas);
    if (!_scene || !_canvas) return;
    this.init();
    this._camera.onViewMatrixChangedObservable.add((evt: ArcRotateCamera) => {
      this._alpha = evt.alpha;
      this._beta = evt.beta;
      this._target.copyFrom(evt.target);

      if (evt.radius !== this._radius) {
        this.onRadiusChangeObserver.notifyObservers(evt);
      }
      if (this._mode === Camera.PERSPECTIVE_CAMERA) this._radius = evt.radius;

      AppEvent.trigger({ type: "CameraUpdate", target: this as any });
    });
  }

  get Alpha() {
    return this._alpha;
  }
  set Alpha(v: number) {
    this._alpha = v;
    this._camera.alpha = v;
  }
  get Beta() {
    return this._beta;
  }
  set Beta(v: number) {
    this._beta = v;
    this._camera.beta = v;
  }
  get Aspect() {
    return this._canvas.width / this._canvas.height;
  }
  get EnablePan() {
    return !!(this._enable & CameraEnable.Pan);
  }
  set EnablePan(v: boolean) {
    if (v) {
      this._enable |= CameraEnable.Pan;
    } else {
      this._enable &= ~CameraEnable.Pan;
    }
    this._pointerInput.enablePan = v;
  }
  get EnableRotate() {
    return !!(this._enable & CameraEnable.Roate);
  }
  set EnableRotate(v: boolean) {
    if (v) {
      this._enable |= CameraEnable.Roate;
    } else {
      this._enable &= ~CameraEnable.Roate;
    }
    this._pointerInput.enableRotate = v;
  }
  get EnableZoom() {
    return !!(this._enable & CameraEnable.Zoom);
  }
  set EnableZoom(v: boolean) {
    if (v === this.EnableZoom) return;
    if (v) {
      this._enable |= CameraEnable.Zoom;
    } else {
      this._enable &= ~CameraEnable.Zoom;
    }
    if (this._mode === Camera.PERSPECTIVE_CAMERA) {
      if (v) {
        this._camera.inputs.attached.mousewheel.attachControl();
      } else this._camera.inputs.attached.mousewheel.detachControl();
    }
  }
  get Radius() {
    return this._radius;
  }
  set Radius(v: number) {
    this._radius = v;
    this._camera.radius = v;
  }
  get IsFlying() {
    return this._flying;
  }
  get Position() {
    return {
      x: this._camera.position.x,
      y: this._camera.position.y,
      z: this._camera.position.z,
    };
  }
  set Position(v: IVector3) {
    const position = new Vector3();
    setVecLike2Vector(position, v);
    this._camera.setPosition(position);
    this._position.copyFrom(position);
  }
  get Target() {
    return { x: this._target.x, y: this._target.y, z: this._target.z };
  }
  set Target(v: IVector3) {
    setVecLike2Vector(this._target, v);
    this._camera.setTarget(this._target);
  }
  get LowerRadiusLimit() {
    return this._lowerRadiusLimit;
  }
  set LowerRadiusLimit(v: number) {
    if (v === this._lowerRadiusLimit) return;
    this._lowerRadiusLimit = v;
    this.update();
  }
  get UpperRadiusLimit() {
    return this._upperRadiusLimit;
  }
  set UpperRadiusLimit(v: number) {
    if (v === this._upperRadiusLimit) return;
    this._upperRadiusLimit = v;
    this.update();
  }
  get LowerAlphaLimit() {
    return this._lowerAlphaLimit;
  }
  set LowerAlphaLimit(v: number) {
    if (v === this._lowerAlphaLimit) return;
    this._lowerAlphaLimit = v;
    this.update();
  }
  get UpperAlphaLimit() {
    return this._upperAlphaLimit;
  }
  set UpperAlphaLimit(v: number) {
    if (v === this._upperAlphaLimit) return;
    this._upperAlphaLimit = v;
    this.update();
  }
  get LowerBetaLimit() {
    return this._lowerBetaLimit;
  }
  set LowerBetaLimit(v: number) {
    if (v === this._lowerBetaLimit) return;
    this._lowerBetaLimit = v;
    this.update();
  }
  get UpperBetaLimit() {
    return this._upperBetaLimit;
  }
  set UpperBetaLimit(v: number) {
    if (v === this._upperBetaLimit) return;
    this._upperBetaLimit = v;
    this.update();
  }
  get PanningAxis() {
    return vec2Vec3Like(this._panningAxis);
  }
  set PanningAxis(v: IVector3) {
    setVecLike2Vector(this._panningAxis, v);
    this.update();
  }
  get OriginPanTarget() {
    return vec2Vec3Like(this._originPanTarget);
  }
  set OriginPanTarget(v: IVector3) {
    setVecLike2Vector(this._originPanTarget, v);
    this.update();
  }
  get PanDistance() {
    return this._panDistance;
  }
  set PanDistance(v: number) {
    if (v === this._panDistance) return;
    this._panDistance = v;
    this.update();
  }
  get MinZ() {
    return this._minZ;
  }
  set MinZ(v: number) {
    if (v === this._minZ) return;
    this._minZ = v;
    this.update();
  }
  get MaxZ() {
    return this._maxZ;
  }
  set MaxZ(v: number) {
    if (v === this._maxZ) return;
    this._maxZ = v;
    this.update();
  }

  /**
   * 使用自然缩放
   *
   * 使用捏合手势进行场景缩放的时候更加跟手
   */
  get UseNaturalPinchZoom() {
    return this._pointerInput.useNaturalPinchZoom;
  }
  set UseNaturalPinchZoom(v: boolean) {
    this._pointerInput.useNaturalPinchZoom = v;
  }

  /**
   * 使用单指触控手势来控制摄像头的旋转
   */
  get UseTouchForRotating() {
    return this._pointerInput.useTouchForRotating;
  }
  set UseTouchForRotating(v: boolean) {
    this._pointerInput.useTouchForRotating = v;
  }

  zoomTo(target: Vector3, size: Vector3) {
    this._camera.setTarget(new Vector3(target.x, target.y));

    if (this.Mode === Camera.ORTHOGRAPHIC_CAMERA) {
      this.zoomScale = 1.2;
      this.Size = Math.max(...size.asArray()) / 2;
      this._camera.position = new Vector3(target.x, target.y, 1);
    } else {
      this._camera.alpha = 0;
      this._camera.beta = 0;

      this._camera.radius = Math.max(...size.asArray()) * this.Aspect;
      this._camera.position = new Vector3(
        target.x,
        target.y,
        this._camera.radius
      );
    }
  }
  zoomByBoundBox(min: Vector3, max: Vector3, autoLimit = false) {
    const camera = this._camera;
    camera.useFramingBehavior = true;
    super.zoomByBoundBox(min, max, autoLimit);
    camera.useFramingBehavior = false;
    if (!autoLimit) {
      camera.lowerRadiusLimit = this._lowerRadiusLimit;
    }
  }
  zoomAll(autoLimit = false) {
    const filterFun = (mesh: Mesh) =>
      mesh.isVisible &&
      mesh.isEnabled() &&
      mesh.name !== "WORLD_GROUND" &&
      !mesh
        .getBoundingInfo()
        .boundingBox.extendSize.equalsWithEpsilon(Vector3.Zero());

    if (this._scene.meshes.filter(filterFun).length) {
      const worldExtends = this._scene.getWorldExtends(filterFun);
      const { max, min } = worldExtends;
      if (max.equalsWithEpsilon(min, 1e-3)) {
        return;
      }

      this.zoomByBoundBox(min, max, autoLimit);
    }
  }

  init() {
    const camera = new ArcRotateCamera(
      "MainArcRotateCamera",
      this._alpha,
      this._beta,
      this._radius,
      this._target,
      this._scene
    );

    this._camera = camera;
    camera.mode = this._mode;
    camera.wheelPrecision = this.wheelSpeed;
    camera.wheelDeltaPercentage = this.wheelDeltaPercentage;
    camera.maxZ = this._maxZ;
    camera.minZ = this._minZ;
    camera.inertia = 0.5;
    camera.panningInertia = 0.5;

    camera.lowerRadiusLimit = this._lowerRadiusLimit;

    // Positions the camera overwriting alpha, beta, radius
    // camera.setPosition(new Vector3(0, 0, this._radius));

    if (!isMobile()) {
      this._pointerInput = new CustomArcRotateCameraPointersInput();
      // 平移，缩放二选一
      this._pointerInput.multiTouchPanAndZoom = false;
      camera.inputs.removeByType("ArcRotateCameraPointersInput");

      this._pointerInput.enableRotate = this.EnableRotate;
      this._pointerInput.enablePan = this.EnablePan;

      camera.inputs.add(this._pointerInput);

      camera.inputs.removeByType("ArcRotateCameraMouseWheelInput");

      camera.inputs.add(new ArcRotateCameraTrackpadInput(true));
    } else {
      this._pointerInput = camera.inputs.attached["pointers"] as any;
      this._pointerInput.panningSensibility = 1;
      this._pointerInput.pinchPrecision = 0.1;

      this._pointerInput.multiTouchPanAndZoom = false;
    }

    const wheelInput = camera.inputs.attached
      .mousewheel as ArcRotateCameraMouseWheelInput;

    if (wheelInput)
      wheelInput.customComputeDeltaFromMouseWheel = (
        wheelDelta: number,
        input: ArcRotateCameraMouseWheelInput,
        event: IWheelEvent
      ) => {
        if (!this.EnableZoom) return 0;
        let delta = 0;
        if (this.wheelDeltaPercentage && !event.altKey) {
          delta = this._computeDeltaFromMouseWheelLegacyEvent(
            wheelDelta,
            camera.radius
          );

          // If zooming in, estimate the target radius and use that to compute the delta for inertia
          // this will stop multiple scroll events zooming in from adding too much inertia
          if (delta > 0) {
            let estimatedTargetRadius = camera.radius;
            let targetInertia = camera.inertialRadiusOffset + delta;
            for (let i = 0; i < 20 && Math.abs(targetInertia) > 0.001; i++) {
              estimatedTargetRadius -= targetInertia;
              targetInertia *= camera.inertia;
            }
            estimatedTargetRadius = Scalar.Clamp(
              estimatedTargetRadius,
              0,
              Number.MAX_VALUE
            );
            delta = this._computeDeltaFromMouseWheelLegacyEvent(
              wheelDelta,
              estimatedTargetRadius
            );
          }
        } else {
          delta = wheelDelta / (camera.wheelPrecision * 40);
        }

        return delta;
      };

    if (this._mode === Camera.ORTHOGRAPHIC_CAMERA) {
      this.initOrthCamera();
    } else {
      this.initPerCamera();
    }
  }
  set WheelSpeed(v: number) {
    this._camera.wheelPrecision = this.wheelSpeed;
  }
  get Mode() {
    return this._mode;
  }
  /**0-透视相机 1-正交相机 */
  set Mode(v: number) {
    if (v !== Camera.ORTHOGRAPHIC_CAMERA && v !== Camera.PERSPECTIVE_CAMERA)
      return;

    this._mode = v;
    this._camera.mode = v;
    if (v === Camera.ORTHOGRAPHIC_CAMERA) {
      this._camera.radius = ORTH_SIZE;
      this.initOrthCamera();
    } else {
      this._camera.radius = this._radius;
      this.initPerCamera();
    }
  }
  set ViewMode(v: CameraViewMode) {
    switch (v) {
      case CameraViewMode.Up:
        this._camera.position = this._camera.target
          .clone()
          .add(new Vector3(0, 0, 10));
        break;
      case CameraViewMode.Down:
        this._camera.position = this._camera.target
          .clone()
          .add(new Vector3(0, 0, -10));
        break;
      case CameraViewMode.Left:
        this._camera.position = this._camera.target
          .clone()
          .add(new Vector3(-10));
        break;
      case CameraViewMode.Right:
        this._camera.position = this._camera.target
          .clone()
          .add(new Vector3(10));
        break;
      case CameraViewMode.Front:
        this._camera.position = this._camera.target
          .clone()
          .add(new Vector3(0, 10));
        break;
      case CameraViewMode.Back:
        this._camera.position = this._camera.target
          .clone()
          .add(new Vector3(0, -10));
        break;
      case CameraViewMode.SW:
        this._camera.position = this._camera.target
          .clone()
          .add(new Vector3(10, 10, 10));
        break;
      default:
        break;
    }
  }

  initPerCamera() {
    if (this.customWheel) {
      this._scene.onPointerObservable.remove(this.customWheel);
      this.customWheel = null;
      if (this.EnableZoom)
        this._camera.inputs.attached.mousewheel.attachControl();
    }
  }
  attachControl() {
    // This attaches the camera to the canvas
    this._camera.attachControl(this._canvas, true, true);
  }
  detachControl() {
    this._camera.detachControl();
  }
  lookAt(target: IVector3) {
    this._camera.setTarget(iVector2Vector(target));
  }
  rotateAround(
    option: ICameraRotationAnimation
  ): UndefinedType<IAnimateReturn> {
    const {
      target,
      angleX,
      angleY,
      frame = 30,
      time = 10,
      loop = false,
    } = option;

    if (!angleX && !angleY) {
      console.error("angleX和angleY至少有一个必填");
      return;
    }

    if (target) {
      this._camera.target.copyFrom(iVector2Vector(target));
    }

    const group = new AnimationGroup("camera-around-group");
    if (angleX) {
      const rotate = new Animation(
        "camera-around",
        "alpha",
        frame,
        Animation.ANIMATIONTYPE_FLOAT
      );

      let origin = this._camera.inertialAlphaOffset;

      rotate.setKeys([
        {
          frame: 0,
          value: 0,
        },
        {
          frame: (time / 2) * frame,
          value: angleX / 2,
        },
        {
          frame: time * frame,
          value: angleX,
        },
      ]);
      group.addTargetedAnimation(rotate, this._camera);
    }
    if (angleY) {
      const rotate = new Animation(
        "camera-around",
        "beta",
        frame,
        Animation.ANIMATIONTYPE_FLOAT
      );

      rotate.setKeys([
        {
          frame: 0,
          value: 0,
        },
        {
          frame: (time / 2) * frame,
          value: angleY / 2,
        },
        {
          frame: time * frame,
          value: angleY,
        },
      ]);
      group.addTargetedAnimation(rotate, this._camera);
    }

    group.play(loop);

    return {
      clear: () => {
        group.dispose();
        this._animateGroup = null;
      },
      stop: () => group.stop(),
      pause: () => group.pause(),
      reset: () => group.reset(),
      restart: () => group.restart(),
    };
  }
  moveTo(option: {
    alpha?: number;
    beta?: number;
    radius?: number;
    target?: IVector3;
    time?: number;
    entity?: Entity;
    type?: string;
  }) {
    let { alpha, beta, radius, target, time = 1, entity } = option;
    if (
      alpha === undefined &&
      beta === undefined &&
      radius === undefined &&
      target === undefined
    )
      return;

    const group = new AnimationGroup("camera-move-to", this._scene);
    const frame = 30;

    if (alpha !== undefined) {
      const alAnimation = new Animation(
        "alpha-a",
        "alpha",
        frame,
        Animation.ANIMATIONTYPE_FLOAT
      );
      alAnimation.setKeys([
        {
          frame: 0,
          value: this._camera.alpha,
        },
        {
          frame: time * frame,
          value: alpha,
        },
      ]);

      group.addTargetedAnimation(alAnimation, this._camera);
    }

    if (beta !== undefined) {
      const btAnimation = new Animation(
        "beta-a",
        "beta",
        frame,
        Animation.ANIMATIONTYPE_FLOAT
      );
      btAnimation.setKeys([
        {
          frame: 0,
          value: this._camera.beta,
        },
        {
          frame: time * frame,
          value: beta,
        },
      ]);

      group.addTargetedAnimation(btAnimation, this._camera);
    }
    if (radius !== undefined) {
      const btAnimation = new Animation(
        "radius-a",
        "radius",
        frame,
        Animation.ANIMATIONTYPE_FLOAT
      );
      btAnimation.setKeys([
        {
          frame: 0,
          value: this._camera.radius,
        },
        {
          frame: time * frame,
          value: radius,
        },
      ]);

      group.addTargetedAnimation(btAnimation, this._camera);
    }

    if (!target && entity) {
      const { extendSize, minimumWorld, maximumWorld } = entity.BoundingBox;
      if (!extendSize.equalsWithEpsilon(Vector3.Zero())) {
        const bottom = minimumWorld.y;
        const top = maximumWorld.y;
        const zoomTargetY = bottom + (top - bottom) * 0.5;
        const radiusWorld = maximumWorld.subtract(minimumWorld).scale(0.5);
        const centerWorld = minimumWorld.add(radiusWorld);
        target = { x: centerWorld.x, y: zoomTargetY, z: centerWorld.z };
      }
    }

    if (target !== undefined) {
      const btAnimation = new Animation(
        "target-a",
        "target",
        frame,
        Animation.ANIMATIONTYPE_VECTOR3
      );
      btAnimation.setKeys([
        {
          frame: 0,
          value: this._camera.target,
        },
        {
          frame: time * frame,
          value: iVector2Vector(target),
        },
      ]);

      group.addTargetedAnimation(btAnimation, this._camera);
    }
    return {
      start: () => {
        group.start();
        return new Promise((res) => group.onAnimationEndObservable.add(res));
      },
      clear: () => {
        group.dispose();
        this._animateGroup = null;
      },
      stop: () => group.stop(),
      pause: () => group.pause(),
      reset: () => group.reset(),
      restart: () => group.restart(),
    };
  }
  update() {
    this._camera.mode = this._mode;
    this._camera.setTarget(this._target);
    this._camera.alpha = this._alpha;
    this._camera.beta = this._beta;

    this._camera.lowerRadiusLimit = this._lowerRadiusLimit;
    this._camera.upperRadiusLimit = this._upperRadiusLimit;
    this._camera.lowerAlphaLimit = this._lowerAlphaLimit;
    this._camera.upperAlphaLimit = this._upperAlphaLimit;
    this._camera.lowerBetaLimit = this._lowerBetaLimit;
    this._camera.upperBetaLimit = this._upperBetaLimit;

    this._camera.minZ = this._minZ;
    this._camera.maxZ = this._maxZ;

    this._camera.panningOriginTarget.copyFrom(this._originPanTarget);
    this._camera.panningAxis.copyFrom(this._panningAxis);

    this._camera.panningDistanceLimit = this.PanDistance;

    if (this._mode === Camera.PERSPECTIVE_CAMERA)
      this._camera.radius = this._radius;
    else this._camera.radius = ORTH_SIZE;

    this._pointerInput.enablePan = this.EnablePan;
    this._pointerInput.enableRotate = this.EnableRotate;

    this._pointerInput.multiTouchPanning = this.EnablePan;

    this._pointerInput.pinchZoom = this.EnableZoom;

    if (this._mode === Camera.PERSPECTIVE_CAMERA) {
      if (this.EnableZoom) {
        this._camera.inputs.attached.mousewheel.attachControl();
      } else this._camera.inputs.attached.mousewheel.detachControl();
    }
  }
  reset() {
    this._target.copyFrom(Vector3.Zero());
    this._alpha = DEFAULT_ALPHA;
    this._beta = DEFAULT_BETA;
    this._radius = DEFAULT_RADIUS;
    this._lowerRadiusLimit = 0.01;
    this._upperRadiusLimit = undefined;
    this._lowerAlphaLimit = undefined;
    this._upperAlphaLimit = undefined;
    this._lowerBetaLimit = undefined;
    this._upperBetaLimit = undefined;
    this._minZ = 1;
    this._maxZ = SKYBOX_SIZE;
    this.update();
  }
  writeFile(filer: HCFiler) {
    filer.write(5);
    super.writeFile(filer);
    filer.write(this._alpha);
    filer.write(this._beta);
    filer.write(this._radius);

    filer.write(this._lowerRadiusLimit);
    filer.write(this._upperRadiusLimit);

    filer.write(this._lowerAlphaLimit);
    filer.write(this._upperAlphaLimit);

    filer.write(this._lowerBetaLimit);
    filer.write(this._upperBetaLimit);

    //pan limit
    filer.write(this._panDistance);
    filer.write(this._panningAxis.x);
    filer.write(this._panningAxis.y);
    filer.write(this._panningAxis.z);
    filer.write(this._originPanTarget.x);
    filer.write(this._originPanTarget.y);
    filer.write(this._originPanTarget.z);

    filer.write(this._minZ);
    filer.write(this._maxZ);
  }
  readFile(filer: HCFiler) {
    const ver = filer.read();
    if (ver > 1) {
      super.readFile(filer);
    }
    this._alpha = filer.read();
    this._beta = filer.read();
    this._radius = filer.read();

    if (ver > 2) {
      this._lowerRadiusLimit = filer.read();
      this._upperRadiusLimit = filer.read();

      this._lowerAlphaLimit = filer.read();
      this._upperAlphaLimit = filer.read();

      this._lowerBetaLimit = filer.read();
      this._upperBetaLimit = filer.read();
    }

    if (ver > 3) {
      this._panDistance = filer.read();
      this._panningAxis.x = filer.read();
      this._panningAxis.y = filer.read();
      this._panningAxis.z = filer.read();
      this._originPanTarget.x = filer.read();
      this._originPanTarget.y = filer.read();
      this._originPanTarget.z = filer.read();
    }

    if (ver > 4) {
      this._minZ = filer.read();
      this._maxZ = filer.read();
    }

    if (this._mode === Camera.ORTHOGRAPHIC_CAMERA) {
      this.initOrthCamera();
    }

    this.update();
  }
  private _computeDeltaFromMouseWheelLegacyEvent(
    mouseWheelDelta: number,
    radius: number
  ) {
    let delta = 0;
    const wheelDelta =
      mouseWheelDelta * 0.01 * this.wheelDeltaPercentage * radius;
    if (mouseWheelDelta > 0) {
      delta = wheelDelta / (1.0 + this.wheelDeltaPercentage);
    } else {
      delta = wheelDelta * (1.0 + this.wheelDeltaPercentage);
    }
    return delta;
  }
}
