import {
  Matrix,
  Mesh,
  MeshBuilder,
  Vector3,
  TransformNode,
} from "../../babylon";
import { Entity } from "../../entitys/Entity";
import { Viewer } from "../../viewer/Viewer";

export class SelectBase {
  selectList = new Set<Mesh | TransformNode>();
  private _tempPlane: Mesh;
  constructor(protected _viewer: Viewer) {}
  get Plane() {
    if (this._tempPlane) return this._tempPlane;
    const mesh = MeshBuilder.CreatePlane("get-point", { size: 1000 });
    mesh.setEnabled(false);
    this._tempPlane = mesh;
    return mesh;
  }
  select(nodes: Mesh[]) {
    //
  }
  destoryTempPlane() {
    if (this._tempPlane) {
      this._tempPlane.dispose();
      this._tempPlane = null;
    }
  }
  world2screen(pt: Vector3) {
    const p = Vector3.Project(
      pt,
      Matrix.IdentityReadOnly,
      this._viewer.Scene.getTransformMatrix(),
      this._viewer.Camera.viewport.toGlobal(
        this._viewer.Scene.getEngine().getRenderWidth(),
        this._viewer.Scene.getEngine().getRenderHeight()
      )
    );
    p.z = 0;
    return p;
  }
  screen2World(p: Vector3, isDestoryPlane = true) {
    const ray = this._viewer.Scene.createPickingRay(
      p.x,
      p.y,
      this._viewer.Scene.getViewMatrix(),
      this._viewer.cameraManager.Camera,
      true
    );
    if (this._viewer.ground) this._viewer.ground.isPickable = true;
    //@ts-ignore
    const pickinfo = ray.intersectsMesh(this._viewer.ground || this.Plane);

    if (this._viewer.ground) this._viewer.ground.isPickable = false;

    if (isDestoryPlane) this.destoryTempPlane();

    if (pickinfo.hit) return pickinfo.pickedPoint;

    return null;
  }
  get SelectEntitys() {
    const enitys: Entity[] = [];
    for (const s of this.selectList) {
      enitys.push(s.metadata.Entity);
    }
    return enitys;
  }
  get SelectMeshs() {
    return [...this.selectList];
  }
}
