import { Factory, HCFiler } from "@hc/core";

import { AppStore } from "../app/AppStore";
import { AppConfig } from "../app/appConfig";
import { Texture } from "../babylon";
import { AppEvent } from "../core/AppEvent";
import { BaseTextureRecord } from "../core/BaseTextureRecord";
import { enableUpdateMaterialDirtyMechanismOnce } from "../utils/babylonUtils";
import { deg2rad, rad2Deg } from "../utils/math";
import { UUID_REG } from "../utils/regx";

@Factory
export class TextureRecord extends BaseTextureRecord {
  static type = "TextureRecord";
  protected texture: Texture;

  constructor(private url = "", texture?: Texture) {
    super();
    if (texture) {
      this.texture = texture;
      this.readFromTexture();
    } else {
      this.texture = new Texture(this.Url2, AppStore.Scene);
      this.texture.isBlocking = false;
      this.texture.onLoadObservable.add(this.onLoad);
      this.texture.name = "TextureRecord";
      this.triggerLoading();
    }
  }
  get Texture() {
    return this.texture;
  }
  get Url() {
    return this.url;
  }
  set Url(url: string) {
    if (url === this.url) return;
    this.writeSnapshoot();
    this.url = url;
    this.triggerLoading();
    this.update();
  }
  get Url2() {
    if (!this.url) return "";

    if (this.url.startsWith("http")) return this.url;

    if (UUID_REG.test(this.url)) {
      return AppConfig.assetURL + this.url;
    } else {
      return AppConfig.Texture_URL + this.url;
    }
  }

  update() {
    enableUpdateMaterialDirtyMechanismOnce(this.texture.getScene());
    if (this.texture.url !== this.Url2) {
      this._isLoading = true;
      this.texture.updateURL(this.Url2, null, this.onLoad);
    }
    this.texture.name = this._name;
    this.texture.wrapU = this.wrapU;
    this.texture.wrapV = this.wrapV;
    this.texture.uScale = this.scale[0];
    this.texture.vScale = this.scale[1];

    this.texture.uOffset = this.offset[0];
    this.texture.vOffset = this.offset[1];

    this.texture.uAng = deg2rad(this.rotation[0]);
    this.texture.vAng = deg2rad(this.rotation[1]);
    this.texture.wAng = deg2rad(this.rotation[2]);
  }
  async waitUpdate(): Promise<boolean> {
    return new Promise((res) => {
      if (!this._isLoading) {
        res(true);
        return true;
      }
      this.once("load", () => {
        res(true);
      });
    });
  }

  writeFile(file: HCFiler) {
    super.writeFile(file);
    file.write(1);
    file.write(this.url);
  }
  readFile(file: HCFiler) {
    super.readFile(file);
    const ver = file.read();
    this.url = file.read();

    this.update();
  }
  readFromTexture() {
    this.wrapU = this.texture.wrapU as 0 | 1 | 2;
    this.wrapV = this.texture.wrapV as 0 | 1 | 2;
    this.scale[0] = this.texture.uScale;
    this.scale[1] = this.texture.vScale;

    this.offset[0] = this.texture.uOffset;
    this.offset[1] = this.texture.vOffset;

    this.rotation[0] = rad2Deg(this.texture.uAng);
    this.rotation[1] = rad2Deg(this.texture.vAng);
    this.rotation[2] = rad2Deg(this.texture.wAng);
  }
  private triggerLoading = () => {
    this.trigger({ type: "loading", title: "加载贴图", target: this });
    AppEvent.trigger({ type: "loading", title: "加载贴图", target: this });
  };
  private onLoad = (tx?: Texture) => {
    this._isLoading = false;
    this.trigger({ type: "load", target: this, object: tx ?? this.texture });
    AppEvent.trigger({
      type: "load",
      target: this,
      object: tx ?? this.texture,
    });
  };
}
