import { StandardMaterial } from "@babylonjs/core/Materials/standardMaterial";
import { Entity } from "./Entity";
import { Color4, Mesh, PBRMaterial, VertexData } from "../babylon";
import { RenderType } from "../types/renderTypes";
import { CommonMaterials } from "../materials/commonMaterials";
import { ColorPalette, getColor3 } from "./../utils/color";
import { AppEvent } from "../core";

/**
 * 3D基础实体基类
 *
 * @export
 * @abstract
 * @class Solid3D
 * @extends {Entity}
 */
export abstract class Solid3D extends Entity {
  protected static VertexDataCache = new Map<string, VertexData>();
  constructor() {
    super();
  }
  get VertextData(): VertexData {
    return null;
  }
  initDrawObject(renderType = RenderType.Conceptual) {
    const mesh = new Mesh(this.UniqueName, Entity.Scene);
    const vx = this.VertextData;
    vx.applyToMesh(mesh);

    if (renderType === RenderType.Wireframe) {
      mesh.enableEdgesRendering();
      mesh.edgesColor = Color4.FromArray(ColorPalette[this.ColorIndex]);
    }
    this.updateMaterial(renderType, mesh);

    mesh.receiveShadows = true;

    //推迟触发加载完成事件,浏览器空闲时触发加载完成事件
    requestIdleCallback(() => {
      AppEvent.trigger({ type: "load", target: this });
      this.trigger({ type: "load", target: this });
    });

    return mesh;
  }
  updateDrawObject(renderType: RenderType, obj: Mesh) {
    if (!obj) return;
    obj.geometry.setAllVerticesData(this.VertextData);
    obj.visibility = this._opacity;
  }
  updateMaterial(type?: RenderType, obj?: Mesh) {
    const m = obj ?? (this.RenderObject as Mesh);
    const renderType = type ?? this.RenderType;
    switch (renderType) {
      case RenderType.Wireframe:
        m.material = CommonMaterials.getWireframeStandMaterial(
          this._colorIndex,
          Entity.Scene
        );
        break;
      case RenderType.Physical:
      case RenderType.Conceptual: {
        const mtl = CommonMaterials.getStandMaterial(
          this.ColorIndex,
          Entity.Scene
        );
        m.material = mtl;
        break;
      }
      default:
        break;
    }

    if (this.Material) {
      m.material = this.Material.Material;
      if (this.Color) {
        if (m.material instanceof StandardMaterial) {
          m.material.diffuseColor = getColor3(this.Color);
        } else if (m.material instanceof PBRMaterial) {
          m.material.albedoColor = getColor3(this.Color);
        }
      }
    }

    for (const [name, mid] of this._materialMap) {
      if (m.id === name) {
        if (mid.Object?.Material) m.material = mid.Object.Material;
      }
    }
  }
}
