import { Entity } from "./../../entitys/Entity";
import { ArcRotateCameraControl } from "./ArcRotateCameraControl";
import { HCFiler, ISerialize, IVector3 } from "@hc/core";
import { FreeCameraControl } from "./FreeCameraControl";
import { Scene } from "../../babylon";
import { BaseCamera } from "./BaseCamera";

export enum CameraType {
  ArcRotate = 0,
  FreeCamera = 1,
  Follow = 2,
}

export class CameraManager implements ISerialize {
  private _type = CameraType.ArcRotate;
  private _control: BaseCamera;
  public ArcRotateControl: ArcRotateCameraControl;
  public FreeControl: FreeCameraControl;
  constructor(private _scene: Scene, private _canvas: HTMLCanvasElement) {
    this.init();
  }
  get Type() {
    return this._type;
  }
  set Type(v: CameraType) {
    this._type = v;
    this.switch();
  }
  get Control() {
    return this._control;
  }
  get Camera() {
    return this._control.CurrentCamera;
  }
  get ArcRotateCamera() {
    return this.ArcRotateControl.CurrentCamera;
  }
  get FreeCamera() {
    return this.FreeControl.CurrentCamera;
  }
  get Alpha() {
    return this._control.Alpha;
  }
  set Alpha(v) {
    this._control.Alpha = v;
  }
  get Beta() {
    return this._control.Beta;
  }
  set Beta(v) {
    this._control.Beta = v;
  }
  get Radius() {
    return this.ArcRotateControl.Radius;
  }
  set Radius(v: number) {
    this.ArcRotateControl.Radius = v;
  }
  get Position() {
    return this.Control.Position;
  }
  set Position(v: IVector3) {
    this.Control.Position = v;
  }
  get Target() {
    return this.Control.Target;
  }
  set Target(v) {
    this.Control.Target = v;
  }
  get Mode() {
    return this.Control.Mode;
  }
  set Mode(v) {
    this.Control.Mode = v;
  }
  get ZoomScale() {
    return this.Control.zoomScale;
  }
  init() {
    this.ArcRotateControl = new ArcRotateCameraControl(
      this._scene,
      this._canvas
    );
    this.FreeControl = new FreeCameraControl(this._scene, this._canvas);
    this._control = this.ArcRotateControl;
    this._control.active();
  }
  switch() {
    this._control.detachControl();
    switch (this._type) {
      case CameraType.ArcRotate:
        this._control = this.ArcRotateControl;
        break;
      case CameraType.FreeCamera:
        this._control = this.FreeControl;
        break;
      case CameraType.Follow:
    }
    this._control.active();
    this.switchEvent(this);
  }
  switchEvent(c: CameraManager) {}
  zoomAll(autoLimit = false) {
    this._control.zoomAll(autoLimit);
  }
  zoomToEntity(en: Entity) {
    this._control.zoomToEntity(en);
  }
  reset() {
    this.ArcRotateControl.reset();
    this.FreeControl.reset();
  }
  destory() {
    this.ArcRotateControl.destory();
    this.FreeControl.destory();
  }

  writeFile(file: HCFiler): void {
    file.write(1);
    file.write(this._type);
    this.ArcRotateControl.writeFile(file);
    this.FreeControl.writeFile(file);
  }
  readFile(file: HCFiler): void {
    file.read();
    this._type = file.read();
    this.ArcRotateControl.readFile(file);
    this.FreeControl.readFile(file);
    this.switch();
  }
}
