import { AppConfig, AppEvent, AppStore, CMD } from "@hc/engine";
import { CommandEnum } from "~/commands/commandEnum";
import { App } from "~/intance";
import { getUUID } from "~/utils/common";
import { ImportScript } from "~/utils/importScript";
import { message } from "~/utils/message";
import { HC2DDom } from "./HC2DDom";

export interface IDataBindings {
  attr: string;
  bindable: boolean;
  defaultValue: any;
  description: string;
  name: string;
  valueType: string;
}

export interface IHC2D {
  DataBindings: IDataBindings[];
  updateAttr: (obj) => void;
}

export let ht: any;
let loadPromise: Promise<any>;

const waitList: ((n: any) => void)[] = [];

function wait() {
  return new Promise((res) => {
    waitList.push(res);
  });
}

export function initHT() {
  if (!ht) {
    if (!loadPromise) {
      loadPromise = ImportScript([
        "https://am-img.gkiiot.com/editor/libs/echarts.min.js",
        "https://am-img.gkiiot.com/editor/libs/hc.min.js",
        "https://am-img.gkiiot.com/img/editor/libs/echarts-tooltip-carousel.js",
        "https://cdn.bootcdn.net/ajax/libs/hls.js/1.2.4/hls.js",
      ])
        .then(() => {
          return ImportScript([
            "https://am-img.gkiiot.com/editor/libs/plug.min.js",
          ]);
        })
        .then(() => {
          return ImportScript([
            "https://am-img.gkiiot.com/editor/libs/hc-ui-carousel.js",
          ]);
        });
      return loadPromise.then((res) => {
        ht = window.ht;

        window["storagePrefix"] = AppConfig.assetURL;

        ht.Default.convertURL = function (url: string) {
          let storagePrefix = AppConfig.assetURL;
          if (
            storagePrefix &&
            url &&
            !/^data:image/.test(url) &&
            !/^http/.test(url) &&
            !/^https/.test(url)
          ) {
            url = storagePrefix + "/" + url;
          }
          return url;
        };
        waitList.forEach((w) => w(true));
        waitList.length = 0;
        return ht;
      });
    } else {
      return wait();
    }
  } else return ht;
}

export const HCCache = new Set<string>();

const TimeStap = Date.now();

/**加入时间戳避免ht缓存 */
export const getUrl = (url: string) => {
  const separator = url.indexOf("?") !== -1 ? "&" : "?";
  return url + separator + "timestap=" + TimeStap;
};

export async function getGraphView() {
  await initHT();
  const dm = new ht.DataModel();
  dm.a("unshow", true);
  const graphView = new ht.graph.GraphView(dm);
  graphView.getSelectWidth = () => 0;
  graphView.setMovableFunc((data) => false);
  graphView.setPannable(false);
  graphView.setRectSelectable(false);
  // graphView.setEditable(true);
  graphView.setScrollBarVisible(false);
  graphView.handleScroll = () => false;
  graphView.onDataClicked = (
    data: { target3d: HC2DDom },
    event: MouseEvent
  ) => {
    if (!data) return;
    AppEvent.trigger({
      type: "click",
      target: data.target3d,
      event: event,
    });
    const hc = data.target3d as HC2DDom;
    hc.trigger({
      type: "click",
      target: data.target3d,
      event: event,
    });
    if (AppStore.PreviewMode) return;
    hc.start(event.clientX, event.clientY);
    if (App.intance.Editor.selectControl)
      App.intance.Editor.selectControl.SelectDOM = hc;
  };
  graphView.getView().addEventListener("keydown", (event: KeyboardEvent) => {
    if (event.key === "Delete") {
      CMD.commandManager.ExecCommand(CommandEnum.Delete);
      event.stopPropagation();
    } else if (event.code === "KeyC" && event.ctrlKey) {
      CMD.commandManager.ExecCommand(CommandEnum.Copy);
      event.stopPropagation();
    } else if (event.code === "KeyV" && event.ctrlKey) {
      CMD.commandManager.ExecCommand(CommandEnum.Paste);
      event.stopPropagation();
    }
  });
  return graphView;
}

export async function getHTNode(dm, url: string) {
  url = getUrl(url);
  const node = new ht.Node();
  dm.add(node);

  const u = getUUID();
  return new Promise((res) => {
    ht.Default.xhrLoad(url, function (n) {
      if (n) {
        ht.Default.setImage(u, ht.Default.parse(n));
        node.setImage(u);
        res(node);
      } else {
        message.error("2d资源未找到");
        res(null);
      }
    });
  });
}

export function getHTData(url: string) {
  return ht.Default.getImage(url);
}
