import { Animation } from "../babylon";

export function getAnimation(
  from: number,
  to: number,
  key: string,
  time: number
) {
  const keys = [
    { frame: 0, value: from },
    { frame: 30 * time, value: to },
  ];

  const animation = new Animation(
    "animation" + Date.now(),
    key,
    30,
    Animation.ANIMATIONTYPE_FLOAT,
    Animation.ANIMATIONLOOPMODE_CONSTANT
  );
  animation.setKeys(keys);

  return animation;
}
