import { AppStore } from "./../../app/AppStore";
import { Wait } from "..";
import { HCFiler, Factory } from "@hc/core";
import { BaseWidget } from "../../core/BaseWidget";
import { Compare } from "./Compare";

let NameIndex = 0;

@Factory
export class Condition extends BaseWidget {
  static type = "Condition";
  readonly IsContainer = true;
  isAll = true;
  compares: Compare[] = [];
  list: BaseWidget[] = [];
  Name = "情形" + NameIndex++;
  check() {
    this.compares.forEach((c) => (c._Params = AppStore.GlobalVariables));
    if (this.isAll) return this.compares.every((c) => c.check());
    else return this.compares.some((c) => c.check());
  }
  async do() {
    if (!this.check()) return false;
    let startTime = 0;
    const promises: Promise<unknown>[] = [];
    for (const w of this.list) {
      //如果等待元件，先执行完毕
      if (w.Type === "Wait") {
        await w.do();
        startTime += (w as Wait)._time / 1000;
      } else {
        w.startTime = startTime;
        promises.push(w.do() as Promise<unknown>);
      }
    }
    return Promise.all(promises).then(() => true);
  }
  add(wg: BaseWidget) {
    this.list.push(wg);
  }
  addCompare(cp: Compare) {
    this.compares.push(cp);
  }
  writeFile(file: HCFiler): void {
    file.write(1);
    file.write(this.isAll);
    file.write(this.Name);
    file.writeAnyArray(this.list, file.writeObject);
    file.writeAnyArray(this.compares, file.writeObject);
  }
  readFile(file: HCFiler): void {
    file.read();
    this.isAll = file.read();
    this.Name = file.read();
    file.readAnyArrayAndPush(this.list, file.readObject);
    file.readAnyArrayAndPush(this.compares, file.readObject);
  }
}
