import { Animation } from "./Animation";
import type { Entity } from "./../entitys/Entity";
import { IMovePathOption } from "../types";
import { Factory, HCFiler, IVector3 } from "@hc/core";
import { Animation as BAnimation, IAnimationKey, Vector3 } from "../babylon";

@Factory
export class MoveAnimation extends Animation {
  static type = "MoveAnimation";
  protected _name = "未命名移动";
  private _points: IVector3[] = [];
  constructor(entity: Entity, option: IMovePathOption) {
    super(entity, option);
    let { path, time = 1, loop = false, frame = 30, loopCount, speed } = option;
    this._time = time;
    this._loop = loop;
    this._frame = frame;
    this._loopCount = loopCount;
    this._speed = speed;
    this._points = Array.isArray(path) ? path : path.Points;
    if (option.name) this._name = option.name;
    this.init();
  }
  init() {
    this._entity.removeAnimation(this._name);
    const renderMesh = this.Target;

    const animate = new BAnimation(
      this._name,
      "position",
      this._frame,
      BAnimation.ANIMATIONTYPE_VECTOR3
    );
    const keyFrames: IAnimationKey[] = [];

    // let i = 0;
    // const n = this._time / this._points.length;
    let n;

    const pos = this._points;
    if (this._speed) {
      this._time = this.totalDistance(this._points) / this._speed;
    }
    for (let i = 0; i < pos.length; i++) {
      n =
        (this.totalDistance(this._points.slice(0, i + 1)) /
          this.totalDistance(this._points)) *
        this._time;
      keyFrames.push({
        frame: this._frame * n,
        value: new Vector3(pos[i].x, pos[i].y, pos[i].z),
      });
    }
    // for (const pos of this._points) {
    //   keyFrames.push({
    //     frame: this._frame * n * i++,
    //     value: new Vector3(pos.x, pos.y, pos.z),
    //   });
    // }
    animate.setKeys(keyFrames);
    renderMesh.animations.push(animate);
    this._animation = animate;
  }
  private totalDistance(path) {
    let total = 0;
    for (let i = 0; i < path.length - 1; i++) {
      total =
        total +
        this.distance(
          new Vector3(path[i].x, path[i].y, path[i].z),
          new Vector3(path[i + 1].x, path[i + 1].y, path[i + 1].z)
        );
    }
    return total;
  }
  private distance(d1, d2) {
    return Vector3.Distance(d1, d2);
  }
  writeFile(file: HCFiler) {
    super.writeFile(file);
    file.write(1);
    file.write(this._points.length);
    for (const p of this._points) {
      file.write(p.x);
      file.write(p.y);
      file.write(p.z);
    }
  }
  readFile(file: HCFiler) {
    super.readFile(file);
    file.read(); // 版本
    const count = file.read();
    this._points.length = 0;
    for (let i = 0; i < count; i++) {
      const p = { x: 0, y: 0, z: 0 };
      p.x = file.read();
      p.y = file.read();
      p.z = file.read();
      this._points.push(p);
    }
  }
}
