import { AssetContainer } from "../babylon";

export class AppCache {
  private assestPromiseCache = new Map<string, Promise<AssetContainer>>();

  getAssetPromise(url: string) {
    return this.assestPromiseCache.get(url);
  }
  setAssetPromise(url: string, asset: Promise<AssetContainer>) {
    this.assestPromiseCache.set(url, asset);
  }
  clear() {
    this.assestPromiseCache.clear();
  }
}

export const appCache = new AppCache();
