import { CreateLineSystemVertexData } from "@babylonjs/core";

import { Factory, HCFiler, IVector3 } from "@hc/core";

import { LinesMesh, MeshBuilder, Vector3 } from "../../babylon";
import { DefaultIntersectionThreshold } from "../../contants";
import { UpdateFlag } from "../../enums";
import { RenderType } from "../../types/renderTypes";
import { arrayLast } from "../../utils/array";
import { iVector2Vector, vec2Vec3Like } from "../../utils/vector";
import { Entity } from "../Entity";
import { Curve } from "./Curve";

@Factory
export class Lines extends Curve {
  static type = "Lines";
  private points: IVector3[] = [];
  constructor(option?: { points?: IVector3[] }) {
    super();
    this.points = option?.points
      ? option?.points.map(iVector2Vector)
      : [new Vector3(), new Vector3(1)];
  }
  get Points() {
    return this.points.map(iVector2Vector);
  }
  get StartPoint() {
    return iVector2Vector(this.points[0]);
  }
  get EndPoint() {
    return iVector2Vector(arrayLast(this.points));
  }
  get Length() {
    return this.points.reduce((preV, value, currentIndex) => {
      if (currentIndex === 0) return 0;
      return (
        preV +
        Vector3.Distance(
          iVector2Vector(this.points[currentIndex - 1]),
          iVector2Vector(value)
        )
      );
    }, 0);
  }
  addPoint(p: IVector3) {
    this.points.push(p);
    this.update(UpdateFlag.Geometry);
  }
  updatePointByIndex(index: number, newP: IVector3) {
    const p = this.points[index];
    if (p) {
      p.x = newP.x;
      p.y = newP.y;
      p.z = newP.z;
      this.update(UpdateFlag.Geometry);
      return true;
    }
    return false;
  }
  pop() {
    this.points.pop();
    this.update(UpdateFlag.Geometry);
  }
  initDrawObject(renderType: RenderType) {
    const lines = MeshBuilder.CreateLines(
      "line" + this.Id,
      {
        points: this.points.map((p) => iVector2Vector(p)),
        updatable: true,
      },
      Entity.Scene
    );
    lines.isNearPickable = true;
    lines.intersectionThreshold = DefaultIntersectionThreshold;
    return lines;
  }
  updateDrawObject(renderType: RenderType, obj: LinesMesh) {
    if (!obj) return;
    const vertexData = CreateLineSystemVertexData({
      lines: [this.Points],
    });

    vertexData.applyToMesh(obj);
  }
  writeFile(file: HCFiler) {
    super.writeFile(file);
    file.write(1);
    file.write(this.points.length);
    for (const p of this.points) {
      file.writeVector3(p);
    }
  }
  readFile(file: HCFiler) {
    super.readFile(file);
    const ver = file.read();
    const count = file.read();
    this.points.length = 0;
    for (let i = 0; i < count; i++) {
      const p = new Vector3();
      file.readVector3(p);
      this.points.push(p);
    }
    this.update();
  }

  getDragPoints(): Vector3[] {
    return this.Points.map((p) => Vector3.TransformCoordinates(p, this.Matrix));
  }
  moveDragPoint(vec: Vector3, index: number) {
    for (let i = 0; i < this.points.length; i++) {
      if (i === index) {
        const p = iVector2Vector(this.points[i]);
        p.addToRef(vec, p);
        Object.assign(this.points[i], vec2Vec3Like(p));
        break;
      }
    }
    this.update(UpdateFlag.Geometry);
  }
}
