import { Factory, HCFiler } from "@hc/core";
import { AppStore, BaseDom, GenerateTexture } from "@hc/engine";
import { handleObjectFunction, sleep } from "~/utils/utils";
import { getGraphView, getHTData, getHTNode, IDataBindings, IHC2D } from "./ht";

@Factory
export class HC2DDom extends BaseDom implements GenerateTexture, IHC2D {
  static type = "HC2DDom";
  private _source: string;
  private graphView;
  private hcNode;
  private _dataBindings: IDataBindings[];
  private _setAttr: Record<string, any> = {};
  constructor(option?: { url: string }) {
    super();
    if (option?.url) {
      this._source = option.url;
    }
    window["gui"] = this;
  }
  get Source() {
    return this._source;
  }
  set Source(url: string) {
    this._source = url;
  }
  get DataBindings() {
    return this._dataBindings ?? [];
  }
  get Attr() {
    return this._setAttr;
  }
  getProp(k: string) {
    if (this._setAttr[k]) {
      return this._setAttr[k];
    } else {
      const d = this.DataBindings.find((d) => d.attr === k);
      return d?.defaultValue;
    }
  }
  /**
   * 设置绑定数据值
   * @param k
   * @param value
   */
  setProp(k: string, value: any) {
    this.updateAttr({ [k]: value });
  }
  updateAttr(obj: Record<string, any>) {
    if (Object.keys(obj).length === 0 || !this.graphView || !this.hcNode)
      return;
    Object.assign(this._setAttr, obj);
    obj = JSON.parse(JSON.stringify(obj));
    obj = handleObjectFunction(obj);
    this.hcNode.a(obj);
  }
  initDrawObject() {
    const temp = document.createElement("div");
    getGraphView().then((graphView) => {
      this.graphView = graphView;
      const resolve = AppStore.Wait();
      getHTNode(graphView.dm(), this._source).then((node: any) => {
        if (!node) return;
        this.hcNode = node;
        node.target3d = this;
        const data = getHTData(node.getImage());
        this.load(data);
        resolve(true);
      });
      temp.replaceWith(graphView.getView());
      this._drawObject = graphView.getView();
      //@ts-ignore
      this._drawObject["metadata"] = { entity: this };
      this._drawObject.style.display =
        this.Visiable && !this._erase ? "block" : "none";
    });
    return temp;
  }
  updateDrawObject(obj: HTMLElement): void {
    super.updateDrawObject(obj);
    const width = this.convertValue(this._width, AppStore.MainViewer.Width);
    const height = this.convertValue(this._height, AppStore.MainViewer.Height);

    this.graphView.setWidth(width);
    this.hcNode.setWidth(width);
    this.graphView.setHeight(height);
    this.hcNode.setHeight(height);
    this.graphView.fitContent(false, 0);

    this.updateAttr(this._setAttr);
  }
  getTexture() {
    return null;
  }
  async toDataURL(blob = true): Promise<Blob | string | undefined> {
    let canvas: HTMLCanvasElement;
    const dom = this.graphView.__htmlOrderList?.[0] as HTMLElement;
    if (dom && dom instanceof HTMLElement) {
      canvas =
        this.graphView.__htmlOrderList[0].getElementsByTagName("canvas")[0];

      if (dom.nodeName === "VIDEO") {
        return;
      }
    } else {
      canvas = this.graphView.getCanvas();
    }

    if (!canvas) canvas = this.graphView.getCanvas();

    await sleep(1000);
    if (blob)
      return new Promise((res) => {
        canvas.toBlob(res, "image/jpeg");
      });
    else {
      return canvas.toDataURL("image/jpeg");
    }
  }
  writeFile(filer: HCFiler): void {
    super.writeFile(filer);
    filer.write(2);
    filer.write(this._source);
    filer.write(JSON.stringify(this._setAttr));
  }
  readFile(filer: HCFiler): void {
    super.readFile(filer);
    const ver = filer.read();
    this._source = filer.read();

    if (ver > 1) {
      this._setAttr = JSON.parse(filer.read());
    }

    this.update();
  }
  private load = (data) => {
    if (!data) return;
    const width =
      typeof data.width === "object" ? data.width.value : data.width;
    const height =
      typeof data.height === "object" ? data.height.value : data.height;

    if (!this._width) this._width = width;
    if (!this._height) this._height = height;

    this._dataBindings = data.dataBindings;

    const dom = this.graphView.getView() as HTMLElement;
    dom.style.cssText = `
    position:absolute;
    left:${this._position.x}px;
    top:${this._position.y}px;
    width:${this._width}px;
    height:${this._height}px;
    outline:none;
    `;
    if (!this.Visiable) {
      dom.style.visibility = "hidden";
    }
    setTimeout(() => {
      dom.style.visibility = "visible";
      dom.style.display = this.Visiable && !this.IsErase ? "block" : "none";
    }, 0);
    this.graphView.fitContent(false, 0);
    this.update();
  };
}
