import { LinesMesh, MeshBuilder, Vector3 } from "../../babylon";
import { Factory, HCFiler, IVector3 } from "@hc/core";
import { RenderType } from "../../types/renderTypes";
import { Curve } from "./Curve";
import { Entity } from "../Entity";
import { iVector2Vector } from "../../utils/vector";
import { DefaultIntersectionThreshold } from "../../contants";

@Factory
export class Line extends Curve {
  static type = "Line";
  private start = new Vector3();
  private end = new Vector3();
  constructor(option?: { start: IVector3; end: IVector3 }) {
    super();
    this.start = option?.start ? iVector2Vector(option.start) : this.start;
    this.end = option?.end ? iVector2Vector(option.end) : this.end;
  }
  get Points() {
    return [this.start, this.end];
  }
  get StartPoint() {
    return this.start;
  }
  get EndPoint() {
    return this.end;
  }
  get MidPoint() {
    return Vector3.Center(this.start, this.end);
  }
  get Length() {
    return Vector3.Distance(this.start, this.end);
  }
  initDrawObject(renderType: RenderType) {
    const lines = MeshBuilder.CreateLines(
      "line" + this.Id,
      {
        points: [this.start, this.end],
        updatable: true,
      },
      Entity.Scene
    );
    lines.isNearPickable = true;
    lines.intersectionThreshold = DefaultIntersectionThreshold;
    return lines;
  }
  updateDrawObject(renderType: RenderType, obj: LinesMesh) {
    if (!obj) return;
    MeshBuilder.CreateLines(
      "line" + this.Id,
      {
        points: [this.start, this.end],
        updatable: true,
        instance: obj,
      },
      Entity.Scene
    );
  }
  reverse() {
    [this.start, this.end] = [this.end, this.start];
    return this;
  }
  writeFile(file: HCFiler) {
    super.writeFile(file);
    file.write(1);
  }
  readFile(file: HCFiler) {
    super.readFile(file);
    this.update();
  }

  getDragPoints(): Vector3[] {
    return this.Points.map((p) => Vector3.TransformCoordinates(p, this.Matrix));
  }
  moveDragPoint(vec: Vector3, index: number) {}
}
