import { Entity } from "./../entitys/Entity";
import { Color3, HighlightLayer, Mesh, TransformNode } from "../babylon";
import { Viewer } from "../viewer/Viewer";

export class HighlightManager {
  private _highlightLayer: HighlightLayer;
  private _color = Color3.White();
  constructor(_viewer: Viewer) {
    this._highlightLayer = new HighlightLayer("select-entity", _viewer.Scene);
    this._highlightLayer.innerGlow = false;
  }
  exclude(mesh: Mesh) {
    this._highlightLayer.addExcludedMesh(mesh);
  }
  addMeshs(hightMeshs: (Mesh | TransformNode)[], color?: Color3) {
    for (const s of hightMeshs) {
      const meshs = s.getChildMeshes() as Mesh[];
      meshs.forEach((m) =>
        this._highlightLayer.addMesh(m, color ?? this._color)
      );
    }
  }
  addEnitys(ens: Entity[], color?: Color3) {
    for (const en of ens) {
      this.addMeshs([en.DrawObject], color);
    }
  }
  clear() {
    this._highlightLayer.removeAllMeshes();
  }
}
