import { HCFiler, ISerialize } from "@hc/core";
import { Vector3 } from "../../babylon";

export type IWindowOption = {
  left?: number;
  bottom?: number;
  width?: number;
  height?: number;
};

export class Win implements ISerialize {
  static type = "Win";
  public left = 0;
  public bottom = 0;
  public width = 1;
  public height = 1;
  public position = Vector3.Zero();
  public url = "";
  constructor(option?: IWindowOption) {
    this.left = option?.left ?? 0;
    this.bottom = option?.bottom ?? 0;
    this.width = option?.width ?? 1;
    this.height = option?.height ?? 1;
  }
  fromJSON(option: IWindowOption) {
    this.left = option?.left ?? 0;
    this.bottom = option?.bottom ?? 0;
    this.width = option?.width ?? 1;
    this.height = option?.height ?? 1;
  }
  writeFile(file: HCFiler): void {
    file.write(1);
    file.write(this.left);
    file.write(this.bottom);
    file.write(this.width);
    file.write(this.height);
  }
  readFile(file: HCFiler): void {
    file.read();
    this.left = file.read();
    this.bottom = file.read();
    this.width = file.read();
    this.height = file.read();
  }
}
