import { Matrix } from "@babylonjs/core/Maths/math.vector";

import { Factory, HCFiler, ObjectId } from "@hc/core";

import {
  Animation as BAnimation,
  AnimationGroup as BAnimationGroup,
  Camera,
  IAnimationKey,
  Quaternion,
  Vector3,
} from "../babylon";
import { Curve } from "../entitys";
import type { Entity } from "../entitys/Entity";
import { IPathOption } from "../types/animation.types";
import { iVector2Vector } from "../utils/vector";
import { AnimationGroup } from "./AnimationGroup";

@Factory
export class PathAnimation extends AnimationGroup {
  static type = "PathAnimation";
  private path: Vector3[];
  protected _name = "路径动画";
  constructor(
    protected _entity?: Entity | Camera,
    private option?: IPathOption
  ) {
    super(_entity);
    if (option?.path?.length) {
      this.path = option.path.map(iVector2Vector);
    }

    if (option?.curve) {
      this.path = option.curve.Object?.Shape ?? [];
    }

    if (option?.name) {
      this.name = option.name;
    }
    if (_entity && option) this.init();
  }
  writeFile(file: HCFiler) {
    super.writeFile(file);
    file.write(1);
    file.write(this.option.time);
    file.write(this.option.frame);
    file.write(this.option.loop);
    file.writeVector3(this.option.forward);
    file.writeAnyArray(this.option.path ?? [], file.writeVector3);
    file.writeHardObjectId(this.option.curve);
  }
  readFile(file: HCFiler) {
    super.readFile(file);
    file.read(); // 版本
    if (!this.option) this.option = {};
    this.option.time = file.read() ?? 1;
    this.option.frame = file.read() ?? 30;
    this.option.loop = file.read() ?? false;
    const v: any = {};
    file.readVector3(v);
    if (v.x !== undefined) {
      this.option.forward = v;
    }
    const path = [];
    file.readAnyArray(() => {
      const v: any = {};
      file.readVector3(v);
      path.push(v);
    });

    if (path.length) {
      this.option.path = path;
    }
    this.option.curve = file.readHardObjectId() as ObjectId<Curve>;

    requestIdleCallback(() => {
      const option = this.option;
      if (option?.path?.length) {
        this.path = option.path.map(iVector2Vector);
      }

      if (option?.curve) {
        this.path = option.curve.Object?.Shape ?? [];
      }
      this.init();
    });
  }
  protected init() {
    if (!this.option) return;

    if (this.path?.length === 0) return;

    let {
      time = 1,
      loop = false,
      frame = 30,
      loopCount,
      forward,
    } = this.option;
    const group = new BAnimationGroup(this.name);

    const renderMesh = this.Target;

    const isCamera = renderMesh instanceof Camera;

    const z = !isCamera ? renderMesh.forward : new Vector3(0, 0, 1);
    const right = !isCamera ? renderMesh.right : new Vector3(1);
    const up = !isCamera ? renderMesh.up : new Vector3(0, 1);

    let ro = 0;
    if (forward) {
      const v = iVector2Vector(forward).normalize();
      Vector3.TransformCoordinatesToRef(
        v,
        renderMesh.getWorldMatrix().getRotationMatrix(),
        v
      );
      ro = Math.acos(Vector3.Dot(v, z));
      const sign = Math.sign(Vector3.Cross(z, v).y);
      ro *= sign;
    }

    const rotation = new BAnimation(
      this._name,
      !isCamera ? "rotationQuaternion" : "target",
      frame,
      isCamera
        ? BAnimation.ANIMATIONTYPE_VECTOR3
        : BAnimation.ANIMATIONTYPE_QUATERNION
    );

    const move = new BAnimation(
      this._name,
      "position",
      frame,
      BAnimation.ANIMATIONTYPE_VECTOR3
    );

    const keyFrames: IAnimationKey[] = [];
    const roKeyFrames: IAnimationKey[] = [];
    const dist = this.getDistance();
    const n = (time * frame) / dist;

    let currentFrame = 0;
    keyFrames.push({
      frame: currentFrame,
      value: this.path[0],
    });

    if (isCamera) {
      renderMesh.position = this.path[0];
      //@ts-ignore
      renderMesh.target = this.path[1];
    }

    for (let i = 0; i < this.path.length - 1; i++) {
      let p1 = this.path[i];
      let p2 = this.path[i + 1];

      const normal = p2.subtract(p1).normalize();

      Vector3.CrossToRef(up, normal, right);

      let mtx = Matrix.FromXYZAxesToRef(right, up, normal, new Matrix());

      Matrix.RotationAxis(up, ro).multiplyToRef(mtx, mtx);

      if (isCamera) {
        currentFrame += frame;
        roKeyFrames.push({
          frame: currentFrame,
          value: p2,
        });
      } else {
        roKeyFrames.push({
          frame: currentFrame,
          value: Quaternion.FromRotationMatrix(mtx),
        });

        roKeyFrames.push({
          frame: currentFrame,
          value: Quaternion.FromRotationMatrix(mtx),
        });
      }

      currentFrame += n * Vector3.Distance(p1, p2);

      keyFrames.push({
        frame: currentFrame,
        value: p2,
      });

      if (isCamera) {
        roKeyFrames.push({
          frame: currentFrame,
          value: p2,
        });
        if (!this.path[i + 2]) {
          currentFrame += frame;
          roKeyFrames.push({
            frame: currentFrame,
            value: p2.add(normal),
          });
        }
      } else {
        roKeyFrames.push({
          frame: currentFrame,
          value: Quaternion.FromRotationMatrix(mtx),
        });
      }
    }

    move.setKeys(keyFrames);
    rotation.setKeys(roKeyFrames);

    group.addTargetedAnimation(move, renderMesh);
    group.addTargetedAnimation(rotation, renderMesh);

    this._group = group;
  }
  private getDistance() {
    let dist = 0;
    for (let i = 0; i < this.path.length - 1; i++) {
      let p1 = this.path[i];
      let p2 = this.path[i + 1];
      dist += Vector3.Distance(p1, p2);
    }
    return dist;
  }
}
