import { ArcRotateCameraMouseWheelInput } from "@babylonjs/core/Cameras/Inputs/arcRotateCameraMouseWheelInput";
import {
  EventConstants,
  IWheelEvent,
} from "@babylonjs/core/Events/deviceInputEvents";
import {
  PointerEventTypes,
  PointerInfo,
} from "@babylonjs/core/Events/pointerEvents";
import { Epsilon } from "@babylonjs/core/Maths/math.constants";
import { Plane } from "@babylonjs/core/Maths/math.plane";
import { Scalar } from "@babylonjs/core/Maths/math.scalar";
import { Matrix, TmpVectors, Vector3 } from "@babylonjs/core/Maths/math.vector";
import { EventState, Observer } from "@babylonjs/core/Misc/observable";
import { Tools } from "@babylonjs/core/Misc/tools";
import { Nullable } from "@babylonjs/core/types";

import { isWheelByTrackpad } from "./trackpadUtils";

/**
 * Firefox uses a different scheme to report scroll distances to other
 * browsers. Rather than use complicated methods to calculate the exact
 * multiple we need to apply, let's just cheat and use a constant.
 * https://developer.mozilla.org/en-US/docs/Web/API/WheelEvent/deltaMode
 * https://stackoverflow.com/questions/20110224/what-is-the-height-of-a-line-in-a-wheel-event-deltamode-dom-delta-line
 */
const ffMultiplier = 40;

/**
 * 在 `ArcRotateCameraMouseWheelInput` 基础上增加了苹果触控板的双指拖动手势支持
 *
 * 双指上下左右拖动可以控制视角上下左右旋转
 *
 * 按住 `Alt` 键并上下左右拖动可以控制视角上下左右平移
 *
 * **注意**：触控板的输入已经实现了鼠标滚轮的输入，所以使用前应移除鼠标滚轮的输入，及二者选其一
 */
export class ArcRotateCameraTrackpadInput extends ArcRotateCameraMouseWheelInput {
  private _panningSensibility = 50.0;

  private _angularSpeed = 0.01;

  private _isPanningEnabled = true;

  private _isRotationEnabled = true;

  private wheel?: Nullable<(p: PointerInfo, s: EventState) => void>;
  private observer?: Nullable<Observer<PointerInfo>>;
  private hitPlane?: Nullable<Plane>;

  public get angularSpeed() {
    // 当禁止转动视角时，角速度为 0
    return this._isRotationEnabled ? this._angularSpeed : 0;
  }
  public set angularSpeed(v: number) {
    this._angularSpeed = v;
  }

  /**
   * 可通过该参数调整垂直方向的角速度
   *
   * 默认垂直方向角速度设置为水平方向角速度的一半，以避免垂直方向速度太快导致操作不便
   */
  public verticalAngularSpeedRatio = 0.5;

  public get panningSensibility() {
    // 当禁止平移时，设置平移系数为非常大的值来模拟禁用
    // WHY: 通过通用参数来控制操作行为，避免侵入到实现内部
    return this._isPanningEnabled
      ? this._panningSensibility
      : Number.MAX_SAFE_INTEGER;
  }
  public set panningSensibility(v: number) {
    this._panningSensibility = v;
  }

  /**
   * 水平平移量
   */
  private panningX = 0;

  /**
   * 水平角增量
   */
  private alphaDelta = 0;

  /**
   * 垂直平移量
   */
  private panningY = 0;

  /**
   * 垂直角增量
   */
  private betaDelta = 0;

  /**
   * @param useMousewheelName 是否伪装成滚轮
   */
  constructor(private useMousewheelName = false) {
    super();
  }

  /**
   * 控制是否响应平移操作
   *
   * @param enabled 是否启用平移
   */
  public enablePanning(enabled = true) {
    this._isPanningEnabled = enabled;
  }

  /**
   * 禁用平移
   */
  public disablePanning() {
    this._isPanningEnabled = false;
  }

  /**
   * 控制是否响应旋转操作
   *
   * @param enabled 是否启用转动
   */
  public enableRotation(enabled = true) {
    this._isRotationEnabled = enabled;
  }

  /**
   * 禁止转动视角
   */
  public disableRotation() {
    this._isRotationEnabled = false;
  }

  override attachControl(noPreventDefault?: boolean): void {
    noPreventDefault = Tools.BackCompatCameraNoPreventDefault(arguments);
    this.wheel = (p) => {
      //sanity check - this should be a PointerWheel event.
      if (p.type !== PointerEventTypes.POINTERWHEEL) {
        return;
      }

      const event = <IWheelEvent>p.event;

      if (event.ctrlKey) {
        // 当 `ctrlKey` 为 `true` 的时候表示用户目前使用的双指缩放的手势，为了避免页面被放大
        // 此时需要使用 `preventDefault()` 拦截该默认行为
        event.preventDefault();
      }

      if (isWheelByTrackpad(event as WheelEvent)) {
        // 左右平移的操作默认情况下会导致页面前进或后退的操作，为了避免页面跳转
        // 此时需要使用 `preventDefault()` 拦截该默认行为
        event.preventDefault();
        // 计算出正负号来控制方向
        const xSign = Math.sign(event.deltaX);
        const ySign = Math.sign(event.deltaY);

        if (event.altKey) {
          this.panningX += xSign / this.panningSensibility;
          this.panningY += -ySign / this.panningSensibility;
        } else {
          this.alphaDelta += xSign * this.angularSpeed;
          this.betaDelta +=
            ySign * this.angularSpeed * this.verticalAngularSpeedRatio;
        }

        return;
      }

      let delta = 0;
      const platformScale =
        event.deltaMode === EventConstants.DOM_DELTA_LINE ? ffMultiplier : 1; // If this happens to be set to DOM_DELTA_LINE, adjust accordingly

      const wheelDelta = -(event.deltaY * platformScale);

      if (this.customComputeDeltaFromMouseWheel) {
        delta = this.customComputeDeltaFromMouseWheel(wheelDelta, this, event);
      } else {
        if (this.wheelDeltaPercentage) {
          delta = this._computeDeltaFromMouseWheelLegacyEvent(
            wheelDelta,
            this.camera.radius
          );

          // If zooming in, estimate the target radius and use that to compute the delta for inertia
          // this will stop multiple scroll events zooming in from adding too much inertia
          if (delta > 0) {
            let estimatedTargetRadius = this.camera.radius;
            let targetInertia = this.camera.inertialRadiusOffset + delta;
            for (let i = 0; i < 20 && Math.abs(targetInertia) > 0.001; i++) {
              estimatedTargetRadius -= targetInertia;
              targetInertia *= this.camera.inertia;
            }
            estimatedTargetRadius = Scalar.Clamp(
              estimatedTargetRadius,
              0,
              Number.MAX_VALUE
            );
            delta = this._computeDeltaFromMouseWheelLegacyEvent(
              wheelDelta,
              estimatedTargetRadius
            );
          }
        } else {
          delta = wheelDelta / (this.wheelPrecision * 40);
        }
      }

      if (delta) {
        if (this.zoomToMouseLocation && this.hitPlane) {
          this.zoomToMouse(delta);
        } else {
          this.camera.inertialRadiusOffset += delta;
        }
      }

      if (event.preventDefault) {
        if (!noPreventDefault) {
          event.preventDefault();
        }
      }
    };

    this.observer = this.camera
      .getScene()
      .onPointerObservable.add(this.wheel, PointerEventTypes.POINTERWHEEL);

    if (this.zoomToMouseLocation) {
      this.inertialPanning.setAll(0);
    }
  }

  /**
   * Detach the current controls from the specified dom element.
   */
  public detachControl(): void {
    if (this.observer) {
      this.camera.getScene().onPointerObservable.remove(this.observer);
      this.observer = null;
      this.wheel = null;
    }
  }

  /**
   * Update the current camera state depending on the inputs that have been used this frame.
   * This is a dynamically created lambda to avoid the performance penalty of looping for inputs in the render loop.
   */
  public checkInputs(): void {
    const camera = this.camera;

    camera.inertialAlphaOffset += this.alphaDelta;
    camera.inertialBetaOffset += this.betaDelta;
    camera.inertialPanningX += this.panningX;
    camera.inertialPanningY += this.panningY;

    this.alphaDelta = 0;
    this.betaDelta = 0;
    this.panningX = 0;
    this.panningY = 0;

    if (!this.zoomToMouseLocation) {
      return;
    }
    const motion =
      0.0 +
      camera.inertialAlphaOffset +
      camera.inertialBetaOffset +
      camera.inertialRadiusOffset;
    if (motion) {
      // if zooming is still happening as a result of inertia, then we also need to update
      // the hit plane.
      this.updateHitPlane();

      // Note we cannot  use arcRotateCamera.inertialPlanning here because arcRotateCamera panning
      // uses a different panningInertia which could cause this panning to get out of sync with
      // the zooming, and for this to work they must be exactly in sync.
      camera.target.addInPlace(this.inertialPanning);
      this.inertialPanning.scaleInPlace(camera.inertia);
      this.zeroIfClose(this.inertialPanning);
    }
  }

  /**
   * 获取当前输入方式的类名
   *
   * @returns 默认返回实际类型名称，当 `useMousewheelName` 为 `true` 时返回 `ArcRotateCameraMouseWheelInput`
   */
  public override getClassName(): string {
    return this.useMousewheelName
      ? super.getClassName()
      : "ArcRotateCameraTrackpadInput";
  }

  /**
   * 获取当前输入方式的名称
   *
   * @returns 默认返回 `trackpad`，当 `useMousewheelName` 为 `true` 时返回 `mousewheel`
   */
  public override getSimpleName(): string {
    return this.useMousewheelName ? super.getSimpleName() : "trackpad";
  }

  private updateHitPlane() {
    const camera = this.camera;
    const direction = camera.target.subtract(camera.position);
    this.hitPlane = Plane.FromPositionAndNormal(camera.target, direction);
  }

  // Get position on the hit plane
  private getPosition(): Vector3 {
    const camera = this.camera;
    const scene = camera.getScene();

    // since the _hitPlane is always updated to be orthogonal to the camera position vector
    // we don't have to worry about this ray shooting off to infinity. This ray creates
    // a vector defining where we want to zoom to.
    const ray = scene.createPickingRay(
      scene.pointerX,
      scene.pointerY,
      Matrix.Identity(),
      camera,
      false
    );
    let distance = 0;
    if (this.hitPlane) {
      distance = ray.intersectsPlane(this.hitPlane) ?? 0;
    }

    // not using this ray again, so modifying its vectors here is fine
    return ray.origin.addInPlace(ray.direction.scaleInPlace(distance));
  }

  private inertialPanning: Vector3 = Vector3.Zero();

  private zoomToMouse(delta: number) {
    const camera = this.camera;
    const inertiaComp = 1 - camera.inertia;
    if (camera.lowerRadiusLimit) {
      const lowerLimit = camera.lowerRadiusLimit ?? 0;
      if (
        camera.radius - (camera.inertialRadiusOffset + delta) / inertiaComp <
        lowerLimit
      ) {
        delta =
          (camera.radius - lowerLimit) * inertiaComp -
          camera.inertialRadiusOffset;
      }
    }
    if (camera.upperRadiusLimit) {
      const upperLimit = camera.upperRadiusLimit ?? 0;
      if (
        camera.radius - (camera.inertialRadiusOffset + delta) / inertiaComp >
        upperLimit
      ) {
        delta =
          (camera.radius - upperLimit) * inertiaComp -
          camera.inertialRadiusOffset;
      }
    }

    const zoomDistance = delta / inertiaComp;
    const ratio = zoomDistance / camera.radius;
    const vec = this.getPosition();

    // Now this vector tells us how much we also need to pan the camera
    // so the targeted mouse location becomes the center of zooming.

    const directionToZoomLocation = TmpVectors.Vector3[6];
    vec.subtractToRef(camera.target, directionToZoomLocation);
    directionToZoomLocation.scaleInPlace(ratio);
    directionToZoomLocation.scaleInPlace(inertiaComp);
    this.inertialPanning.addInPlace(directionToZoomLocation);

    camera.inertialRadiusOffset += delta;
  }

  // Sets x y or z of passed in vector to zero if less than Epsilon.
  private zeroIfClose(vec: Vector3) {
    if (Math.abs(vec.x) < Epsilon) {
      vec.x = 0;
    }
    if (Math.abs(vec.y) < Epsilon) {
      vec.y = 0;
    }
    if (Math.abs(vec.z) < Epsilon) {
      vec.z = 0;
    }
  }
}
