import { BaseObject } from "../BaseObject";
import { Factory } from "../Factory";
import { HCFiler } from "../filer/HCFiler";
import { ObjectId } from "../ObjectId";
import { CreateObjectData } from "./CreateObjectData";
import { EraseEntityData } from "./EraseEntityData";
import { HistoryRecord } from "./HistoryRecord";
import { ObjectAllDataHistoryRecord } from "./ObjectAllDataHistoryRecord";

/**
 * 命令的历史记录
 * 对象快照？
 *
 */
@Factory
export class CommandHistoryRecord extends BaseObject {
  static type = "CommandHistoryRecord";
  //历史记录表
  private _HistoryList = new Map<ObjectId, HistoryRecord[]>();
  //新生成的对象
  private _CreateObjects = new Map<BaseObject, HistoryRecord>();
  constructor(public commandName = "") {
    super();
  }
  get HistoryList(): Map<ObjectId, HistoryRecord[]> {
    return this._HistoryList;
  }
  //获取对象快照记录(如果有的话)
  getObjectAllDataRecord(
    historyList: HistoryRecord[]
  ): ObjectAllDataHistoryRecord[] {
    let hs: ObjectAllDataHistoryRecord[] = [];
    for (const h of historyList) {
      if (h instanceof ObjectAllDataHistoryRecord) {
        hs.push(h);
      }
    }
    return hs;
  }

  writeObjectSnapshoot(obj: BaseObject): void {
    if (!obj.ObjectId) {
      console.warn("生成对象数据错误，对象还没分配ID");
      return;
    }

    if (this._CreateObjects.has(obj)) return;

    let hrs = this.getObjectHistoryList(obj.ObjectId);
    if (this.getObjectAllDataRecord(hrs)?.length) return;

    let hr = new ObjectAllDataHistoryRecord(obj.ObjectId);
    hrs.push(hr);
  }
  private getObjectHistoryList(id: ObjectId) {
    if (!this._HistoryList.has(id)) this._HistoryList.set(id, []);
    return this._HistoryList.get(id);
  }
  createEraseHistory(obj: BaseObject, isErase: boolean) {
    let hr = new HistoryRecord();
    hr.undoData = new EraseEntityData(!isErase);
    hr.redoData = new EraseEntityData(isErase);
    this.writeObjectHistoryPath(obj, hr);
  }

  //对象写入历史记录
  writeObjectHistoryPath(obj: BaseObject, history: HistoryRecord) {
    if (this._CreateObjects.has(obj)) return;

    let hrs = this.getObjectHistoryList(obj.ObjectId);
    if (this.getObjectAllDataRecord(hrs)?.length) return;

    if (history.redoData instanceof CreateObjectData) {
      this._CreateObjects.set(history.redoData.Object, history);
    }

    hrs.push(history);
  }
  destory() {
    //
  }
  writeFile(file: HCFiler) {
    file.write(1);
    file.write(this.commandName);
  }
  readFile(file: HCFiler) {
    file.read();
    this.commandName = file.read();
  }
}
