import { Video } from "./Video";
import { BaseGui } from "./Gui";
import { Container, StackPanel } from "../babylon";
import { Factory, HCFiler } from "@hc/core";
import { StrNum } from "../types/types";
import { Entity } from "../entitys/Entity";

@Factory
export class VideoGui extends BaseGui {
  static type = "VideoGui";
  private _url = "";
  protected _width: StrNum = "200px";
  protected _height: StrNum = "100px";
  private _videoDom: HTMLVideoElement;
  constructor(option: { url: string; width: string; height: string }) {
    super();
    if (option?.url) {
      this._url = option.url;
    }
    if (option?.width) {
      this._width = option.width;
    }
    if (option?.height) {
      this._height = option.height;
    }
  }
  get VideoDom() {
    return this._videoDom;
  }
  get Url() {
    return this._url;
  }
  set Url(v: string) {
    this._url = v;
    this.update();
  }
  initDrawObject() {
    const root = new Container(this.UniqueName);
    root.width = this._width;
    root.height = this._height;
    const p = new Video(
      this.UniqueName,
      this._url,
      this._width,
      this._height,
      Entity.Scene
    );
    root.background = "#000";
    this._videoDom = p.VideoDom;
    p.keepSize = true;
    root.addControl(p);
    return root;
  }
  updateDrawObject(obj: Container): void {
    super.updateDrawObject(obj);
    const v = obj.children[0] as Video;
    v.source = this._url;
  }
  readFile(filer: HCFiler): void {
    super.readFile(filer);
    filer.read();
    this.update();
  }
  writeFile(filer: HCFiler): void {
    super.writeFile(filer);
    filer.write(1);
  }
  erase(e = true): void {
    super.erase(e);
  }
  destory(): void {
    super.destory();
  }
}
