import {
  Color3,
  DirectionalLight as DirectionalLight2,
  Mesh,
  MeshBuilder,
  TransformNode,
  Vector3,
} from "../../babylon";

import { Factory, HCFiler, IVector3 } from "@hc/core";
import { CommonMaterials } from "../../materials/commonMaterials";
import { Light } from "./Light";
import { Entity } from "./../Entity";
import { iVector2Vector, vec2Vec3Like } from "../../utils/vector";
import { autoUpdate } from "../../core";

const LENGTH = 1000;

/**
 * 日光
 * @see https://hc3dol.gkiiot.com/#22
 */
@Factory
export class DirectionalLight extends Light {
  static type = "DirectionalLight";
  protected _name = "方向光";
  protected _position = new Vector3(0, 100);
  /**方向 */
  @autoUpdate Direction = { x: -1, y: -1, z: -1 };

  get BDirection() {
    return iVector2Vector(this.Direction);
  }

  initDrawObject() {
    const root = new Mesh(this.UniqueName + "_root", Entity.Scene);
    const light = new DirectionalLight2(
      this.UniqueName,
      this.BDirection,
      Entity.Scene
    );
    light.parent = root;
    light.autoCalcShadowZBounds = true;
    this.initShadowGenerator(light);

    this.createHelper(root);

    return root;
  }

  updateDrawObject(type, obj: Mesh) {
    const nodes = obj.getChildren();
    const light = nodes[0] as DirectionalLight2;
    light.direction = this.BDirection;
    light.diffuse = CommonMaterials.getColor3fromHex(this.Diffuse);
    light.specular = CommonMaterials.getColor3fromHex(this.Specular);
    light.intensity = this.Intensity;
    this.updateHelper(nodes[1] as TransformNode);
    this.updateShadowCaster();
  }
  getDragPoints() {
    return [];
  }
  moveDragPoint(endPoint: Vector3) {
    this.Direction = vec2Vec3Like(
      endPoint.subtract(this.RenderObject.absolutePosition).normalize()
    );
  }
  writeFile(file: HCFiler) {
    super.writeFile(file);
    file.write(1);

    file.writeVector3(this.Direction);
  }
  readFile(file: HCFiler) {
    super.readFile(file);
    const ver = file.read();
    file.readVector3(this.Direction);
    this.update();
  }
  protected getHelper() {
    const root = new TransformNode("root");

    const dirctSphere = MeshBuilder.CreateCylinder(
      "light_helper",
      {
        diameterBottom: 0,
        diameterTop: 5,
        height: 10,
      },
      Entity.Scene
    );

    dirctSphere.parent = root;
    dirctSphere.rotation.x = -Math.PI / 2;
    // dirctSphere.position.z = -LENGTH / 10;

    // 创建线

    let lines = MeshBuilder.CreateLines(
      "lines",
      {
        points: [new Vector3(0, 0, -LENGTH / 2), new Vector3(0, 0, LENGTH / 2)],
      },
      Entity.Scene
    );
    lines.color = Color3.Green();
    lines.isPickable = false;
    lines.parent = root;
    return root;
  }
  protected updateHelper(obj: TransformNode) {
    if (!obj) return;
    obj.setEnabled(this.VisiableHelper);
    obj.lookAt(this.BDirection);
  }
}
