import { Factory, HCFiler, ISerialize, ObjectId } from "@hc/core";
import { hcFetch } from "../utils/fetch";

export interface IApiDataOption {
  type: "GET" | "POST";
  interfaceSource: string;
  serviceType: string;
  service: string;
  specificInterface: string;
  interfaceParams: Record<string, unknown>;
  params: { type: string; value: string; key: string; id: number }[];
  url: string;
  time: number;
  filter: string;
  response?: string;
  useTime: boolean;
  select: string;
  queryDataStr: string;
}
export interface IWsDataOption {
  ws: string;
  key: string;
  [key: string]: string;
}

export enum DataBindType {
  Ws = "1",
  Api = "2",
}

@Factory
export class DataBind implements ISerialize {
  static type = "DataBind";
  static WSList: Map<string, WebSocket> = new Map();
  private _time: NodeJS.Timeout;
  data: IApiDataOption | IWsDataOption;
  type = DataBindType.Ws;
  /*key1/key2*/
  constructor(public target?: ObjectId, public targetKey?: string) {}
  do() {
    if (this.type === DataBindType.Api) {
      this.startApiRequest();
    } else {
      this.startWs();
    }
  }
  setEntityValue(value: any) {
    const entity = this.target.Object;

    if (entity) {
      const keys = this.targetKey.split("/");
      let data: any = entity;
      const lastKey = keys.pop();
      for (const key of keys) {
        data = data[key];
      }
      if ("setProp" in data) {
        data.setProp(lastKey, value);
      } else data[lastKey] = value;
    }
  }
  destory() {
    this._time && clearTimeout(this._time);
    if ("ws" in this.data) {
      const ws = DataBind.WSList.get(this.data.ws);
      if (ws) {
        ws.close();
        DataBind.WSList.delete(this.data.ws);
      }
    }
  }

  writeFile(file: HCFiler): void {
    file.write(1);
    file.writeObjectId(this.target);
    file.write(this.targetKey);
    file.write(this.type);
    file.write(JSON.stringify(this.data));
  }
  readFile(file: HCFiler): void {
    file.read();
    this.target = file.readObjectId();
    this.targetKey = file.read();
    this.type = file.read();

    const json = file.read();
    if (json) {
      this.data = JSON.parse(json);
    }
  }
  private async startApiRequest() {
    const { type, params, time, url, filter, interfaceSource, select } = this
      .data as IApiDataOption;

    let filerFun = (data) => data;

    try {
      const f = new Function("return " + filter);
      if (f) {
        filerFun = f();
      }
    } catch (err) {
      console.log("表达式错误");
    }
    const data: Record<string, any> = {};

    if (params) {
      for (const par of params) {
        data[par.key] = par.value;
      }
    }
    let res;
    if (interfaceSource === "custom") {
      res = await hcFetch(url, {
        method: type,
        ...(type === "POST" ? { data } : { params: data }),
      });
      res = filerFun(res);
    } else {
      res = await this.queryService(filerFun);
    }
    this.setEntityValue(res[select]);
    if (time && time > 0) {
      this._time = setTimeout(() => {
        this.startApiRequest();
      }, time * 1000);
    }
  }
  private async queryService(filerFun) {
    try {
      let q = new Function("return " + this.data.queryDataStr),
        queryData;
      queryData = q();
      let res = await queryData(this.data);
      return filerFun(res);
    } catch (e) {
      console.log(e);
    }
  }
  /**开始websocket请求 */
  private startWs() {
    const data = this.data as IWsDataOption;
    if (!data.ws) return;

    let ws = DataBind.WSList.get(data.ws);

    if (!ws) {
      let url = data.ws;
      if (data.ws.startsWith("//")) {
        let wsfix = location.protocol === "https:" ? "wss:" : "ws:";
        url = wsfix + data.ws;
      }
      ws = new WebSocket(url);
      DataBind.WSList.set(data.ws, ws);
    }

    ws.addEventListener("message", (evt) => {
      if (!evt.data) return;
      const returnData = JSON.parse(evt.data),
        source = String(data.source);

      if (source === "0") {
        //海创云ws数据
        if (returnData?.params && data.key in returnData.params) {
          this.setEntityValue(returnData.params[data.key]);
        }
      } else if (source === "1") {
        //阿里云ws数据
        if (returnData && data.key in returnData) {
          this.setEntityValue(returnData[data.key].value);
        }
      }
    });
  }
}
