import { TransformNode } from "../babylon";

import { TransformMode } from "../types/editor.types";
import { getEntity } from "./../utils/utils";
import { Viewer } from "./../viewer/Viewer";
import { GizmoManager } from "./gizmo/GizmoManager";
import { PositionGizmo } from "./gizmo/PositionGizmo";
import { RotationGizmo } from "./gizmo/RotationGizmo";
import { ScaleGizmo } from "./gizmo/ScaleGizmo";

//TODO:锚点修改
//TODO:缩放时按相对方向进行缩放

/**
 * 变换控制
 */

export class TransformControl {
  private _mode: TransformMode = TransformMode.Translate;
  private gizmoManager: GizmoManager;
  private attachNodes: TransformNode[];
  positionGizmo: PositionGizmo;
  rotationGizmo: RotationGizmo;
  scaleGizmo: ScaleGizmo;
  constructor(private _viewer: Viewer) {
    this.gizmoManager = new GizmoManager(
      this._viewer.Scene,
      1,
      _viewer.utilLayer
    );
    this.positionGizmo = new PositionGizmo(_viewer.utilLayer, 1);
    this.positionGizmo.planarGizmoEnabled = true;

    this.rotationGizmo = new RotationGizmo(_viewer.utilLayer);
    this.scaleGizmo = new ScaleGizmo(_viewer.utilLayer, 1);

    this.gizmoManager.gizmos.positionGizmo = this.positionGizmo;
    this.gizmoManager.gizmos.rotationGizmo = this.rotationGizmo;
    this.gizmoManager.gizmos.scaleGizmo = this.scaleGizmo;

    this.gizmoManager.usePointerToAttachGizmos = false;
    this.Mode = this._mode;
    this._register();
  }
  get Mode() {
    return this._mode;
  }
  set Mode(mode: TransformMode) {
    this._mode = mode;
    this.gizmoManager.positionGizmoEnabled = !!(mode & TransformMode.Translate);
    this.gizmoManager.rotationGizmoEnabled = !!(mode & TransformMode.Rotate);
    this.gizmoManager.scaleGizmoEnabled = !!(mode & TransformMode.Scale);

    if (
      this.gizmoManager.positionGizmoEnabled &&
      this.gizmoManager.scaleGizmoEnabled
    ) {
      this.gizmoManager.gizmos.positionGizmo.scaleRatio = 1.5;
    }
  }
  attachMeshs(nodes: TransformNode[]) {
    this.detachMeshs();

    if (nodes.length === 0) return;
    this.attachNodes = nodes;

    this.gizmoManager.attachToNodes([...nodes]);
  }
  detachMeshs() {
    this.attachNodes = null;
    this.gizmoManager.attachToNode(null);
  }
  private _register() {
    const updateMatrix = (meshs: TransformNode[]) => {
      if (!meshs) return;
      const entity = getEntity(meshs?.[0]);
      if (entity?.DB) entity.DB.HistoryManager.startCmd("实体变换");
      for (const mesh of meshs) {
        if (!mesh) {
          console.error("未绑定有效实体");
          continue;
        }
        const entity = getEntity(mesh);

        entity.Matrix = mesh._worldMatrix;
      }
      if (entity?.DB) entity.DB.HistoryManager.endCmd();
    };

    this.positionGizmo.onDragEndObservable.add((data) => {
      updateMatrix(this.attachNodes);
    });

    this.rotationGizmo.onDragEndObservable.add((data) => {
      updateMatrix(this.attachNodes);
    });
    this.scaleGizmo.onDragEndObservable.add((data) => {
      updateMatrix(this.attachNodes);
    });
  }
}
