import { CreateLineSystemVertexData } from "@babylonjs/core";

import { Factory, HCFiler } from "@hc/core";

import {
  Curve3,
  LinesMesh,
  MeshBuilder,
  Vector2,
  Vector3,
} from "../../babylon";
import { DefaultIntersectionThreshold } from "../../contants";
import { RenderType } from "../../types/renderTypes";
import { equaln } from "../../utils";
import { getPolylineCenter } from "../../utils/curveUtils";
import { Entity } from "../Entity";
import { Curve } from "./Curve";
import { IPolylineData, PolylineData } from "./types";

@Factory
export class Polyline extends Curve {
  static type = "Polyline";
  protected _name = "多段线";
  private dataList: PolylineData[] = [];
  constructor(option?: { dataList: IPolylineData[] }) {
    super();
    if (option?.dataList) {
      this.dataList = option.dataList.map((d) => this.toPolylineData(d));
    }
  }

  /**
   * 顶点数据
   *
   * 使用浅拷贝，避免直接修改顶点信息
   */
  get Vertices() {
    return [...this.dataList];
  }

  /**
   * 顶点数量
   */
  get VertexCount() {
    return this.dataList.length;
  }

  get Points(): Vector3[] {
    let points = [];
    this.dataList.forEach((data, i) => {
      if (i > 0) {
        const fp = this.dataList[i - 1].pt;
        const ep = data.pt;
        const sp = getPolylineCenter(fp, ep, this.dataList[i - 1].bulge);

        points = data.bulge
          ? [
              ...points,
              ...Curve3.ArcThru3Points(
                new Vector3(fp.x, 0, fp.y),
                new Vector3(sp.x, 0, sp.y),
                new Vector3(ep.x, 0, ep.y)
              ).getPoints(),
            ]
          : [...points, new Vector3(sp.x, 0, sp.y), new Vector3(ep.x, 0, ep.y)];
      } else {
        points.push(new Vector3(data.pt.x, 0, data.pt.y));
      }
    });

    return points;
  }
  get Shape() {
    let points: Vector3[] = [];
    for (let i = 0; i < this.dataList.length - 1; i++) {
      const data = this.dataList[i];
      const data2 = this.dataList[i + 1];
      const p1 = data.pt;
      const p2 = data2.pt;
      if (equaln(data.bulge, 0)) {
        points.push(new Vector3(p1.x, 0, p1.y));
      } else {
        const center = getPolylineCenter(p1, p2, data.bulge);
        const curve = Curve3.ArcThru3Points(
          new Vector3(p1.x, 0, p1.y),
          new Vector3(center.x, 0, center.y),
          new Vector3(p2.x, 0, p2.y)
        );
        const pts = curve.getPoints();
        pts.pop();
        points.push(...pts);
      }

      if (i + 1 === this.dataList.length - 1) {
        points.push(new Vector3(p2.x, 0, p2.y));
      }
    }
    return points;
  }
  get AllPoints() {
    return this.Shape.map((p) => Vector3.TransformCoordinates(p, this.Matrix));
  }
  //返回3D展示对象
  initDrawObject(renderType = RenderType.Wireframe) {
    const arcLine = MeshBuilder.CreateLines(
      this.UniqueName,
      { points: this.Shape, updatable: true },
      Entity.Scene
    );
    arcLine.isNearPickable = true;
    arcLine.intersectionThreshold = DefaultIntersectionThreshold;
    return arcLine;
  }
  //更新3D对象
  updateDrawObject(renderType: RenderType, obj: LinesMesh) {
    if (!obj) return;

    const vertexData = CreateLineSystemVertexData({
      lines: [this.Shape],
    });

    vertexData.applyToMesh(obj);
  }
  //序列化对象
  writeFile(file: HCFiler) {
    super.writeFile(file);
    file.write(1);
    file.writeAnyArray(this.dataList, (d) => {
      file.write(d.pt.x);
      file.write(d.pt.y);
      file.write(d.bulge);
    });
  }
  //反序列化对象
  readFile(file: HCFiler) {
    super.readFile(file);
    file.read();
    file.readAnyArray(() => {
      const data: PolylineData = {
        pt: new Vector2(),
        bulge: 0,
      };
      data.pt.x = file.read();
      data.pt.y = file.read();
      data.bulge = file.read();
      this.dataList.push(data);
    });
    this.update();
  }

  /**
   * 添加新的点
   *
   * @param data 需要添加的点数据
   */
  appendVertex(...data: IPolylineData[]) {
    this.dataList.push(...data.map((item) => this.toPolylineData(item)));
    this.update();
  }

  /**
   * 更新指定点的数据
   *
   * @param index 点的索引位置
   * @param data 新的点数据，会使用该数据替换指定 `index` 位置的值
   * @returns
   */
  updateVertexAt(index: number, data: IPolylineData) {
    const dataList = [...this.dataList];

    if (!dataList[index]) {
      return;
    }

    dataList.splice(index, 1, this.toPolylineData(data));

    this.dataList = dataList;

    this.update();
  }

  /**
   * 移除指定位置的顶点
   *
   * @param index 点的索引位置，当为负数的时候表示倒数位置
   * @returns
   */
  removeVertexAt(index: number) {
    if (!this.dataList.at(index)) {
      return;
    }

    this.dataList.splice(index, 1);
    this.update();
  }

  private toPolylineData(data: IPolylineData): PolylineData {
    return {
      pt: new Vector2(data.pt.x, data.pt.y),
      bulge: data.bulge,
    };
  }
}
