import {
  CreateCapsuleVertexData,
  ICreateCapsuleOptions,
  VertexData,
} from "../babylon";
import { Factory, HCFiler } from "@hc/core";
import { Solid3D } from "./Solid3D";

@Factory
export class Capsule extends Solid3D {
  static type = "Capsule";
  private radius: number;
  private radiusTop: number;
  private radiusBottom: number;
  private height: number;
  private subdivisions = 20;
  private tessellation = 16;
  private capSubdivisions = 6;
  private topCapSubdivisions: number;
  private bottomCapSubdivisions: number;
  constructor(
    option: ICreateCapsuleOptions = {
      radius: 1,
      radiusTop: undefined,
      radiusBottom: undefined,
      height: 1,
      subdivisions: 2,
      tessellation: 2,
      capSubdivisions: 6,
    }
  ) {
    super();

    this.height = option.height ?? 20;
    this.radius = option.radius ?? 5;
    this.subdivisions = option.subdivisions ?? 2;
    this.tessellation = option.tessellation ?? 16;
    this.capSubdivisions = option.capSubdivisions ?? 6;

    this.radiusTop = option.radiusTop;
    this.radiusBottom = option.radiusBottom;
    this.topCapSubdivisions = option.topCapSubdivisions;
    this.bottomCapSubdivisions = option.bottomCapSubdivisions;
  }
  get Radius() {
    return this.radius;
  }
  set Radius(v: number) {
    if (v === this.radius) return;
    this.writeSnapshoot();
    this.radius = v;
    this.update();
  }
  get RadiusTop() {
    return this.radiusTop;
  }
  set RadiusTop(v: number) {
    if (v === this.radiusTop) return;
    this.writeSnapshoot();
    this.radiusTop = v;
    this.update();
  }
  get RadiusBottom() {
    return this.radiusBottom;
  }
  set RadiusBottom(v: number) {
    if (v === this.radiusBottom) return;
    this.writeSnapshoot();
    this.radiusBottom = v;
    this.update();
  }
  get CapSubdivisions() {
    return this.capSubdivisions;
  }
  set CapSubdivisions(v: number) {
    if (v === this.capSubdivisions) return;
    this.writeSnapshoot();
    this.capSubdivisions = v;
    this.update();
  }
  get TopCapSubdivisions() {
    return this.topCapSubdivisions;
  }
  set TopCapSubdivisions(v: number) {
    if (v === this.topCapSubdivisions) return;
    this.writeSnapshoot();
    this.topCapSubdivisions = v;
    this.update();
  }
  get BottomCapSubdivisions() {
    return this.bottomCapSubdivisions;
  }
  set BottomCapSubdivisions(v: number) {
    if (v === this.bottomCapSubdivisions) return;
    this.writeSnapshoot();
    this.bottomCapSubdivisions = v;
    this.update();
  }

  get Height() {
    return this.height;
  }
  set Height(v: number) {
    if (v === this.height) return;
    this.writeSnapshoot();
    this.height = v;
    this.update();
  }
  get Subdivisions() {
    return this.subdivisions;
  }
  set Subdivisions(v: number) {
    if (v === this.subdivisions) return;
    this.writeSnapshoot();
    this.subdivisions = v;
    this.update();
  }
  get Tessellation() {
    return this.tessellation;
  }
  set Tessellation(v: number) {
    if (v === this.tessellation) return;
    this.writeSnapshoot();
    this.tessellation = v;
    this.update();
  }
  get VertextData(): VertexData {
    return CreateCapsuleVertexData({
      radius: this.radius,
      radiusTop: this.radiusTop,
      radiusBottom: this.radiusBottom,
      height: this.height,
      subdivisions: this.subdivisions,
      tessellation: this.tessellation,
      capSubdivisions: this.capSubdivisions,
      topCapSubdivisions: this.topCapSubdivisions,
      bottomCapSubdivisions: this.bottomCapSubdivisions,
    });
  }
  writeFile(file: HCFiler) {
    super.writeFile(file);
    file.write(1);
    file.write(this.radius);
    file.write(this.height);
    file.write(this.subdivisions);
    file.write(this.tessellation);
    file.write(this.capSubdivisions);

    file.write(this.radiusTop);
    file.write(this.radiusBottom);
    file.write(this.topCapSubdivisions);
    file.write(this.bottomCapSubdivisions);
  }
  readFile(file: HCFiler) {
    super.readFile(file);
    const ver = file.read();
    this.radius = file.read();
    this.height = file.read();
    this.subdivisions = file.read();
    this.tessellation = file.read();
    this.capSubdivisions = file.read();

    this.radiusTop = file.read();
    this.radiusBottom = file.read();
    this.topCapSubdivisions = file.read();
    this.bottomCapSubdivisions = file.read();
    this.update();
  }
}
