import { Factory, HCFiler } from "@hc/core";
import {
  AppStore,
  BaseGui,
  CanvasControl,
  GenerateTexture,
  Utils,
  Video,
} from "@hc/engine";
import { handleObjectFunction, sleep } from "~/utils/utils";
import { getGraphView, getHTData, getHTNode, IDataBindings, IHC2D } from "./ht";

@Factory
export class HC2DGui extends BaseGui implements GenerateTexture, IHC2D {
  static override type = "HC2DGui";
  private static _container: HTMLElement;
  static get Container() {
    if (this._container) return this._container;
    const div = (this._container = document.createElement("div"));
    div.style.pointerEvents = "none";
    div.style.visibility = "hidden";
    div.style.position = "fixed";
    document.body.append(div);

    return div;
  }
  private _source: string;
  private graphView;
  private hcNode;
  private _dataBindings: IDataBindings[];
  private _setAttr: Record<string, any> = {};
  private timer: NodeJS.Timeout;
  constructor(option?: { url: string }) {
    super();
    if (option?.url) {
      this._source = option.url;
    }
  }
  get Source() {
    return this._source;
  }
  set Source(url: string) {
    this._source = url;
  }
  get DataBindings() {
    return this._dataBindings ?? [];
  }
  get Attr() {
    return this._setAttr;
  }
  getProp(k: string) {
    if (this._setAttr[k]) {
      return this._setAttr[k];
    } else {
      const d = this.DataBindings.find((d) => d.attr === k);
      return d?.defaultValue;
    }
  }
  /**
   * 设置绑定数据值
   * @param k
   * @param value
   */
  setProp(k: string, value: any) {
    this.updateAttr({ [k]: value });
  }
  updateAttr(obj: Record<string, any>) {
    if (Object.keys(obj).length === 0 || !this.graphView || !this.hcNode)
      return;

    const dom = this.graphView.getView() as HTMLElement;
    if (!dom.isConnected) HC2DGui.Container.append(dom);
    this.clearDom(dom);
    Object.assign(this._setAttr, obj);
    obj = JSON.parse(JSON.stringify(obj));
    obj = handleObjectFunction(obj);
    this.hcNode.a(obj);
  }

  initDrawObject() {
    const root = Utils.getGuiContainer(this.UniqueName);
    root.width = "1px";
    root.height = "1px";
    root.horizontalAlignment = 0;
    root.verticalAlignment = 0;
    const resolve = AppStore.Wait();
    getGraphView().then((graphView) => {
      // graphView.addViewListener((event) => {
      //   console.log("event: ", event);
      //   if (event?.kind === "validate") this.updateDrawObject(root);
      // });
      this.graphView = graphView;
      getHTNode(graphView.dm(), this._source).then((node: any) => {
        if (!node) return;
        this.hcNode = node;
        const data = getHTData(node.getImage());
        this.load(data);
        this.generateLinkLine(root, 100);
        this.updateDrawObject(root);
        resolve(true);
      });
    });

    return root;
  }
  updateDrawObject(obj: Utils.Container): void {
    super.updateDrawObject(obj);
    if (this.linkEntity) {
      obj.linkWithMesh(this.linkEntity.Object.DrawObject);
    } else {
      obj.linkWithMesh(null);
    }

    this.graphView.fitContent(false, 0);
    let canvas: HTMLCanvasElement;
    let video: HTMLVideoElement | null = null;
    const dom = this.graphView.__htmlOrderList?.[0] as HTMLElement;
    if (dom && dom instanceof HTMLElement) {
      canvas =
        this.graphView.__htmlOrderList[0].getElementsByTagName("canvas")[0];

      if (dom.nodeName === "VIDEO") {
        video = dom as HTMLVideoElement;
      }
    } else {
      canvas = this.graphView.getCanvas();
    }

    if (!canvas) {
      canvas = this.graphView.getCanvas();
    }

    if (obj.children.length === 0) {
      if (video) {
        this.DataBindings.find;
        const gui = new Video(
          this.UniqueName + "_video",
          video,
          this._width,
          this._height,
          AppStore.MainScene
        );
        obj.addControl(gui);
        gui.width = this._width;
        gui.height = this._height;
      } else {
        const gui = new CanvasControl(
          this.UniqueName,
          canvas,
          AppStore.MainScene
        );
        obj.addControl(gui);
        if (
          this.graphView.getView() &&
          dom?.childNodes?.[0]?.nodeName === "VIDEO"
        ) {
          video = dom.childNodes[0] as HTMLVideoElement;
          const gui = new Video(
            this.UniqueName + "_video",
            video,
            this._width,
            this._height,
            AppStore.MainScene
          );
          obj.addControl(gui);
          let { width: videoWidth, height: videoHeight } = video.style;
          gui.width = this.calculateWidthOrHeight(videoWidth, "_width");
          gui.height = this.calculateWidthOrHeight(videoHeight, "_height");
        }
        gui.width = this._width;
        gui.height = this._height;
      }
    } else {
      const control = obj.children[0];
      if (video) {
        (control as Video).source = video;
      } else {
        (control as CanvasControl).CanvasDom = canvas;
      }
    }

    obj.width = this._width;
    obj.height = this._height;
    this.updateAttr(this._setAttr);
  }
  // 跟据百分比转换Video标签的宽度或高度
  calculateWidthOrHeight(value, type) {
    if (value.endsWith("%")) {
      let oldValue = this[type];
      let percentage = parseInt(value.split("%")[0]) / 100;
      if (typeof oldValue === "string") {
        oldValue = parseInt(oldValue.split("px")[0]);
      }
      value = oldValue * percentage + "px";
    }
    return value;
  }
  getTexture() {
    return null;
  }
  get IsSelect() {
    return this._isSelect;
  }
  set IsSelect(v: boolean) {
    this._isSelect = v;
    if (!this._drawObject) return;
    const root = this._drawObject as Utils.Container;
    if (v) {
      root.background = "#9595d8";
    } else {
      root.background = "";
    }
  }
  async toDataURL(blob = true): Promise<Blob | string | undefined> {
    const canvasControl = (this._drawObject as Utils.Container).children[0];

    if (canvasControl instanceof CanvasControl) {
      const canvas = canvasControl.CanvasDom;
      await sleep(1000);
      if (blob)
        return new Promise((res) => {
          canvas.toBlob(res, "image/jpeg");
        });
      else {
        return canvas.toDataURL("image/jpeg");
      }
    }

    return null;
  }
  writeFile(filer: HCFiler): void {
    super.writeFile(filer);
    filer.write(1);
    filer.write(this._source);
    filer.write(JSON.stringify(this._setAttr));
  }
  readFile(filer: HCFiler): void {
    super.readFile(filer);
    filer.read();
    this._source = filer.read();
    this._setAttr = JSON.parse(filer.read());
    this.update();
  }
  private load = (data) => {
    if (!data) return;
    const width =
      typeof data.width === "object" ? data.width.value : data.width;
    const height =
      typeof data.height === "object" ? data.height.value : data.height;

    this._dataBindings = data.dataBindings;

    const dom = this.graphView.getView() as HTMLElement;
    this._width = width + "px";
    this._height = height + "px";

    dom.style.cssText = `position:fixed;left:0;top:0;width:${width}px;height:${height}px;`;
    // this.graphView.getCanvas().width = width;
    // this.graphView.getCanvas().height = height;
    HC2DGui.Container.append(dom);

    this.clearDom(dom);
  };
  private clearDom(dom: HTMLElement) {
    this.timer && clearTimeout(this.timer);
    this.timer = setTimeout(() => {
      dom.remove();
    }, 30 * 1000);
  }
}
