import { AssetContainer } from "@babylonjs/core/assetContainer";
import {
  ISceneLoaderAsyncResult,
  ISceneLoaderPlugin,
  ISceneLoaderPluginAsync,
  ISceneLoaderPluginExtensions,
  ISceneLoaderPluginFactory,
  ISceneLoaderProgressEvent,
  SceneLoader,
} from "@babylonjs/core/Loading/sceneLoader";
import { IFileRequest } from "@babylonjs/core/Misc/fileRequest";
import { Tools } from "@babylonjs/core/Misc/tools";
import { IDisposable, Scene } from "@babylonjs/core/scene";
import { OBJFileLoader } from "@babylonjs/loaders";
import { GLTFFileLoader } from "@babylonjs/loaders/glTF/glTFFileLoader";
import { Nullable, Observable } from "../../babylon";
import { HC3DLoader, IHC3DLoader } from "./HC3DLoader";

function loadWasm(path: string) {
  const go = new window.Go();
  go.importObject.env["syscall/js.finalizeRef"] = () => {};
  return new Promise((resolve, reject) => {
    WebAssembly.instantiateStreaming(fetch(path), go.importObject)
      .then((result) => {
        go.run(result.instance);
        resolve(result.instance);
      })
      .catch((error) => {
        reject(error);
      });
  });
}

interface IFileRequestInfo extends IFileRequest {
  _lengthComputable?: boolean;
  _loaded?: number;
  _total?: number;
}

/**
 * HC3D文件加载器，解析hc3d,mod文件
 */
export class HC3DFileLoader
  implements IDisposable, ISceneLoaderPluginAsync, ISceneLoaderPluginFactory
{
  //TODO:没文档可用的地址作为默认配置
  static DecodeConfig = {
    exec_url: "",
    wasm_url: "",
  };
  static Decode: (buf: ArrayBuffer) => any;
  static LoadPromise: Promise<(buf: ArrayBuffer) => any>;
  static LoadWasm() {
    if (!this.DecodeConfig.exec_url) throw new Error("wasm执行文件丢失");
    if (!this.DecodeConfig.wasm_url) throw new Error("wasm文件丢失");
    this.LoadPromise = new Promise((res) => {
      Tools.LoadScript(this.DecodeConfig.exec_url, () => {
        loadWasm(this.DecodeConfig.wasm_url).then(() => {
          this.Decode = window.decode;
          res(this.Decode);
        });
      });
    });
  }
  private _gltfLoader: GLTFFileLoader;
  private _objLoader: OBJFileLoader;
  private _requests = new Array<IFileRequestInfo>();
  private _loader: Nullable<IHC3DLoader> = null;

  name = "hc3d";
  extensions: string | ISceneLoaderPluginExtensions = ".hc3d";
  /**
   * Observable raised when an error occurs.
   */
  public readonly onErrorObservable = new Observable<any>();
  constructor() {
    this._loader = new HC3DLoader(this);
  }
  get GLTFLoader() {
    if (!this._gltfLoader) this._gltfLoader = new GLTFFileLoader();

    return this._gltfLoader;
  }
  get OBJLoader() {
    if (!this._objLoader)
      this._objLoader = new OBJFileLoader().createPlugin() as OBJFileLoader;

    return this._objLoader;
  }
  createPlugin(): ISceneLoaderPluginAsync | ISceneLoaderPlugin {
    return new HC3DFileLoader();
  }

  public getFileLoader(type: string) {
    switch (type) {
      case "gltf":
      case "glb":
        return this.GLTFLoader;
      case "obj":
        return this.OBJLoader;
      default:
        return null;
    }
  }
  loadAssetContainerAsync(
    scene: Scene,
    data: string,
    rootUrl: string,
    onProgress?: (event: ISceneLoaderProgressEvent) => void,
    fileName?: string
  ): Promise<AssetContainer> {
    return this._loader.loadAssetContainerAsync(
      scene,
      JSON.parse(data),
      rootUrl,
      onProgress
    );
  }
  importMeshAsync(
    meshesNames: any,
    scene: Scene,
    data: any,
    rootUrl: string,
    onProgress?: (event: ISceneLoaderProgressEvent) => void
  ): Promise<ISceneLoaderAsyncResult> {
    return this._loader.importMeshAsync(
      meshesNames,
      scene,
      JSON.parse(data),
      rootUrl,
      onProgress
    );
  }
  loadAsync(
    scene: Scene,
    data: any,
    rootUrl: string,
    onProgress?: (event: ISceneLoaderProgressEvent) => void,
    fileName?: string
  ): Promise<void> {
    return this._loader.loadAsync(
      scene,
      JSON.parse(data),
      rootUrl,
      onProgress,
      fileName
    );
  }
  dispose(): void {
    if (this._loader) {
      this._loader.dispose();
      this._loader = null;
    }

    for (const request of this._requests) {
      request.abort();
    }

    this._requests.length = 0;
  }
}

SceneLoader.RegisterPlugin(new HC3DFileLoader());
