import { Animation } from "./Animation";
import type { Entity } from "./../entitys/Entity";
import { IRotationOption } from "../types";
import { Factory, HCFiler, IVector3 } from "@hc/core";
import {
  Animation as BAnimation,
  IAnimationKey,
  Matrix,
  Quaternion,
  Vector3,
} from "../babylon";
import { iVector2Vector } from "./../utils/vector";

@Factory
export class RotateAnimation extends Animation {
  static type = "RotateAnimation";
  protected _name = "未命名旋转";
  private _from = 0;
  private _angle = 0;
  private _angles = [];
  private _dir: IVector3;
  private _origin: IVector3;
  constructor(entity: Entity, option: IRotationOption) {
    super(entity, option);
    let {
      angle,
      dir = { x: 0, y: 1, z: 0 },
      time = 1,
      frame = 30,
      loop = true,
      from = 0,
      angles = [],
      loopCount,
      origin,
    } = option;
    this._time = time;
    this._loop = loop;
    this._frame = frame;
    this._loopCount = loopCount;
    this._dir = dir;
    this._angle = angle;
    this._from = from;
    if (option.name) this._name = option.name;
    this._origin = origin;
    this._angles = angles;
    this.init();
  }
  init() {
    this._entity.removeAnimation(this._name);
    const renderMesh = this.Target;

    const animate = new BAnimation(
      this._name,
      "rotationQuaternion",
      this._frame,
      BAnimation.ANIMATIONTYPE_QUATERNION
    );
    if (!renderMesh.rotationQuaternion) {
      renderMesh.rotationQuaternion = renderMesh.rotation.toQuaternion();
      renderMesh.rotation.setAll(0);
    }
    const axes = Vector3.FromArray(Object.values(this._dir)).normalize();

    const keyFrames: IAnimationKey[] = [];

    if (this._angles?.length) {
      const range = this._time / this._angles.length;
      for (let i = 0; i < this._angles.length; i++) {
        keyFrames.push({
          frame: this._frame * i * range,
          value: Quaternion.RotationAxis(axes, this._angles[i]),
        });
      }
    } else {
      keyFrames.push({
        frame: 0,
        value: Quaternion.RotationAxis(axes, this._from),
      });
      const angle = Math.abs(this._from - this._angle);

      if (angle > Math.PI * 2 - 1e-3) {
        const unit = Math.PI / 4;
        const count = Math.round(angle / unit);
        const timeUnit = this._time / count;

        for (let i = 1; i <= count; i++) {
          const addQ = Quaternion.RotationAxis(axes, this._from + i * unit);
          keyFrames.push({
            frame: this._frame * (timeUnit * i),
            value: addQ,
          });
        }
      }
    }

    animate.setKeys(keyFrames);

    if (this._origin) {
      const origin = Vector3.FromArray(Object.values(this._origin)).negate();

      const pilotStart = iVector2Vector(
        this._entity ? this._entity.Position : Vector3.Zero()
      ).add(origin);

      const pivotTranslate = pilotStart;

      renderMesh.setPivotMatrix(
        Matrix.Translation(pivotTranslate.x, pivotTranslate.y, pivotTranslate.z)
      );
    }

    renderMesh.animations.push(animate);

    this._animation = animate;
  }
  writeFile(file: HCFiler) {
    super.writeFile(file);
    file.write(1);
  }
  readFile(file: HCFiler) {
    super.readFile(file);
    file.read(); // 版本
    const count = file.read();
  }
}
