import { end } from "@hc/aop";
import { BaseObject } from "..//BaseObject";
import { HCFiler } from "../filer/HCFiler";
import { BaseDatabase } from "./BaseDatabase";
import {
  CollectionWriteFileOptions,
  ObjectCollection,
} from "./ObjectCollection";

export class BlockTableRecord<T extends BaseObject> extends BaseObject {
  static ObjectType: typeof BaseObject[] = [];
  private entityCol = new ObjectCollection<T>();
  private checkout: (en: T) => boolean;
  constructor() {
    super();
    end(this.entityCol, this.entityCol.appendEvent, (e) => {
      this.appendEvent(e);
    });
  }
  get Entitys(): T[] {
    return this.entityCol.objects.slice();
  }
  IsVaildObject(Object: BaseObject) {
    return BlockTableRecord.ObjectType.some((O) => Object instanceof O);
  }
  setCheckout(callback: (e: T) => boolean) {
    this.checkout = callback;
    return this;
  }
  append(e: T) {
    if ((!this.checkout || this.checkout(e)) && this.IsVaildObject(e))
      this.entityCol.append(e);
    else console.error("加入的实体和此块表不匹配");
  }
  appendEvent(e: T) {
    //inject
  }
  remove(object: T) {
    this.entityCol.remove(object);
  }
  _SetOwnerDatabase(db: BaseDatabase) {
    super._SetOwnerDatabase(db);
    this.entityCol._SetOwnerDatabase(db);
    this.entityCol.Owner = this.ObjectId;
    return this;
  }
  /**实体替换功能
   * # 不允许单独调用
   * 请使用Database的方法进行模型替换
   * @hiddren
   */
  replace(source: T, target: T) {
    this.entityCol.replace(source, target);
  }
  writeFile(file: HCFiler, options?: CollectionWriteFileOptions<T>) {
    super.writeFile(file);
    file.write(1);
    this.entityCol.writeFile(file, options);
  }
  readFile(file: HCFiler) {
    super.readFile(file);
    let ver = file.read();
    this.entityCol.readFile(file);
  }
  destory() {
    super.destory();
    this.entityCol.destory();
  }
}
