import { BaseObject } from "../BaseObject";
import { BaseDatabase } from "../database/BaseDatabase";
import { HCFiler } from "../filer/HCFiler";
import { ISerialize } from "../types/iSerialize.types";

export class CreateObjectData implements ISerialize {
  private filer = new HCFiler();
  constructor(public Object?: BaseObject) {}
  save() {
    if (this.Object && this.filer.Data.length === 0) {
      this.filer.writeObject(this.Object);
    }
    return this;
  }

  getObject(db: BaseDatabase): BaseObject {
    this.filer.reset();
    this.filer.database = db;
    this.Object = this.filer.readObject();
    this.filer.reset();
    return this.Object;
  }

  readFile(file: HCFiler) {
    file.read();
    let data = file.read();
    this.filer.Data = data;
  }
  writeFile(file: HCFiler) {
    file.write(1);
    file.write(this.filer.Data);
  }
}
