import { CreateSphere } from "@babylonjs/core/Meshes/Builders/sphereBuilder";
import { AppStore } from "../app";
import { CreateBox, CreatePlane, Mesh } from "../babylon";
import { Database } from "../database";
import { MaterialRecord } from "../record/MaterialRecord";
import { PBRMaterialRecord } from "../record/PBRMaterialRecord";
import { Viewer } from "./Viewer";

export class MaterialViewer {
  viewer: Viewer;
  TempDatabase = new Database(false);
  private showObjects: Mesh[] = [];
  private showIndex = 0;
  private _material: MaterialRecord;
  constructor(public canvas: HTMLCanvasElement) {
    this.viewer = new Viewer(canvas, true, { defaultLight: true });
    this.viewer.sceneControl.createDefaultEnv();
    this.viewer.cameraManager.Radius = 2;
    this.viewer.cameraManager.ArcRotateControl.LowerRadiusLimit = 1.5;
    this.viewer.sceneControl.BlockMaterialUpdate = false;
    this.showObjects.push(
      CreateSphere("show-sphere", { diameter: 1 }, this.scene),
      CreateBox("show-box", { size: 1 }, this.scene),
      CreatePlane("show-plane", { size: 1 }, this.scene)
    );

    this.toggleObject();
  }
  init(mtl?: PBRMaterialRecord) {
    if (!mtl) {
      mtl = new PBRMaterialRecord();
      this.TempDatabase.MaterialTable.append(mtl);
    }
    this.Material = mtl;
  }
  get scene() {
    return this.viewer.Scene;
  }
  get Material() {
    return this._material;
  }
  set Material(mtl: MaterialRecord) {
    AppStore.ToggleScene(this.scene, () => {
      if (mtl.DB === this.TempDatabase) {
        this._material = mtl;
      } else {
        if (!mtl.DB) {
          this.TempDatabase.MaterialTable.append(mtl);
          this._material = mtl;
        } else {
          const newMTL = this.TempDatabase.cloneBlockObjects(
            [mtl],
            this.TempDatabase.MaterialTable,
            new Map()
          )[0] as MaterialRecord;
          this._material = newMTL;
        }
      }
      this.update();
    });
  }
  get ShowIndex() {
    return this.showIndex;
  }
  set ShowIndex(i: number) {
    this.showIndex = i;
    this.toggleObject();
  }
  destory() {
    this.viewer.destory();
  }
  debugger(config: any = {}) {
    this.viewer.sceneControl.debugger(config);
  }
  update() {
    for (const o of this.showObjects) o.material = this._material.Material;
  }
  async waitRender() {
    if (this.Material instanceof PBRMaterialRecord)
      await this.Material.waitRender();

    return true;
  }
  toJSON() {
    const db = new Database();
    db.cloneBlockObjects([this.Material], db.MaterialTable, new Map());
    return db.writeFile().Data;
  }
  private toggleObject() {
    for (let i = 0; i < this.showObjects.length; i++) {
      this.showObjects[i].setEnabled(i === this.showIndex);
    }
  }
}
