import { IVector2 } from "@hc/core";
import { PointerEventTypes, PointerInfo } from "../babylon";
import { BaseGui } from "../guis/Gui";
import { Viewer } from "../viewer";

/**处理GUI鼠标事件 */
export class GUISelectTool {
  constructor(private viewer: Viewer) {
    //TODO:GUI 鼠标移动，抬起等事件处理
    viewer.Scene.onPointerObservable.add((pointerInfo: PointerInfo) => {
      const pt = {
        x: pointerInfo.event.offsetX,
        y: pointerInfo.event.offsetY,
      };
      switch (pointerInfo.type) {
        case PointerEventTypes.POINTERDOWN:
          if (pointerInfo.event.button === 0) {
            const gui = this.getGui(pt);
            if (gui) {
              gui.click(pt);
            }
          }
      }
    });
  }
  getGui(pt: IVector2): BaseGui | null {
    const list = this.viewer.GuiContainer.getChildren()[0]?.children;
    for (const c of list) {
      if (!c.metadata?.entity) continue;
      if (!c.isVisible || !c.isEnabled) continue;
      if (c.contains(pt.x, pt.y)) {
        return c.metadata.entity as BaseGui;
      }
    }
    return null;
  }
}
