import { CreateCylinderVertexData, VertexData } from "../babylon";
import { HCFiler, Factory } from "@hc/core";
import { Solid3D } from "./Solid3D";

@Factory
export class Cylinder extends Solid3D {
  static type = "Cylinder";
  protected _name = "圆柱";
  private diameterTop = 10;
  private diameterBottom = 10;
  private height = 10;
  private tessellation: number = undefined;
  private arc = 10;
  constructor(
    option = { diameterTop: 10, diameterBottom: 10, height: 10, arc: 10 }
  ) {
    super();
    this.diameterTop = option.diameterTop ?? 10;
    this.diameterBottom = option.diameterBottom ?? 10;
    this.height = option.height ?? 10;
    this.arc = option.arc ?? 10;
  }
  get DiameterTop() {
    return this.diameterTop;
  }
  set DiameterTop(v: number) {
    if (v === this.diameterTop) return;
    this.writeSnapshoot();
    this.diameterTop = v;
    this.update();
  }
  get DiameterBottom() {
    return this.diameterBottom;
  }
  set DiameterBottom(v: number) {
    if (v === this.diameterBottom) return;
    this.writeSnapshoot();
    this.diameterBottom = v;
    this.update();
  }
  get Height() {
    return this.height;
  }
  set Height(v: number) {
    if (v === this.height) return;
    this.writeSnapshoot();
    this.height = v;
    this.update();
  }
  get Tessellation() {
    return this.tessellation;
  }
  set Tessellation(v: number) {
    if (v === this.tessellation) return;
    this.writeSnapshoot();
    this.tessellation = v;
    this.update();
  }
  get VertextData(): VertexData {
    return CreateCylinderVertexData({
      diameterTop: this.diameterTop,
      diameterBottom: this.diameterBottom,
      height: this.height,
      arc: this.arc,
      tessellation: this.tessellation,
    });
  }
  writeFile(file: HCFiler) {
    super.writeFile(file);
    file.write(1);
    file.write(this.diameterTop);
    file.write(this.diameterBottom);
    file.write(this.height);
    file.write(this.tessellation);
    file.write(this.arc);
  }
  readFile(file: HCFiler) {
    super.readFile(file);
    const ver = file.read();
    this.diameterTop = file.read();
    this.diameterBottom = file.read();
    this.height = file.read();
    this.tessellation = file.read();
    this.arc = file.read();
    this.update();
  }
}
