import {
  Color3,
  HemisphericLight as HemisphericLight2,
  Mesh,
  TransformNode,
  Vector3,
} from "../../babylon";
import { Light } from "./Light";
import { Vec3Array } from "../../types";
import { Entity } from "../Entity";
import { HCFiler, Factory } from "@hc/core";

@Factory
export class HemisphericLight extends Light {
  static type = "HemisphericLight";
  private _dir: Vec3Array = [1, 1, 1];
  protected _name = "全局光";
  initDrawObject(renderType) {
    const node = new Mesh(this.UniqueName, Entity.Scene);
    const light = new HemisphericLight2(
      this.UniqueName + "-1",
      Vector3.FromArray(this._dir),
      Entity.Scene
    );
    light.parent = node;
    light.diffuse = Color3.FromHexString(this.Color);
    return node;
  }
  updateDrawObject(renderType, obj: TransformNode) {
    if (!obj || obj.getChildren().length === 0) return;
    const light = obj.getChildren()[0] as HemisphericLight2;
    light.intensity = this.Intensity;
    light.direction.fromArray(this._dir);
    light.diffuse = Color3.FromHexString(this.Color);
  }
  readFile(filer: HCFiler) {
    super.readFile(filer);
    this.update();
  }
}
