import { HCFiler, ObjectRecord } from "@hc/core";

import { BaseTexture, Texture } from "../babylon";

export class BaseTextureRecord extends ObjectRecord {
  protected _isLoading = false;
  protected wrapU: 0 | 1 | 2 = Texture.WRAP_ADDRESSMODE;
  protected wrapV: 0 | 1 | 2 = Texture.WRAP_ADDRESSMODE;
  protected scale: [number, number] = [1, 1];
  protected rotation: [number, number, number] = [0, 0, 0];
  protected offset: [number, number] = [0, 0];
  protected texture: BaseTexture;
  constructor() {
    super();
  }
  get Texture() {
    return this.texture;
  }
  async getTexture() {
    return this.texture;
  }
  get Scale() {
    return this.scale;
  }
  set Scale(v: [number, number]) {
    if (v[0] === this.scale[0] && v[1] === this.scale[1]) return;
    this.writeSnapshoot();
    this.scale = v;
    this.update();
  }
  get Offset() {
    return this.offset;
  }
  set Offset(v: [number, number]) {
    if (v[0] === this.offset[0] && v[1] === this.offset[1]) return;
    this.writeSnapshoot();
    this.offset = v;
    this.update();
  }
  get Rotation() {
    return this.rotation;
  }
  set Rotation(v: [number, number, number]) {
    if (
      v[0] === this.rotation[0] &&
      v[1] === this.rotation[1] &&
      v[2] === this.rotation[2]
    )
      return;
    this.writeSnapshoot();
    this.rotation = v;
    this.update();
  }
  update() {
    if (!this.texture) return;
    this.texture.name = this._name;
    this.texture.wrapU = this.wrapU;
    this.texture.wrapV = this.wrapV;
  }
  writeFile(file: HCFiler) {
    super.writeFile(file);
    file.write(1);
    file.write(this.wrapU);
    file.write(this.wrapV);
    file.write(this.scale[0]);
    file.write(this.scale[1]);
    file.write(this.rotation[0]);
    file.write(this.rotation[1]);
    file.write(this.rotation[2]);
    file.write(this.offset[0]);
    file.write(this.offset[1]);
  }
  readFile(file: HCFiler) {
    super.readFile(file);
    const ver = file.read();
    this.wrapU = file.read();
    this.wrapV = file.read();
    this.scale[0] = file.read();
    this.scale[1] = file.read();
    this.rotation[0] = file.read();
    this.rotation[1] = file.read();
    this.rotation[2] = file.read();
    this.offset[0] = file.read();
    this.offset[1] = file.read();
  }
}
