import { BaseObject } from "../BaseObject";
import { HCFiler } from "../filer/HCFiler";
import { ISerialize } from "../types/iSerialize.types";

export class RemoveObjectData implements ISerialize {
  private index: number;
  removeObject: BaseObject; //用于记录被删除的对象
  constructor(index?: number) {
    this.index = index;
  }
  get Index() {
    return this.index;
  }
  readFile(file: HCFiler) {
    file.read(); //ver
    this.index = file.read();
  }
  writeFile(file: HCFiler) {
    file.write(1);
    file.write(this.index);
  }
}
