export const ISPROXYKEY = "_isProxy";

export const defineProperty = (
  target: any,
  property: string,
  privateKey: string,
  isUpdate = false
) => {
  Object.defineProperty(target, property, {
    set: function (value) {
      if (value instanceof Array) {
        if (!this[privateKey]) {
          if (value[ISPROXYKEY]) this[privateKey] = value;
          else
            this[privateKey] = new Proxy(value, {
              set: (target, key, value, receiver) => {
                if (Reflect.get(target, key, receiver) !== value) {
                  this.writeSnapshoot();
                }
                const result = Reflect.set(target, key, value, receiver);
                if (isUpdate) this.update();
                return result;
              },
              get: (target, key, receiver) => {
                if (key === ISPROXYKEY) return true;
                //实体先被删除后在触发length = xxx
                if (key === "splice" || key === "pop" || key === "shift") {
                  this.writeSnapshoot();
                }
                return Reflect.get(target, key, receiver);
              },
            });
        } else {
          let arr = this[privateKey] as Array<any>;
          arr.length = 0;
          arr.push(...value);
        }
      } else {
        let oldv = this[privateKey];
        if (oldv !== value) {
          this.writeSnapshoot();
          this[privateKey] = value;
          if (isUpdate) this.update();
        }
      }
    },
    get: function () {
      return this[privateKey];
    },
    enumerable: true,
    configurable: true,
  });
};

/**属性修改自动记录更新 */
export function autoUpdate(
  target: any,
  property: string,
  descriptor?: PropertyDecorator
) {
  defineProperty(target, property, "__" + property, true);
}
