import { CreateBoxVertexData, Matrix, VertexData } from "../babylon";
import { Factory, HCFiler } from "@hc/core";
import { CenterType } from "../types/entity.types";
import { Solid3D } from "./Solid3D";

@Factory
export class Box extends Solid3D {
  static type = "Box";
  private width = 10;
  private height = 10;
  private depth = 10;
  private _center: CenterType = CenterType.Center;
  protected _name = "盒子";
  constructor(
    option = { width: 10, height: 10, depth: 10, center: CenterType.Center }
  ) {
    super();
    this.width = option.width ?? this.width;
    this.height = option.height ?? this.height;
    this.depth = option.depth ?? this.depth;
    this._center = option.center ?? CenterType.Center;
  }
  get Width() {
    return this.width;
  }
  set Width(v: number) {
    if (v === this.width) return;
    this.writeSnapshoot();
    this.width = v;
    this.update();
  }
  get Height() {
    return this.height;
  }
  set Height(v: number) {
    if (v === this.height) return;
    this.writeSnapshoot();
    this.height = v;
    this.update();
  }
  get Depth() {
    return this.depth;
  }
  set Depth(v: number) {
    if (v === this.depth) return;
    this.writeSnapshoot();
    this.depth = v;
    this.update();
  }
  get CenterPosition() {
    return this._center;
  }
  set CenterPosition(c: CenterType) {
    if (c === this.depth) return;
    this.writeSnapshoot();
    this._center = c;
    this.update();
  }
  get TranslateMtx() {
    let [x, y, z] = [0, 0, 0];
    switch (this._center) {
      case CenterType.Center:
        break;
      case CenterType.Bottom:
        z = this.depth / 2;
        break;
      case CenterType.Top:
        z = -this.depth / 2;
        break;
      case CenterType.Left:
        x = this.width / 2;
        break;
      case CenterType.Right:
        x = -this.width / 2;
        break;
      case CenterType.Front:
        y = -this.Height / 2;
        break;
      case CenterType.Back:
        y = this.Height / 2;
        break;
      case CenterType.LFB:
        [x, y, z] = [this.width / 2, this.height / 2, this.depth / 2];
        break;
      case CenterType.RFB:
        [x, y, z] = [-this.width / 2, this.height / 2, this.depth / 2];
        break;
      case CenterType.LBB:
        [x, y, z] = [this.width / 2, -this.height / 2, this.depth / 2];
        break;
      case CenterType.RBB:
        [x, y, z] = [-this.width / 2, -this.height / 2, this.depth / 2];
        break;
    }

    return Matrix.Translation(x, y, z);
  }
  get VertextData(): VertexData {
    const key = `${this.width}-${this.height}-${this.depth}-${this._center}`;
    let vd = Box.VertexDataCache.get(key);
    if (!vd) {
      vd = CreateBoxVertexData({
        width: this.width,
        height: this.height,
        depth: this.depth,
      });
      vd.transform(this.TranslateMtx);
      Box.VertexDataCache.set(key, vd);
    }
    return vd;
  }

  writeFile(file: HCFiler) {
    super.writeFile(file);
    file.write(1);
    file.write(this.width);
    file.write(this.height);
    file.write(this.depth);
    file.write(this._center);
  }
  readFile(file: HCFiler) {
    super.readFile(file);
    const ver = file.read();
    this.width = file.read();
    this.height = file.read();
    this.depth = file.read();
    this._center = file.read();
    this.update();
  }
}
