import {
  Factory,
  HCFiler,
  IRange,
  IVector2,
  IVector3,
  ObjectId,
} from "@hc/core";

import { AppConfig } from "../app/appConfig";
import {
  ParticleSystem as BParticleSystem,
  Color4,
  Texture,
  Vector2,
  Vector3,
} from "../babylon";
import { AppEvent } from "../core/AppEvent";
import { Box3 } from "../utils";
import { setVecLike2Vector, vec2Vec2Like, vec2Vec3Like } from "../utils/vector";
import { Entity } from "./../entitys/Entity";
import { Particle } from "./Particle";
import { SubEmitter } from "./SubEmitter";
import { ConeEmitter } from "./emiters/ConeEmiter";
import { CylinderEmitter } from "./emiters/CylinderEmitter";
import { DirectedCylinder } from "./emiters/DirectedCylinderEmitter";
import { Emiter } from "./emiters/Emiter";
import { HemisphericEmiter } from "./emiters/HemisphericEmiter";
import { SphereEmiter } from "./emiters/SphereEmiter";
import { EmiterShape, EmiterType } from "./emiters/types";

export type IGradient = {
  key: "Size" | "Color";
  period: number;
  value: any;
  otherValue?: any;
};

@Factory
export class ParticleSystem extends Particle {
  static type = "ParticleSystem";
  static Reading = false;
  private _intance: BParticleSystem;
  protected capacity = 1e3;
  protected _updateSpeed = 0.01;
  protected _size = { min: 0.1, max: 0.5 };
  protected _image: string;
  protected _scale = {
    min: { x: 0.1, y: 0.1 },
    max: { x: 0.5, y: 0.5 },
  };
  protected color1: string = "#CD581BFF";
  protected color2: string = "#000000FF";
  protected colorDead: string = "#00000000";
  protected _manualEmitCount = -1;
  protected emitPower = {
    min: 1,
    max: 3,
  };
  protected _angularSpeed = {
    min: 0,
    max: Math.PI,
  };
  protected _pivot = new Vector2();
  protected direction1 = new Vector3(0, 1);
  protected direction2 = new Vector3(0, 1);
  protected minEmitBox = new Vector3(-0.5, -0.5, -0.5);
  protected maxEmitBox = new Vector3(0.5, 0.5, 0.5);
  protected _gravity = new Vector3(0, 0);
  protected _rate = 100;
  protected lifeTime = {
    min: 0.3,
    max: 1.5,
  };
  protected spriteOption = {
    isAnimationSheetEnabled: false,
    spriteCellHeight: 32,
    spriteCellWidth: 32,
    spriteRandomStartCell: true,
    startSpriteCellID: 0,
    endSpriteCellID: 3,
    spriteCellChangeSpeed: 0,
  };
  protected emiterShape: EmiterShape = EmiterShape.Box;
  protected emiterType: EmiterType = EmiterType.Point;
  protected emiter: Emiter;
  protected emiterEntity: ObjectId<Entity>;
  protected emiterPosition = new Vector3();
  protected blendMode = BParticleSystem.BLENDMODE_ONEONE;
  private subParticleSystems: SubEmitter[] = [];
  private needUpdateSubEmiters = false;
  private gradients: IGradient[] = [];
  private _targetStopDuration = 0;
  private _position = Vector3.Zero();

  constructor(option?: {
    image: string;
    capacity?: number;
    manualEmitCount?: number;
  }) {
    super();
    if (option?.image) {
      this._image = option.image;
    }
    if (option?.capacity) {
      this.capacity = option.capacity;
    }
    if (option?.manualEmitCount) {
      this._manualEmitCount = option.manualEmitCount;
    }
  }
  get HasIntance() {
    return !!this._intance;
  }
  get Intance() {
    if (this._intance) return this._intance;
    this._intance = this.initParticle();
    if (this.Id) this.updateStatus();
    return this._intance;
  }
  get ManualEmitCount() {
    return this._manualEmitCount;
  }
  set ManualEmitCount(v: number) {
    if (v === this._manualEmitCount) return;
    this._manualEmitCount = v;
    this.update();
  }
  get Capacity() {
    return this.capacity;
  }
  set Capacity(v: number) {
    if (this.capacity === v) return;
    this.capacity = v;
    if (ParticleSystem.Reading) return;
    if (this._intance) {
      this._intance.dispose();
      this._intance = null;
    }
    this.Intance;
    this.updateStatus();
  }
  get UpdateSpeed() {
    return this._updateSpeed;
  }
  set UpdateSpeed(v: number) {
    if (v === this._updateSpeed) return;
    this._updateSpeed = v;
    this.update();
  }
  get Size() {
    return this._size;
  }
  set Size(v: IRange) {
    this._size = v;
    this.update();
  }
  get TextureUrl() {
    if (!this._image) return "";

    if (this._image.startsWith("http")) return this._image;
    else {
      return AppConfig.Texture_URL + this._image;
    }
  }
  get Image() {
    return this._image;
  }
  set Image(v: string) {
    if (v === this._image) return;
    this._image = v;
    this.update();
  }
  get Scale() {
    return this._scale;
  }
  set Scale(v: IRange<IVector2>) {
    this.writeSnapshoot();
    this._scale = v;
    this.update();
  }
  get Color1() {
    return this.color1;
  }
  set Color1(v: string) {
    this.color1 = v;
    this.update();
  }
  get Color2() {
    return this.color2;
  }
  set Color2(v: string) {
    if (v === this.color2) return;
    this.color2 = v;
    this.update();
  }
  get ColorDead() {
    return this.colorDead;
  }
  set ColorDead(v: string) {
    if (v === this.colorDead) return;
    this.colorDead = v;
    this.update();
  }
  get EmitPower() {
    return this.emitPower;
  }
  set EmitPower(v: IRange) {
    if (v === this.emitPower) return;
    this.emitPower = v;
    this.update();
  }
  get AngularSpeed() {
    return this._angularSpeed;
  }
  set AngularSpeed(v: IRange) {
    this._angularSpeed = v;
    this.update();
  }
  get Pivot() {
    return vec2Vec2Like(this._pivot);
  }
  set Pivot(v: IVector2) {
    setVecLike2Vector(this._pivot, v);
    this.update();
  }
  get Direction1() {
    return vec2Vec3Like(this.direction1);
  }
  set Direction1(v: IVector3) {
    setVecLike2Vector(this.direction1, v);
    this.update();
  }
  get Direction2() {
    return vec2Vec3Like(this.direction2);
  }
  set Direction2(v: IVector3) {
    setVecLike2Vector(this.direction2, v);
    this.update();
  }
  get MinEmitBox() {
    return vec2Vec3Like(this.minEmitBox);
  }
  set MinEmitBox(v: IVector3) {
    setVecLike2Vector(this.minEmitBox, v);
    this.update();
  }
  get MaxEmitBox() {
    return vec2Vec3Like(this.maxEmitBox);
  }
  set MaxEmitBox(v: IVector3) {
    setVecLike2Vector(this.maxEmitBox, v);
    this.update();
  }
  get Gravity() {
    return vec2Vec3Like(this._gravity);
  }
  set Gravity(v: IVector3) {
    setVecLike2Vector(this._gravity, v);
    this.update();
  }
  get EmiterPosition() {
    return vec2Vec3Like(this.emiterPosition);
  }
  set EmiterPosition(v: IVector3) {
    setVecLike2Vector(this.emiterPosition, v);
    this.update();
  }
  get Rate() {
    return this._rate;
  }
  set Rate(v: number) {
    if (v === this._rate) return;
    this._rate = v;
    this.update();
  }
  get LifeTime() {
    return this.lifeTime;
  }
  set LifeTime(v: IRange) {
    this.lifeTime = v;
    this.update();
  }
  get SpriteOption() {
    return this.spriteOption;
  }
  set SpriteOption(v: any) {
    this.spriteOption = v;
    this.update();
  }
  get EmiterType() {
    return this.emiterType;
  }
  set EmiterType(v: EmiterType) {
    if (v === this.emiterType) return;
    this.emiterType = v;
    this.updateEmitType();
  }
  get EmiterShape() {
    return this.emiterShape;
  }
  set EmiterShape(v: EmiterShape) {
    if (v === this.emiterShape) return;
    this.emiterShape = v;
    this.updateEmitShape();
  }
  get EmiterEntity() {
    return this.emiterEntity;
  }
  set EmiterEntity(v: ObjectId<Entity>) {
    this.emiterEntity = v;
    this.update();
  }
  get BlendMode() {
    return this.blendMode;
  }
  set BlendMode(v: number) {
    if (v === this.blendMode) return;
    this.blendMode = v;
    this.update();
  }
  get SubParticleSystems() {
    return [...this.subParticleSystems];
  }
  set SubParticleSystems(v: SubEmitter[]) {
    this.subParticleSystems = v;
    this.needUpdateSubEmiters = true;
    this.update();
  }
  get Emiter() {
    return this.emiter;
  }
  get Gradients() {
    return [...this.gradients];
  }
  set Gradients(v: IGradient[]) {
    this.removeGradients();
    this.gradients = v;
    this.updateGradient();
  }
  get TargetStopDuration() {
    return this._targetStopDuration;
  }
  set TargetStopDuration(v: number) {
    if (v === this._targetStopDuration) return;
    this._targetStopDuration = v;
    this.update();
  }
  get Position() {
    return vec2Vec3Like(this._position);
  }
  set Position(p: IVector3) {
    this.writeSnapshoot();
    setVecLike2Vector(this._position, p);
    this.update();
  }
  fillScene(box?: Box3) {
    const box3 = box ?? this.SceneSize;
    if (box3) {
      let { minimum, maximum } = box3;
      minimum = minimum.clone();
      maximum = maximum.clone();

      if (this.emiterType === EmiterType.Point) {
        const center = new Vector3(0, maximum.y + 10);
        minimum.y = 0;
        minimum.y = 0;
        this.emiterPosition = center;
        if (this.emiterShape === EmiterShape.Box) {
          this.minEmitBox = minimum;
          this.maxEmitBox = maximum;
          this.update();
        }
      } else {
        return;
      }
    }
  }
  appendGradient(v: IGradient) {
    this.gradients.push(v);
    this.updateGradient();
  }
  removeGradient(index: number) {
    if (this.gradients[index]) {
      const removeV = this.gradients.splice(index, 1);
      this.removeGradients(removeV);
    }
  }
  initParticle(): BParticleSystem {
    const pcs = new BParticleSystem(
      this.UniupeName,
      this.capacity,
      Entity.Scene
    );
    pcs.particleTexture = new Texture(this.TextureUrl, Entity.Scene);
    this.updateParticle(pcs);
    return pcs;
  }

  updateParticle(pcs: BParticleSystem) {
    pcs.translationPivot = this._pivot;

    const tur = pcs.particleTexture as Texture;
    if (tur.url !== this.TextureUrl) {
      tur.updateURL(this.TextureUrl);
    }

    this.updateEmitType(pcs);

    this.updateEmitShape(pcs);

    pcs.blendMode = this.blendMode;

    //box range
    pcs.minEmitBox = this.minEmitBox;
    pcs.maxEmitBox = this.maxEmitBox;

    //color
    if (this.color1) pcs.color1 = Color4.FromHexString(this.color1);
    if (this.color2) pcs.color2 = Color4.FromHexString(this.color2);
    if (this.colorDead) pcs.colorDead = Color4.FromHexString(this.colorDead);

    //size range
    pcs.minSize = this._size.min;
    pcs.maxSize = this._size.max;

    // Life time of each particle (random between...
    pcs.minLifeTime = this.lifeTime.min;
    pcs.maxLifeTime = this.lifeTime.max;
    pcs.targetStopDuration = this._targetStopDuration;

    // Emission rate
    pcs.emitRate = this._rate;
    // ps1.manualEmitCount = 100;

    // Set the gravity of all particles
    pcs.gravity = this._gravity;

    // Direction of each particle after it has been emitted
    pcs.direction1 = this.direction1;
    pcs.direction2 = this.direction2;

    //sprite
    if (this.spriteOption.isAnimationSheetEnabled) {
      pcs.isAnimationSheetEnabled = this.spriteOption.isAnimationSheetEnabled;
      pcs.spriteCellHeight = this.spriteOption.spriteCellHeight;
      pcs.spriteCellWidth = this.spriteOption.spriteCellWidth;
      pcs.spriteRandomStartCell = this.spriteOption.spriteRandomStartCell;
      pcs.startSpriteCellID = this.spriteOption.startSpriteCellID;
      pcs.endSpriteCellID = this.spriteOption.endSpriteCellID;
      pcs.spriteCellChangeSpeed = this.spriteOption.spriteCellChangeSpeed;
    } else {
      pcs.isAnimationSheetEnabled = this.spriteOption.isAnimationSheetEnabled;
    }

    // Angular speed, in radians
    pcs.minAngularSpeed = this._angularSpeed.min;
    pcs.maxAngularSpeed = this._angularSpeed.max;

    // Speed
    pcs.minEmitPower = this.emitPower.min;
    pcs.maxEmitPower = this.emitPower.max;

    pcs.updateSpeed = this._updateSpeed;
    pcs.manualEmitCount = this._manualEmitCount;

    if (this.needUpdateSubEmiters) {
      this.updateSubEmiters(pcs);
    }

    this.updateGradient(pcs);

    pcs.worldOffset.copyFrom(this._position);
  }

  update() {
    if (ParticleSystem.Reading) return;
    if (this._intance) {
      this.updateParticle(this._intance);
    }
  }
  start() {
    if (this._intance) {
      this._intance.start();
    }
  }
  stop() {
    if (this._intance) {
      this._intance.stop();
    }
  }
  destory(): void {
    super.destory();
    if (this._intance) {
      this._intance.dispose();
      this._intance = null;
      for (const sub of this.subParticleSystems) {
        sub.destory();
      }
    }
  }
  fromJSON(
    json: {
      [P in keyof ParticleSystem]: P extends "SubParticleSystems"
        ? Record<string, any>[]
        : ParticleSystem[P];
    },
    isPart = false
  ) {
    if (!isPart) ParticleSystem.Reading = true;
    let emiterJson: Record<string, any>;
    for (const key in json) {
      if (key in this) {
        if (key === "SubParticleSystems") {
          const subs = json.SubParticleSystems.map((s) =>
            new SubEmitter(new ParticleSystem()).fromJSON(s)
          );
          this.subParticleSystems = subs;
          this.needUpdateSubEmiters = true;
        } else if (key === "Emiter") {
          emiterJson = json[key];
        } else {
          this[key] = json[key];
        }
      }
    }
    if (!isPart) ParticleSystem.Reading = false;

    if (emiterJson && this.emiter) {
      this.emiter.fromJSON(emiterJson);
      setTimeout(() => {
        this.emiter.AttachSystem = this;
      }, 0);
    }

    // this.update();
    return this;
  }
  erase(e?: boolean): void {
    super.erase(e);
    if (this._intance) this._intance.stop();
    AppEvent.trigger({ type: "remove", target: this });
    this.trigger({ type: "remove", target: this });
  }
  toJSON() {
    return {
      capacity: this.capacity,
      updateSpeed: this._updateSpeed,
      size: { ...this._size },
      image: this._image,
      scale: this._scale,
      color1: this.color1,
      color2: this.color2,
      colorDead: this.colorDead,
    };
  }
  clone() {
    const pcs = new ParticleSystem({
      image: this._image,
      capacity: this.capacity,
    });
    let filer = new HCFiler();
    this.writeFile(filer);
    pcs.readFile(filer);
    this._id = null;

    return pcs;
  }
  writeFile(filer: HCFiler): void {
    super.writeFile(filer);
    filer.write(2);
    filer.write(this.capacity);
    filer.write(this._manualEmitCount);
    filer.write(this._updateSpeed);
    filer.write(this._size.min);
    filer.write(this._size.max);
    filer.write(this._image);

    filer.write(this._scale.min.x);
    filer.write(this._scale.min.y);
    filer.write(this._scale.max.x);
    filer.write(this._scale.max.y);

    filer.write(this.color1);
    filer.write(this.color2);
    filer.write(this.colorDead);

    filer.write(this.emitPower.min);
    filer.write(this.emitPower.max);

    filer.write(this._angularSpeed.min);
    filer.write(this._angularSpeed.max);

    filer.write(this._pivot.x);
    filer.write(this._pivot.y);

    filer.writeArray(this.direction1.asArray());
    filer.writeArray(this.direction2.asArray());
    filer.writeArray(this.minEmitBox.asArray());
    filer.writeArray(this.maxEmitBox.asArray());
    filer.writeArray(this._gravity.asArray());

    filer.write(this._rate);

    filer.write(this.lifeTime.min);
    filer.write(this.lifeTime.max);
    filer.write(this._targetStopDuration);

    filer.write(this.spriteOption.isAnimationSheetEnabled);
    filer.write(this.spriteOption.spriteCellHeight);
    filer.write(this.spriteOption.spriteCellWidth);
    filer.write(this.spriteOption.spriteRandomStartCell);
    filer.write(this.spriteOption.startSpriteCellID);
    filer.write(this.spriteOption.endSpriteCellID);
    filer.write(this.spriteOption.spriteCellChangeSpeed);

    filer.write(this.emiterShape);
    filer.write(this.emiterType);
    filer.writeArray(this.emiterPosition.asArray());
    filer.writeObjectId(this.emiterEntity);

    filer.write(this.blendMode);

    filer.writeAnyArray(this.subParticleSystems, filer.writeObject);

    filer.writeObject(this.emiter);

    filer.writeAnyArray(this.gradients, (g) => {
      filer.write(g.key);
      filer.write(g.period);
      filer.write(g.value);
      filer.write(g.otherValue);
    });

    filer.writeVector3(this._position);
  }
  readFile(filer: HCFiler): void {
    super.readFile(filer);
    const ver = filer.read();

    this.capacity = filer.read();
    this._manualEmitCount = filer.read();
    this._updateSpeed = filer.read();
    this._size.min = filer.read();
    this._size.max = filer.read();
    this._image = filer.read();

    this._scale.min.x = filer.read();
    this._scale.min.y = filer.read();
    this._scale.max.x = filer.read();
    this._scale.max.y = filer.read();

    this.color1 = filer.read();
    this.color2 = filer.read();
    this.colorDead = filer.read();

    this.emitPower.min = filer.read();
    this.emitPower.max = filer.read();

    this._angularSpeed.min = filer.read();
    this._angularSpeed.max = filer.read();

    this._pivot.x = filer.read();
    this._pivot.y = filer.read();

    let count = filer.read();
    let arr = filer.readArray(count);
    this.direction1.fromArray(arr);

    count = filer.read();
    arr = filer.readArray(count);
    this.direction2.fromArray(arr);

    count = filer.read();
    arr = filer.readArray(count);
    this.minEmitBox.fromArray(arr);

    count = filer.read();
    arr = filer.readArray(count);
    this.maxEmitBox.fromArray(arr);

    count = filer.read();
    arr = filer.readArray(count);
    this._gravity.fromArray(arr);

    this._rate = filer.read();

    this.lifeTime.min = filer.read();
    this.lifeTime.max = filer.read();
    this._targetStopDuration = filer.read();

    this.spriteOption.isAnimationSheetEnabled = filer.read();
    this.spriteOption.spriteCellHeight = filer.read();
    this.spriteOption.spriteCellWidth = filer.read();
    this.spriteOption.spriteRandomStartCell = filer.read();
    this.spriteOption.startSpriteCellID = filer.read();
    this.spriteOption.endSpriteCellID = filer.read();
    this.spriteOption.spriteCellChangeSpeed = filer.read();

    this.emiterShape = filer.read();
    this.emiterType = filer.read();

    count = filer.read();
    arr = filer.readArray(count);
    this.emiterPosition.fromArray(arr);
    this.emiterEntity = filer.readObjectId() as any;

    this.blendMode = filer.read();

    filer.readAnyArrayAndPush(this.subParticleSystems, filer.readObject);
    this.needUpdateSubEmiters = this.subParticleSystems.length > 0;

    this.emiter = filer.readObject(this.emiter);

    if (this.emiter && !this.emiter.AttachSystem) {
      setTimeout(() => {
        this.emiter.AttachSystem = this;
      }, 0);
    }
    this.gradients.length = 0;
    filer.readAnyArray(() => {
      const g: IGradient = { key: "Size", period: 0, value: 0, otherValue: 0 };
      g.key = filer.read();
      g.period = filer.read();
      g.value = filer.read();
      g.otherValue = filer.read();
      this.gradients.push(g);
    });

    if (ver > 1) {
      filer.readVector3(this._position);
    }

    this.update();
    this.updateStatus();
  }
  private updateSubEmiters(pcs = this._intance) {
    if (pcs) {
      pcs.subEmitters = [this.subParticleSystems.map((s) => s.Intance)];
      this.updateStatus();
      this.needUpdateSubEmiters = false;
    }
  }
  private updateEmitShape(pcs = this._intance) {
    this.emiter = null;
    switch (this.emiterShape) {
      case EmiterShape.Point:
        if (pcs) pcs.createPointEmitter(this.direction1, this.direction2);
        break;
      case EmiterShape.Box:
        if (pcs)
          pcs.createBoxEmitter(
            this.direction1,
            this.direction2,
            this.minEmitBox,
            this.maxEmitBox
          );
        break;
      case EmiterShape.Sphere:
        this.emiter = new SphereEmiter(this);
        break;
      case EmiterShape.Hemisphere:
        this.emiter = new HemisphericEmiter(this);
        break;
      case EmiterShape.Cylinder:
        this.emiter = new CylinderEmitter(this);
        break;
      case EmiterShape.DirctedCylinder:
        this.emiter = new DirectedCylinder(this);
        break;
      case EmiterShape.Cone:
        this.emiter = new ConeEmitter(this);
        break;
      case EmiterShape.Mesh:
        break;
      case EmiterShape.Custom:
        break;
    }
  }
  private updateEmitType(pcs = this._intance) {
    if (!pcs) return;
    switch (this.emiterType) {
      case EmiterType.Point:
        pcs.emitter = this.emiterPosition;
        break;
      case EmiterType.Entity:
        if (this.emiterEntity?.Object)
          pcs.emitter = this.emiterEntity.Object.RenderObject;
        break;
      case EmiterType.Camera:
        //@ts-ignore
        pcs.emitter = Entity.Scene.activeCamera;
    }
  }
  private updateGradient(pcs = this._intance) {
    if (!pcs) return;
    for (const gradient of this.gradients) {
      switch (gradient.key) {
        case "Size":
          pcs.removeSizeGradient(gradient.period);
          pcs.addSizeGradient(
            gradient.period,
            gradient.value,
            gradient.otherValue
          );
          break;
        case "Color":
          pcs.removeColorGradient(gradient.period);
          pcs.addColorGradient(
            gradient.period,
            Color4.FromHexString(gradient.value),
            Color4.FromHexString(gradient.otherValue)
          );
          break;
      }
    }
  }
  private removeGradients(
    gs: IGradient[] = this.gradients,
    pcs = this._intance
  ) {
    if (!pcs) return;
    for (const gradient of gs) {
      switch (gradient.key) {
        case "Size":
          pcs.removeSizeGradient(gradient.period);
          break;
        case "Color":
          pcs.removeColorGradient(gradient.period);
          break;
      }
    }
  }
}
