import { SphereParticleEmitter } from "../../babylon";
import { Factory } from "@hc/core";
import type { ParticleSystem } from "../ParticleSystem";
import { RadiusEmiter } from "./RadiusEmiter";

@Factory
export class SphereEmiter extends RadiusEmiter {
  static type = "SphereEmiter";
  protected _emiter: SphereParticleEmitter;
  constructor(
    protected attachSystem: ParticleSystem,
    option?: { radius?: number; radiusRange?: number }
  ) {
    super(attachSystem, option);
    if (attachSystem?.HasIntance) this.setEmiter();
  }
  setEmiter() {
    this._emiter = this.attachSystem.Intance.createSphereEmitter(
      this._radius,
      this._radiusRange
    );
  }
}
