import { AbstractMesh, Mesh, PointerInfo } from "../babylon";
import { Entity } from "../entitys/Entity";
import {
  IPointerEvent,
  PointerCallback,
  PointerEventTypes,
  PointTypeEnum,
} from "../types/event";
import { Viewer } from "./../viewer/Viewer";
import { getEntity, getRoot } from "./../utils/utils";
import { AppEvent } from "../core/AppEvent";
import { IVector3 } from "@hc/core";

export class MouseControl {
  private _pointerdownEvs = new Set<PointerCallback>();
  private _pointermoveEvs = new Set<PointerCallback>();
  private _pointerupEvs = new Set<PointerCallback>();
  private _pointerwheelEvs = new Set<PointerCallback>();
  private _pointerPickEvs = new Set<PointerCallback>();
  private _pointerTapEvs = new Set<PointerCallback>();
  private _pointerDbTapEvs = new Set<PointerCallback>();
  constructor(private _viewer: Viewer) {
    this._register();
  }
  get Scene() {
    return this._viewer.Scene;
  }
  get ScreenPoint() {
    return {
      x: this.Scene.pointerX,
      y: this.Scene.pointerY,
    };
  }
  get WorldPoint(): IVector3 | null {
    if (this._viewer.ground) this._viewer.ground.isPickable = true;
    const pickInfo = this.Scene.pick(
      this.Scene.pointerX,
      this.Scene.pointerY,
      (mesh) => mesh.name === "WORLD_GROUND"
    );
    if (this._viewer.ground) this._viewer.ground.isPickable = false;
    if (pickInfo.hit) {
      return pickInfo.pickedPoint;
    }

    return null;
  }
  getCurrentEntity(x?: number, y?: number) {
    const p = this.ScreenPoint;
    const info = this.Scene.pick(x ?? p.x, y ?? p.y);
    if (info?.hit && info?.pickedMesh) {
      const target = getRoot(info.pickedMesh);
      return {
        entity: getEntity(target),
        pickMesh: info.pickedMesh,
      };
    }
    return null;
  }
  on(typeName: string, callback: PointerCallback) {
    const type = PointerEventTypes[typeName.toUpperCase()];
    switch (type) {
      case PointerEventTypes.POINTERDOWN:
        this._pointerdownEvs.add(callback);
        break;
      case PointerEventTypes.POINTERMOVE:
        this._pointermoveEvs.add(callback);
        break;
      case PointerEventTypes.POINTERUP:
        this._pointerupEvs.add(callback);
        break;
      case PointerEventTypes.POINTERWHEEL:
        this._pointerwheelEvs.add(callback);
        break;
      case PointerEventTypes.POINTERPICK:
        this._pointerPickEvs.add(callback);
        break;
      case PointerEventTypes.POINTERTAP:
        this._pointerTapEvs.add(callback);
        break;
      case PointerEventTypes.POINTERDOUBLETAP:
        this._pointerDbTapEvs.add(callback);
        break;
    }
    return () => {
      this.off(typeName, callback);
    };
  }
  off(typeName: string, callback: PointerCallback) {
    if (!typeName) return;
    const type = PointerEventTypes[typeName.toUpperCase()];
    switch (type) {
      case PointerEventTypes.POINTERDOWN:
        this._pointerdownEvs.delete(callback);
        break;
      case PointerEventTypes.POINTERMOVE:
        this._pointermoveEvs.delete(callback);
        break;
      case PointerEventTypes.POINTERUP:
        this._pointerupEvs.delete(callback);
        break;
      case PointerEventTypes.POINTERWHEEL:
        this._pointerwheelEvs.delete(callback);
        break;
      case PointerEventTypes.POINTERPICK:
        this._pointerPickEvs.delete(callback);
        break;
      case PointerEventTypes.POINTERTAP:
        this._pointerTapEvs.delete(callback);
        break;
      case PointerEventTypes.POINTERDOUBLETAP:
        this._pointerDbTapEvs.delete(callback);
        break;
    }
  }

  private getEvent(info: PointerInfo): IPointerEvent {
    let target: AbstractMesh;
    let entity: Entity;
    let pickedMesh: AbstractMesh;

    if (info.pickInfo?.hit && info.pickInfo?.pickedMesh) {
      target = getRoot(info.pickInfo.pickedMesh);
      entity = getEntity(target);
      pickedMesh = info.pickInfo.pickedMesh as AbstractMesh;
    }
    return {
      type: PointTypeEnum[info.type],
      screenX: this.Scene.pointerX,
      screenY: this.Scene.pointerY,
      worldPoint: this.WorldPoint,
      target: entity,
      object: target,
      event: info.event as MouseEvent,
      pickedMesh,
      pickInfo: info.pickInfo,
    };
  }
  private _register() {
    this._viewer.Scene.onPointerObservable.add((info) => {
      const event = this.getEvent(info);

      //点击到实体，触发实体对应监听事件
      if (event.target) {
        event.target.trigger(event);
      }

      let funcList: Set<PointerCallback>;
      switch (info.type) {
        case PointerEventTypes.POINTERDOWN:
          funcList = this._pointerdownEvs;
          break;
        case PointerEventTypes.POINTERUP:
          funcList = this._pointerupEvs;
          break;
        case PointerEventTypes.POINTERMOVE:
          funcList = this._pointermoveEvs;
          break;
        case PointerEventTypes.POINTERWHEEL:
          funcList = this._pointerwheelEvs;
          break;
        case PointerEventTypes.POINTERPICK:
          funcList = this._pointerPickEvs;
          break;
        case PointerEventTypes.POINTERTAP:
          funcList = this._pointerTapEvs;
          break;
        case PointerEventTypes.POINTERDOUBLETAP:
          funcList = this._pointerDbTapEvs;
          break;
      }

      funcList.forEach((f) => f(event));
      AppEvent.trigger(event);
    });
  }
}
