import { BaseObject, Factory, HCFiler, ISerialize } from "@hc/core";

import {
  Animation as BAnimation,
  AnimationGroup as BAnimationGroup,
  Camera,
  TransformNode,
} from "../babylon";
import type { Entity } from "./../entitys/Entity";
import { IAnimation } from "./../types/animation.types";
import { Animation } from "./Animation";

//https://playground.babylonjs.com/#9WUJN#1965

@Factory
export class AnimationGroup implements IAnimation, ISerialize {
  static type = "AnimationGroup";
  private _animation: Animation[] = [];
  protected _group: BAnimationGroup;
  protected _name = "动画组";
  constructor(protected _entity?: Entity | Camera) {
    this.init();
  }
  get AnimationGroup() {
    return this._group;
  }
  get Target() {
    if (this._entity instanceof Camera) {
      return this._entity;
    }
    return (
      (this._entity.RenderObject.parent as TransformNode) ||
      this._entity.RenderObject
    );
  }
  protected init() {
    const group = new BAnimationGroup(this.name);
    for (const n of this._animation) {
      group.addTargetedAnimation(n.Animation as BAnimation, this.Target);
    }
    this._group = group;
  }
  get name() {
    return this._name;
  }
  set name(v: string) {
    this._name = v;
  }
  /**
   * 开启动画
   */
  start() {
    this._group.start();
  }
  /**
   * 异步开始动画组,如果动画存在循环,那会无法出循环状态,需要手动停止
   * @returns
   */
  startAync() {
    this.start();
    return new Promise((res) => {
      this._group.onAnimationGroupEndObservable.add(res);
    });
  }
  pause() {
    this._group.pause();
  }
  restart() {
    this._group.restart();
  }
  stop() {
    this._group.stop();
  }
  reset() {
    this._group.reset();
  }
  append(animate: Animation) {
    this._group.addTargetedAnimation(
      animate.Animation as BAnimation,
      this.Target
    );
  }
  writeFile(file: HCFiler) {
    file.write(1);
    const isCamera = this._entity instanceof Camera;
    file.write(isCamera);
    if (!isCamera) file.writeHardObjectId((this._entity as Entity).ObjectId);
    file.writeAnyArray(this._animation, file.writeObject);
  }
  readFile(file: HCFiler) {
    file.read(); // 版本
    let isCamera = file.read();
    if (!isCamera) {
      const obj = file.readHardObjectId();
      this._entity = obj.Object as Entity;
    }

    file.readAnyArrayAndPush(this._animation, file.readObject);

    this.init();
  }
}
