import { Factory, HCFiler } from "@hc/core";
import { Entity } from "./Entity";
import { RenderType } from "../types/renderTypes";
import { Curve } from "./curves/Curve";
import { autoRecord } from "../core";
import { CreateTube, Mesh, Vector3 } from "../babylon";
import { Lines } from "./curves/Lines";
import { Solid3D } from "./Solid3D";

/**
 * 管道
 *
 */
@Factory
export class Tube extends Solid3D {
  static type = "Tube";
  private _curve: Curve;
  @autoRecord Radius: number;
  constructor(option?: { radius?: number; curve: Curve }) {
    super();
    this.Radius = option?.radius ?? 10;
    this._curve = option?.curve ?? new Lines();
  }
  get Curve() {
    return this._curve;
  }
  initDrawObject(renderType = RenderType.Conceptual) {
    return CreateTube(
      this.UniqueName,
      {
        path: this._curve.Points,
        sideOrientation: Mesh.DOUBLESIDE,
        updatable: true,
      },
      Entity.Scene
    );
  }
  updateDrawObject(renderType: RenderType, obj: Mesh) {
    CreateTube(this.UniqueName, {
      path: this._curve.Points,
      instance: obj,
    });
  }
  /**
   * 管道变形动画
   * @param updateFunction 变形函数 path 为各个节点，k为当前帧
   * @param speed 变形速度，默认为0.01
   * @see https://hc3dol.gkiiot.com/#19
   * @returns
   */
  morph(updateFunction: (path: Vector3[], k: number) => void, speed = 0.01) {
    if (!updateFunction) return;
    const originPoints = this._curve.Points;

    let k = 0;
    let pause = false;
    const points = originPoints.map((p) => p.clone());

    const callback = () => {
      if (pause) return;
      updateFunction(points, k);
      CreateTube(null, { path: points, instance: this.RenderObject });
      k += speed;
    };
    Entity.Scene.registerBeforeRender(callback);

    return {
      stop: () => {
        Entity.Scene.unregisterBeforeRender(callback);
        this.update();
      },
      pause: () => {
        pause = true;
      },
      restart: () => {
        pause = false;
      },
    };
  }
  writeFile(file: HCFiler) {
    super.writeFile(file);
    file.write(1);
  }
  readFile(file: HCFiler) {
    super.readFile(file);
    file.read(); // 版本
    this.update();
  }
}
