import { Color4, Scene, PointsCloudSystem, Vector3 } from "../babylon";

let pcs: PointsCloudSystem;
/**渲染点 */
export function renderPoints(points: Vector3[], scene: Scene) {
  // if (pcs?.mesh) pcs.dispose();
  let pcs = new PointsCloudSystem("pcs", 12, scene);
  function myfunc(particle, i) {
    particle.position = points[i];
    particle.color = new Color4(
      Math.random(),
      Math.random(),
      Math.random(),
      Math.random()
    );
    particle.pivot = Vector3.Zero();
  }
  pcs.addPoints(points.length, myfunc);
  pcs.buildMeshAsync();
}

export function disposePoint() {
  pcs && pcs.dispose();
}
