import { IVector3 } from "@hc/core";
import { Factory, HCFiler } from "@hc/core";

import { appCache } from "../app/AppCache";
import { AppStore } from "../app/AppStore";
import { AppConfig } from "../app/appConfig";
import {
  AssetContainer,
  Color3,
  CreateBoxVertexData,
  Material,
  Matrix,
  Mesh,
  MeshBuilder,
  PBRMaterial,
  SceneLoader,
  StandardMaterial,
  TransformNode,
  Vector3,
} from "../babylon";
import { AppEvent } from "../core/AppEvent";
import { EventEnum, UpdateFlag } from "../enums";
import { PBRMaterialRecord } from "../record/PBRMaterialRecord";
import { RenderType } from "../types/renderTypes";
import { arrayLast } from "../utils";
import { iVector2Vector } from "../utils/vector";
import { Box3 } from "./../utils/Box3";
import { Entity } from "./Entity";

export enum LockSizeType {
  None = 0b000,
  X = 0b001,
  Y = 0b010,
  Z = 0b100,
  All = 0b111,
}

@Factory
export class ImportEntity extends Entity {
  static type = "ImportEntity";
  #isReady = true;
  private __ver: number;
  public isRead = false;
  private url: string;
  private _preSize: IVector3 = { x: 10, y: 10, z: 10 };
  public isAsync = true;
  public originBoundbox: Box3;
  public lockSizeType = LockSizeType.X;
  constructor(option = { url: "" }) {
    super();
    this.url = option?.url ?? "";
  }
  get isVaildUrl() {
    return this.url && typeof this.url === "string";
  }
  get Opacity() {
    return this._opacity;
  }
  set Opacity(v: number) {
    if (v === this._opacity) return;
    this.writeSnapshoot();
    this._opacity = v;
    const meshs = this._drawObject.getChildMeshes();
    for (const m of meshs) {
      if (m.material) {
        //FIXME:航运的材质需要需要修改
        m.material.transparencyMode = Material.MATERIAL_ALPHABLEND;
        m.material.alpha = v;
      }
    }
  }
  get Source() {
    return this.url;
  }
  set Source(v: string) {
    if (v === this.url) return;
    this.writeSnapshoot();
    //如果是空盒子，根据盒子大小自适应模型
    if (!this.url) {
      this.once("load", (evt) => {
        setTimeout(() => {
          const model = evt.target as ImportEntity;
          const box = model.originBoundbox;
          const size = box.Size;
          const oldScale = model.Scale;
          const scale = { x: 1, y: 1, z: 1 };
          if (this.lockSizeType & LockSizeType.X) {
            scale.x = Math.abs(model.PreSize.x) / Math.abs(size.x);
            scale.y = scale.x * oldScale.y;
            scale.z = scale.x * oldScale.z;
            scale.x *= oldScale.x;
          }
          if (this.lockSizeType & LockSizeType.Y) {
            scale.y =
              (Math.abs(model.PreSize.y) / Math.abs(size.y)) * oldScale.y;
          }
          if (this.lockSizeType & LockSizeType.Z) {
            scale.z =
              (Math.abs(model.PreSize.z) / Math.abs(size.z)) * oldScale.z;
          }
          model.Scale = scale;
        }, 100);
      });
    }
    this.url = v;
    const parent = this.RenderObject;
    if (parent) {
      const children = parent.getChildren();
      for (const c of children) {
        c.dispose();
      }
    }
    this.isRead = true;
    this.load(this.RenderType, this.RenderObject);
  }
  get PreSize() {
    return { ...this._preSize };
  }
  set PreSize(size: IVector3) {
    Object.assign(this._preSize, size);
    if (!this.isVaildUrl && this.Id) {
      const renderObject = this.RenderObject;
      if (renderObject) {
        const box = renderObject
          .getChildren()
          .find((m) => m.name === "虚拟模型盒子") as Mesh;
        if (box) {
          const boxVerData = CreateBoxVertexData({
            width: this._preSize.x,
            height: this._preSize.y,
            depth: this._preSize.z,
          });
          boxVerData.applyToMesh(box, true);
          box.enableEdgesRendering();
        }
      }
    }
  }
  get WorldPreSize() {
    const size = iVector2Vector(this._preSize);
    Vector3.TransformCoordinatesToRef(
      size,
      this.Matrix.getRotationMatrix(),
      size
    );
    return size;
  }
  /**把模型的左下点归零 */
  move2Zero(moveVec?: Vector3) {
    const root = this.RenderObject;

    if (!moveVec) {
      const v = this.BoundingVectors;
      moveVec = Vector3.Center(v.min, v.max).negate();
      moveVec.y = 0;
    }
    this.Position = root.position.add(moveVec);

    return moveVec;
  }
  get BoundingBox() {
    const { min, max } = this.BoundingVectors;
    return new Box3(min, max);
  }
  get LocalBoundingBox() {
    const { min, max } = this.BoundingVectors;
    return new Box3(min, max, this.Matrix.invert());
  }
  initDrawObject(renderType: RenderType) {
    const node = new Mesh("import" + this.Id, Entity.Scene);
    node.doNotSyncBoundingInfo = true;

    if (this.isVaildUrl) {
      this.load(renderType, node, true);
    } else {
      const box = MeshBuilder.CreateBox(
        "虚拟模型盒子",
        {
          width: this._preSize.x,
          height: this._preSize.y,
          depth: this._preSize.z,
        },
        Entity.Scene
      );
      box.bakeTransformIntoVertices(
        Matrix.Translation(0, this._preSize.y / 2, 0)
      );
      box.visibility = 0.2;
      box.enableEdgesRendering();
      box.edgesColor = Color3.Gray().toColor4();
      box.edgesWidth = 8;
      node.addChild(box);
    }

    return node;
  }
  updateDrawObject(renderType: RenderType, obj: Mesh) {
    super.updateDrawObject(renderType, obj);
  }

  updateMaterial(type?: RenderType, obj?: Mesh) {
    const m = obj ?? (this.RenderObject as Mesh);
    const meshs = m.getChildMeshes();
    for (const m of meshs) {
      const mtlId = this._materialMap.get(m.id);
      if (mtlId) {
        if (mtlId.Object?.Material) {
          m.metadata = {
            oldMtl: m.material,
          };
          m.material = mtlId.Object.Material;
        }
      } else {
        if (m.metadata?.oldMtl) {
          m.material = m.metadata.oldMtl;
        }
      }
    }
  }
  getReady() {
    return new Promise((res) => {
      if (this.#isReady) {
        res(true);
      }
      this.once(EventEnum.Load, () => res(true));
    });
  }
  writeFile(file: HCFiler) {
    super.writeFile(file);
    file.write(2);
    file.write(this.url);
    file.writeVector3(this._preSize);
  }
  readFile(file: HCFiler) {
    this.isRead = true;
    super.readFile(file);
    const ver = file.read();
    this.url = file.read();
    if (AppConfig.isEncrypt) this.encrypt();
    if (ver > 1) file.readVector3(this._preSize);
    this.update();
  }
  private model2Zero(root: TransformNode) {
    // if (this.isPart) return;
    const v = root.getHierarchyBoundingVectors();
    const moveVec = Vector3.Center(v.min, v.max);
    moveVec.y = v.min.y;
    const vec = moveVec.negate();
    root.position = vec;

    if (!this.isRead) {
      this.Position = vec.negate();
      this.isRead = false;
    }

    this.originBoundbox = new Box3(v.min, v.max);
  }
  private handleAssetContainer(asset: AssetContainer) {
    let entries = asset.instantiateModelsToScene((n) => n + (this.Id ?? ""));
    this.modelAnimations = entries.animationGroups;
    let root: TransformNode;
    if (entries.rootNodes.length === 1) {
      root = entries.rootNodes[0];
    } else {
      root = new TransformNode("assetContainerRootMesh", Entity.Scene);

      entries.rootNodes.forEach((m) => {
        if (!m.parent) {
          m.parent = root;
        }
      });
    }
    root.getChildMeshes().forEach((m) => {
      m.isPickable = this.IsPickable;
    });

    this._ownMaterials.length = 0;
    for (const mtl of asset.materials) {
      if (mtl instanceof PBRMaterial) {
        const mtlRecord = new PBRMaterialRecord(mtl, this);
        mtlRecord.Name = mtl.name;
        mtlRecord.updateFromDiff();
        this._ownMaterials.push(mtlRecord);
      }
    }
    return root;
  }
  private load(renderType: RenderType, node: Mesh, needWait = false) {
    if (this.url && node) {
      this.#isReady = false;
      this.encrypt();
      let url = this.url.startsWith("http")
        ? this.url
        : AppConfig.assetURL + this.url;

      if (!this.Name) this.Name = arrayLast(this.url.split("/")).split(".")[0];

      const key = url + "&" + Entity.Scene.uid;

      const AssetPromise = appCache.getAssetPromise(key);

      if (AssetPromise) {
        AssetPromise.then((res) => {
          if (res) {
            const root = this.handleAssetContainer(res);
            //模型坐标调整
            this.model2Zero(root);
            root.parent = node;
            this.updateMaterial(renderType, node);
            this.update(UpdateFlag.Geometry);
          }
          this.#isReady = true;
          this.trigger2({ type: "load", target: this });
        });
      } else {
        let resolve: (v) => void;

        if (needWait) {
          resolve = AppStore.Wait();
        }

        appCache.setAssetPromise(
          key,
          AppStore.getLastPromise() as Promise<AssetContainer>
        );

        const pulgin = SceneLoader.LoadAssetContainer(
          url,
          undefined,
          Entity.Scene,
          (res: AssetContainer) => {
            if (
              !this.isPart &&
              !this._db &&
              Entity.Scene === AppStore.MainScene
            )
              return;

            for (const mtl of res.materials) {
              if (mtl instanceof StandardMaterial) {
                mtl.diffuseColor = Color3.White();
                mtl.ambientColor = Color3.White();
                // mtl.backFaceCulling = false;
              }

              mtl.alpha = this._opacity;
            }

            const drawObject = node;
            //纹理会重复添加
            res.textures.length = 0;

            res.meshes.forEach(
              (m) => m instanceof Mesh && (m.receiveShadows = true)
            );

            const root = this.handleAssetContainer(res);

            if (renderType === RenderType.Wireframe) {
              res.materials.forEach((m) => (m.wireframe = true));
            }
            //模型坐标调整
            this.model2Zero(root);
            root.parent = drawObject;
            this.trigger2({ type: EventEnum.Load, target: this });
            this.#isReady = true;
            this.updateMaterial(renderType, node);
            this.update(UpdateFlag.Geometry);
            resolve?.(res);
          },
          (event) => {
            const percentage = event.lengthComputable
              ? Math.floor((event.loaded / event.total) * 100)
              : 0;
            this.trigger2({
              type: EventEnum.Loading,
              target: this,
              percentage,
            });
          },
          (scene, msg, event) => {
            console.error("innerError: ", event.innerError);
            console.error("err_msg: ", msg);
            if (
              !this.isPart &&
              !this._db &&
              Entity.Scene === AppStore.MainScene
            )
              return;
            resolve?.(false);
            this.trigger({
              type: EventEnum.Load,
              target: this,
              error: event.innerError,
            });
            AppEvent.trigger({
              type: EventEnum.Load,
              target: this,
              error: msg,
            });
            this.#isReady = true;
          }
        );
        AppConfig.loadPlugins.add(pulgin);
      }
    }
  }
  private encrypt() {
    if (this.url.endsWith(".hc3d")) {
      return;
    } else {
      if (AppConfig.isEncrypt)
        [".gltf", ".glb", ".obj", ".STL"].forEach(
          (s) => (this.url = this.url.replace(s, ".hc3d"))
        );
    }
  }
}
