import type { Application } from "@hc/engine";
import { AppConfig } from "@hc/engine";
import { action, observable } from "mobx";
import { AppApi } from "./apis";
import { getReq, ResponseStatus } from "./utils/axios";

export const App: {
  intance: Application | null;
  prefix: string;
} = observable({
  intance: null,
  prefix: "",
});

export const setAppIntance = action((intance: Application) => {
  App.intance = intance;
});

export const setPrefix = action(async () => {
  if (App.prefix) return;
  const res = await getReq(AppApi.prefix);
  if (res.code === ResponseStatus.Ok) {
    App.prefix = res.data.prefix;
    AppConfig.assetURL = res.data.prefix;
  }
});
