import { autoUpdate } from "../../core/autoUpdate";
import { Entity } from "../Entity";
import { HCFiler } from "@hc/core";
import {
  IShadowLight,
  Mesh,
  ShadowGenerator,
  TransformNode,
  Vector3,
} from "../../babylon";
import { AppStore } from "../../app/AppStore";
import { RenderTargetTexture } from "@babylonjs/core/Materials/Textures/renderTargetTexture";

export abstract class Light extends Entity {
  isLight = true;
  @autoUpdate Intensity = 1;
  /**颜色 */
  @autoUpdate Diffuse = "#ffffff";
  /**高光 */
  @autoUpdate Specular = "#ffffff";
  @autoUpdate VisiableHelper = true;
  /**偏差 */
  private _bias = 0.0001;
  /**深度偏差 */
  private _normalBias = 0;
  private _filteringQuality = ShadowGenerator.QUALITY_MEDIUM;
  private _darkness = 0;
  protected _color = "#ffffff";
  protected helper: TransformNode;
  protected _shadowGenerator: ShadowGenerator;
  get HasShadowGenerator() {
    return !!this._shadowGenerator;
  }
  get Color() {
    return this._color;
  }
  set Color(v: string) {
    if (v === this._color) return;
    this.writeSnapshoot();
    this._color = v;
    this.update();
  }
  get Bias() {
    return this._bias;
  }
  set Bias(v: number) {
    if (v === this._bias) return;
    this.writeSnapshoot();
    this._bias = v;
    this.updateShadowCaster();
  }

  get NormalBias() {
    return this._normalBias;
  }
  set NormalBias(v: number) {
    if (v === this._normalBias) return;
    this.writeSnapshoot();
    this._normalBias = v;
    this.updateShadowCaster();
  }
  get Darkness() {
    return this._darkness;
  }
  set Darkness(v: number) {
    if (v === this._darkness) return;
    this.writeSnapshoot();
    this._darkness = v;
    this.updateShadowCaster();
  }
  /**值为0-高质量,1-中等质量，3-低质量 */
  get FilteringQuality() {
    return this._filteringQuality;
  }
  set FilteringQuality(v: number) {
    if (v === this._filteringQuality) return;
    this.writeSnapshoot();
    this._filteringQuality = v;
    this.updateShadowCaster();
  }

  addShadowCaster(entitys: Entity[]) {
    if (!this._shadowGenerator) return;
    for (const en of entitys) {
      if (!en.Visiable) continue;
      if (en.EnableShadow) {
        const ms = en.DrawObject.getChildMeshes();
        for (const m of ms) this._shadowGenerator.addShadowCaster(m);
      }
    }
    this.updateShadowCaster();
  }
  updateShadowCaster() {
    if (this._shadowGenerator) {
      this._shadowGenerator.filteringQuality = this._filteringQuality;
      this._shadowGenerator.bias = this._bias;
      this._shadowGenerator.normalBias = this._normalBias;
      this._shadowGenerator.darkness = this._darkness;
      this._shadowGenerator.getShadowMap().resetRefreshCounter();
    }
  }
  removeShadowCaster(entitys?: Entity[]) {
    if (!this._shadowGenerator) return;
    if (!entitys) {
      const ms = this._shadowGenerator.getShadowMap().renderList.slice();

      for (const m of ms) {
        this._shadowGenerator.removeShadowCaster(m);
      }
    } else {
      for (const en of entitys) {
        const ms = en.DrawObject.getChildMeshes();
        for (const m of ms) this._shadowGenerator.removeShadowCaster(m);
      }
    }
  }
  writeFile(file: HCFiler) {
    super.writeFile(file);
    file.write(3);
    file.write(this.Intensity);
    file.write(this.Diffuse);
    file.write(this.Specular);
    //v3
    file.write(this.HasShadowGenerator);
    if (this.HasShadowGenerator) {
      file.write(this._bias);
      file.write(this._normalBias);
      file.write(this._darkness);
      file.write(this._filteringQuality);
    }
  }
  readFile(file: HCFiler) {
    super.readFile(file);
    const ver = file.read();
    this.Intensity = file.read();
    if (ver > 1) {
      this.Diffuse = file.read();
      this.Specular = file.read();
    }

    if (ver > 2) {
      const hasShadow = file.read();
      if (hasShadow) {
        this._bias = file.read();
        this._normalBias = file.read();
        this._darkness = file.read();
        this._filteringQuality = file.read();
      }
    }
  }
  protected initShadowGenerator(light: IShadowLight) {
    this._shadowGenerator = new ShadowGenerator(2048, light, true);
    this._shadowGenerator.useExponentialShadowMap = false;
    this._shadowGenerator.usePercentageCloserFiltering = true;
    this._shadowGenerator.filteringQuality = ShadowGenerator.QUALITY_MEDIUM;
    // 默认只生成一次阴影;
    this._shadowGenerator.getShadowMap().refreshRate =
      RenderTargetTexture.REFRESHRATE_RENDER_ONCE;
    this.updateShadowCaster();
  }
  protected createHelper(root: Mesh) {
    if (AppStore.MainViewer.IsPreview) {
      return;
    }
    const helper = this.getHelper();
    if (helper) helper.parent = root;
  }
  protected getHelper(): TransformNode {
    return null;
  }
  protected updateHelper(helper: TransformNode) {}
}
