import { DracoCompression } from "@babylonjs/core";

export function setDracoConfig(url: string) {
  DracoCompression.Configuration = {
    decoder: {
      wasmUrl: url + "libs/draco/draco_wasm_wrapper_gltf.js",
      wasmBinaryUrl: url + "libs/draco/draco_decoder_gltf.wasm",
      fallbackUrl: url + "libs/draco/draco_decoder_gltf.js",
    },
  };
}
