import { CreateTorusVertexData } from "../babylon";

import { Factory, HCFiler } from "@hc/core";
import { Solid3D } from "./Solid3D";

@Factory
export class Torus extends Solid3D {
  static type = "Torus";
  private diameter = 1;
  private tessellation: number;
  private thickness = 0.1;
  constructor(
    option = {
      diameter: 1,
      tessellation: 2,
      thickness: 0.1,
    }
  ) {
    super();
    this.diameter = option.diameter ?? 1;
    this.tessellation = option.tessellation ?? 32;
    this.thickness = option.thickness ?? 0.1;
  }
  get Diameter() {
    return this.diameter;
  }
  set Diameter(v: number) {
    if (v === this.diameter) return;
    this.writeSnapshoot();
    this.diameter = v;
    this.update();
  }
  get Tthickness() {
    return this.thickness;
  }
  set Tthickness(v: number) {
    if (v === this.thickness) return;
    this.writeSnapshoot();
    this.thickness = v;
    this.update();
  }
  get Tessellation() {
    return this.tessellation;
  }
  set Tessellation(v: number) {
    if (v === this.tessellation) return;
    this.writeSnapshoot();
    this.tessellation = v;
    this.update();
  }
  get VertextData() {
    return CreateTorusVertexData({
      diameter: this.diameter,
      tessellation: this.tessellation,
      thickness: this.thickness,
    });
  }
  writeFile(file: HCFiler) {
    super.writeFile(file);
    file.write(1);
    file.write(this.diameter);
    file.write(this.thickness);
    file.write(this.tessellation);
  }
  readFile(file: HCFiler) {
    super.readFile(file);
    const ver = file.read();
    this.diameter = file.read();
    this.thickness = file.read();
    this.tessellation = file.read();
    this.update();
  }
}
