import { firstLower } from "../utils/utils";
import { MaterialRecord } from "./MaterialRecord";

export function autoRecordMaterialFilter(
  old2new?: (v) => any,
  new2old?: (v) => any
) {
  return (target, property) =>
    autoRecordMaterial(target, property, old2new, new2old);
}

export function autoRecordMaterial<T = MaterialRecord>(
  target: T,
  property: string,
  fun?: (v: any) => any,
  fun2?: (v: any) => any
) {
  if (!fun) fun = (v) => v;
  if (!fun2) fun2 = (v) => v;

  const privateKey = "_" + firstLower(property);
  const recordKey = firstLower(property);

  Object.defineProperty(target, property, {
    set: function (value) {
      let oldv = this[privateKey];
      value = fun2(value);
      if (oldv !== value) {
        this.writeSnapshoot();
        this[privateKey] = value;
        if (!this.init) {
          if (this.isOwn) {
            let diff = this.entity.diffMaterials.get(this.material.id);
            if (!diff) {
              diff = {};
              this.entity.diffMaterials.set(this.material.id, diff);
            }
            diff[property] = value;
          }
          this.update();
        } else {
          if (this.isOwn) {
            const v = this.material[recordKey];
            this[privateKey] = fun(v);
          }
        }
      }
    },
    get: function () {
      return this[privateKey];
    },
    enumerable: true,
    configurable: true,
  });
}
