import {
  AllObjectData,
  BaseObject,
  EraseEntityData,
  HCFiler,
  IEvent,
  ISerialize,
  IVector2,
  ObjectId,
} from "@hc/core";

import { AppStore } from "../app/AppStore";
import { Control, Nullable, Vector2 } from "../babylon";
import { AppEvent } from "../core/AppEvent";
import { Entity } from "../entitys/Entity";
import { EventEnum } from "../enums";
import { StrNum } from "../types";
import { CollectionRecord } from "./../records/CollectionRecord";
import { MultiLine2 } from "./MultiLine2";

export type IDataOption = {
  key: string;
  label: string;
  value: any;
  unit: string;
};

export class BaseGui extends BaseObject {
  readonly Is3D = false;
  protected _color = "#000000";
  protected _fontSize = "12px";
  protected _width: StrNum = "200px";
  protected _height: StrNum = "30px";
  protected _rotation = 0;
  protected _scale = { x: 1, y: 1 };
  protected _zIndex = 0;
  protected _alpha = 0.9;
  protected linkEntity: ObjectId<Entity>;
  protected _drawObject: Control;
  private _start: Vector2;
  private _end = new Vector2();
  private _position: { x: string; y: string } = { x: "0", y: "0" };
  protected _isSelect = false;
  protected _name = "";
  protected dataList: IDataOption[] = [];
  private _isVisiable = true;
  private _isPickable = true;
  protected lines: MultiLine2[] = [];
  private timeID?: NodeJS.Timeout;
  public GroupId: Nullable<ObjectId<CollectionRecord>>;

  get DataList() {
    return [...this.dataList];
  }
  set DataList(list: IDataOption[]) {
    this.dataList = list;
  }
  get UniqueName() {
    return this.constructor["type"] + this.Id;
  }
  get Name() {
    return this._name || this.UniqueName;
  }
  set Name(n: string) {
    this._name = n;
    AppEvent.trigger({ type: "updateName", target: this });
  }
  get Position() {
    return { ...this._position };
  }
  set Position(p: { x: string; y: string }) {
    this.writeSnapshoot();
    this._position.x = p.x;
    this._position.y = p.y;
    this.move(p);
  }
  get Rotation() {
    return this._rotation;
  }
  set Rotation(v: number) {
    this.writeSnapshoot();
    this._rotation = v;
    this._drawObject.rotation = v;
  }
  get Scale() {
    return { ...this._scale };
  }
  set Scale(v: { x: number; y: number }) {
    this.writeSnapshoot();
    Object.assign(this._scale, v);
    this._drawObject.scaleX = v.x;
    this._drawObject.scaleY = v.y;
  }
  get Width() {
    return this._width;
  }
  set Width(v) {
    if (v === this._width) return;
    this.writeSnapshoot();
    this._width = v;
    this.update();
  }
  get Height() {
    return this._height;
  }
  set Height(v) {
    if (v === this._height) return;
    this.writeSnapshoot();
    this._height = v;
    this.update();
  }
  get DrawObject() {
    if (this._drawObject) return this._drawObject;

    this._drawObject = this.initDrawObject();

    this._drawObject.metadata = { entity: this };

    this._drawObject.isVisible = this._isVisiable && !this._erase;

    this.supportDrag();

    return this._drawObject;
  }
  get Draging() {
    return !!this._start;
  }
  get IsSelect() {
    return this._isSelect;
  }
  set IsSelect(v: boolean) {
    this._isSelect = v;
    if (!this._drawObject) return;
    if (v) {
      this._drawObject.shadowColor = "#000000";
    } else {
      this._drawObject.shadowColor = "#168df8";
    }
  }
  get Visiable() {
    return this._isVisiable;
  }
  set Visiable(v: boolean) {
    if (v === this._isVisiable) return;
    this.writeSnapshoot();
    this._isVisiable = v;
    this.trigger2({ type: EventEnum.UpdateVisiable, target: this });
    this.update();
  }
  get IsPickable() {
    return this._isPickable;
  }
  set IsPickable(v: boolean) {
    this.writeSnapshoot();
    this._isPickable = v;
    // if (this._drawObject) {
    //   this._drawObject.isPointerBlocker = v;
    // }
  }
  attachEntity(objectId) {
    this.linkEntity = objectId;
  }
  initDrawObject(): Control {
    return null;
  }
  setProp(k: string, value: any) {
    for (const d of this.dataList) {
      if (d.key === k) d.value = value;
    }
  }
  updateDrawObject(obj: Control) {
    obj.width = this._width;
    obj.color = this._color;
    obj.rotation = this._rotation;
    obj.scaleX = this._scale.x;
    obj.scaleY = this._scale.y;
    obj.zIndex = this._zIndex;
    obj.fontSize = this._fontSize;
    obj.alpha = this._alpha;
    obj.isVisible = this._isVisiable;
    this.lines.forEach((l) => (l.isVisible = this._isVisiable));
    this.move(this._position, obj);
  }
  update() {
    if (!this._drawObject) return;
    this.updateDrawObject(this._drawObject);
    this.trigger2({ type: "update", target: this });
  }
  drag = (coordinates: { x: number; y: number }) => {
    if (!this._start) return;
    const diff = this._start.subtract(
      new Vector2(coordinates.x, coordinates.y)
    );
    let [x, y] = [-diff.x + this._end.x, -diff.y + this._end.y];

    this.move({ x, y });
  };
  click(coordinates: { x: number; y: number }) {
    if (!this._isPickable) return;
    this.trigger({
      type: "pointerDownGui",
      target: this,
      point: coordinates,
    });
    AppEvent.trigger({
      type: "pointerDownGui",
      target: this,
      point: coordinates,
    });
  }
  startDrag(coordinates: { x: number; y: number }) {
    if (!this._isPickable) return;
    if (AppStore.PreviewMode) return;
    if (this._db) this._db.HistoryManager.startCmd("拖拽GUI");
    this.writeSnapshoot();
    this._start = new Vector2(coordinates.x, coordinates.y);

    let left = this._drawObject.left;
    let top = this._drawObject.top;
    if (this.linkEntity) {
      left = this._drawObject.linkOffsetX;
      top = this._drawObject.linkOffsetY;
    }

    const width = AppStore.MainViewer.Width;
    const height = AppStore.MainViewer.Height;

    let x = this.getPositionValue(left, width);
    let y = this.getPositionValue(top, height);

    this._end = new Vector2(x, y);
  }
  endDrag(coordinates: { x: number; y: number }) {
    AppEvent.trigger({
      type: "pointerUpGui",
      target: this,
      point: coordinates,
    });
    this._start = null;
    if (this._db) this._db.HistoryManager.endCmd();
  }
  move(p: { x: StrNum; y: StrNum }, obj = this._drawObject) {
    if (!obj) return;
    const width = AppStore.MainViewer.Width;
    const height = AppStore.MainViewer.Height;
    if (this.linkEntity) {
      obj.linkOffsetX = p.x;
      obj.linkOffsetY = p.y;
      this._position.x = p.x.toString();
      this._position.y = p.y.toString();
    } else {
      if (typeof p.x === "number") {
        obj.left = (p.x / width) * 100 + "%";
        this._position.x = (p.x / width) * 100 + "%";
      } else {
        obj.left = p.x;
        this._position.x = p.x;
      }
      if (typeof p.y === "number") {
        obj.top = (p.y / height) * 100 + "%";
        this._position.y = (p.y / height) * 100 + "%";
      } else {
        obj.top = p.y;
        this._position.y = p.y;
      }
    }

    AppEvent.trigger({ type: "update", target: this });
    this.trigger({ type: "update", target: this });
  }
  erase(e = true) {
    if (e === this._erase) return;
    super.erase(e);
    this._drawObject.isVisible = !e;
    this.lines.forEach((l) => (l.isVisible = !e));
    AppEvent.trigger({ type: "remove", target: this });
    this.trigger({ type: "remove", target: this });
  }
  ptInContainer(pt: IVector2) {
    if (!this._drawObject) return false;
    return this._drawObject.contains(pt.x, pt.y);
  }
  destory() {
    super.destory();
    this.timeID && clearTimeout(this.timeID);
    if (this._drawObject) this._drawObject.dispose();
    this._drawObject = null;
    this.lines.forEach((l) => l.dispose());
    this.lines.length = 0;
  }
  writeFile(filer: HCFiler): void {
    super.writeFile(filer);
    filer.write(4);
    filer.write(this._position.x);
    filer.write(this._position.y);
    filer.write(this._rotation);
    filer.write(this._scale.x);
    filer.write(this._scale.y);
    filer.write(this._name);
    filer.write(this._width);
    filer.write(this._height);
    filer.write(this._color);
    filer.write(this._zIndex);
    filer.write(this._fontSize);
    filer.write(this._alpha);
    filer.writeObjectId(this.linkEntity);

    filer.writeAnyArray(this.dataList, (d) => {
      filer.write(d.key);
      filer.write(d.label);
      filer.write(d.value);
      filer.write(d.unit);
    });

    filer.write(this._isVisiable);

    filer.write(this._isPickable);

    filer.writeObjectId(this.GroupId);
  }
  readFile(filer: HCFiler): void {
    super.readFile(filer);
    const ver = filer.read();
    this._position.x = filer.read();
    this._position.y = filer.read();
    this._rotation = filer.read();
    this._scale.x = filer.read();
    this._scale.y = filer.read();
    this._name = filer.read();
    this._width = filer.read();
    this._height = filer.read();
    this._color = filer.read();
    this._zIndex = filer.read();
    this._fontSize = filer.read();
    this._alpha = filer.read();
    this.linkEntity = filer.readObjectId() as ObjectId<Entity>;

    this.dataList.length = 0;
    filer.readAnyArray(() => {
      const d = {} as any;
      d.key = filer.read();
      d.label = filer.read();
      d.value = filer.read();
      d.unit = filer.read();
      this.dataList.push(d);
    });

    if (ver > 1) this._isVisiable = filer.read();

    if (ver > 2) {
      this._isPickable = filer.read();
    }
    if (ver > 3) {
      this.GroupId = filer.readObjectId();
    }
  }
  _applyHistoryRecord(record: ISerialize) {
    super._applyHistoryRecord(record);
    if (record instanceof EraseEntityData) {
      this._drawObject.isVisible = !this._erase;
    } else if (record instanceof AllObjectData) {
      this.readFile(record.Filer);
    }
  }
  /**这个触发事件会同时发出自身 和全局的事件 */
  trigger2(event: IEvent) {
    super.trigger(event);
    AppEvent.trigger(event);
  }
  private getPositionValue(v: string | number, refValue: number) {
    if (typeof v === "string") {
      if (v.endsWith("%")) {
        return (parseFloat(v) / 100) * refValue;
      } else {
        return parseFloat(v);
      }
    }
    return v;
  }
  private supportDrag() {
    this._drawObject.isPointerBlocker = false;
  }
  protected generateLinkLine(panel: Control, time = 0) {
    this.timeID && clearTimeout(this.timeID);
    this.timeID = setTimeout(() => {
      if (this.linkEntity?.Object && panel?.parent) {
        const line = new MultiLine2("linkLine" + this.Id);
        line.isVisible = this._isVisiable;
        line.color = "rgba(14,232,210,0.2)";
        line.lineWidth = 2;
        line.add(panel);
        const p = line.getAt(1);
        p.mesh = this.linkEntity.Object.RenderObject;
        panel.zIndex = 1;
        panel.parent.addControl(line);

        panel.linkWithMesh(this.linkEntity.Object.DrawObject);
        // panel.linkOffsetY = 0;

        this.move(this.Position, panel);
        this.lines.push(line);
      }
    }, time);
  }
}
