import * as HC from "@hc/engine";
import * as HCCore from "@hc/core";
import { IComponentData } from "../components/editorModal/types";

Object.assign(HC, HCCore);

export function getUniupComponentType(id: number) {
  return `CustomComponent_${id}`;
}

export function getComponent(code: string): typeof HC.Component {
  if (!code) return;
  try {
    const genC = new Function("HC", "return " + code.trim());
    return genC(HC);
  } catch (err) {
    console.log("err: ", err);
    return null;
  }
}

export function isRegisterComponent(id: number) {
  const type = getUniupComponentType(id);
  return HCCore.HCFactory.isRegister(type);
}

export function registerComponent(data: IComponentData) {
  const { code, id, propertys, title } = data;
  if (!code) return;
  const Com = getComponent(code);
  Com.type = getUniupComponentType(id);
  Com.Schema = {
    title: title,
    propertys,
  };
  HC.RegisterComponent(Com);
}

export function unRegisterComponent(data: IComponentData) {
  HC.UnRegisterComponent(getUniupComponentType(data.id));
}

export function getFunction(code: string) {
  return new Function("return " + code.trim());
}
