import { removeOne } from "../utils";
import { Factory, HCFiler, BaseObject } from "@hc/core";
import { DataBind } from "../dataBind/DataBind";

@Factory
export class DataBindTable extends BaseObject {
  static type = "DataBindTable";
  list: DataBind[] = [];
  append(data: DataBind) {
    this.list.push(data);
  }
  do() {
    for (const db of this.list) {
      db.do();
    }
  }
  remove(db: DataBind) {
    removeOne(this.list, db);
  }
  destory(): void {
    super.destory();
    for (const d of this.list) {
      d.destory();
    }
  }
  writeFile(filer: HCFiler): void {
    filer.write(1);
    filer.writeAnyArray(this.list, filer.writeObject);
  }
  readFile(filer: HCFiler): void {
    filer.read();
    filer.readAnyArrayAndPush(this.list, filer.readObject);
  }
}
