import { ParticleSystem } from "./ParticleSystem";
import { Particle } from "./Particle";
import {
  ParticleSystemSet,
  ParticleSystem as BParticleSystem,
} from "../babylon";
import { HCFiler, Factory } from "@hc/core";
import { AppEvent } from "../core/AppEvent";

@Factory
export class ParticleSystemGroup extends Particle {
  static type = "ParticleSystemGroup";
  protected _name: string = "粒子组合";
  private _intance: ParticleSystemSet;
  systems: ParticleSystem[] = [];
  get Intance() {
    if (this._intance) return this._intance;
    this._intance = this.initParticle();
    this.updateStatus();
    return this._intance;
  }
  initParticle(): ParticleSystemSet {
    const set = new ParticleSystemSet();
    for (const s of this.systems) {
      set.systems.push(s.Intance);
    }
    return set;
  }
  updateParticle(pcset: ParticleSystemSet): void {
    for (let i = 0; i < this.systems.length; i++) {
      this.systems[i].updateParticle(pcset.systems[i] as BParticleSystem);
    }
  }
  start(): void {
    if (this._intance) this._intance.start();
  }
  stop(): void {
    this.systems.forEach((s) => s.stop());
  }
  fillScene() {
    const box3 = this.SceneSize;
    if (!box3) return;
    for (let i = 0; i < this.systems.length; i++) {
      this.systems[i].fillScene(box3);
    }
  }
  destory(): void {
    this._intance?.dispose?.();
    this.systems.length = 0;
  }
  erase(e = true) {
    super.erase(e);
    if (this._intance) this._intance.systems.forEach((s) => s.stop());
    AppEvent.trigger({ type: "remove", target: this });
    this.trigger({ type: "remove", target: this });
  }
  fromJSON(jsons: Record<string, any>[]): this {
    ParticleSystem.Reading = true;
    for (const json of jsons) {
      const pcs = new ParticleSystem();
      pcs.fromJSON(json as any, true);
      this.systems.push(pcs);
    }
    ParticleSystem.Reading = false;
    return this;
  }
  writeFile(filer: HCFiler): void {
    super.writeFile(filer);
    filer.writeAnyArray(this.systems, filer.writeObject);
  }
  readFile(filer: HCFiler): void {
    super.readFile(filer);
    filer.readAnyArrayAndPush(this.systems, filer.readObject);
    this.updateStatus();
  }
}
