import { Entity } from "../..";
import { Factory, HCFiler, ObjectId } from "@hc/core";
import { BaseWidget } from "../../core/BaseWidget";

@Factory
export class Opacity extends BaseWidget {
  static type = "Opacity";
  static schema = {
    label: "设置不透明",
  };
  List: {
    opacity: number;
    time: number;
    animationType: string;
    objectId: ObjectId<Entity>;
  }[] = [];

  async do() {
    const pros: Promise<unknown>[] = [];
    for (const data of this.List) {
      const en = data.objectId?.Object;
      if (!en) continue;
      pros.push(en.opacityTo({ ...data, startTime: this.startTime }));
    }
    return Promise.all(pros);
  }
  fromJSON(data: any): void {
    this.List = data;
  }
  readFile(filer: HCFiler): void {
    filer.read();
    this.List.length = 0;
    filer.readAnyArray(() => {
      const data = {} as any;
      data.opacity = filer.read();
      data.time = filer.read();
      data.animationType = filer.read();
      data.objectId = filer.readObjectId();
      this.List.push(data);
    });
  }
  writeFile(filer: HCFiler): void {
    filer.write(1);
    filer.writeAnyArray(this.List, (o) => {
      filer.write(o.opacity);
      filer.write(o.time);
      filer.write(o.animationType);
      filer.writeObjectId(o.objectId);
    });
  }
}
