import { HCFiler } from "../filer/HCFiler";
import { ISerialize } from "../types/iSerialize.types";

export class EraseEntityData implements ISerialize {
  constructor(public isErase = true) {}
  readFile(file: HCFiler): this {
    this.isErase = file.read();
    return this;
  }
  writeFile(file: HCFiler): this {
    file.write(this.isErase);
    return this;
  }
}
