import { HCFiler, ISerialize } from "@hc/core";
import { Vector3 } from "../../babylon";
import { Door } from "./Door";
import { Win } from "./Windows";

export class WallPlane implements ISerialize {
  static type = "WallPlane";
  corner: Vector3;
  doors: Door[] = [];
  windows: Win[] = [];
  constructor(v: Vector3 = new Vector3()) {
    this.corner = v;
  }
  writeFile(file: HCFiler): void {
    file.write(1);
    file.writeVector3(this.corner);
    file.writeAnyArray(this.doors, file.writeObject);
    file.writeAnyArray(this.windows, file.writeObject);
  }
  readFile(file: HCFiler): void {
    file.read();
    file.readVector3(this.corner);
    file.readAnyArrayAndPush(this.doors, () => file.readObject(new Door()));
    file.readAnyArrayAndPush(this.windows, () => file.readObject(new Win()));
  }
}
