import { Vector3 } from "../../babylon";
import { IPoint } from "dxf-parser";

export function toRgba(color: number) {
  let rgb = [];

  let b = color & 0xff;

  let g = (color >> 8) & 0xff;

  let r = (color >> 16) & 0xff;

  rgb[0] = r;

  rgb[1] = g;

  rgb[2] = b;

  return rgb;
}

export function asVector3(p: IPoint, scale = 1) {
  return new Vector3(p.x / scale, p.y / scale, (p.z ?? 0) / scale);
}
