import { BaseGui } from "./Gui";
import { StackPanel, TextBlock } from "../babylon";
import { Factory, HCFiler } from "@hc/core";

@Factory
export class StackPanelGui extends BaseGui {
  static type = "StackPanelGui";
  protected _background = "#4a4b4b";
  get Visiable() {
    return super.Visiable;
  }
  set Visiable(v: boolean) {
    super.Visiable = v;
    this.lines.forEach((l) => (l.isVisible = !this._erase && v));
  }
  setProp(k: string, v: string) {
    super.setProp(k, v);

    const obj = this._drawObject as StackPanel;
    const texts = obj.children as TextBlock[];
    const oldLen = texts.length;
    let i = 0;
    for (; i < this.dataList.length; i++) {
      const data = this.dataList[i];
      if (texts[i]) {
        texts[i].text = data.label + "：" + data.value + " " + data.unit;
      } else {
        const textBlock = new TextBlock(
          data.key,
          data.label + "：" + data.value + " " + data.unit
        );
        textBlock.height = this._height;
        textBlock.color = "#fff";
        obj.addControl(textBlock);
      }
    }

    for (; i < oldLen; i++) {
      obj.removeControl(texts[i]);
    }
  }
  initDrawObject() {
    const panel = new StackPanel(this.UniqueName);
    // panel.ignoreLayoutWarnings = true;
    panel.shadowOffsetX = 1;
    panel.shadowOffsetY = 1;
    panel.shadowBlur = 8;
    panel.shadowColor = "#168df8";
    panel.background = this._background;

    this.updateDrawObject(panel);

    this.generateLinkLine(panel);

    return panel;
  }
  updateDrawObject(obj: StackPanel): void {
    super.updateDrawObject(obj);
    obj.background = this._background;
    const texts = obj.children as TextBlock[];
    const oldLen = texts.length;
    let i = 0;
    for (; i < this.dataList.length; i++) {
      const data = this.dataList[i];
      if (texts[i]) {
        texts[i].text = data.label + "：" + data.value + " " + data.unit;
      } else {
        const textBlock = new TextBlock(
          data.key,
          data.label + "：" + data.value + " " + data.unit
        );
        textBlock.height = this._height;
        textBlock.color = "#fff";
        obj.addControl(textBlock);
      }
    }

    for (; i < oldLen; i++) {
      obj.removeControl(texts[i]);
    }
  }
  readFile(filer: HCFiler): void {
    super.readFile(filer);
    filer.read();
    this._background = filer.read();
    this.update();
  }
  writeFile(filer: HCFiler): void {
    super.writeFile(filer);
    filer.write(1);
    filer.write(this._background);
  }
}
