import { Node, StringTools } from "../babylon";
import { Entity } from "./../entitys/Entity";

export function getEntity<T extends Node>(mesh: T): Entity {
  while (mesh && !mesh.metadata?.Entity) {
    mesh = mesh.parent as T;
  }
  return mesh?.metadata?.Entity;
}

export function getRoot<T extends Node>(mesh: T) {
  while (mesh.parent) {
    mesh = mesh.parent as T;
  }
  return mesh;
}

export function isTextureAsset(name: string): boolean {
  const queryStringIndex = name.indexOf("?");
  if (queryStringIndex !== -1) {
    name = name.substring(0, queryStringIndex);
  }

  return (
    StringTools.EndsWith(name, ".ktx") ||
    StringTools.EndsWith(name, ".ktx2") ||
    StringTools.EndsWith(name, ".png") ||
    StringTools.EndsWith(name, ".jpg") ||
    StringTools.EndsWith(name, ".jpeg")
  );
}

export function equaln(v1: number, v2: number, fuzz = 1e-5) {
  return Math.abs(v1 - v2) <= fuzz;
}

export function sleep(t: number) {
  return new Promise((res) => setTimeout(res, t));
}

export function b64toBlob(dataURI: string) {
  const mimeString = dataURI.split(",")[0].split(":")[1].split(";")[0]; // mime类型
  const byteString = atob(dataURI.split(",")[1]); //base64 解码
  const arrayBuffer = new ArrayBuffer(byteString.length); //创建缓冲数组
  const intArray = new Uint8Array(arrayBuffer); //创建视图

  for (let i = 0; i < byteString.length; i++) {
    intArray[i] = byteString.charCodeAt(i);
  }
  return new Blob([intArray], { type: mimeString });
}

export function simpleObjectEqual(
  o1: Record<string, number | string>,
  o2: Record<string, number | string>
) {
  for (const k1 in o1) {
    if (o2.hasOwnProperty(k1)) {
      if (o1[k1] !== o2[k1]) return false;
    } else {
      return false;
    }
  }

  return true;
}

export function updateQueryStringParameter(
  uri: string,
  key: string,
  value: string
) {
  if (value === undefined) {
    return uri;
  }
  const re = new RegExp("([?&])" + key + "=.*?(&|$)", "i");
  const separator = uri.indexOf("?") !== -1 ? "&" : "?";
  if (uri.match(re)) {
    return uri.replace(re, "$1" + key + "=" + value + "$2");
  } else {
    return uri + separator + key + "=" + value;
  }
}

export function isNone(v: number | undefined | null) {
  return typeof v === "undefined" || (!v && v !== 0);
}

/**首字母小写 */
export function firstLower(str: string) {
  return str.replace(str[0], str[0].toLowerCase());
}

/**首字母大写 */
export function firstUpper(str: string) {
  return str.replace(str[0], str[0].toUpperCase());
}
