import {
  ISceneLoaderAsyncResult,
  SceneLoader,
} from "@babylonjs/core/Loading/sceneLoader";
import { AppConfig } from "../../app/appConfig";
import {
  AssetContainer,
  IDisposable,
  ISceneLoaderProgressEvent,
  Nullable,
  Scene,
} from "../../babylon";
import type { HC3DFileLoader } from "./HC3DFileLoader";

export type TModType = "glb" | "gltf" | "STL" | "obj";

const BufferDecoder = new TextDecoder("utf-8");

/* 
Interface that contains the data for the glTF asset.
 */
export interface IHC3DLoaderData {
  /**
   * 加密文件入口.
   */
  entry: string;
  version: string;
  type: TModType;
}

/** @internal */
export interface IHC3DLoader extends IDisposable {
  importMeshAsync: (
    meshesNames: any,
    scene: Scene,
    data: any,
    rootUrl: string,
    onProgress?: (event: ISceneLoaderProgressEvent) => void,
    fileName?: string
  ) => Promise<ISceneLoaderAsyncResult>;
  loadAsync: (
    scene: Scene,
    data: any,
    rootUrl: string,
    onProgress?: (event: ISceneLoaderProgressEvent) => void,
    fileName?: string
  ) => Promise<void>;
  loadAssetContainerAsync(
    scene: Scene,
    data: IHC3DLoaderData,
    rootUrl: string,
    onProgress?: (event: ISceneLoaderProgressEvent) => void,
    fileName?: string
  ): Promise<AssetContainer>;
}

export class HC3DLoader implements IHC3DLoader {
  /** @internal */
  public readonly _completePromises = new Array<Promise<any>>();

  /** @internal */
  public _assetContainer: Nullable<AssetContainer> = null;
  private readonly _parent: HC3DFileLoader;
  private _disposed = false;
  private _rootUrl: Nullable<string> = null;
  private _babylonScene: Scene;
  constructor(parent: HC3DFileLoader) {
    this._parent = parent;
  }
  async loadAssetContainerAsync(
    scene: Scene,
    data: IHC3DLoaderData,
    rootUrl: string,
    onProgress?: (event: ISceneLoaderProgressEvent) => void
  ): Promise<AssetContainer> {
    this._rootUrl = rootUrl;
    this._babylonScene = scene;
    if (AppConfig.isEncrypt) {
      const file = await this._loadAsync(rootUrl, data);
      return SceneLoader.LoadAssetContainerAsync(
        rootUrl,
        file,
        scene,
        onProgress
      );
    } else {
      const url = data.entry.replace(".mod", "." + data.type);
      return SceneLoader.LoadAssetContainerAsync(
        rootUrl,
        url,
        scene,
        onProgress
      );
    }
  }
  /**
   * @internal
   */
  public async importMeshAsync(
    meshesNames: any,
    scene: Scene,
    data: any,
    rootUrl: string,
    onProgress?: (event: ISceneLoaderProgressEvent) => void,
    fileName?: string
  ): Promise<ISceneLoaderAsyncResult> {
    this._rootUrl = rootUrl;
    this._babylonScene = scene;
    const file = await this._loadAsync(rootUrl, data);
    return SceneLoader.ImportMeshAsync(
      meshesNames,
      rootUrl,
      file,
      scene,
      onProgress
    );
  }

  /**
   * @internal
   */
  public async loadAsync(
    scene: Scene,
    data: IHC3DLoaderData,
    rootUrl: string,
    onProgress?: (event: ISceneLoaderProgressEvent) => void,
    fileName?: string
  ): Promise<void> {
    this._rootUrl = rootUrl;
    this._babylonScene = scene;
    const file = await this._loadAsync(rootUrl, data);

    return SceneLoader.LoadAsync(
      rootUrl,
      file,
      scene.getEngine(),
      onProgress
    ).then();
  }
  private _loadAsync(rootUrl: string, data: IHC3DLoaderData): Promise<File> {
    return this.loadModFile(data).catch((error) => {
      if (!this._disposed) {
        this._parent.onErrorObservable.notifyObservers(error);
        this._parent.onErrorObservable.clear();

        this.dispose();
      }

      throw error;
    });
  }
  dispose(): void {
    if (this._disposed) {
      return;
    }

    this._disposed = true;

    this._completePromises.length = 0;

    this._parent.dispose();
  }
  private async loadModFile(mod: IHC3DLoaderData) {
    const Decode = await this._parent.constructor["LoadPromise"];
    const json = await this._babylonScene._loadFileAsync(
      this._rootUrl + mod.entry,
      () => {},
      true,
      true
    );
    const data: Uint8Array = Decode(
      new Uint8Array(json as ArrayBuffer),
      "hctest"
    );

    if (mod.type !== "glb") {
      const decodeData = BufferDecoder.decode(data);
      return new File([decodeData], "temp." + mod.type);
    } else {
      return new File([data], "temp." + mod.type);
    }
  }
}
