import { WidgetValue, IVariableOption } from "./../types/interaction.types";

export function getWidgetValue(
  wgValue: WidgetValue,
  variables: IVariableOption[] = []
) {
  if (!wgValue) return;
  if (wgValue.type === "value") {
    return wgValue.value;
  } else if (wgValue.type === "param") {
    const targetPar = variables.find((p) => p.key === wgValue.value);
    return targetPar?.value;
  } else if (wgValue.type === "entity") {
    switch (wgValue.entityKey) {
      default:
        return wgValue.value?.Object?.[wgValue.entityKey];
    }
  }
  return;
}

export function getEventName(eventName: string) {
  if (eventName === "RightClick" || eventName === "Click") {
    return "pointerTap";
  }

  if (eventName === "Load") return "load";
  return eventName;
}
