import { CreateSphereVertexData, VertexData } from "../babylon";

import { Factory, HCFiler } from "@hc/core";
import { Solid3D } from "./Solid3D";

@Factory
export class Sphere extends Solid3D {
  static type = "Sphere";
  protected _name = "球";
  private diameter = 10;
  private arc = 10;
  constructor(option = { diameter: 1, arc: 1 }) {
    super();
    this.diameter = option.diameter ?? this.diameter;
    this.arc = option.arc ?? this.arc;
  }
  get Diameter() {
    return this.diameter;
  }
  set Diameter(v: number) {
    if (v === this.diameter) return;
    this.writeSnapshoot();
    this.diameter = v;
    this.update();
  }

  get VertextData(): VertexData {
    return CreateSphereVertexData({
      diameter: this.diameter,
      arc: this.arc,
    });
  }
  writeFile(file: HCFiler) {
    super.writeFile(file);
    file.write(1);
    file.write(this.diameter);
    file.write(this.arc);
  }
  readFile(file: HCFiler) {
    super.readFile(file);
    const ver = file.read();
    this.diameter = file.read();
    this.arc = file.read();
    this.update();
  }
}
