import { GroupRecord } from "../records";
import { ObjectCollection, Factory, HCFiler } from "@hc/core";

@Factory
export class GroupRecordTable extends ObjectCollection<GroupRecord> {
  static type = "GroupRecordTable";
  append(object: GroupRecord) {
    let id = super.append(object);

    if (id) object.Owner = this.ObjectId;

    return id;
  }
  readFile(file: HCFiler) {
    let ver = file.read();
    super.readFile(file);
  }
  writeFile(file: HCFiler) {
    file.write(1);
    super.writeFile(file);
  }
}
