import {
  Color3,
  CreateGroundVertexData,
  Mesh,
  StandardMaterial,
  Texture,
  VertexData,
} from "../babylon";
import { Factory, HCFiler } from "@hc/core";
import { Solid3D } from "./Solid3D";
import { RenderType } from "../types";
import { UpdateFlag } from "../enums";

@Factory
export class Ground extends Solid3D {
  static type = "Ground";
  protected _name = "地面";
  private width = 10;
  private height = 10;
  onlyGround = false;
  constructor(option?: { width: number; height: number }) {
    super();
    this.width = option?.width ?? this.width;
    this.height = option?.height ?? this.height;
  }
  get Width() {
    return this.width;
  }
  set Width(v: number) {
    if (v === this.width) return;
    this.writeSnapshoot();
    this.width = v;
    this.update(UpdateFlag.Geometry);
  }
  get Height() {
    return this.height;
  }
  set Height(v: number) {
    if (v === this.height) return;
    this.writeSnapshoot();
    this.height = v;
    this.update(UpdateFlag.Geometry);
  }
  get VertextData(): VertexData {
    return CreateGroundVertexData({
      width: this.width,
      height: this.height,
    });
  }
  loadGroundImage(image: string | File, isInit = true) {
    this.onlyGround = true;
    let texture: Texture;
    if (typeof image === "string") {
      texture = new Texture(image, this.Scene);
    } else {
      texture = Texture.LoadFromDataString("ground-image", image, this.Scene);
    }

    const mtl = new StandardMaterial("ground-image-mtl", this.Scene);
    mtl.specularColor = Color3.Black();
    mtl.diffuseTexture = texture;
    mtl.backFaceCulling = false;
    texture.vAng = Math.PI;
    texture.wAng = Math.PI;
    return new Promise((res) => {
      texture.onLoadObservable.addOnce((data) => {
        const size = data.getSize();
        if (isInit) {
          this.width = size.width;
          this.Height = size.height;
        }
        this.RenderObject.material = mtl;
        mtl.freeze();
        res(this);
      });
    });
  }
  updateMaterial(type?: RenderType, obj?: Mesh) {
    if (this.onlyGround) return;
    super.updateMaterial(type, obj);
  }
  writeFile(file: HCFiler) {
    super.writeFile(file);
    file.write(1);
    file.write(this.width);
    file.write(this.height);
  }
  readFile(file: HCFiler) {
    super.readFile(file);
    const ver = file.read();
    this.width = file.read();
    this.height = file.read();
    this.update();
  }
}
