import { BaseObject } from "./BaseObject";

/*
对象拥有Id属性,用来记录引用关系.
通过id可以得到对应的关联实体,或者记录实体的关联关系.

ObjectId必须使用 Database分配(db里面会存id的列表,以便同时更新id指向实体)
*/
export class ObjectId<T extends BaseObject = BaseObject> {
  private index: number;
  private object?: T;

  //对外隐藏构造函数,如果需要构造一个id,请使用 Create 静态方法.
  constructor(index = -1, obj?: T) {
    this.index = index;
    this.object = obj;
  }

  get isErase(): boolean {
    return !this.object || this.object.IsErase;
  }
  set Object(v: T | undefined) {
    this.object = v;
  }
  get Object(): T | undefined {
    return this.object;
  }
  get Index(): number {
    return this.index;
  }
  set Index(index: number) {
    this.index = index;
  }
  toJSON() {
    return this.index;
  }
}
