import { Entity } from "../..";
import { BaseWidget } from "../../core/BaseWidget";
import { Factory, HCFiler, ObjectId } from "@hc/core";

export type DisplayType = "Show" | "Hidden" | "Toggle";

@Factory
export class Display extends BaseWidget {
  static type = "Display";
  static schema = {
    label: "显示/隐藏",
  };
  List: { type: DisplayType; objectId: ObjectId<Entity> }[] = [];

  async do() {
    for (const data of this.List) {
      const { type, objectId } = data;
      switch (type) {
        case "Show":
          objectId.Object.Visiable = true;
          break;
        case "Hidden":
          objectId.Object.Visiable = false;
          break;
        case "Toggle":
          objectId.Object.Visiable = !objectId.Object.Visiable;
          break;
        default:
          break;
      }
    }
    return true;
  }
  fromJSON(data: any): void {
    this.List = data;
  }
  readFile(filer: HCFiler): void {
    filer.read();
    this.List.length = 0;
    filer.readAnyArray(() => {
      const data = {} as any;
      data.type = filer.read();
      data.objectId = filer.readObjectId();
      this.List.push(data);
    });
  }
  writeFile(filer: HCFiler): void {
    filer.write(1);
    filer.writeAnyArray(this.List, (o) => {
      filer.write(o.type);
      filer.writeObjectId(o.objectId);
    });
  }
}
