import { UndoCommand, RedoCommand } from "./undo";
import { Command, commandManager } from "./CommandManager";

export function registerCommand(cmds: [string, Command][] = []) {
  commandManager.RegisterCommand("undo", new UndoCommand());
  commandManager.RegisterCommand("redo", new RedoCommand());

  for (const cmd of cmds) {
    commandManager.RegisterCommand(cmd[0], cmd[1]);
  }
}
