import {
  AllObjectData,
  BaseObject,
  EraseEntityData,
  HCFiler,
  ISerialize,
  ObjectId,
} from "@hc/core";

import { AppStore } from "../app/AppStore";
import { IVector2Like, Nullable, Vector2 } from "../babylon";
import { CollectionRecord } from "../records";
import { simpleObjectEqual } from "../utils";
import { StrNum, Align as TAlign } from "./../types/types";
import { AppEvent } from "./AppEvent";

export class BaseDom extends BaseObject {
  protected _drawObject: HTMLElement;
  protected _width: StrNum;
  protected _height: StrNum;
  private _start: Vector2;
  protected _position = { x: "0", y: "0" };
  protected _rotation = 0;
  protected _scale = { x: 1, y: 1 };
  private isSelect = false;
  protected _name: string;
  private _isVisiable = true;
  private _horAlign: TAlign = 0;
  private _verAlign: TAlign = 0;
  private _isPickable = true;
  private _hasTransition = false;
  public GroupId: Nullable<ObjectId<CollectionRecord>>;

  get Name() {
    return this._name ?? "图标" + this.Id;
  }
  set Name(v: string) {
    this._name = v;
    AppEvent.trigger({ type: "updateName", target: this });
  }
  get DrawObject() {
    if (this._drawObject) return this._drawObject;

    this._drawObject = this.initDrawObject();

    this._drawObject["metadata"] = { entity: this };

    this._drawObject.style.display =
      this._isVisiable && !this._erase ? "block" : "none";

    return this._drawObject;
  }
  get Visiable() {
    return this._isVisiable;
  }
  set Visiable(v: boolean) {
    if (v === this._isVisiable) return;
    this.writeSnapshoot();
    this._isVisiable = v;
    this.update();
  }
  get IsPickable() {
    return this._isPickable;
  }
  set IsPickable(v: boolean) {
    this._isPickable = v;
    this.update();
  }
  get Position() {
    return { ...this._position };
  }
  set Position(p: { x: string; y: string }) {
    if (simpleObjectEqual(this._position, p)) return;
    this.writeSnapshoot();
    this._position.x = p.x;
    this._position.y = p.y;
    this.update();
  }
  get Rotation() {
    return this._rotation;
  }
  set Rotation(v: number) {
    if (v === this._rotation) return;
    this.writeSnapshoot();
    this._rotation = v;
    this.update();
  }
  get Scale() {
    return this._scale;
  }
  set Scale(v: IVector2Like) {
    if (simpleObjectEqual(this._scale, v as any)) return;
    this.writeSnapshoot();
    this._scale = v;
    this.update();
  }
  get IsSelect() {
    return this.isSelect;
  }
  set IsSelect(v: boolean) {
    if (v === this.isSelect) return;

    this.isSelect = v;

    if (!this._drawObject) return;
    if (v) {
      this._drawObject.style.outline = "2px solid #cccccc";
    } else {
      this._drawObject.style.outline = "none";
    }
  }
  get Draging() {
    return !!this._start;
  }
  get Width() {
    return this._width;
  }
  set Width(v: StrNum) {
    if (v === this._width) return;
    this.writeSnapshoot();
    this._width = v;
    this.update();
  }
  get Height() {
    return this._height;
  }
  set Height(v: StrNum) {
    if (v === this._height) return;
    this.writeSnapshoot();
    this._height = v;
    this.update();
  }
  get HorAlign() {
    return this._horAlign;
  }
  set HorAlign(v: TAlign) {
    if (v === this._horAlign) return;
    this.writeSnapshoot();
    this._horAlign = v;
    this.update();
  }
  get VerAlign() {
    return this._verAlign;
  }
  set VerAlign(v: TAlign) {
    if (v === this._verAlign) return;
    this.writeSnapshoot();
    this._verAlign = v;
    this.update();
  }
  get HasTransition() {
    return this._hasTransition;
  }
  set HasTransition(v: boolean) {
    if (v === this._hasTransition) return;
    this.writeSnapshoot();
    this._hasTransition = v;
    this.update();
  }

  start(x: number, y: number) {
    if (this._db) this._db.HistoryManager.startCmd("拖拽DOM");
    this.writeSnapshoot();
    this._start = new Vector2(x, y);
  }
  end() {
    this._start = null;
    if (this._db) this._db.HistoryManager.endCmd();
  }

  move(x: number, y: number, obj = this._drawObject) {
    if (!obj) return;

    let diffx = x - this._start.x;
    let diffy = y - this._start.y;

    const left = this.convertValue(this._position.x, AppStore.MainViewer.Width);
    const top = this.convertValue(this._position.y, AppStore.MainViewer.Height);

    let h = this._horAlign === 2 ? left - diffx : left + diffx;
    let v = this._verAlign === 2 ? top + diffy : top + diffy;

    this.Position = {
      x: (h / AppStore.MainViewer.Width) * 100 + "%",
      y: (v / AppStore.MainViewer.Height) * 100 + "%",
    };
    this._start.set(x, y);
  }
  initDrawObject(): HTMLElement {
    return null;
  }
  updateDrawObject(obj: HTMLElement) {
    obj.style.width =
      this.convertValue(this._width, AppStore.MainViewer.Width) + "px";

    obj.style.height =
      this.convertValue(this._height, AppStore.MainViewer.Height) + "px";

    this.setPosition(obj);

    obj.style.zIndex = "2";

    obj.style.overflow = "hidden";

    obj.style.transform = `rotate(${this._rotation}deg) scale(${this._scale.x},${this._scale.y})`;

    obj.style.transition = this._hasTransition ? `all .1s` : "";

    obj.style.display = this._isVisiable ? "block" : "none";

    obj.style.pointerEvents = this._isPickable ? "auto" : "none";

    if (this.isSelect) {
      this._drawObject.style.outline = "2px solid #cccccc";
    } else {
      this._drawObject.style.outline = "none";
    }
  }
  update() {
    if (!this._drawObject) return;
    this.updateDrawObject(this._drawObject);
    this.trigger({ type: "update", target: this });
    AppEvent.trigger({ type: "update", target: this });
  }
  erase(e = true) {
    if (e === this._erase) return;
    super.erase(e);
    this._drawObject.style.display = e ? "none" : "block";
    AppEvent.trigger({ type: "remove", target: this });
    this.trigger({ type: "remove", target: this });
  }
  destory() {
    super.destory();
    if (this._drawObject) this._drawObject.remove();
    this._drawObject = null;
  }
  writeFile(filer: HCFiler): void {
    super.writeFile(filer);
    filer.write(6);
    filer.write(this._position.x);
    filer.write(this._position.y);

    filer.write(this._rotation);

    filer.write(this._scale.x);
    filer.write(this._scale.y);

    filer.write(this._width);
    filer.write(this._height);

    filer.write(this._isVisiable);

    filer.write(this._name);

    filer.write(this._horAlign);
    filer.write(this._verAlign);
    filer.write(this._isPickable);
    filer.write(this._hasTransition);
    filer.writeObjectId(this.GroupId);
  }
  readFile(filer: HCFiler): void {
    super.readFile(filer);
    const ver = filer.read();
    this._position.x = filer.read();
    this._position.y = filer.read();

    this._rotation = filer.read();

    this._scale.x = filer.read();
    this._scale.y = filer.read();

    this._width = filer.read();
    this._height = filer.read();

    this._isVisiable = filer.read();

    if (ver > 1) {
      this._name = filer.read();
    }
    if (ver > 2) {
      this._horAlign = filer.read();
      this._verAlign = filer.read();
    }
    if (ver > 3) {
      this._isPickable = filer.read();
    }
    if (ver > 4) {
      this._hasTransition = filer.read();
    }
    if (ver > 5) {
      this.GroupId = filer.readObjectId();
    }
  }
  _applyHistoryRecord(record: ISerialize) {
    super._applyHistoryRecord(record);
    if (record instanceof EraseEntityData) {
      this._drawObject.style.display = !this._erase ? "block" : "none";
    } else if (record instanceof AllObjectData) {
      this.readFile(record.Filer);
    }
  }
  protected convertValue(value: string | number, refValue: number) {
    if (typeof value === "string") {
      if (value.endsWith("%")) return (parseFloat(value) / 100) * refValue;
      return parseFloat(value);
    }

    return value;
  }
  private setPosition(obj: HTMLElement) {
    const h = this.convertValue(this._position.x, AppStore.MainViewer.Width);
    const v = this.convertValue(this._position.y, AppStore.MainViewer.Height);
    const width = this.convertValue(this._width, AppStore.MainViewer.Width);
    const height = this.convertValue(this._height, AppStore.MainViewer.Height);

    if (this._horAlign === 0) {
      obj.style.left = h + "px";
      obj.style.right = "auto";
    } else if (this._horAlign === 2) {
      obj.style.right = h + "px";
      obj.style.left = "auto";
    } else {
      obj.style.left = AppStore.MainViewer.Width / 2 - width / 2 + "px";
      obj.style.right = "auto";
    }
    if (this._verAlign === 0) {
      obj.style.top = v + "px";
      obj.style.bottom = "auto";
    } else if (this._verAlign === 2) {
      obj.style.bottom = v + "px";
      obj.style.top = "auto";
    } else {
      obj.style.top = AppStore.MainViewer.Height / 2 - height / 2 + "px";
      obj.style.bottom = "auto";
    }
  }
}
