import { HCFiler } from "@hc/core";
import { WidgetValue } from "../types/interaction.types";

declare module "@hc/core/lib/filer/HCFiler" {
  export interface HCFiler {
    writeWidgetValue(v: WidgetValue): void;
    readWidgetValue(v: WidgetValue): void;
  }
}

HCFiler.prototype.writeWidgetValue = function (v: WidgetValue) {
  this.write(v?.type);

  if (v?.type === "entity") {
    this.writeObjectId(v?.value);
  } else {
    this.write(v?.value);
  }

  this.write(v?.entityKey);
};
HCFiler.prototype.readWidgetValue = function (v: WidgetValue) {
  v.type = this.read();

  if (v.type === "entity") {
    v.value = this.readObjectId();
  } else v.value = this.read();

  v.entityKey = this.read();
};
