import { Vector3 } from "../babylon";
import { Entity } from "../entitys";
import { iVector2Vector } from "../utils/vector";
import { IPointerEvent } from "./../types/event";
import { DragPointsManager } from "./DragPointsManager";
import { EditorService, EditorStatus } from "./interface";

import type { Editor } from "./Editor";

/**
 * 拖拽点控制服务
 */
export class DragControlService implements EditorService {
  isReady = false;
  manager = new DragPointsManager();
  private startPoint = new Vector3();
  private cleaner: (() => void)[] = [];
  private _dragList: [Entity, number][] = [];
  constructor(private _editor: Editor) {}
  attach(nodes: Entity[]) {
    if (!nodes?.length) {
      this.cancel();
      return;
    }
    this.isReady = true;
    for (const node of nodes) this.manager.append(node);
  }
  do(event: IPointerEvent) {
    const result = this.manager.intersect(event.pickInfo.ray);
    if (result.length === 0) return false;
    this._editor.Status = EditorStatus.Drag;
    this.register();
    this.startPoint.copyFrom(iVector2Vector(event.worldPoint));
    this._dragList.push(...result);
    return true;
  }

  update() {
    this.manager.update();
  }
  cancel() {
    this.isReady = false;
    this.manager.clear();
    this.unRegister();
    this._dragList.length = 0;
  }
  private move = (event: IPointerEvent) => {
    this.manager.hide();
    const current = iVector2Vector(event.worldPoint);
    const vec = current.subtract(this.startPoint);
    this.startPoint.copyFrom(current);
    for (const [en, index] of this._dragList) {
      en.moveDragPoint(vec, index);
    }
  };
  private moveEnd = () => {
    this.manager.update();
    this.unRegister();
    this._editor.Status = EditorStatus.None;
    this._dragList.length = 0;
  };

  private register() {
    this.cleaner.push(
      this._editor.mouseCtrl.on("pointerMove", this.move),
      this._editor.mouseCtrl.on("pointerUp", this.moveEnd)
    );
  }
  private unRegister() {
    this.cleaner.forEach((c) => c());
    this.cleaner.length = 0;
  }
}
