import { Factory, HCFiler, ObjectId } from "@hc/core";
import { BaseWidget } from "../../core/BaseWidget";
import { Entity } from "../../entitys/Entity";
import { MoveType } from "../../types/interaction.types";
import { evalExpr } from "../../utils/eval";

export interface IRotateItemProps {
  objectId: ObjectId<Entity>;
  animationType: string;
  time: number;
  angle: number;
  type: MoveType;
  dir: 0 | 1;
  pivot: { x: string; y: string; z: string };
  axis: { x: string; y: string; z: string };
}

@Factory
export class Rotate extends BaseWidget {
  static type = "Rotate";
  static schema = {
    label: "旋转",
  };
  private _List: IRotateItemProps[] = [];

  get List() {
    return [...this._List];
  }
  async do() {
    const ps: Promise<unknown>[] = [];
    console.log("rotate");
    for (const data of this._List) {
      const en = data.objectId.Object;
      if (!en) continue;

      const size = en.BoundingBox.extendSize.scale(2);
      const param2 = {
        x: size.x,
        X: size.x,
        y: size.y,
        Y: size.y,
        z: size.z,
        Z: size.z,
      };
      const pivot = {
        x: evalExpr(data.pivot.x, param2),
        y: evalExpr(data.pivot.y, param2),
        z: evalExpr(data.pivot.z, param2),
      };
      const axis = {
        x: parseFloat(data.axis.x) || 0,
        y: parseFloat(data.axis.y) || 0,
        z: parseFloat(data.axis.z) || 0,
      };

      ps.push(
        en.rotateTo({
          angle: data.dir === 0 ? data.angle * -1 : data.angle,
          time: data.time,
          isOffset: data.type === MoveType.Offset,
          animation: true,
          pivot: pivot,
          startTime: this.startTime,
          dir: axis,
        })
      );
    }
    return Promise.all(ps).then((rs) => {
      return true;
    });
  }
  add(data: IRotateItemProps) {
    this._List.push(data);
  }
  fromJSON(data: any[]): void {
    console.log("data: ", data);
    this._List.length = 0;
    for (const d of data) {
      this._List.push({
        objectId: d.objectId,
        time: d.time,
        angle: d.angle,
        type: d.type,
        animationType: d.animationType,
        dir: d.dir,
        pivot: d.pivot,
        axis: d.axis,
      });
    }
  }
  readFile(filer: HCFiler): void {
    const ver = filer.read();
    const count = filer.read();
    this._List.length = 0;
    for (let i = 0; i < count; i++) {
      const data: Partial<IRotateItemProps> = {
        pivot: { x: "", y: "", z: "" },
        axis: { x: "0", y: "1", z: "0" },
      };
      data.objectId = filer.readObjectId() as ObjectId<Entity>;
      data.type = filer.read();
      data.time = filer.read();
      data.animationType = filer.read();
      data.angle = filer.read();
      data.dir = filer.read();
      data.pivot.x = filer.read();
      data.pivot.y = filer.read();
      data.pivot.z = filer.read();
      if (ver > 1) {
        data.axis.x = filer.read();
        data.axis.y = filer.read();
        data.axis.z = filer.read();
      }
      this._List.push(data as IRotateItemProps);
    }
  }
  writeFile(filer: HCFiler): void {
    filer.write(2);
    filer.write(this._List.length);
    for (const data of this._List) {
      filer.writeObjectId(data.objectId);
      filer.write(data.type);
      filer.write(data.time);
      filer.write(data.animationType);
      filer.write(data.angle);
      filer.write(data.dir);
      filer.write(data.pivot.x);
      filer.write(data.pivot.y);
      filer.write(data.pivot.z);

      filer.write(data.axis.x);
      filer.write(data.axis.y);
      filer.write(data.axis.z);
    }
  }
}
