import {
  AbstractMesh,
  BoundingBox,
  Mesh,
  TransformNode,
  Vector2,
  Vector3,
} from "../../babylon";
import { Entity } from "../../entitys";
import { SelectType } from "../../types";
import { Box3 } from "../../utils";
import { Viewer } from "../../viewer/Viewer";
import { SelectBase } from "./SelectBase";

export class SelectBox extends SelectBase {
  _SelectType;
  private activeMeshes = new Set<AbstractMesh>();
  constructor(
    protected _Viewer: Viewer,
    private start: Vector2,
    private end: Vector2
  ) {
    super(_Viewer);
    const activeMeshesData = this._Viewer.Scene.getActiveMeshes();
    this.activeMeshes = new Set(
      activeMeshesData.data.slice(0, activeMeshesData.length)
    );
  }
  select(nodes?: (Mesh | TransformNode)[]) {
    nodes = nodes ?? (this._Viewer.Scene.rootNodes as (Mesh | TransformNode)[]);
    for (const n of nodes) {
      if (this.targetIsSelect(n)) {
        this.selectList.add(n);
      }
    }
  }

  targetIsSelect(node: Mesh | TransformNode) {
    if (!node.metadata?.Entity?.Visiable) return false;
    if (!node.metadata?.Entity?.IsPickable) return false;
    if (node.name === "WORLD_GROUND") return false;
    if (this._SelectType === SelectType.None) return false;

    const startPosition = this.start;
    const endPosition = this.end;

    //FIXME: 大场景框选优化
    const minX = Math.min(startPosition.x, endPosition.x);
    const minY = Math.min(startPosition.y, endPosition.y);
    const maxX = Math.max(startPosition.x, endPosition.x);
    const maxY = Math.max(startPosition.y, endPosition.y);

    const en = node.metadata?.Entity as Entity;

    const boundBox = en.BoundingBox;

    const points = boundBox.vectorsWorld.map((p) => this.world2screen(p));
    const box = new Box3(points[0], points[1]);
    box.setFromPoints(points);

    const box2 = new Box3(new Vector3(minX, minY), new Vector3(maxX, maxY));

    if (!BoundingBox.Intersects(box, box2)) {
      return false;
    }
    const meshs = node.getChildMeshes() as Mesh[];
    for (const mesh of meshs) {
      if (!mesh.geometry) continue;
      // if (!this._Viewer.Camera.isInFrustum(mesh)) continue;
      if (!this.activeMeshes.has(mesh)) continue;
      const pts = mesh.getPositionData();
      const mtx = mesh.getWorldMatrix();
      for (let i = 0; i < pts.length; i += 3) {
        const wp = new Vector3(pts[i], pts[i + 1], pts[i + 2]);
        Vector3.TransformCoordinatesToRef(wp, mtx, wp);
        const sp = this.world2screen(wp);
        if (box2.intersectsPoint(sp)) {
          return true;
        }
        continue;
      }
    }

    return false;
  }
}
