import { EventCallback, IEvent, IEventOptions } from "./types/event.types";
import { arrayRemoveIf } from "./utils/array";

export class EventDispatcher {
  protected _listeners: Map<string, EventCallback[]> = new Map();
  on(
    type: string,
    listener: EventCallback,
    option?: IEventOptions
  ): () => void {
    let listents = this._listeners.get(type);

    if (!listents) {
      listents = [];
      this._listeners.set(type, listents);
    }

    if (listents.indexOf(listener) === -1) {
      if (option) listener["__customOption__"] = option;
      listents.push(listener);
    }
    return () => this.off(type, listener);
  }
  once(type: string, listener: EventCallback, option?: IEventOptions) {
    listener["__isOnce__"] = true;
    return this.on(type, listener, option);
  }

  hasEventListener(type: string, listener: EventCallback) {
    let ls = this._listeners.get(type);

    if (!ls) return false;

    return ls.indexOf(listener) !== -1;
  }

  off(type: string, listener: EventCallback) {
    let ls = this._listeners.get(type);

    if (ls) {
      const index = ls.indexOf(listener);
      if (index !== -1) {
        ls.splice(index, 1);
      }
    }
  }

  trigger(event: IEvent) {
    let ls = this._listeners.get(event.type);
    if (ls) {
      if (!event.target) event.target = this as any;

      // 避免事件被移除了
      const array = ls.slice(0);
      for (let i = 0, l = array.length; i < l; i++) {
        const customOption = array[i]["__customOption__"] as IEventOptions;

        if (customOption) {
          if (customOption.userData) {
            event.data = customOption.userData;
          }
          if (!customOption.condition?.(event)) {
            continue;
          }
        }
        array[i].call(this, event);
      }

      //移除一次性事件
      arrayRemoveIf(ls, (l) => l["__isOnce__"]);
    }
  }
}
