import { HemisphericParticleEmitter } from "../../babylon";
import { RadiusEmiter } from "./RadiusEmiter";
import { Factory } from "@hc/core";
import type { ParticleSystem } from "../ParticleSystem";

@Factory
export class HemisphericEmiter extends RadiusEmiter {
  static type = "HemisphericEmiter";
  protected _emiter: HemisphericParticleEmitter;
  constructor(
    protected attachSystem: ParticleSystem,
    option?: { radius?: number; radiusRange?: number }
  ) {
    super(attachSystem, option);
    if (attachSystem?.HasIntance) this.setEmiter();
  }
  setEmiter() {
    this._emiter = this.attachSystem.Intance.createHemisphericEmitter(
      this._radius,
      this._radiusRange
    );
  }
}
