import type { Application } from "@hc/engine";
import { Axios } from "axios";
import { FileApi } from "~/apis";
import { IScriptProps } from "~/types";
import { getReq, ResponseStatus } from "./axios";
import {
  getFunction,
  getUniupComponentType,
  registerComponent,
} from "./component";
import { ImportScript } from "./importScript";
import { updateQueryStringParameter } from "./url";

export async function loadOnlineFile(
  data: { components: number[]; data },
  intance: Axios,
  app
) {
  const components = data.components ?? [];
  for (const id of components) {
    const res = await intance.get(FileApi.detail2 + id);

    if (res["status"] === 200 && res.data.code === 0) {
      const json = res.data.data.json_data;
      const data = JSON.parse(json);
      registerComponent({ ...data.data, id });
    }
  }
  app.loadFile(data.data);
}

export async function loadOnlineComponent(
  id: number,
  app: Application,
  option: any = {}
) {
  const result = await getReq(FileApi.detail + "/" + id);
  if (result.code === ResponseStatus.Ok) {
    const info = result.data.info;
    const json = info.json_data;
    const jsonData = JSON.parse(json);
    const d = jsonData.data;
    registerComponent({ ...d, id });
    const en = app.create({ type: getUniupComponentType(id), ...option });
    app.Editor.selectControl.SelectEntitys = [en];
    return true;
  }
  return false;
}

export async function loadOnlineModel(
  id: number,
  app: Application,
  option: any = {}
) {
  const result = await getReq(FileApi.detail + "/" + id);
  if (result.code === ResponseStatus.Ok) {
    const info = result.data.info;
    const url = info?.uuid + "/" + info.file_key;
    const en = app.create({ type: "ImportEntity", url, ...option });
    app.Editor.selectControl.SelectEntitys = [en];
    return true;
  }
  return false;
}

export function updateUrl(id: number) {
  const newUrl = updateQueryStringParameter(location.href, "id", id.toString());
  history.replaceState(
    {
      path: newUrl,
    },
    "",
    newUrl
  );
}

export async function execScript(
  scripts: IScriptProps[],
  app: Application,
  HC
) {
  HC.ImportScript = ImportScript;
  for (const script of scripts) {
    try {
      if (script.enabled !== undefined && !script.enabled) continue;
      if (!script.version) {
        const f = getFunction(script.code)();
        await f(app, HC);
      } else {
        const f = Object.getPrototypeOf(async function () {}).constructor(
          "app",
          "HC",
          script.code
        );
        await f(app, HC);
      }
    } catch (err) {
      console.error(script.name + ": \t\n" + err);
    }
  }
}

export function downloadBlob(fname: string, blob: Blob) {
  let download = document.createElement("a");
  download.download = fname;
  download.href = window.URL.createObjectURL(blob);
  download.style.display = "none";
  download.onclick = function () {
    document.body.removeChild(download);
  };
  document.body.appendChild(download);

  download.click();
}

export async function readFile(
  file: File,
  encoding?: string
): Promise<string | null> {
  const reader = new FileReader();
  reader.readAsText(file, encoding);

  return new Promise((res, rej) => {
    reader.onload = (e) => {
      res(e.target.result as string);
    };
    reader.onerror = (e) => {
      console.log("e: ", e);
      rej(null);
    };
  });
}
