import { IVector2, IVector3 } from "@hc/core";
import { AppStore } from "../../app/AppStore";
import {
  Color3,
  LinesMesh,
  MeshBuilder,
  Nullable,
  PickingInfo,
  Vector3,
} from "../../babylon";
import { AppEvent } from "../../core";
import { Entity } from "../../entitys";
import { EventEnum } from "../../enums";
import { iVector2Vector } from "../../utils/vector";
import type { Editor } from "../Editor";
import {
  EditorService,
  EditorStatus,
  ServiceOption,
  ServiceResult,
  ServiceStatus,
} from "../interface";
import { IPointerEvent } from "./../../types/event";
import { MouseKey } from "./../../utils/keyenum";

export interface PointResult {
  worldPoint?: IVector3;
  screenPoint?: IVector2;
  pickInfo?: PickingInfo;
  entity?: Entity;
  key?: string;
}

export type PointServiceResult = ServiceResult<PointResult>;

export class GetPointService implements EditorService {
  public isReady = false;
  private resolve: (v: PointServiceResult) => void;
  private cleaners: (() => void)[] = [];
  private auxiliaryLine: LinesMesh;
  private option?: ServiceOption;
  private _UpdatePoint: Nullable<(p) => void>;
  constructor(private editor: Editor) {}
  async start(option?: ServiceOption): Promise<PointServiceResult> {
    if (this.editor.Status === EditorStatus.GetPoint) return;
    this.option = option;
    this.isReady = true;
    this.editor.Status = EditorStatus.GetPoint;
    this.editor.focus();
    this._UpdatePoint = option?.getPointCallback;
    this.initAuxiliaryLine();
    this.initMouseEvent();
    this.initKeyDownEvent();
    //发送当前关键字信息
    AppEvent.trigger({
      type: EventEnum.ServiceStatus,
      msg: this.option?.msg ?? "请选择点",
      keywords: (this.option?.keywordList ?? []).concat([
        { label: "退出", key: "ESC" },
      ]),
    });

    return new Promise<PointServiceResult>((resolve) => {
      this.resolve = resolve;
    });
  }
  do(event: IPointerEvent) {
    if (event.event.button === MouseKey.Left) {
      this.returnPoint({
        worldPoint: event.worldPoint,
        screenPoint: { x: event.screenX, y: event.screenY },
        pickInfo: event.pickInfo,
        entity: event.target as Entity,
      });
    }
    return true;
  }
  returnPoint(result: PointResult) {
    if (!this.resolve) return;
    this.resolve({
      status: ServiceStatus.Ok,
      ...result,
    });
    this.reset();
  }
  returnKeyWord(result: PointResult) {
    if (!this.resolve) return;
    this.resolve({
      status: ServiceStatus.KeyWord,
      ...result,
    });
    this.reset();
  }
  cancel() {
    if (!this.resolve) return;
    this.resolve({
      status: ServiceStatus.Cancel,
    });
    this.reset();
  }
  private initAuxiliaryLine() {
    if (!this.option?.hasAuxiliaryLine || !this.option?.basePoint) return;
    if (!this.auxiliaryLine) {
      this.auxiliaryLine = MeshBuilder.CreateDashedLines(
        "auxiliaryLine",
        {
          points: [new Vector3(), new Vector3(1)],
          updatable: true,
          dashSize: 1000,
          gapSize: 800,
          dashNb: 50,
        },
        AppStore.MainScene
      );
      this.auxiliaryLine.color = Color3.Green();
    }

    this.auxiliaryLine.setEnabled(true);
  }
  private updateAuxiliaryLine(currentPt: IVector3) {
    if (this.auxiliaryLine?.isEnabled?.())
      MeshBuilder.CreateDashedLines(null, {
        points: [
          iVector2Vector(this.option.basePoint),
          iVector2Vector(currentPt),
        ],
        updatable: true,
        instance: this.auxiliaryLine,
      });
  }
  private reset() {
    this.isReady = false;
    this.editor.Status &= ~EditorStatus.GetPoint;
    this.resolve = null;
    this._UpdatePoint = null;
    this.cleaners.forEach((c) => c());
    this.option = null;
    this.cleaners.length = 0;
    //清楚关键字信息
    AppEvent.trigger({
      type: EventEnum.ServiceStatus,
    });
    if (this.auxiliaryLine) {
      this.auxiliaryLine.setEnabled(false);
    }
  }
  private initMouseEvent() {
    this.cleaners.push(
      this.editor.mouseCtrl.on("pointermove", (event) => {
        this.updateAuxiliaryLine(event.worldPoint);
        this._UpdatePoint?.(event.worldPoint);
      })
    );
  }
  private initKeyDownEvent() {
    if (this.option?.keywordList?.length)
      this.cleaners.push(
        this.editor.keyboardCtrl.on("keydown", (event) => {
          for (const kw of this.option.keywordList) {
            if (kw.key.toUpperCase() === event.key.toUpperCase()) {
              this.returnKeyWord({ key: kw.key });
              return;
            }
          }
        })
      );
  }
}
