import { Vector3 } from "../../babylon";
import { Entity } from "../..";
import { Factory, HCFiler, ObjectId } from "@hc/core";
import { BaseWidget } from "../../core/BaseWidget";
import { evalExpr } from "../../utils/eval";

export interface IScaleItemProps {
  objectId: ObjectId<Entity>;
  type: string;
  bySize: boolean;
  animationType: string;
  time: number;
  scale: { x: string; y: string; z: string };
  pivot: { x: string; y: string; z: string };
}

@Factory
export class Scale extends BaseWidget {
  static type = "Scale";
  static schema = {
    label: "缩放",
  };
  private _List: IScaleItemProps[] = [];

  get List() {
    return [...this._List];
  }

  async do() {
    const ps: Promise<unknown>[] = [];
    console.log("scale");
    for (const data of this._List) {
      const en = data.objectId.Object;
      if (!en) continue;
      let x: number, y: number, z: number;

      const size = en.BoundingBox.extendSize.scale(2);
      const origin = data.bySize
        ? en.BoundingBox.extendSizeWorld.scale(2)
        : en.Scale;
      const param = {
        x: origin.x,
        X: origin.x,
        y: origin.y,
        Y: origin.y,
        z: origin.z,
        Z: origin.z,
      };
      const param2 = {
        x: size.x,
        X: size.x,
        y: size.y,
        Y: size.y,
        z: size.z,
        Z: size.z,
      };
      x = evalExpr(data.scale.x, param);
      y = evalExpr(data.scale.y, param);
      z = evalExpr(data.scale.z, param);
      if (data.bySize) {
        x /= origin.x;
        y /= origin.y;
        z /= origin.z;
      }

      const povit = {
        x: evalExpr(data.pivot.x, param2),
        y: evalExpr(data.pivot.y, param2),
        z: evalExpr(data.pivot.z, param2),
      };

      ps.push(
        en.scaleTo({
          scale: new Vector3(x, y, z),
          time: data.time,
          animation: true,
          isOffset: data.bySize,
          pivot: povit,
          startTime: this.startTime,
        })
      );
    }
    return Promise.all(ps).then((rs) => {
      return true;
    });
  }
  add(data: IScaleItemProps) {
    this._List.push(data);
  }
  toJSON() {
    return {};
  }
  fromJSON(data: any[]): void {
    this._List.length = 0;
    for (const d of data) {
      this._List.push({
        ...d,
      });
    }
  }
  readFile(filer: HCFiler): void {
    filer.read();
    this._List.length = 0;
    filer.readAnyArray(() => {
      const data: Partial<IScaleItemProps> = {
        scale: { x: "1", y: "1", z: "1" },
        pivot: { x: "x/2", y: "y/2", z: "z/2" },
      };
      data.objectId = filer.readObjectId() as ObjectId<Entity>;
      data.scale.x = filer.read();
      data.scale.y = filer.read();
      data.scale.z = filer.read();
      data.pivot.x = filer.read();
      data.pivot.y = filer.read();
      data.pivot.z = filer.read();
      data.type = filer.read();
      data.time = filer.read();
      data.animationType = filer.read();
      data.bySize = filer.read();
      this._List.push(data as IScaleItemProps);
    });
  }
  writeFile(filer: HCFiler): void {
    filer.write(1);
    filer.writeAnyArray(this._List, (data) => {
      filer.writeObjectId(data.objectId);
      filer.write(data.scale.x);
      filer.write(data.scale.y);
      filer.write(data.scale.z);
      filer.write(data.pivot.x);
      filer.write(data.pivot.y);
      filer.write(data.pivot.z);
      filer.write(data.type);
      filer.write(data.time);
      filer.write(data.animationType);
      filer.write(data.bySize);
    });
  }
}
