import { EventDispatcher } from "./EventDispatcher";
import { BaseDatabase } from "./database/BaseDatabase";
import { ObjectId } from "./ObjectId";
import { HCFiler } from "./filer/HCFiler";

import { ISerialize } from "./types/iSerialize.types";
import { EraseEntityData } from "./history/EraseEntityData";

/**
 * 数据库储存对象基类
 *
 * @export
 * @abstract
 * @class BaseObject
 * @extends {EventDispatcher}
 * @implements {ISerialize}
 */
export abstract class BaseObject extends EventDispatcher implements ISerialize {
  static type = "";
  protected _ver: number;
  protected _Owner: ObjectId;
  protected _db: BaseDatabase;
  protected _id: ObjectId<this>;
  protected _erase = false;
  protected _name: string;

  /**@hidden */
  set Owner(owner: ObjectId) {
    this._Owner = owner;
  }
  /**@hidden */
  get Owner() {
    return this._Owner;
  }
  get IsErase() {
    return this._erase;
  }
  get ObjectId() {
    return this._id;
  }
  /**@hidden */
  set ObjectId(id: ObjectId<this>) {
    this._id = id;
  }
  get Id() {
    return this._id?.Index;
  }
  /**@hidden */
  get DB() {
    return this._db;
  }
  get Name() {
    return this._name;
  }
  get Type() {
    //@ts-ignore
    return this.constructor.type;
  }
  /**
   * 删除对象
   * @param e 删除还是取消删除
   * @returns
   */
  erase(e = true) {
    if (this._erase === e) return;
    this._erase = e;
    const r = this.UndoRecord;
    if (r) {
      r.createEraseHistory(this, e);
    }
  }
  clone() {
    //@ts-ignore
    const cloneC = new this.constructor();
    const backId = this._id;
    this._id = null;
    let filer = new HCFiler();
    filer.database = this._db;
    this.writeFile(filer);
    cloneC.readFile(filer);
    cloneC._db = undefined;
    cloneC._id = null;
    this._id = backId;
    return cloneC;
  }
  /**
   * 从其他同类实体中拷贝数据
   * @param obj
   * @returns
   */
  copyFrom(obj: this) {
    let idBak = this._id;
    let ownerBak = this._Owner;
    this.writeSnapshoot();
    let f = new HCFiler();
    obj.writeFile(f);
    this.readFile(f);
    this._id = idBak;
    this._Owner = ownerBak;
    return this;
  }
  replace(...arg: any) {}
  destory(all?: boolean) {
    this._db = undefined;
  }
  /**@hidden */
  writeFile(filer: HCFiler) {
    filer.write(3);
    filer.writeObjectId(this._id);
    filer.write(this._erase);
    filer.writeObjectId(this._Owner);
  }
  /**@hidden */
  readFile(filer: HCFiler) {
    this._ver = filer.read();
    const id = filer.readObjectId() as ObjectId<this>;
    if (!this._id && id) {
      this._id = id;
      this._id.Object = this;
    }
    this._erase = filer.read();
    this._Owner = filer.readObjectId();
  }
  //私有的方法,暴露给Db的添加对象,方法使用.
  //只用对象加入到db中,我们才初始化ObjectId.
  //从db池中分配id给自身使用. 除非你创建对象往db里面加,否则不要调用该方法
  _SetOwnerDatabase(db: BaseDatabase) {
    if (!this._db) {
      this._db = db;
      this._id = db.allocateId() as ObjectId<this>;
      this._id.Object = this;
    } else console.warn("重复设置源Database!");

    return this;
  }
  _SetDefaultDb(db: BaseDatabase) {
    if (!this._db) this._db = db;
    else console.warn("重复设置默认Database!");

    return this;
  }
  _SetDatabase(db: BaseDatabase) {
    this._db = db;
  }

  /*实体修改撤销*/
  _applyHistoryRecord(record: ISerialize) {
    if (record instanceof EraseEntityData) {
      this._erase = record.isErase;
    }
  }
  //获取最近一条历史记录表
  protected get UndoRecord() {
    if (!this._db) return;
    const hm = this._db.HistoryManager;

    return hm.LastCmdRecord;
  }
  protected writeSnapshoot() {
    const record = this.UndoRecord;
    if (record) {
      record.writeObjectSnapshoot(this);
    }
  }
}
