export type INode = Mesh | TransformNode;

export type StartAnimateOptions = {
  loop?: boolean;
  speedRatio?: number;
  onComplete?: () => void;
};

/**
 * 绘制实体对象基类
 *
 * @export
 * @class Entity
 * @extends {BaseObject}
 */
export class Entity extends BaseObject {
  static type: string;
  static renderType = RenderType.Conceptual;
  static Scene: Scene;
  public isAsync = false;
  public isLight = false;
  isPart = false;
  /**选中状态 */
  IsSelect = false;
  private _enableShadow = true;
  //自身坐标系
  private _matrix = Matrix.Identity();
  private _scale = new Vector3(1, 1, 1);
  private _rotation = new Vector3();
  private _rotationQuaternion = new Quaternion();
  protected _position = new Vector3();
  protected _drawObject: TransformNode;
  private _isVisiable = true;
  protected _colorIndex = 9;
  protected _color: string;
  private _renderType: RenderType;
  /**实体材质 */
  protected _materialMap = new Map<string, ObjectId<MaterialRecord>>();
  protected _cacheDrawObject = new Map<RenderType, Mesh>();
  private _animations: IAnimation[] = [];
  private _isPickable = true;
  protected modelAnimations: BAnimationGroup[];
  protected _name: string;
  private _highlight = false;
  /**
   * 模型材质透明度
   * @deprecated
   * @protected
   * @memberof Entity
   */
  protected _opacity = 1;
  private _animationing: BAnimation[] = [];
  /**模型自带的材质 */
  protected _ownMaterials: MaterialRecord[] = [];
  public GroupId: Nullable<ObjectId<GroupRecord>>;
  protected updateFlag = UpdateFlag.None;
  public AutoUpdate = true;
  public registerAfterRenderFun;
  /**用于存一些临时的数据 */
  public TempData: any;
  /**
   * 模型自带的材质修改记录
   *
   * @memberof Entity
   */
  public diffMaterials = new Map<string, Record<string, any>>();
  private _material: ObjectId<MaterialRecord>;
  #visibility = 1;
  #enableEdge = false;
  #edgeWidth = 1;
  #edgeColor = new Color4(1, 1, 1, 1);

  constructor() {
    super();
  }

  get UniqueName() {
    return this.constructor["type"] + this.Id;
  }
  get Name() {
    return this._name ?? this.UniqueName;
  }
  set Name(n: string) {
    this._name = n;
    this.trigger2({ type: EventEnum.UpdateName, target: this });
  }
  private get CanUpdate() {
    return !!this.ObjectId?.Index && !this._erase;
  }
  get RenderType() {
    return this._renderType ?? Entity.renderType;
  }
  get IsPickable() {
    return this._isPickable;
  }
  set IsPickable(v: boolean) {
    this._isPickable = v;

    // 实体有可能还未绘制到画布上
    if (!this._drawObject) {
      return;
    }

    this._drawObject.getChildMeshes().forEach((m) => (m.isPickable = v));
  }
  get Highlight() {
    return this._highlight;
  }
  get IsComponent() {
    return false;
  }
  /**
   * 获取渲染的实体容器对象
   * # 注意，如果实体没渲染，该getter 会创建一个新的实体并添加到画布场景!!!
   * @readonly
   * @memberof Entity
   */
  get DrawObject() {
    if (this._drawObject) return this._drawObject;
    const obj = this.GetDrawObjectFromRenderType(
      this._renderType ?? Entity.renderType
    );
    this._drawObject = new TransformNode(
      this.Name ?? this._id?.Index.toString(),
      Entity.Scene,
      false
    );
    if (!this.isPart) {
      obj.parent = this._drawObject;
      this._drawObject.metadata = {
        Entity: this,
      };
    }

    this.update();

    return this._drawObject;
  }
  get Scene() {
    return this._drawObject?.getScene?.();
  }
  get RenderObject(): Mesh {
    if (!this._drawObject && !this.isPart) return null;
    return this.GetDrawObjectFromRenderType(this.RenderType);
  }
  get ColorIndex() {
    return this._colorIndex;
  }
  set ColorIndex(v) {
    if (this._colorIndex === v) return;
    this.writeSnapshoot();
    this._colorIndex = v;
    this.update();
  }

  get Matrix() {
    return this._matrix.clone();
  }
  set Matrix(mtx: Matrix) {
    this.writeSnapshoot();
    this._matrix.copyFrom(mtx);
    this._matrix.decompose(
      this._scale,
      this._rotationQuaternion,
      this._position
    );
    this._rotationQuaternion.toEulerAnglesToRef(this._rotation);
    this.update();
  }
  get MatrixInv() {
    return this._matrix.invert();
  }
  get TransfromFromMtx() {
    const position = new Vector3();
    const rq = new Quaternion();
    const scale = new Vector3();
    this._matrix.decompose(scale, rq, position);

    return {
      position,
      rotation: rq.toEulerAngles(),
      scale,
    };
  }
  get BoundingVectors() {
    if (this._drawObject) return this._drawObject.getHierarchyBoundingVectors();
    else
      return {
        min: new Vector3(),
        max: new Vector3(),
      };
  }
  get BoundingBox(): Box3 {
    if (this.RenderObject) {
      const b = this.RenderObject.getBoundingInfo().boundingBox;
      return new Box3(b.minimum, b.maximum, b.getWorldMatrix());
    }
    return new Box3();
  }
  /**
   * @deprecated
   *
   * @memberof Entity
   */
  get Opacity() {
    return this._opacity;
  }
  set Opacity(v: number) {
    if (v === this._opacity) return;
    this.writeSnapshoot();
    this._opacity = v;

    this.update(UpdateFlag.Geometry);
  }
  get Color() {
    return this._color;
  }
  set Color(v: string) {
    if (v === this._color) return;
    this.writeSnapshoot();
    this._color = v;
    this.update(UpdateFlag.Material);
  }

  get Position() {
    return { x: this._position.x, y: this._position.y, z: this._position.z };
  }
  set Position(v: IVector3) {
    this.writeSnapshoot();
    setVecLike2Vector(this._position, v);
    this.updateEntityMatrix();
    this.update();
  }

  get Rotation() {
    return { x: this._rotation.x, y: this._rotation.y, z: this._rotation.z };
  }
  set Rotation(rotation: IVector3) {
    this.writeSnapshoot();
    setVecLike2Vector(this._rotation, rotation);
    Quaternion.FromEulerAnglesToRef(
      this._rotation.x,
      this._rotation.y,
      this._rotation.z,
      this._rotationQuaternion
    );
    this.updateEntityMatrix();
    this.update();
  }
  get Scale() {
    return { x: this._scale.x, y: this._scale.y, z: this._scale.z };
  }
  set Scale(v: IVector3) {
    this.writeSnapshoot();
    setVecLike2Vector(this._scale, v);
    this._scale.copyFromFloats(v.x, v.y, v.z);
    this.updateEntityMatrix();
    this.update();
  }
  get Visiable() {
    return this._isVisiable && !this.IsErase;
  }
  set Visiable(v: boolean) {
    if (v === this._isVisiable) return;
    this.writeSnapshoot();
    this._isVisiable = v;
    this._drawObject?.setEnabled(!this._erase && this._isVisiable);
    this.trigger2({ type: EventEnum.UpdateVisiable, target: this });
  }
  get AnimationNames() {
    const names: string[] = [];
    if (this.modelAnimations) {
      this.modelAnimations.forEach((a) => names.push(a.name));
    }
    this._animations.forEach((a) => names.push(a.name));
    return names;
  }
  get Animations() {
    const animations: IAnimation[] = [...this._animations];
    if (this.modelAnimations?.length) {
      animations.push(...this.modelAnimations);
    }
    return animations;
  }
  get EnableShadow() {
    return this._enableShadow;
  }
  set EnableShadow(v) {
    if (this._enableShadow === v) return;
    this._enableShadow = v;
    AppEvent.trigger({ type: EventEnum.UpdateShadow, target: this });
  }
  get MaterialMap() {
    return this._materialMap;
  }
  /**设置实体的材质，实体中所有的网格都将应用这个实体 */
  get Material(): MaterialRecord {
    return this._material?.Object;
  }
  set Material(mtl: MaterialRecord | ObjectId<MaterialRecord>) {
    this.trigger2({ type: EventEnum.UpdateMaterial, target: this });
    this._material = mtl instanceof MaterialRecord ? mtl.ObjectId : mtl;
    this.update(UpdateFlag.Material);
  }
  /**实体自带的材质 */
  get OwnMaterials() {
    return [...this._ownMaterials];
  }
  get AllMaterils() {
    if (this.Material) {
      return [this.Material];
    } else {
      const mtls = new Set<MaterialRecord>();
      for (const [id, mtl] of this._materialMap) {
        mtls.add(mtl.Object);
      }

      if (this.RenderObject) {
        const meshes = this.RenderObject.getChildMeshes() as Mesh[];
        for (const m of meshes) {
          if (m.geometry && m.material && !this._materialMap.has(m.id)) {
            const mtl = m.material;
            const record = mtl.metadata?.record;
            if (record) {
              mtls.add(record);
            }
          }
        }
      }

      return Array.from(mtls);
    }
  }
  /**
   * 网格透明度
   * - 透明度会和材质透明度叠加
   *
   * @memberof Entity
   */
  get Visibility() {
    return this.#visibility;
  }
  set Visibility(v: number) {
    if (v === this.#visibility) return;
    this.writeSnapshoot();
    this.#visibility = v;
    this.update(UpdateFlag.Geometry);
  }
  /**
   * 开启网格线宽渲染
   *
   * @memberof Entity
   */
  get EnableEdge() {
    return this.#enableEdge;
  }
  set EnableEdge(v: boolean) {
    if (v === this.#enableEdge) return;
    this.writeSnapshoot();
    this.#enableEdge = v;
    this.update(UpdateFlag.Geometry);
  }
  get EdgeWidth() {
    return this.#edgeWidth;
  }
  set EdgeWidth(v: number) {
    if (v === this.#edgeWidth) return;
    this.writeSnapshoot();
    this.#edgeWidth = v;
    this.update(UpdateFlag.Geometry);
  }
  get EdgeColor() {
    return hexToRgba(this.#edgeColor.toHexString());
  }
  set EdgeColor(v: string) {
    this.writeSnapshoot();
    const obj = parseColorString(v);
    this.#edgeColor = new Color4(obj.r / 255, obj.g / 255, obj.b / 255, obj.a);
    this.update(UpdateFlag.Geometry);
  }
  /**
   * 给指定网格分配材质
   * @param id 网格id
   * @param mtl 材质对象
   * @see https://hc3dol.gkiiot.com/#18
   */
  setMaterial(id: string, mtl: MaterialRecord) {
    this.trigger2({ type: EventEnum.UpdateMaterial, target: this });
    this._materialMap.set(id, mtl.ObjectId);
    this.update(UpdateFlag.Material);
  }
  getAllMesh(filter = (m: Mesh) => !!m.geometry) {
    return this.RenderObject.getChildMeshes().filter(filter);
  }
  /**高亮物体
   * color 轮廓颜色，为空时取消高亮
   */
  highlight(color: string) {
    const hg = Entity.Scene.getHighlightLayerByName("select-entity");
    this._drawObject
      .getChildMeshes()
      .forEach((c) =>
        color
          ? hg.addMesh(c as Mesh, Color3.FromHexString(color))
          : hg.removeMesh(c as Mesh)
      );
  }
  translate(vec: IVector3) {
    this.writeSnapshoot();
    this.Position = this._position.add(iVector2Vector(vec));
  }
  GetDrawObjectFromRenderType(
    renderType: RenderType = RenderType.Conceptual
  ): Mesh {
    if (this._cacheDrawObject.has(renderType)) {
      const obj = this._cacheDrawObject.get(renderType);
      obj.setEnabled(true);
      return obj;
    } else {
      const drawObj = this.initDrawObject(renderType);
      if (!drawObj) {
        return;
      }

      this._cacheDrawObject.set(renderType, drawObj);
      return drawObj;
    }
  }
  lookAt(v: IVector3) {
    this._drawObject.lookAt(iVector2Vector(v));
    if (this._drawObject.rotationQuaternion) {
      this._rotationQuaternion.copyFrom(this._drawObject.rotationQuaternion);
      this._rotation.copyFrom(this._rotationQuaternion.toEulerAngles());
    } else {
      this._rotation.copyFrom(this._drawObject.rotation);
      this._rotationQuaternion.copyFrom(this._rotation.toQuaternion());
    }
    this.updateEntityMatrix();
  }
  /**
   * 调整透明度优先级
   * @param index
   * @param name
   * @returns
   */
  setAlphaIndex(index: number, name?: string[]) {
    if (!this._drawObject) return;
    const meshs = this._drawObject.getChildMeshes() as Mesh[];
    for (const m of meshs) {
      if (name && !name.every((n) => m.name.includes(n))) continue;
      m.alphaIndex = index;
    }
  }
  setRenderGroupId(index = 0) {
    if (!this._drawObject) return;
    const meshs = this._drawObject.getChildMeshes() as Mesh[];
    for (const m of meshs) {
      m.renderingGroupId = index;
    }
  }
  removeAnimation(n: string) {
    const an = this._animations.find((an) => an.name === n);
    if (an) {
      an.clear();
      removeOne(this._animations, an);
    }
  }
  /**
   * 开始运行动画
   * @param name
   * @param loop
   * @param speedRatioAnimations
   */
  startAnimates(name: string, option?: StartAnimateOptions) {
    const { loop = true, speedRatio = 1, onComplete = () => {} } = option ?? {};
    for (const animate of this.Animations) {
      if (name === animate.name) {
        if (animate instanceof AnimationGroup) {
          animate.onAnimationEndObservable.addOnce(onComplete);
        }
        animate.start(loop, speedRatio);
      }
    }
  }
  stopAnimates(names?: string[]) {
    for (const animate of this.Animations) {
      if (!names || names.includes(animate.name)) {
        animate.stop();
      }
    }
  }
  clearAnimatable() {
    this.RenderObject.animations.length = 0;
    for (const an of this._animations) {
      an.pause();
      an.stop();
      an.reset();
    }
    this._animations.length = 0;
    this.stopAnimates();
  }
  appendAnimation(an: IAnimation) {
    this.writeSnapshoot();
    this._animations.push(an);
    this.trigger2({ type: EventEnum.AddAnimation, target: this });
  }
  /**
   * 镜像材质
   * @param target
   * @param source
   * @returns
   */
  mirrorMaterial(target: Mesh, source: Mesh[]) {
    const material = target.material as StandardMaterial | PBRMaterial;
    if (!material) return;
    const glass_worldMatrix = target.getWorldMatrix();
    //Obtain normals for plane and assign one of them as the normal
    const glass_vertexData = target.getVerticesData("normal");
    let glassNormal0 = new Vector3(
      glass_vertexData[0],
      glass_vertexData[1],
      glass_vertexData[2]
    );
    //Use worldMatrix to transform normal into its current value
    let glassNormal = Vector3.TransformNormal(glassNormal0, glass_worldMatrix);
    const reflector = Plane.FromPositionAndNormal(
      target.position,
      glassNormal.scale(-1)
    );
    const texture = new MirrorTexture(
      "mirror" + this.Id,
      1024,
      Entity.Scene,
      true
    );
    material.reflectionTexture = texture;
    texture.mirrorPlane = reflector;
    texture.renderList = source;
    texture.level = 1;
  }
  /**实体移动动画
   ** 并未真正改变实体位置
   ** 可重置可循环
   */
  public moveAnimate(option: IMovePathOption): IAnimation {
    const an = new MoveAnimation(this, option);
    this.appendAnimation(an);
    return an;
  }
  /**
   * 实体按轨迹移动动画
   */
  public followAnimate(option: {
    path: IVector3[];
    loop: boolean;
    complete: () => void;
    time: number;
  }) {
    let points = [];
    option.path.forEach((element) => {
      points.push(new Vector3(element.x, element.y, element.z));
    });

    let path3d = new Path3D(points),
      normals = path3d.getNormals(),
      theta = Math.acos(Vector3.Dot(Axis.X, normals[0]));
    this._drawObject.rotate(Axis.Y, theta, Space.WORLD);
    this.Scene.registerAfterRender(
      this.updateRotateAndPosition(points, normals, option)
    );
  }
  private totalDistance(path) {
    let total = 0;
    for (let i = 0; i < path.length - 1; i++) {
      total = total + Vector3.Distance(path[i], path[i + 1]);
    }
    return total;
  }
  private updateRotateAndPosition(path, normals, option) {
    let i = 0,
      startTime: any = new Date(),
      time = option?.time,
      currentTime,
      offsetTime,
      totalDistance,
      offsetDistance;
    this.registerAfterRenderFun = () => {
      if (i === path.length - 1) {
        if (option?.loop) {
          i = 0;
          startTime = new Date();
          let thetaZero = Math.acos(Vector3.Dot(Axis.X, normals[0]));
          this._drawObject.rotate(Axis.Y, thetaZero, Space.WORLD);
        } else {
          option?.complete?.();
          this.Scene.unregisterAfterRender(this.registerAfterRenderFun);
          return;
        }
      }

      currentTime = new Date();
      offsetTime = (currentTime - startTime) / 1000;
      totalDistance = this.totalDistance(path);
      offsetDistance = this.totalDistance(path.slice(0, i + 1)) / totalDistance;
      if (offsetTime < offsetDistance * time) {
        return;
      }

      let theta = Math.acos(Vector3.Dot(normals[i], normals[i + 1]));
      let dir = Vector3.Cross(normals[i], normals[i + 1]).y;

      dir = dir === 0 ? dir : dir / Math.abs(dir);
      this._drawObject.position.x = path[i].x;
      this._drawObject.position.z = path[i].z;

      this._drawObject.rotate(Axis.Y, dir * theta, Space.WORLD);

      i = i + 1;
    };
    return this.registerAfterRenderFun;
  }
  /**
   * 旋转动画
   * @param option
   * @returns
   */
  public rotateAnimate(option: IRotationOption): IAnimation {
    const an = new RotateAnimation(this, option);
    this.appendAnimation(an);
    return an;
  }

  scaleAnimate(opt: IScaleOption): IAnimateReturn {
    const {
      scale,
      time = 1,
      frame = 30,
      loop = false,
      loopCount,
      complete,
    } = opt;

    const endScale = Vector3.FromArray([
      scale.x ?? this._scale.x,
      scale.y ?? this._scale.y,
      scale.z ?? this._scale.z,
    ]);

    const renderObject = this.RenderObject;
    const scene = renderObject.getScene();

    const animate = new BAnimation(
      "rotate-animate",
      "scaling",
      30,
      BAnimation.ANIMATIONTYPE_VECTOR3
    );
    animate.setKeys([
      {
        frame: 0,
        value: this._scale.clone(),
      },
      {
        frame: time * frame,
        value: endScale,
      },
    ]);
    renderObject.animations.push(animate);

    const animatable = scene.beginAnimation(
      renderObject,
      0,
      time * frame,
      loop
    );

    animatable.onAnimationEndObservable.addOnce(() => {
      complete?.();
    });
    let clearObserable = this.setLoopCount(loopCount, animatable);
    const clear = () => {
      animatable.stop();
      clearObserable?.();
      removeOne(renderObject.animations, animate);
    };
    return {
      clear,
      restart: () => animatable.restart(),
      stop: () => animatable.stop(),
      pause: () => animatable.pause(),
      reset: () => animatable.reset(),
    };
  }

  moveByPath(option: IPathOption) {
    const an = new PathAnimation(this, option);
    this.appendAnimation(an);
    return an;
  }

  /*
   * 实体移动到目标位置
   */
  moveTo(
    to: IVector3,
    isOffset = true,
    animateOption?: { time: number; type?: string; startTime?: number }
  ) {
    return new Promise((res) => {
      if (animateOption) {
        const { time = 1, startTime = 0 } = animateOption;

        const origin = this._drawObject.position.clone();

        const frames = [
          {
            frame: startTime * 30,
            value: origin,
          },
          {
            frame: (time + startTime) * 30,
            value: isOffset
              ? origin.clone().add(iVector2Vector(to))
              : iVector2Vector(to),
          },
        ];

        if (equlnVector3(frames[0].value, frames[1].value)) {
          res(null);
          return;
        }

        const animation = new BAnimation(
          "move-animate",
          "position",
          30,
          BAnimation.ANIMATIONTYPE_VECTOR3
        );
        animation.setKeys(frames);
        this._animationing.push(animation);
        this._drawObject.animations.push(animation);
        const animatable = this._drawObject
          .getScene()
          .beginDirectAnimation(
            this._drawObject,
            this._animationing,
            startTime * 30,
            30 * (time + startTime),
            false
          );
        animatable.onAnimationEndObservable.addOnce(() => {
          removeOne(this._drawObject.animations, animation);
          removeOne(this._animationing, animation);
          this._position = frames[1].value;
          console.log("move-end");
          res(null);
        });
      } else {
        if (isOffset) {
          this.applyMatrix(Matrix.Translation(to.x ?? 0, to.y ?? 0, to.z ?? 0));
        } else this.Position = to;
        res(null);
      }
    });
  }
  /**
   * 选中实体
   * @param animateOption
   * @returns
   */
  rotateTo(animateOption: {
    angle: number;
    dir?: IVector3;
    pivot?: IVector3;
    time: number;
    type?: string;
    isOffset: boolean;
    animation?: boolean;
    startTime?: number;
  }) {
    return new Promise((res) => {
      const {
        time = 1,
        dir = { x: 0, y: 1, z: 0 },
        angle,
        animation,
        isOffset,
        pivot,
        startTime = 0,
      } = animateOption;

      const axes = Vector3.FromArray(Object.values(dir)).normalize();
      if (animation) {
        const origin = (
          this._drawObject.rotationQuaternion ||
          Quaternion.FromEulerVector(this._drawObject.rotation)
        ).clone();

        const frames = [
          {
            frame: startTime * 30,
            value: origin.clone(),
          },
        ];

        if (!isOffset) {
          if (
            !this._drawObject.metadata?.rotation ||
            this._drawObject.metadata.rotation !== angle
          ) {
            this._drawObject.metadata.rotation = angle;
          } else {
            res(true);
            return;
          }
        }

        //旋转一周，增加一个缓冲
        if (angle > Math.PI * 2 - 1e-3) {
          const unit = Math.PI / 4;
          const count = Math.round(angle / unit);
          const timeUnit = time / count;

          for (let i = 1; i <= count; i++) {
            const addQ = Quaternion.RotationAxis(axes, i * unit);
            frames.push({
              frame: 30 * (startTime + timeUnit * i),
              value: isOffset ? origin.multiply(addQ) : addQ,
            });
          }
        } else {
          const addQ = Quaternion.RotationAxis(axes, angle);
          frames.push({
            frame: 30 * (startTime + time),
            value: isOffset ? origin.multiply(addQ) : addQ,
          });
        }

        if (
          frames.length === 2 &&
          equlnVector3(frames[0].value, frames[1].value)
        ) {
          res(null);
          return;
        }

        if (pivot) {
          this._drawObject.setPivotPoint(iVector2Vector(pivot));
        }

        const animation = new BAnimation(
          "rotate-animation",
          "rotationQuaternion",
          30,
          BAnimation.ANIMATIONTYPE_QUATERNION
        );

        this._animationing.push(animation);

        animation.setKeys(frames);
        this._drawObject.animations.push(animation);
        const animatable = this._drawObject
          .getScene()
          .beginDirectAnimation(
            this._drawObject,
            this._animationing,
            startTime * 30,
            30 * (startTime + time),
            false
          );
        animatable.onAnimationEndObservable.addOnce(() => {
          removeOne(this._drawObject.animations, animation);
          removeOne(this._animationing, animation);
          this._rotation = arrayLast(frames).value.toEulerAngles();
          console.log("rotate-end");
          res(null);
        });
      } else {
        const rotation = Quaternion.RotationAxis(axes, angle).toEulerAngles();
        if (isOffset) {
          this.applyMatrix(Matrix.RotationAxis(axes, angle));
        } else this.Rotation = rotation;
        res(null);
      }
    });
  }
  scaleTo(option: {
    scale: IVector3;
    pivot?: IVector3;
    time: number;
    animation?: boolean;
    isOffset?: boolean;
    startTime?: number;
  }) {
    const { scale, time, animation, isOffset, pivot, startTime } = option;
    return new Promise((res) => {
      if (animation) {
        const object3D = this._drawObject;
        if (!object3D) {
          res(null);
          return;
        }
        const scene = object3D.getScene();
        const frame = 30;

        const frames = [
          {
            frame: startTime * 30,
            value: this._scale.clone(),
          },
          {
            frame: (time + startTime) * frame,
            value: iVector2Vector(
              isOffset
                ? this._scale.multiplyByFloats(scale.x, scale.y, scale.z)
                : scale
            ),
          },
        ];

        if (equlnVector3(frames[0].value, frames[1].value)) {
          res(null);
          return;
        }

        const animate = new BAnimation(
          "scale-animate",
          "scaling",
          30,
          BAnimation.ANIMATIONTYPE_VECTOR3
        );
        this._animationing.push(animate);

        animate.setKeys(frames);
        object3D.animations.push(animate);

        if (pivot) {
          object3D.setPivotPoint(iVector2Vector(pivot));
        }

        const animatable = scene.beginDirectAnimation(
          object3D,
          this._animationing,
          startTime * frame,
          frame * (time + startTime),
          false
        );
        animatable.onAnimationEndObservable.addOnce(() => {
          removeOne(this._drawObject.animations, animate);
          removeOne(this._animationing, animate);
          this._scale = frames[1].value;
          console.log("scale-end");
          res(null);
        });
      } else {
        this.Scale = scale;
        res(null);
      }
    });
  }
  opacityTo(option: {
    opacity: number;
    time: number;
    animationType?: string;
    startTime?: number;
  }) {
    return new Promise((res) => {
      const { opacity, time, startTime } = option;

      const frames = [
        {
          frame: 30 * startTime,
          value: this._opacity,
        },
        {
          frame: (startTime + time) * 30,
          value: opacity,
        },
      ];

      if (frames[0].value === frames[1].value) {
        res(false);
        return;
      }

      const animation = new BAnimation(
        "opacity" + this.UniqueName,
        "Opacity",
        30,
        BAnimation.ANIMATIONTYPE_FLOAT
      );
      this._animationing.push(animation);

      animation.setKeys(frames);

      const animatable = this.Scene.beginDirectAnimation(
        this,
        this._animationing,
        30 * startTime,
        (time + startTime) * 30
      );

      animatable.onAnimationEndObservable.add(() => {
        console.log("opacity end");
        this._opacity = frames[1].value;
        res(true);
      });
    });
  }
  updateRenderType(type: RenderType) {
    if (
      this._renderType !== type ||
      (this._drawObject && this._drawObject.getChildMeshes().length === 0)
    ) {
      this._drawObject.getChildren().forEach((c) => {
        c.setEnabled(false);
      });
      this._renderType = type;
      const obj = this.GetDrawObjectFromRenderType(type);

      if (obj) {
        if (!obj.parent) obj.parent = this._drawObject;
      }
    }
  }
  updateEntityMatrix() {
    Matrix.ComposeToRef(
      this._scale,
      this._rotationQuaternion,
      this._position,
      this._matrix
    );
    this.trigger2({ type: EventEnum.UpdateMatrix, target: this });
  }
  updateMatrix() {
    if (this._drawObject) {
      this._drawObject.position.copyFrom(this._position);
      this._drawObject.rotation.copyFrom(this._rotation);
      this._drawObject.scaling.copyFrom(this._scale);
    }
  }

  updatePart() {
    if (this.isPart) {
      const object = this.GetDrawObjectFromRenderType(this.RenderType);
      object.position.copyFrom(this._position);
      object.rotation.copyFrom(this._rotation);
      object.scaling.copyFrom(this._scale);
    }
  }
  /**
   * 更新实体
   * @returns
   */

  update(flag = UpdateFlag.All) {
    this.updateFlag |= flag;

    if (!this.AutoUpdate) return;

    if (this.isPart) {
      this.updatePart();
      return;
    }

    if (!this.CanUpdate || !this._drawObject) return;

    if (this.updateFlag & UpdateFlag.Matrix) {
      this._drawObject.position.copyFrom(this._position);
      this._drawObject.rotation.copyFrom(this._rotation);
      this._drawObject.scaling.copyFrom(this._scale);
      this._drawObject.setEnabled(this._isVisiable);
    }

    if (this.updateFlag & UpdateFlag.Geometry) {
      this._drawObject
        .getChildMeshes()
        .forEach((m) => (m.isPickable = this._isPickable));
      this.updateDrawObject(
        this._renderType ?? Entity.renderType,
        this.RenderObject
      );
      if (this.RenderObject) {
        const meshes = this.RenderObject.getChildMeshes().concat(
          this.RenderObject
        ) as Mesh[];
        for (const m of meshes) {
          if (m.geometry) {
            m.visibility = this.#visibility;
            if (this.#enableEdge) {
              m.enableEdgesRendering();
              m.edgesWidth = this.#edgeWidth;
              m.edgesColor = this.#edgeColor;
            } else {
              m.disableEdgesRendering();
            }
          }
        }
      }
    }

    if (this.updateFlag & UpdateFlag.Material) {
      this.updateMaterial();
      this._drawObject.setEnabled(this._isVisiable && !this._erase);
    }

    this._drawObject
      .getChildMeshes()
      .forEach((m) => (m.isPickable = this._isPickable));

    this.trigger2({ type: EventEnum.Update, target: this });

    this.updateFlag = UpdateFlag.None;
  }

  applyMatrix(m: Matrix) {
    this._matrix.multiplyToRef(m, this._matrix);
    const qu = new Quaternion();
    this._matrix.decompose(this._scale, qu, this._scale);
    qu.toEulerAnglesToRef(this._rotation);
    this.update();
  }

  /**
   * 初始化3D实体对象
   * @param renderType 渲染类型
   * @returns
   */
  initDrawObject(renderType: RenderType): Mesh {
    return null;
  }
  /**
   * 更新3d实体对象
   * @param renderType
   * @param obj 初始化的3d实体对象进行更新
   */
  updateDrawObject(renderType: RenderType, obj: INode) {
    //子类实现
  }
  /**
   * 更新实体材质
   */
  updateMaterial() {
    //子类实现
  }
  /**
   * 删除实体
   * @param e 删除还是恢复
   * @returns
   */
  erase(e = true) {
    if (e === this._erase) return;
    super.erase(e);
    if (this._drawObject) this._drawObject.setEnabled(!e);
    this.trigger2({ type: EventEnum.Remove, target: this });
  }
  /**实体替换功能
   * # 不允许单独调用
   * 请使用Database的方法进行模型替换
   * @hiddren
   */
  replace(target: Entity) {
    this.erase();
    target.ObjectId = this.ObjectId;
    target.ObjectId.Object = target;
    target._SetDefaultDb(this._db);
    target._position = this._position;
    target._rotation = this._rotation;
    target._scale = this._scale;
    target._listeners = this._listeners;
    if ("isRead" in target) {
      target["isRead"] = true;
    }
  }
  /**
   * 回收实体资源
   */
  destory(destoryAll = false) {
    super.destory();
    if (this.registerAfterRenderFun) {
      this.Scene.unregisterAfterRender(this.registerAfterRenderFun); //销毁按轨迹移动动画
      this.registerAfterRenderFun = null;
    }
    for (const [k, o] of this._cacheDrawObject) {
      o.dispose(false, destoryAll);
    }
    this._cacheDrawObject.clear();
    if (this._drawObject) this._drawObject.dispose(false, destoryAll);
    this._drawObject = null;
  }
  /**
   * 给实体注册鼠标移入移出的动作
   * @param typeName ‘pointerOver-鼠标移入’ ‘pointerOut-鼠标移出’ ‘pointerLongPress-长按’
   * @param callback
   * @returns dispose 返回回收函数
   */
  registerAction(typeName: string, callback: (c: ActionEvent) => void) {
    const type = ActionManagerType[typeName];

    let mesh = this.RenderObject;

    if (mesh instanceof Mesh) {
      if (!mesh.geometry) {
        let box = mesh
          .getChildren()
          .find((c) => c.name === "actionHelper") as Mesh;

        if (!box) {
          //TODO:对于旋转的有问题
          let bb = mesh.getHierarchyBoundingVectors();
          bb.min.divideToRef(this._scale, bb.min);
          bb.max.divideToRef(this._scale, bb.max);

          const height = bb.max.y - bb.min.y;
          box = MeshBuilder.CreateBox(
            "actionHelper",
            {
              width: bb.max.x - bb.min.x,
              height: height,
              depth: bb.max.z - bb.min.z,
            },
            Entity.Scene
          );
          box.visibility = 0;
          box.parent = mesh;
          box.position.y = height / 2;
        }

        mesh = box;
      }

      if (!mesh.actionManager) {
        mesh.actionManager = new ActionManager(this.Scene);
      }
      const actWatcher = mesh.actionManager.registerAction(
        new ExecuteCodeAction(
          {
            trigger: type,
            parameter: "r",
          },
          callback
        )
      );
      return () => {
        mesh.actionManager.unregisterAction(actWatcher);
      };
    }
  }
  /**
   * 把模型的左下点归零
   *
   * @param {Vector3} [moveVec]
   * @memberof Entity
   */
  move2Zero(moveVec?: Vector3) {
    const root = this.RenderObject;
    if (!moveVec) {
      moveVec = root.getHierarchyBoundingVectors().min;
    }
    const position = this._position.clone().subtract(moveVec);
    this.Position = position;
  }
  /**
   * 显示边框
   * @param lw 边框线框
   * @param color 边框颜色 ”#ffffff“
   */
  renderEdge(lw = 10, color = "#ffffff") {
    if (this._drawObject) {
      const meshs = this._drawObject.getChildMeshes() as Mesh[];
      for (const m of meshs) {
        if (m.geometry) {
          m.enableEdgesRendering();
          m.edgesWidth = lw;
          m.edgesColor = Color4.FromHexString(color);
        }
      }
    }
  }
  /**克隆一份材质 */
  cloneMaterial() {
    if (this._drawObject) {
      const meshs = this._drawObject.getChildMeshes() as Mesh[];
      for (const m of meshs) {
        if (m.material) {
          m.material = m.material.clone(m.material.name + Date.now());
        }
      }
    }
  }
  /**
   * 物体发光
   * @param color
   * @example
   * ```ts
   * entity.emissive('#ff00ff')
   * ```
   */
  emissive(color: string) {
    if (this._drawObject) {
      const c = color ? Color3.FromHexString(color) : Color3.Black();
      const meshs = this._drawObject.getChildMeshes() as Mesh[];
      for (const m of meshs) {
        if (m.material) {
          if (
            m.material instanceof PBRMaterial ||
            m.material instanceof StandardMaterial
          ) {
            if (!getNodeMetaData(m.material, "emissiveColor")) {
              setNodeMetaData(
                m.material,
                "emissiveColor",
                m.material.emissiveColor
              );
            }
            m.material.emissiveColor = c;
          }
        }
      }
    }
  }
  /**
   * 重置发光色
   */
  resetEmissive() {
    if (this._drawObject) {
      const meshs = this._drawObject.getChildMeshes() as Mesh[];
      for (const m of meshs) {
        if (m.material) {
          if (
            m.material instanceof PBRMaterial ||
            m.material instanceof StandardMaterial
          ) {
            const color = getNodeMetaData(m.material, "emissiveColor");
            if (color) m.material.emissiveColor = color;
          }
        }
      }
    }
  }

  /**关闭边框渲染  */
  disabledRenderEdge() {
    if (this._drawObject) {
      const meshs = this._drawObject.getChildMeshes() as Mesh[];
      for (const m of meshs) {
        if (m.geometry) {
          m.disableEdgesRendering();
        }
      }
    }
  }
  /**返回实体自身允许的拖拽点 */
  getDragPoints(): Vector3[] {
    return [];
  }
  /**移动拖拽点 */
  moveDragPoint(vec: Vector3, index: number) {}
  /**
   * 实体渲染好了,可以进行其他操作了
   * @returns
   */
  getReady(): Promise<any> {
    return Promise.resolve();
  }
  /**@hidden */
  writeFile(file: HCFiler) {
    super.writeFile(file);
    file.write(4);
    file.write(this._name);
    file.write(this._colorIndex);
    file.writeArray(this._position.asArray());
    file.writeArray(this._rotation.asArray());
    file.writeArray(this._scale.asArray());

    file.write(this._isPickable);
    file.write(this._isVisiable);
    file.write(this._color);

    file.writeObjectId(this.GroupId);

    file.write(this._materialMap.size);
    for (const [n, id] of this._materialMap) {
      file.write(n);
      file.writeObjectId(id);
    }

    file.write(this.diffMaterials.size);
    for (const [id, object] of this.diffMaterials) {
      file.write(id);
      file.write(JSON.stringify(object));
    }

    file.writeObjectId(this._material);

    //@ts-ignore
    file.writeAnyArray(this._animations, file.writeObject);

    file.write(this.#visibility);
    file.write(this.#enableEdge);
    file.write(this.#edgeColor.toHexString());
    file.write(this.#edgeWidth);
  }
  /**@hidden */
  readFile(file: HCFiler) {
    super.readFile(file);
    const ver = file.read();
    this._name = file.read();
    this._colorIndex = file.read();

    //加载位置
    let count = file.read();
    let arr = file.readArray(count);
    this._position.fromArray(arr);

    //载入旋转
    count = file.read();
    arr = file.readArray(count);
    this._rotation.fromArray(arr);
    Quaternion.FromEulerVectorToRef(this._rotation, this._rotationQuaternion);

    //载入缩放
    count = file.read();
    arr = file.readArray(count);
    this._scale.fromArray(arr);

    this._isPickable = file.read();
    this._isVisiable = file.read();

    this._color = file.read();

    if (ver > 1) {
      this.GroupId = file.readObjectId() as ObjectId<GroupRecord>;
    }

    if (ver > 2) {
      this._materialMap.clear();
      let size = file.read();
      for (let i = 0; i < size; i++) {
        const k = file.read();
        const mid = file.readObjectId() as ObjectId<MaterialRecord>;
        this._materialMap.set(k, mid);
      }
      size = file.read();
      this.diffMaterials.clear();
      for (let i = 0; i < size; i++) {
        const k = file.read();
        const object = JSON.parse(file.read());
        this.diffMaterials.set(k, object);
      }
      this._material = file.readObjectId() as ObjectId<MaterialRecord>;
    }

    if (ver > 3) {
      file.readAnyArrayAndPush(this._animations, file.readObject);
      this.#visibility = file.read();
      this.#enableEdge = file.read();
      let colorstr = file.read();
      this.#edgeColor.copyFrom(Color4.FromHexString(colorstr));
      this.#edgeWidth = file.read();
    }

    this.updateEntityMatrix();
  }
  _applyHistoryRecord(record: ISerialize) {
    super._applyHistoryRecord(record);
    if (record instanceof EraseEntityData) {
      this._drawObject.setEnabled(!this._erase);
    } else if (record instanceof AllObjectData) {
      this.readFile(record.Filer);
    }
  }
  on(type: string, listener: EventCallback, option?: IEventOptions) {
    //如果需要监听移动拾取，需要手动开启，暂时不需要实时监控场景中的全部实体
    if (type === PointTypeEnum[PointerEventTypes.POINTERMOVE])
      if (this.RenderObject) this.RenderObject.enablePointerMoveEvents = true;
    return super.on(type, listener, option);
  }
  /**这个触发事件会同时发出自身 和全局的事件 */
  trigger2(event: IEvent) {
    super.trigger(event);
    AppEvent.trigger(event);
  }
  private setLoopCount(loopCount: number, animatable: Animatable) {
    let loopObserable: Observer<Animatable>;
    if (loopCount) {
      let count = 0;
      loopObserable = animatable.onAnimationLoopObservable.add((data) => {
        count++;
        if (count === loopCount) {
          data.stop();
        }
      });
    }
    return () => {
      loopObserable &&
        animatable.onAnimationEndObservable.remove(loopObserable);
    };
  }
}
