import {
  DynamicTexture,
  Matrix,
  Mesh,
  MirrorTexture,
  PBRMaterial,
  Plane,
  Scene,
  StandardMaterial,
  Texture,
  Vector3,
  VideoTexture,
} from "../babylon";

export function getCanvasTexture(
  name: string,
  canvas: HTMLCanvasElement,
  scene: Scene
) {
  return new DynamicTexture(name, canvas, scene);
}
export function getVideoTexture(
  name: string,
  source: string | HTMLVideoElement,
  scene: Scene
) {
  return new VideoTexture(name, source, scene);
}

export function mirrorMaterial(target: Mesh, source: Mesh[], scene: Scene) {
  const material = target.material as StandardMaterial | PBRMaterial;
  if (!material) return;
  scene.blockMaterialDirtyMechanism = false;
  const glass_worldMatrix = target.getWorldMatrix();
  //Obtain normals for plane and assign one of them as the normal
  const glass_vertexData = target.getVerticesData("normal");
  let glassNormal0 = new Vector3(
    glass_vertexData[0],
    glass_vertexData[1],
    glass_vertexData[2]
  );
  //Use worldMatrix to transform normal into its current value
  let glassNormal = Vector3.TransformNormal(glassNormal0, glass_worldMatrix);
  const reflector = Plane.FromPositionAndNormal(
    target.absolutePosition,
    glassNormal.scale(-1)
  );
  const texture = new MirrorTexture("mirror" + this.Id, 1024, scene, true);
  material.reflectionTexture = texture;
  texture.mirrorPlane = reflector;
  texture.renderList = source;
  texture.level = 1;
  scene.blockMaterialDirtyMechanism = true;
}

export function getImageTexture(url: string, scene: Scene) {
  return new Texture(url, scene);
}
