import { Factory, HCFiler, ObjectId, ObjectRecord } from "@hc/core";

import { Nullable } from "../babylon";

@Factory
export class CollectionRecord extends ObjectRecord {
  private static VERSION = 3;

  static type = "CollectionRecord";
  entitys: ObjectId[];
  private _isPickable = true;
  private _visiable = true;
  private _isSelect = false;

  private _groupId: Nullable<ObjectId<CollectionRecord>>;

  constructor() {
    super();
    const self = this;
    this.entitys = new Proxy([], {
      get(target, key, receiver) {
        return Reflect.get(target, key, receiver);
      },
      set(target, key, value, receiver) {
        if (Reflect.get(target, key, receiver) !== value) {
          if (self.UndoRecord) {
            if (value instanceof ObjectId) {
              if (!self.Id) console.warn("请先添加到Database后在进行操作!");
              else
                value.Object.GroupId =
                  self.ObjectId as ObjectId<CollectionRecord>;
            }
          }
        }
        return Reflect.set(target, key, value, receiver);
      },
    });
  }

  get GroupId() {
    return this._groupId;
  }
  set GroupId(v) {
    this._groupId = v;
  }

  get IsSelect() {
    return this._isSelect;
  }
  set IsSelect(v: boolean) {
    this._isSelect = v;
  }
  get IsPickable() {
    return this._isPickable;
  }
  set IsPickable(v: boolean) {
    if (v === this._isPickable) return;
    this.writeSnapshoot();
    this._isPickable = v;

    for (const en of this.entitys) {
      if ("IsPickable" in en.Object) {
        en.Object["IsPickable"] = v;
      }
    }
  }
  get Visiable() {
    return this._visiable;
  }
  set Visiable(v) {
    if (v === this._visiable) return;
    this.writeSnapshoot();
    this._visiable = v;
    for (const en of this.entitys) {
      if ("Visiable" in en.Object) {
        en.Object["Visiable"] = v;
      }
    }
  }

  erase(e?: boolean): void {
    super.erase(e);
    for (const en of this.entitys) {
      en.Object?.erase?.(e);
    }
  }
  readFile(filer: HCFiler): void {
    super.readFile(filer);
    const version = filer.read();
    filer.readAnyArrayAndPush(this.entitys, filer.readObjectId);

    if (version >= 2) {
      this.GroupId = filer.readObjectId();
    }

    if (version >= 3) {
      this._visiable = filer.read();
      this._isPickable = filer.read();
    }

    if (version <= 3) {
      this.entitys.forEach((e) => {
        if (e.Object) {
          if (!e.Object["GroupId"]) e.Object["GroupId"] = this.ObjectId;
        }
      });
    }
  }
  writeFile(filer: HCFiler): void {
    super.writeFile(filer);
    filer.write(CollectionRecord.VERSION);
    filer.writeAnyArray(this.entitys, filer.writeObjectId);
    filer.writeObjectId(this._groupId);

    filer.write(this._visiable);
    filer.write(this._isPickable);
  }
}
