import { AppStore } from "../app/AppStore";
import {
  PointsCloudSystem,
  CloudPoint,
  Ray,
  Color4,
  Vector3,
} from "../babylon";
import { Entity } from "../entitys";
import { equaln } from "../utils";

/**
 * 拖拽点管理
 */
export class DragPointsManager {
  private _pcsList = new Map<Entity, PointsCloudSystem>();
  private color = new Color4(0, 122 / 255, 204 / 255, 1);
  intersect(ray: Ray) {
    const dir = ray.direction;
    const origin = ray.origin;
    const result: [Entity, number][] = [];
    for (const [e, pcs] of this._pcsList) {
      for (let i = 0; i < pcs.particles.length; i++) {
        const vec = pcs.particles[i].position.subtract(origin);
        const angle = Vector3.Dot(vec.normalize(), dir);
        if (equaln(angle, 0, 1e-3) || equaln(angle, 1, 1e-3)) {
          result.push([e, i]);
          break;
        }
      }
    }
    return result;
  }
  append(en: Entity) {
    if (this._pcsList.has(en)) return;
    const points = en.getDragPoints();
    if (points.length === 0) return;
    const pcs = new PointsCloudSystem("pac" + en.Id, 12, AppStore.MainScene);
    this._pcsList.set(en, pcs);
    pcs.addPoints(points.length, (particle: CloudPoint, i: number) => {
      particle.position = points[i];
      particle.color = this.color;
    });
    pcs.buildMeshAsync();
  }
  update() {
    for (const [e, pcs] of this._pcsList) {
      const points = e.getDragPoints();
      if (points.length === 0) return;
      pcs.mesh.setEnabled(true);
      for (let i = 0; i < pcs.particles.length; i++) {
        pcs.particles[i].position.copyFrom(points[i]);
      }
      pcs.setParticles();
    }
  }
  hide() {
    for (const [e, pcs] of this._pcsList) {
      pcs.mesh.setEnabled(false);
    }
  }
  clear() {
    for (let [e, pcs] of this._pcsList) {
      pcs.dispose();
      this._pcsList.delete(e);
      e = null;
    }
    this._pcsList.clear();
  }
}
