import { Application } from "../app/Application";
import { AppEvent } from "../core/AppEvent";

export interface Command {
  noHistory?: boolean;
  exec(): any; //函数可以返回true,实现放弃命令的任何操作.
}

/*命令管理*/
export class CommandManager {
  //命令表
  CommandMap: Map<string, Command> = new Map<string, Command>();
  LastCmd: string;
  private _CommandNameList = new Set<string>();
  doing = false;
  async ExecCommand(cmdName: string) {
    if (this.CommandMap.has(cmdName)) {
      let cmd = this.CommandMap.get(cmdName);

      //在透明命令判断完成,避免无法运行透明命令
      if (this.doing) {
        return;
      }

      this.CommandStart(cmdName, cmd.noHistory);

      let abort = true;
      try {
        abort = await cmd.exec();
      } catch (error) {
        AppEvent.trigger({ type: "error", target: null, error });
        console.log("error: ", error);
      }
      await this.CommandEnd(cmd.noHistory);
    } else {
      console.error("未知命令");
    }
  }

  RegisterCommand(cmdName: string, cmd: Command) {
    cmdName = cmdName.toUpperCase();
    this.CommandMap.set(cmdName, cmd);
    this._CommandNameList.add(cmdName);
  }
  RemoveCommand(cmdName: string) {
    if (!cmdName) return;
    cmdName = cmdName.toUpperCase();
    this.CommandMap.delete(cmdName);
    this._CommandNameList.delete(cmdName);
  }
  /**
   * 意味着命令开始,系统将会帮你初始化一些事情.
   * @param cmdName
   * @returns 命令开始状态,如果成功返回true.
   */
  CommandStart(cmdName: string, noHistory = false): boolean {
    if (this.doing) {
      return false;
    }
    this.LastCmd = cmdName;

    this.doing = true;
    if (!noHistory)
      Application.Intance.Database.HistoryManager.startCmd(cmdName);
    return true;
  }

  /**
   * 意味着命令结束了,调用这个,系统会帮你做一些事情
   */
  async CommandEnd(noHistory = false) {
    //还原状态
    this.doing = false;
    if (!noHistory) Application.Intance.Database.HistoryManager.endCmd();
  }

  get CommandNameList(): Set<string> {
    return this._CommandNameList;
  }
}

export const commandManager = new CommandManager();
