import { IKeyboardEvent, KeyboardEventTypes } from "../babylon";
import { AppEvent } from "../core/AppEvent";
import { Viewer } from "./../viewer/Viewer";

type KeyFun = (e: IKeyboardEvent) => void;

export class KeyboardControl {
  private keyeunm = {
    keydown: KeyboardEventTypes.KEYDOWN,
    keyup: KeyboardEventTypes.KEYUP,
  };
  private keydownEvents = new Set<KeyFun>();
  private keyupEvents = new Set<KeyFun>();
  private _ctrlKey = false;
  private _shiftKey = false;
  private _altKey = false;
  constructor(private _viewer: Viewer) {
    this._register();
  }
  get IsCtrl() {
    return this._ctrlKey;
  }
  get IsShift() {
    return this._shiftKey;
  }
  get IsAlt() {
    return this._altKey;
  }
  on(type: "keydown" | "keyup", callback: KeyFun) {
    if (this.keyeunm[type] === KeyboardEventTypes.KEYDOWN) {
      this.keydownEvents.add(callback);
    } else if (this.keyeunm[type] === KeyboardEventTypes.KEYUP) {
      this.keyupEvents.add(callback);
    } else {
      throw new TypeError("事件类型错误");
    }
    return () => {
      this.off(type, callback);
    };
  }
  off(type: "keydown" | "keyup", callback: KeyFun) {
    if (this.keyeunm[type] === KeyboardEventTypes.KEYDOWN) {
      this.keydownEvents.delete(callback);
    } else if (this.keyeunm[type] === KeyboardEventTypes.KEYUP) {
      this.keyupEvents.delete(callback);
    }
  }
  removeAll() {
    this.keydownEvents.clear();
    this.keyupEvents.clear();
  }
  private _register() {
    this._viewer.Scene.onKeyboardObservable.add((info) => {
      if (info.type === KeyboardEventTypes.KEYDOWN) {
        this._ctrlKey = info.event.ctrlKey;
        this._shiftKey = info.event.shiftKey;
        this._altKey = info.event.altKey;

        for (const f of this.keydownEvents) {
          f(info.event);
        }
      } else {
        for (const f of this.keyupEvents) {
          this._ctrlKey = info.event.ctrlKey;
          this._shiftKey = info.event.shiftKey;
          this._altKey = info.event.altKey;
          f(info.event);
        }
      }
      AppEvent.trigger({
        type: ["keydown", "keyup"][info.type - 1],
        event: info.event as any,
        target: null,
      });
    });
  }
}
