import { Vector2 } from "../babylon";
import { SelectType } from "../types";
import { ZINDEX } from "../utils/zIndex";

//矩形选框
export class SelectMarquee {
  start: Vector2 = new Vector2();
  end: Vector2 = new Vector2();
  selectType: SelectType = SelectType.None;
  private rightColor = "rgba(225, 240, 255, 0.5)";
  private leftColor = "rgba(225, 240, 255, 0.4)";
  private rightBorder = "1px dashed rgb(179, 179, 245)";
  private leftBorder = "1px solid rgb(179, 179, 245)";
  private dom: HTMLElement;
  private width: number;
  private height: number;

  constructor(container: HTMLElement) {
    this.dom = document.createElement("div");
    container.appendChild(this.dom);

    let st = this.dom.style;
    st.position = "absolute";
    st.zIndex = ZINDEX.SelectMarquee;
    st.background = "rgba(0, 63, 255, 0.2)";
    st.margin = "0";
    st.pointerEvents = "none";
    st.top = "460px";
    st.left = "100px";
    st.width = "100px";
    st.height = "200px";
    st.display = "none";
    st.border = this.leftBorder;
  }

  show() {
    this.dom.style.display = "block";
  }

  hide() {
    this.dom.style.display = "none";
  }

  update() {
    this.dom.style.top = Math.min(this.start.y, this.end.y) + "px";
    this.dom.style.left = Math.min(this.start.x, this.end.x) + "px";

    this.width = Math.abs(this.start.x - this.end.x);
    this.height = Math.abs(this.start.y - this.end.y);
    this.dom.style.width = this.width + "px";
    this.dom.style.height = this.height + "px";

    let type = this.selectType;
    if (this.selectType === SelectType.None) {
      if (this.start.x > this.end.x) type = SelectType.C;
      else type = SelectType.W;
    }

    if (type === SelectType.C) {
      this.dom.style.background = this.leftColor;
      this.dom.style.border = this.leftBorder;
    } else {
      this.dom.style.background = this.rightColor;
      this.dom.style.border = this.rightBorder;
    }
  }

  setStart(x: number, y: number) {
    this.start.set(x, y);
  }

  setEnd(x: number, y: number) {
    this.end.set(x, y);
    this.update();
  }
}
