import { AppStore } from "../../app/AppStore";
import { Factory, HCFiler, IVector3 } from "@hc/core";
import { BaseWidget } from "../../core/BaseWidget";

@Factory
export class MoveCamera extends BaseWidget {
  static type = "MoveCamera";
  static schema = {
    label: "移动相机到",
  };
  private current = false;
  private _alpha: number;
  private _beta: number;
  private _radius: number;
  private _target: IVector3 = { x: undefined, y: undefined, z: undefined };
  private _time = 1000;

  constructor() {
    super();
  }
  async do() {
    const animation = AppStore.MainViewer.cameraManager.Control.moveTo({
      alpha: this._alpha,
      beta: this._beta,
      target: this._target,
      radius: this._radius,
      time: this._time,
    });
    await animation.start();
    return true;
  }
  fromJSON(data: Record<string, any>) {
    this._alpha = data.alpha ?? this._alpha;
    this._beta = data.beta ?? this._beta;
    this._radius = data.radius ?? this._radius;
    this._target = data.target ?? this._target;
    this._time = data.time ?? this._time;
  }

  readFile(filer: HCFiler): void {
    filer.read();
    this._alpha = filer.read();
    this._beta = filer.read();
    this._radius = filer.read();
    this._target.x = filer.read();
    this._target.y = filer.read();
    this._target.z = filer.read();
    this._time = filer.read();
  }
  writeFile(filer: HCFiler): void {
    filer.write(1);
    filer.write(this._alpha);
    filer.write(this._beta);
    filer.write(this._radius);
    filer.write(this._target.x);
    filer.write(this._target.y);
    filer.write(this._target.z);
    filer.write(this._time);
  }
}
