import { Entity } from "./../../entitys/Entity";
import { Factory, HCFiler, ObjectId } from "@hc/core";
import { BaseWidget } from "../../core/BaseWidget";
import { AppEvent } from "../../core/AppEvent";
import { getEventName } from "../../utils/interactionUtils";

@Factory
export class Dispatch extends BaseWidget {
  static type = "Dispatch";
  static schema = {
    label: "触发事件",
  };
  private _list: { triggerNames: string[]; objectId: ObjectId<Entity> }[] = [];
  get List() {
    return [...this._list];
  }
  do() {
    console.log("%c 执行:触发事件", "color:#ff00ff");
    for (const data of this._list) {
      const en = data.objectId?.Object;
      if (en) {
        data.triggerNames.forEach((n) => {
          const eventData = {
            ...this.getTriggerEvent(n),
            type: getEventName(n),
            target: en,
          };
          en.trigger(eventData);
        });
      } else {
        data.triggerNames.forEach((n) => {
          const eventData = this.getTriggerEvent(n);
          AppEvent.trigger(eventData);
        });
      }
    }
    return true;
  }
  fromJSON(data: any): void {
    this._list = data;
  }
  readFile(filer: HCFiler): void {
    filer.read();
    this._list.length = 0;
    filer.readAnyArray(() => {
      let data = { triggerNames: [], objectId: null };
      filer.readAnyArrayAndPush(data.triggerNames, filer.read);
      data.objectId = filer.readObjectId();
      this._list.push(data);
    });
  }
  writeFile(filer: HCFiler): void {
    filer.write(1);
    filer.writeAnyArray(this._list, (d) => {
      filer.writeAnyArray(d.triggerNames, filer.write);
      filer.writeObjectId(d.objectId);
    });
  }
  private getTriggerEvent(
    n: string,
    target?: any
  ): {
    type: string;
    target: any;
    event?: any;
    isEnd?: boolean;
    isChild: boolean;
  } {
    switch (n) {
      case "Click":
        return {
          type: "pointerTap",
          event: { button: 0 },
          isChild: true,
          target,
        };
      case "RightClick":
        return {
          type: "pointerTap",
          event: { button: 2 },
          isChild: true,
          isEnd: true,
          target,
        };
      case "DoubleClick":
        return {
          type: "pointerDoubleTap",
          event: { button: 0 },
          isChild: true,
          target,
        };
      case "Load":
        return {
          type: "loadFile",
          isChild: true,
          target,
        };
      default:
        return {
          type: n,
          target,
          isChild: true,
        };
    }
  }
}
