import { Factory, HCFiler, ObjectId } from "@hc/core";
import { AppStore } from "../app/AppStore";
import { StandardMaterial } from "../babylon";
import { Entity } from "../entitys/Entity";
import { autoRecordMaterialFilter } from "./autoRecord";
import { MaterialRecord } from "./MaterialRecord";
import { TextureRecord } from "./TextureRecord";
import { initTextureISNull, setTexture } from "./utils";

@Factory
export class StandardMaterialRecord extends MaterialRecord<StandardMaterial> {
  static type = "StandardMaterialRecord";
  protected _name = "标准材质";
  //漫反射纹理
  @autoRecordMaterialFilter(initTextureISNull, setTexture)
  DiffuseTexture: ObjectId<TextureRecord>;

  constructor(mtl?: StandardMaterial, entity?: Entity) {
    super(mtl, entity);
    if (!mtl) {
      this.material = new StandardMaterial("标准材质", AppStore.Scene);
      this.updateMaterial();
    }
    this.init = false;
    if (!this.material.metadata) this.material.metadata = {};
    this.material.metadata.record = this;
  }
  updateMaterial() {
    if (!this.material) return;
    super.updateMaterial();
    // this.material.freeze();

    // this.material.unfreeze();
  }
  updateTextures() {
    if (this.DiffuseTexture?.Object) {
      const txr = this.DiffuseTexture.Object as TextureRecord;
      this.material.diffuseTexture = txr.Texture;
    }
  }
  update() {
    super.update();
    this.updateTextures();
  }
  writeFile(file: HCFiler) {
    super.writeFile(file);
    file.write(1);
    file.writeObjectId(this.DiffuseTexture);
  }
  readFile(file: HCFiler) {
    super.readFile(file);
    const ver = file.read();
    this.DiffuseTexture = file.readObjectId() as ObjectId<TextureRecord>;

    this.update();
  }
  _SetOwnerDatabase(db) {
    super._SetOwnerDatabase(db);
    this.material.name = `standardmtl-${this.Id}`;
    this.material.id = `standardmtl-${this.Id}`;
    return this;
  }
}
