import { CylinderDirectedParticleEmitter } from "../../babylon";
import { ParticleSystem } from "../..";
import { Factory } from "@hc/core";
import { iVector2Vector } from "../../utils/vector";
import { CylinderEmitter } from "./CylinderEmitter";

@Factory
export class DirectedCylinder extends CylinderEmitter {
  static type = "DirectedCylinder";
  protected _emiter: CylinderDirectedParticleEmitter;
  constructor(
    protected attachSystem: ParticleSystem,
    option?: { radius?: number; radiusRange?: number; height?: number }
  ) {
    super(attachSystem, option);

    if (attachSystem?.HasIntance) this.setEmiter();
  }
  setEmiter() {
    this._emiter = this.attachSystem.Intance.createDirectedCylinderEmitter(
      this._radius,
      this.height,
      this._radiusRange,
      iVector2Vector(this.attachSystem.Direction1),
      iVector2Vector(this.attachSystem.Direction2)
    );
  }
}
