import { Viewer } from "./../viewer/Viewer";
import { Scene } from "../babylon";
import { Entity } from "./../entitys/Entity";
import { IVariableOption } from "../types/interaction.types";
import { arrayLast } from "../utils";
import type { Application } from "./Application";

export class AppStore {
  private static _MainViewer: Viewer;
  private static _MainScene: Scene;
  private static _Scene: Scene;
  private static waitList: Promise<unknown>[] = [];
  private static waiting = false;
  static Application: Application;
  static PreviewMode = false;
  static Reading = false;
  static get MainViewer() {
    return this._MainViewer;
  }
  static set MainViewer(v: Viewer) {
    this._MainViewer = v;
    this._MainScene = v.Scene;
  }
  static get MainScene() {
    return this._MainScene;
  }
  static get Scene() {
    return this._Scene;
  }
  static set Scene(s: Scene) {
    this._Scene = s;
    Entity.Scene = s;
  }
  static GlobalVariables: IVariableOption[] = [];
  static StartWait() {
    this.waiting = true;
  }
  static Wait() {
    if (!this.waiting) return () => {};
    let res: (v) => void;
    const p = new Promise((r) => {
      res = r;
    });
    this.waitList.push(p);
    return res;
  }
  static async WaitComplete() {
    return Promise.all(this.waitList).then((res) => {
      this.waiting = false;
      this.waitList.length = 0;
      return res;
    });
  }
  static getLastPromise() {
    return arrayLast(this.waitList);
  }
  static ToggleScene(scene: Scene, callback: () => void) {
    const oldScene = this.Scene;
    this.Scene = scene;
    callback();
    this.Scene = oldScene;
  }
}
