import { ConeParticleEmitter } from "../../babylon";

import { Factory, HCFiler } from "@hc/core";
import { ParticleSystem } from "../ParticleSystem";
import { Emiter } from "./Emiter";

@Factory
export class ConeEmitter extends Emiter {
  static type = "ConeEmitter";
  protected _emiter: ConeParticleEmitter;
  protected _radius = 1;
  protected angle = Math.PI / 4;
  constructor(
    protected attachSystem: ParticleSystem,
    option?: { radius?: number; angle?: number }
  ) {
    super(attachSystem);
    if (option?.radius) {
      this._radius = option.radius;
    }
    if (option?.angle) {
      this.angle = option.angle;
    }
    if (attachSystem?.HasIntance) this.setEmiter();
  }
  setEmiter() {
    this._emiter = this.attachSystem.Intance.createConeEmitter(
      this._radius,
      this.angle
    );
  }
  get AttachSystem() {
    return this.attachSystem;
  }
  set AttachSystem(v: ParticleSystem) {
    this.attachSystem = v;
    this.setEmiter();
  }
  get Radius() {
    return this._radius;
  }
  set Radius(v: number) {
    if (v === this._radius) return;
    this._radius = v;
    this.update();
  }

  get Angle() {
    return this.angle;
  }
  set Angle(v: number) {
    if (v === this.angle) return;
    this.angle = v;
    this.update();
  }

  update(): void {
    if (!this._emiter) {
      // if (this.attachSystem) {
      //   this._emiter = this.attachSystem.Intance.createSphereEmitter(
      //     this._radius,
      //     this._radiusRange
      //   );
      // }
      return;
    }
    this._emiter.radius = this._radius;
    this._emiter.angle = this.angle;
  }
  toJSON() {
    return {
      Radius: this.Radius,
      Angle: this.Angle,
    };
  }
  fromJSON(json): this {
    if (json.Radius) this._radius = json.Radius;
    if (json.Angle) this.angle = json.Angle;
    return this;
  }
  writeFile(file: HCFiler): void {
    super.writeFile(file);
    file.write(1);
    file.write(this._radius);
    file.write(this.angle);
  }
  readFile(file: HCFiler): void {
    super.readFile(file);
    file.read();
    this._radius = file.read();
    this.angle = file.read();
  }
}
