import { Factory, HCFiler } from "@hc/core";
import { BaseWidget } from "../../core/BaseWidget";
import { updateQueryStringParameter } from "../../utils";

@Factory
export class Open extends BaseWidget {
  static type = "Open";
  static schema = {
    label: "打开链接",
  };
  data = {
    type: "url",
    value: "",
    target: "_blank",
  };
  do() {
    switch (this.data.type) {
      case "url":
        this.openUrl(this.data.value);
        break;
      case "file":
        this.openUrl(
          updateQueryStringParameter(location.href, "id", this.data.value) +
            "&time=" +
            Date.now()
        );
        break;
      case "back":
        history.back();
        break;
      case "reload":
        location.reload();
        break;
    }
    return true;
  }
  readFile(filer: HCFiler): void {
    filer.read();
    this.data = JSON.parse(filer.read());
  }
  writeFile(filer: HCFiler): void {
    filer.write(1);
    filer.write(JSON.stringify(this.data));
  }
  toJSON() {
    return { ...this.data };
  }
  fromJSON(data: any): void {
    Object.assign(this.data, data);
  }
  private openUrl(url: string) {
    const id = "OpenWidgetHref";

    let a = document.getElementById(id);

    if (!a) {
      a = document.createElement("a");
      document.body.appendChild(a);
      a.setAttribute("id", id);
    }
    a.setAttribute("target", this.data.target);
    a.setAttribute("href", url);
    a.click();
  }
}
