import { Vector3 } from "@babylonjs/core/Maths/math";
import {
  BaseDatabase,
  BaseObject,
  BlockTableRecord,
  DeepCloneFiler,
  HCFiler,
  HistoricManager,
  ObjectId,
  ObjectRecord,
  RecordTable,
} from "@hc/core";
import { Entity } from "../entitys";
import { Light } from "../entitys/lights/Light";
import { BaseGui } from "../guis/Gui";
import { Particle } from "../particleSystem/Particle";
import { IdMaping, OwnerContainer } from "../types/db.types";
import { BaseDom } from "./../core/BaseDom";
import { DataBindTable } from "./DataBindTable";
import { GroupRecordTable } from "./GroupTableRecord";
import { InteractionTable } from "./InteractionTable";

function getArea(vector: Vector3): number {
  return vector.x * vector.z;
}

BlockTableRecord.ObjectType = [Entity, BaseGui, BaseDom, Particle];

export class Database extends BaseDatabase {
  ModelSpace: BlockTableRecord<Entity>;
  LightSpace: BlockTableRecord<Light>;
  TextureTable: RecordTable;
  MaterialTable: RecordTable;
  HistoryManager: HistoricManager;
  InteractionTable: InteractionTable;
  DataBindTable: DataBindTable;
  GuiSpace: BlockTableRecord<BaseGui>;
  DomSpace: BlockTableRecord<BaseDom>;
  ParticleSystemSpace: BlockTableRecord<Particle>;
  GroupTable: GroupRecordTable;
  private Tables: BaseObject[] = [];
  protected idIndex = 1;
  protected idMap = new Map<number, ObjectId>();
  constructor(protected preview = false) {
    super(preview);
    this.ModelSpace = new BlockTableRecord<Entity>()._SetOwnerDatabase(this);
    this.LightSpace = new BlockTableRecord<Light>()
      ._SetOwnerDatabase(this)
      .setCheckout((en) => en.isLight);

    this.TextureTable = new RecordTable()._SetOwnerDatabase(this);
    this.MaterialTable = new RecordTable()._SetOwnerDatabase(this);

    this.HistoryManager = new HistoricManager()._SetOwnerDatabase(this);

    this.InteractionTable = new InteractionTable()._SetOwnerDatabase(this);
    if (preview) this.InteractionTable.watch();

    this.DataBindTable = new DataBindTable()._SetOwnerDatabase(this);

    this.GuiSpace = new BlockTableRecord<BaseGui>()._SetOwnerDatabase(this);

    this.DomSpace = new BlockTableRecord<BaseDom>()._SetOwnerDatabase(this);
    this.ParticleSystemSpace =
      new BlockTableRecord<Particle>()._SetOwnerDatabase(this);

    this.GroupTable = new GroupRecordTable()._SetOwnerDatabase(this);

    this.Tables.push(
      this.ModelSpace,
      this.LightSpace,
      this.TextureTable,
      this.MaterialTable,
      this.InteractionTable,
      this.DataBindTable,
      this.GuiSpace,
      this.DomSpace,
      this.ParticleSystemSpace,
      this.GroupTable
    );

    //保留100个id内部对象使用
    this.idIndex = 100;
  }
  get AllObjects() {
    const objects: BaseObject[] = [];
    for (const [id, objectId] of this.idMap) {
      if (id < 100) continue;
      objects.push(objectId.Object);
    }
    return objects;
  }
  allocateId() {
    return this.getObjectId(this.idIndex++);
  }
  getObjectId(index: number) {
    //id 从1开始,0被保留作为空id
    if (index === 0) return undefined;

    let id = this.idMap.get(index);
    if (id) return id;

    id = new ObjectId(index);
    this.idMap.set(index, id);

    return id;
  }
  /**
   * 根据id获取数据库对象
   * @param id
   * @returns 场景中的对象
   */
  findObjectById(id: number) {
    return this.idMap.get(id)?.Object;
  }
  /**
   * 根据名称获取数据库中的对象
   * @param name
   * @param isLike 是否模糊查询 可选参数
   * @param filter 过滤查找，返回true时跳过，可选参数
   * @returns BaseObject[] 目标对象数组
   * @example app.Database.findObjectByName("box",true,(o)=>!(o intanceof GUI)) 查找数据库中名字包含box的GUI
   */
  findObjectByName(
    name: string,
    isLike = true,
    filter?: (o: BaseObject) => boolean
  ): BaseObject[] {
    const objects: BaseObject[] = [];
    if (!filter) filter = (o) => false;
    for (const [, oid] of this.idMap) {
      if (oid.isErase) continue;
      if (!oid.Object?.Name || filter(oid.Object)) continue;
      if (isLike) {
        if (oid.Object.Name.includes(name)) {
          objects.push(oid.Object);
        }
      } else {
        if (oid.Object.Name === name) {
          objects.push(oid.Object);
        }
      }
    }
    return objects;
  }
  /**模型替换
   * > 目标实体将继承源实体的ObjectId，空间信息，以及绑定事件
   * > 目标实体和源实体 所属的数据表必须相同
   * @param source 源目标实体
   * @param target 目标实体
   * @see https://hc3dol.gkiiot.com/#20
   * @example
   * ```ts
   * const box=app.create({type:"Box",position:{z:20}});
   * const en=new HC.ImportEntity({url})
     app.Database.replace(box,en)
   * ```
   */
  replace(source: Entity, target: Entity) {
    if (target.ObjectId) {
      console.error("已经添加到数据库的对象无法替换");
      return;
    }
    if (source instanceof Entity && target instanceof Entity) {
      if (source.Owner.Object instanceof BlockTableRecord) {
        (<BlockTableRecord<Entity>>source.Owner.Object).replace(source, target);
      }
    } else {
      console.error("请传入实体");
    }
  }
  cloneMaterial2Database(db: Database) {
    const materials = db.MaterialTable.Objects;
    const map = new Map<number, ObjectRecord>();
    for (const object of materials) {
      const index = object.Id - 100;
      map.set(index, object);
      this.MaterialTable.add(object);
    }
    const textures = db.TextureTable.Objects;
    for (const ture of textures) {
      const index = ture.Id - 100;
      map.set(index, ture);
      this.TextureTable.add(ture);
    }

    for (let i = 0; i < map.size; i++) {
      const objectid = this.allocateId();
      const obj = map.get(i);
      this.idMap.set(objectid.Index, obj.ObjectId);
      obj.ObjectId.Index = objectid.Index;
      obj._SetDatabase(this);
    }
    return materials;
  }
  cloneBlockObjects(
    objects: BaseObject[],
    owner: OwnerContainer,
    idMap: IdMaping
  ) {
    let f = new DeepCloneFiler();
    for (const object of objects) {
      this.cloneBlockObject(object, owner, idMap, f);
    }
    return objects.map((o) => idMap.get(o.ObjectId).Object);
  }
  cloneBlockObject(
    object: BaseObject,
    owner: OwnerContainer,
    idMap: IdMaping,
    filer: DeepCloneFiler
  ) {
    //克隆的对象有可能被其他的对象依赖并且克隆完毕了.
    let cloneId = filer.idMaping.get(object.Id);
    if (cloneId && cloneId.Object) return;

    filer.Data.length = 0;

    //表示该对象已经被拷贝了
    filer.cloned.add(object.Id);

    filer.writeObject(object);
    this.cloneBlockReferenceObject(object, filer, idMap);
    filer.reset();
    let newObject = filer.readObject();
    this.allocationObjectId(newObject); //先给予Id,避免历史记录里面没有id
    owner.append(newObject, false);
    idMap.set(object.ObjectId, newObject.ObjectId);
  }
  cloneBlockReferenceObject(
    object: BaseObject,
    f: DeepCloneFiler,
    idMap: IdMaping
  ) {
    let oldData = f.Data;
    f.Data = [];

    let oldDb = object.DB;
    let hardObjectIds = f.hardObjectIds;
    f.hardObjectIds = new Set();
    for (let idIndex of hardObjectIds) {
      let oldId = oldDb.getObjectId(idIndex);
      if (!oldId?.Object) continue;
      //使用旧的OwnerId得到新的OwnerId,假设所有者都是数据库默认存在的.
      let newOwnerId = this.getObjectId(oldId.Object.Owner.Index); //owner.Db === this
      let newOwner = newOwnerId.Object as any;
      this.cloneBlockObject(oldId.Object, newOwner, idMap, f);
    }

    f.Data = oldData;
  }
  allocationObjectId(object: BaseObject) {
    object.ObjectId.Index = this.idIndex++;
    this.idMap.set(object.Id, object.ObjectId);
  }
  writeFile(filer?: HCFiler) {
    if (!filer) filer = new HCFiler();
    filer.write(2);
    filer.write(this.idIndex);
    this.ModelSpace.writeFile(filer, {
      sort: (a, b) => {
        // 实体按面积大小排序，面积大的优先加载
        const aBounding = a.BoundingVectors;
        const bBounding = b.BoundingVectors;

        if (!aBounding && !bBounding) {
          return 0;
        }

        if (!aBounding) {
          return 1;
        }

        if (!bBounding) {
          return -1;
        }

        const aVolume = getArea(aBounding.max.subtract(aBounding.min));
        const bVolume = getArea(bBounding.max.subtract(bBounding.min));

        return bVolume - aVolume;
      },
    });
    this.LightSpace.writeFile(filer);
    this.TextureTable.writeFile(filer);
    this.MaterialTable.writeFile(filer);
    this.InteractionTable.writeFile(filer);
    this.DataBindTable.writeFile(filer);
    this.GuiSpace.writeFile(filer);

    this.DomSpace.writeFile(filer);
    this.ParticleSystemSpace.writeFile(filer);
    this.GroupTable.writeFile(filer);

    //保存历史记录??
    // this.HistoryManager.writeFile(filer);
    return filer;
  }
  readFile(filer: HCFiler) {
    this.destory();
    this.reset();
    filer.database = this;
    let ver = filer.read();
    this.idIndex = filer.read();
    this.ModelSpace.readFile(filer);
    this.LightSpace.readFile(filer);
    this.TextureTable.readFile(filer);
    this.MaterialTable.readFile(filer);
    this.InteractionTable.readFile(filer);
    this.DataBindTable.readFile(filer);
    this.GuiSpace.readFile(filer);

    this.DomSpace.readFile(filer);

    this.ParticleSystemSpace.readFile(filer);

    if (ver > 1) this.GroupTable.readFile(filer);

    if (this.preview) this.DataBindTable.do();

    // this.HistoryManager.readFile(filer);
    return this;
  }
  destory() {
    this.idMap.clear();

    this.HistoryManager.destory();
    this.Tables.forEach((table) => table.destory());
    this.idIndex = 1;
  }
  reset() {
    this.Tables.forEach((table) => table._SetOwnerDatabase(this));

    this.idIndex = 100;
  }
}
