import { BaseObject } from "../BaseObject";
import { ObjectId } from "../ObjectId";
import { HCFiler } from "./HCFiler";

/**
 * 深度拷贝文件读写
 */
export class DeepCloneFiler extends HCFiler {
  constructor(public idMaping = new Map<number, ObjectId>()) {
    super();
  }

  override readObjectId<T extends BaseObject = BaseObject>():
    | ObjectId<T>
    | undefined {
    let index = this.read();
    if (index <= 0) return;

    let id = this.idMaping.get(index);
    if (id) return id as ObjectId<T>;

    id = new ObjectId<BaseObject>();
    this.idMaping.set(index, id);

    return id as ObjectId<T>;
  }

  readSoftObjectId() {
    let id = this.readObjectId();
    return id;
  }

  readHardObjectId() {
    let id = this.readObjectId();
    return id;
  }

  hardObjectIds: Set<number> = new Set();
  cloned: Set<number> = new Set();
  writeHardObjectId(id: ObjectId) {
    if (id && id.Index >= 100 && !this.cloned.has(id.Index))
      //当存在id时,表示对象正在被拷贝,或者已经拷贝完成
      this.hardObjectIds.add(id.Index);

    return this.writeObjectId(id);
  }
}
