import type { Entity } from "../../entitys/Entity";
import { BaseWidget } from "../../core/BaseWidget";
import { Factory, HCFiler, ObjectId, IVector3 } from "@hc/core";
import { MoveType } from "../../types/interaction.types";

export interface IMoveItemProps {
  objectId: ObjectId<Entity>;
  type: MoveType;
  animationType: string;
  time: number;
  target: IVector3;
}

@Factory
export class Move extends BaseWidget {
  static type = "Move";
  static schema = {
    label: "移动",
  };
  private _list: IMoveItemProps[] = [];
  get List() {
    return [...this._list];
  }
  async do() {
    console.log("执行:移动");
    const ps: Promise<unknown>[] = [];
    for (const data of this._list) {
      const en = data.objectId.Object;
      if (!en) continue;
      ps.push(
        en.moveTo(data.target, data.type === MoveType.Offset, {
          time: data.time,
          startTime: this.startTime,
        })
      );
    }
    return Promise.all(ps).then((rs) => {
      return true;
    });
  }
  add(data: IMoveItemProps) {
    this._list.push(data);
  }
  fromJSON(data: any[]) {
    this._list.length = 0;
    for (const d of data) {
      this._list.push({
        objectId: d.objectId,
        time: d.time,
        target: d.target,
        type: d.type,
        animationType: d.animationType,
      });
    }
  }
  readFile(filer: HCFiler): void {
    filer.read();
    const count = filer.read();
    this._list.length = 0;
    for (let i = 0; i < count; i++) {
      const data: Partial<IMoveItemProps> = { target: { x: 0, y: 0, z: 0 } };
      data.objectId = filer.readObjectId() as ObjectId<Entity>;
      data.type = filer.read();
      data.target.x = filer.read();
      data.target.y = filer.read();
      data.target.z = filer.read();
      data.time = filer.read();
      data.animationType = filer.read();
      this._list.push(data as IMoveItemProps);
    }
  }
  writeFile(filer: HCFiler): void {
    filer.write(1);
    filer.write(this._list.length);
    for (const data of this._list) {
      filer.writeObjectId(data.objectId);
      filer.write(data.type);
      filer.write(data.target.x);
      filer.write(data.target.y);
      filer.write(data.target.z);
      filer.write(data.time);
      filer.write(data.animationType);
    }
  }
}
