export enum MoveType {
  Offset = 0,
  To = 1,
}

export type IVariableOption = {
  key: string;
  value: any;
  desc?: string;
};

export type ValueType =
  | "value"
  | "param"
  | "entity"
  | "text"
  | "keycode"
  | "boolean";

export type WidgetValue<T = any> = {
  type: ValueType;
  value: T;
  entityKey?: string;
};

export interface ISetParam {
  key: string;
  value: WidgetValue;
}

export enum CompareType {
  Equal = 0,
  UnEqual = 1,
  Great = 2,
  Less = 3,
  GtEqual = 4,
  LessEqual = 5,
  Include = 6,
  UnInclude = 7,
  Is = 8,
  IsNot = 9,
}
