import { BaseObject } from "../BaseObject";
import { HCFiler } from "../filer/HCFiler";

export class ObjectRecord extends BaseObject {
  protected _name = "";
  get Name() {
    return this._name;
  }
  set Name(n: string) {
    this._name = n;
  }
  writeFile(file: HCFiler) {
    super.writeFile(file);
    file.write(1);
    file.write(this._name);
  }
  readFile(file: HCFiler) {
    super.readFile(file);
    if (this._ver < 3) return;
    let ver = file.read();
    this._name = file.read();
  }
}
