import {
  AbstractMesh,
  ActionManager,
  PickingInfo,
  PointerEventTypes,
} from "../babylon";
import { IEvent, IVector3 } from "@hc/core";

export { PointerEventTypes } from "../babylon";

export interface IPointerEvent extends IEvent {
  screenX: number;
  screenY: number;
  worldPoint: IVector3;
  object: AbstractMesh;
  /**实际选中的网格 */
  pickedMesh: AbstractMesh;
  pickInfo: PickingInfo;
}

export type PointerCallback = (event: IPointerEvent) => void;

export const PointTypeEnum = {
  [PointerEventTypes.POINTERDOWN]: "pointerDown",
  [PointerEventTypes.POINTERUP]: "pointerUp",
  [PointerEventTypes.POINTERMOVE]: "pointerMove",
  [PointerEventTypes.POINTERPICK]: "pointerPick",
  [PointerEventTypes.POINTERWHEEL]: "pointerWheel",
  [PointerEventTypes.POINTERTAP]: "pointerTap",
  [PointerEventTypes.POINTERDOUBLETAP]: "pointerDoubleTap",
};

export interface IEventOptions {
  userData?: Record<string, any>;
  condition?: (event: IEvent) => boolean;
}

export type EventCallback = (e: IEvent) => void;

export const ActionManagerType = {
  pointerOver: ActionManager.OnPointerOverTrigger,
  pointerOut: ActionManager.OnPointerOutTrigger,
  pointerLongPress: ActionManager.OnLongPressTrigger,
};
