import { Entity } from "../..";
import { BaseWidget } from "../../core/BaseWidget";
import { Factory, HCFiler, ObjectId } from "@hc/core";

export type SelectEnType = "select" | "unSelect" | "toggle";

@Factory
export class Select extends BaseWidget {
  static type = "Select";
  static schema = {
    label: "设置选中",
  };
  private _list: { type: SelectEnType; objectId: ObjectId<Entity> }[] = [];

  constructor() {
    super();
  }
  get List() {
    return [...this._list];
  }

  async do() {
    for (const data of this._list) {
      switch (data.type) {
        case "select":
          data.objectId.Object.IsSelect = true;
          break;
        case "unSelect":
          data.objectId.Object.IsSelect = false;
          break;
        case "toggle":
          data.objectId.Object.IsSelect = !data.objectId.Object.IsSelect;
          break;
        default:
          break;
      }
    }
    return true;
  }
  fromJSON(data: any[]) {
    this._list.length = 0;
    for (const d of data) {
      if (d.type && d.objectId)
        this._list.push({ type: d.type, objectId: d.objectId });
    }
    return this;
  }
  readFile(filer: HCFiler): void {
    filer.read();
    this._list.length = 0;
    filer.readAnyArray(() => {
      const data = { type: "", objectId: null } as any;
      data.type = filer.read();
      data.objectId = filer.readObjectId();
      this._list.push(data);
    });
  }
  writeFile(filer: HCFiler): void {
    filer.write(1);
    filer.writeAnyArray(this._list, (a) => {
      filer.write(a.type);
      filer.writeObjectId(a.objectId);
    });
  }
}
