import { BaseObject } from "../BaseObject";
import { HCFiler } from "../filer/HCFiler";
import { arrayRemoveIf } from "../utils";
import { ObjectRecord } from "./Record";

/**记录表 */
export class RecordTable extends BaseObject {
  private records: ObjectRecord[] = [];
  get Objects() {
    return [...this.records];
  }
  append(record: ObjectRecord, isCheck = true) {
    if (!isCheck) {
      record._SetDefaultDb(this.DB);
    } else {
      record._SetOwnerDatabase(this.DB);
    }
    record.Owner = this.ObjectId;
    this.add(record);
  }
  add(record: ObjectRecord) {
    this.records.push(record);
  }
  remove(id: number) {
    return arrayRemoveIf(this.records, (r) => r.Id === id);
  }
  writeFile(file: HCFiler) {
    super.writeFile(file);
    file.write(1);
    file.write(this.records.length);
    for (const record of this.records) {
      file.writeObject(record);
    }
  }
  readFile(file: HCFiler) {
    super.readFile(file);
    if (this._ver < 2) return;
    let ver = file.read();
    this.records.length = 0;
    const size = file.read();
    for (let i = 0; i < size; i++) {
      const record = file.readObject();
      this.records.push(record);
    }
  }
  destory() {
    super.destory();
    for (const o of this.Objects) {
      o.destory();
    }
    this.Objects.length = 0;
  }
}
