import { Mesh, TransformNode } from "../../babylon";
import { GroupRecord } from "../../records/GroupRecord";
import { Entity } from "./../../entitys/Entity";

export class SelectSet {
  private _selectSetList = new Set<Mesh | TransformNode>();
  get SelectMeshs() {
    return [...this._selectSetList];
  }
  get SelectEntitys(): Entity[] {
    return this.SelectMeshs.map((s) => s.metadata.Entity);
  }
  add(data: (Mesh | TransformNode)[]) {
    for (const d of data) {
      //TODO:文件夹可能嵌套
      if (d.metadata?.Entity?.GroupId) {
        const en = d.metadata?.Entity?.GroupId?.Object as GroupRecord;
        const ens = en.entitys.map((e) => e.Object) as Entity[];
        for (const en of ens) {
          if (en instanceof Entity && en.Visiable && en.DrawObject)
            this._selectSetList.add(en.DrawObject);
        }
      }
      this._selectSetList.add(d);
    }
  }
  addEns(ens: Entity[]) {
    ens.forEach((e) => this._selectSetList.add(e.DrawObject));
  }
  remove() {
    //
  }
  clear() {
    this._selectSetList.forEach((d) => {
      if (d?.metadata?.Entity) d.metadata.Entity.IsSelect = false;
    });
    this._selectSetList.clear();
  }
}
