import { commandManager } from "./CommandManager";

/**
 * 命令包装器
 ** 注意，这个包装器是异步的
 * @param exec
 * @param cmdName
 * @returns
 */
export async function CommandWrap(
  exec: () => boolean | void | undefined,
  cmdName = ""
) {
  if (!commandManager.CommandStart(cmdName)) return;

  let abort = true;
  try {
    const status = await exec();
    if (typeof status === "boolean") {
      abort = status;
    } else abort = false;
  } catch (error) {
    console.log("error: ", error);
  }
  await commandManager.CommandEnd(abort);
}
