import { AppConfig } from "../app/appConfig";
import {
  ArcRotateCamera,
  AxesViewer,
  Camera,
  Color3,
  Engine,
  EngineOptions,
  HemisphericLight as BHLight,
  Matrix,
  Mesh,
  MeshBuilder,
  Observer,
  Scene,
  Tools,
  UtilityLayerRenderer,
  Vector3,
  GridMaterial,
  Texture,
} from "../babylon";
import { AdvancedDynamicTexture } from "../babylon";
import { GoundSize } from "../contants";
import { HighlightManager } from "../editor/HighlightManager";
import { HemisphericLight } from "../entitys/lights/HemisphericLight";
import { b64toBlob, sleep } from "../utils/utils";
import { AppStore } from "./../app/AppStore";
import { iVector2Vector, TempVector } from "./../utils/vector";
import { CameraManager, CameraType } from "./cameras/CameraManager";
import { SceneControl } from "./scenes/Scene";
import { SceneEffectManager } from "./scenes/SceneEffectManager";
import { SceneHelper } from "./scenes/SceneHelper";
import { StandardMaterial } from "@babylonjs/core/Materials/standardMaterial";
import { IScreenshotSize } from "@babylonjs/core/Misc/interfaces/screenshotSize";
import { begin } from "@hc/aop";
import { HCFiler, IVector3 } from "@hc/core";

/**渲染视图 */
export class Viewer {
  engine: Engine;
  sceneControl: SceneControl;
  cameraManager: CameraManager;
  ground: Mesh;
  utilLayer: UtilityLayerRenderer;
  hemisphericLight: HemisphericLight;
  highlightManager: HighlightManager;
  private _showWorldAxes = false;
  private _worldAxes: AxesViewer;
  private _engineOption: EngineOptions = {
    preserveDrawingBuffer: false,
    stencil: true,
    useHighPrecisionMatrix: true,
    premultipliedAlpha: false,
    antialias: !AppConfig.lowPerformance,
    forceSRGBBufferSupportState: false,
    doNotHandleContextLost: AppConfig.lowPerformance,
  };
  private _sceneHelper: SceneHelper;
  private _advancedDynamicTexture: AdvancedDynamicTexture;
  private Reading = false;
  private pauseRender = false;
  private sceneObjects: Mesh[] = [];
  #sceneEffectManager: SceneEffectManager;
  constructor(
    public canvas: HTMLCanvasElement,
    private preview = false,
    private _option = { defaultLight: false }
  ) {
    this.init();
    this._regiser();
    this.render();
  }
  get Scene() {
    return this.sceneControl.Scene;
  }
  get ShowAxesViewer() {
    return this._showWorldAxes;
  }
  set ShowAxesViewer(v: boolean) {
    this._showWorldAxes = v;
    if (this._worldAxes) {
      this._worldAxes.xAxis.setEnabled(v);
      this._worldAxes.yAxis.setEnabled(v);
      this._worldAxes.zAxis.setEnabled(v);
    }
  }
  get AmbientColor() {
    return this.Scene.ambientColor;
  }
  set AmibentColor(v: string) {
    this.Scene.ambientColor = Color3.FromHexString(v);
  }
  get GuiContainer() {
    return this._advancedDynamicTexture;
  }
  get PauseRender() {
    return this.PauseRender;
  }
  set PauseRender(v: boolean) {
    if (v === this.pauseRender) return;
    this.pauseRender = v;
    if (v)
      setTimeout(() => {
        this.engine.clear(
          this.Scene.clearColor,
          this.Scene.autoClear ||
            this.Scene.forceWireframe ||
            this.Scene.forcePointsCloud,
          this.Scene.autoClearDepthAndStencil,
          this.Scene.autoClearDepthAndStencil
        );
      }, 0);
  }
  get IsPreview() {
    return this.preview;
  }
  async getPreviewUrl(
    isReset = false,
    size: number | IScreenshotSize = 400,
    isCover = false
  ): Promise<string> {
    //截图时隐藏视图无关的工具
    if (this._sceneHelper) this._sceneHelper.stopRender = true;
    if (this.utilLayer)
      this.utilLayer.utilityLayerScene.rootNodes.forEach((r) => {
        r.metadata = r.isEnabled();
        r.setEnabled(false);
      });

    let [width, height] =
      typeof size === "number"
        ? [size, size]
        : [size.width ?? size.height, size.height ?? size.width];

    if (isReset) {
      this.setSize(width, height);
      this.stopRender();
      this.sceneControl.render();
    }
    const png = this.canvas.toDataURL();

    if (isReset) {
      this.setSize();
      this.render();
    }

    //还原工具
    if (this._sceneHelper) this._sceneHelper.stopRender = false;
    if (this.utilLayer)
      this.utilLayer.utilityLayerScene.rootNodes.forEach((r) => {
        r.setEnabled(r.metadata);
      });

    return png;
  }
  async getBlob(isReset = false) {
    const url = await this.getPreviewUrl(isReset);
    return b64toBlob(url);
  }
  get ShowGround() {
    return this.ground.isEnabled();
  }
  set ShowGround(v: boolean) {
    this.ground.setEnabled(v);
  }
  get EffectManager() {
    return this.#sceneEffectManager;
  }
  //初始化
  init() {
    this._initEngine();
    this._initScene();
    this._initCamera();
    if (this._option.defaultLight) {
      this._initLight();
    }

    if (!this.preview) {
      this._initSceneHelper();
    }

    AppStore.Scene = this.Scene;

    this.highlightManager = new HighlightManager(this);

    this.setSize();

    const advancedTexture = AdvancedDynamicTexture.CreateFullscreenUI(
      "MainGuiContainer",
      true,
      this.Scene
    );

    this._advancedDynamicTexture = advancedTexture;
    this.#sceneEffectManager = new SceneEffectManager(this.Scene, this.Camera);
  }
  initEditor() {
    this.utilLayer = new UtilityLayerRenderer(this.Scene);
    this._worldAxes = new AxesViewer(this.utilLayer.utilityLayerScene, 1, 0);
    this._worldAxes.xAxis.setEnabled(this._showWorldAxes);
    this._worldAxes.yAxis.setEnabled(this._showWorldAxes);
    this._worldAxes.zAxis.setEnabled(this._showWorldAxes);

    this.cameraManager.ArcRotateControl.onRadiusChangeObserver.add((camera) => {
      this.updateAxes();
    });
    this.cameraManager.ArcRotateControl.onZommScaleChangeObserver.add(
      (camera) => {
        this.updateAxes();
      }
    );

    const ground = MeshBuilder.CreateGround(
      "WORLD_GROUND",
      { width: GoundSize, height: GoundSize },
      this.Scene
    );

    ground.isPickable = false;

    const m = new GridMaterial("groundMaterial", this.Scene);
    m.lineColor = new Color3(79 / 255, 79 / 255, 79 / 255);
    m.mainColor = new Color3(61 / 255, 61 / 255, 61 / 255);
    m.opacity = 0.98;
    m.backFaceCulling = false;
    m.gridRatio = 10;
    m.freeze();

    ground.material = m;

    this.ground = ground;
    this.highlightManager.exclude(ground);

    begin(
      this.cameraManager,
      this.cameraManager.switchEvent,
      (c: CameraManager) => {
        if (this._worldAxes) {
          const isShow = this._showWorldAxes && c.Type === CameraType.ArcRotate;
          this._worldAxes.xAxis.setEnabled(isShow);
          this._worldAxes.yAxis.setEnabled(isShow);
          this._worldAxes.zAxis.setEnabled(isShow);
        }
      }
    );
  }
  get Width() {
    return this.engine.getRenderWidth();
  }
  get Height() {
    return this.engine.getRenderHeight();
  }
  get Camera() {
    return this.cameraManager.Camera;
  }
  get FPS() {
    return this.engine.getFps();
  }
  setSize(w?: number, h?: number) {
    this.engine.setSize(
      w ?? this.canvas.parentElement.clientWidth,
      h ?? this.canvas.parentElement.clientHeight
    );
  }
  world2screen(pt: IVector3) {
    return Vector3.Project(
      iVector2Vector(pt),
      Matrix.IdentityReadOnly,
      this.Scene.getTransformMatrix(),
      this.Camera.viewport.toGlobal(
        this.Scene.getEngine().getRenderWidth(),
        this.Scene.getEngine().getRenderHeight()
      )
    );
  }
  screen2World(p: { x: number; y: number }) {
    const ray = this.Scene.createPickingRay(
      p.x,
      p.y,
      this.Scene.getViewMatrix(),
      this.cameraManager.Camera,
      true
    );
    if (this.ground) this.ground.isPickable = true;
    //@ts-ignore
    const pickinfo = ray.intersectsMesh(this.ground || this.Plane);

    if (this.ground) this.ground.isPickable = false;

    if (pickinfo.hit) return pickinfo.pickedPoint;

    return null;
  }
  render() {
    this.engine.runRenderLoop(this.renderFunction);
  }
  stopRender() {
    this.engine.stopRenderLoop();
  }
  destory() {
    if (this.Scene !== AppStore.MainScene && AppStore.Scene === this.Scene) {
      console.info("切换回主场景");
      AppStore.Scene = AppStore.MainScene;
    }
    this.cameraManager.destory();
    this.engine.dispose();
  }
  on(type: "rendered", callback: (d: Scene) => void) {
    let observer: Observer<Scene>;
    if (type === "rendered") {
      observer = this.Scene.onAfterRenderObservable.add(callback);
      return () => {
        this.Scene.onAfterRenderObservable.remove(observer);
      };
    }

    return () => {};
  }
  zoomAll(autoLimit = false) {
    this.cameraManager.zoomAll(autoLimit);
  }
  reset() {
    this.cameraManager.reset();
    this.sceneControl.reset();
    this.#sceneEffectManager.reset();
    this.updateAxes(true);
  }
  setCursor(type: string) {
    this.canvas.parentElement.style.cursor = type;
  }
  loadGroundImage(image: string | File): Promise<Mesh> {
    let texture: Texture;
    if (typeof image === "string") {
      texture = new Texture(image, this.Scene);
    } else {
      texture = Texture.LoadFromDataString("ground-image", image, this.Scene);
    }
    const mtl = new StandardMaterial("ground-image-mtl", this.Scene);
    mtl.specularColor = Color3.Black();
    mtl.diffuseTexture = texture;
    mtl.backFaceCulling = false;
    texture.vAng = Math.PI;
    texture.wAng = Math.PI;
    return new Promise((res) => {
      texture.onLoadObservable.addOnce((data) => {
        const plane = MeshBuilder.CreateGround(
          "ground-iamge",
          data.getSize(),
          this.Scene
        );
        plane.material = mtl;
        plane.freezeWorldMatrix();
        mtl.freeze();
        this.sceneObjects.push(plane);
        res(plane);
      });
    });
  }
  destoryGroundImage() {
    this.sceneObjects.forEach((o) => o.dispose(false, true));
    this.sceneObjects.length = 0;
  }
  focus() {
    this.canvas.focus();
  }
  private _regiser() {
    //
  }
  private _initEngine() {
    const engine = new Engine(
      this.canvas,
      !AppConfig.lowPerformance,
      this._engineOption
    );
    this.engine = engine;
  }
  private _initScene() {
    this.sceneControl = new SceneControl(this.engine);
  }
  private _initCamera() {
    this.cameraManager = new CameraManager(this.Scene, this.canvas);
  }
  private _initLight() {
    const light = new BHLight("global-light", new Vector3(1, 1, 1), this.Scene);
  }
  private _initSceneHelper() {
    this._sceneHelper = new SceneHelper(
      this.engine,
      this.Scene,
      this.cameraManager
    );
  }
  private updateAxes(init = false) {
    if (!this.utilLayer) return;
    const activeCamera = this.utilLayer.utilityLayerScene
      .activeCamera as ArcRotateCamera;
    const cameraPosition = activeCamera.globalPosition;
    this._worldAxes.xAxis.position.subtractToRef(cameraPosition, TempVector);
    const dist = TempVector.length();

    if (this.Camera.mode === Camera.ORTHOGRAPHIC_CAMERA) {
      const fuzz = this.cameraManager.ZoomScale;
      this._worldAxes.xAxis.scaling.setAll(100 * fuzz);
      this._worldAxes.yAxis.scaling.setAll(100 * fuzz);
      this._worldAxes.zAxis.scaling.setAll(100 * fuzz);
    } else if (init) {
      this._worldAxes.xAxis.scaling.setAll(1);
      this._worldAxes.yAxis.scaling.setAll(1);
      this._worldAxes.zAxis.scaling.setAll(1);
    } else {
      const FUZZ = 15;
      this._worldAxes.xAxis.scaling.setAll((dist / FUZZ) * 4);
      this._worldAxes.yAxis.scaling.setAll((dist / FUZZ) * 4);
      this._worldAxes.zAxis.scaling.setAll((dist / FUZZ) * 4);
    }
  }
  renderEvent() {}
  clearScene() {
    for (const root of this.Scene.rootNodes) {
      if (root.metadata?.needDispose) {
        root.dispose(false, true);
      }
    }
  }
  writeFile(filer: HCFiler) {
    filer.write(2);
    this.cameraManager.writeFile(filer);
    this.sceneControl.writeFile(filer);
    this.#sceneEffectManager.writeFile(filer);
  }
  readFile(filer: HCFiler) {
    this.Reading = true;
    const ver = filer.read();
    this.cameraManager.readFile(filer);
    this.sceneControl.readFile(filer);
    if (ver > 1) {
      this.#sceneEffectManager.readFile(filer);
    } else {
      this.#sceneEffectManager.reset();
    }

    this.Reading = false;
    this.updateAxes(true);
  }
  clearCache() {
    this.Scene.cleanCachedTextureBuffer();
    this.engine.clearInternalTexturesCache();
  }
  private renderFunction = () => {
    this.renderEvent();
    if (this.Reading || this.pauseRender) return;
    this.sceneControl.render();
    if (this._sceneHelper) this._sceneHelper.render();
  };
}
