import { AppStore, BaseTextureRecord, Utils } from "@hc/engine";

import { Factory, HCFiler } from "@hc/core";

import { getGraphView, getHTData, getHTNode, IDataBindings } from "./ht";

@Factory
export class HC2DTexture extends BaseTextureRecord {
  static type = "HC2DTexture";
  private graphView;
  private hcNode;
  private _dataBindings: IDataBindings[];
  protected texture: any;
  private timer: NodeJS.Timeout;
  constructor(private _source: string) {
    super();
  }
  get Texture() {
    if (!this.texture) {
      this.update();
    }

    return this.texture;
  }
  get Source() {
    return this._source;
  }
  set Source(url: string) {
    if (url === this._source) return;
    this._source = url;
    this.update();
  }
  initTexture() {
    const graphView = getGraphView();

    let resFun: (target: unknown) => void;

    graphView.addViewListener((event) => {
      this.timer && clearTimeout(this.timer);
      if (event?.kind === "validate") {
        this.timer = setTimeout(() => {
          let isUpdate = false;
          const dom = this.graphView.__htmlOrderList?.[0];
          if (dom?.nodeName === "VIDEO") {
            if (!this.texture) {
              this.texture = Utils.getVideoTexture(
                "video" + Date.now(),
                dom.src,
                AppStore.MainScene
              );
              resFun(this);
            } else {
              if (dom.src !== this.texture.video.src) {
                console.log(123);
                isUpdate = true;
                this.texture.updateURL(dom.src);
              }
            }
          } else {
            let canvas: HTMLCanvasElement;
            if (dom && dom instanceof HTMLElement)
              canvas = dom.getElementsByTagName("canvas")[0];
            if (!canvas) canvas = this.graphView.getCanvas();
            if (canvas) {
              if (!this.texture) {
                this.texture = Utils.getCanvasTexture(
                  "canvas" + Date.now(),
                  canvas,
                  AppStore.MainScene
                );
                resFun(this);
              } else {
                isUpdate = true;
                this.texture._canvas = canvas;
                this.texture._context = canvas.getContext("2d");
              }
            }
          }

          if (isUpdate) {
            this.graphView.fitContent(false, 0);
            this.texture.update();
          }
        }, 100);
      }
    });

    this.graphView = graphView;
    return new Promise((res) => {
      getHTNode(this.graphView.dm(), this._source).then((node: any) => {
        this.hcNode = node;
        const data = getHTData(node.getImage());
        this.load(data);
        resFun = res;
      });
    });
  }

  update() {
    super.update();
    if (!this.graphView) return;
  }
  writeFile(file: HCFiler): void {
    super.writeFile(file);
    file.write(1);
    file.write(this._source);
  }
  readFile(file: HCFiler): void {
    super.readFile(file);
    file.read();
    this._source = file.read();
    this.initTexture();
  }
  private load = (data) => {
    if (!data) return;
    const width =
      typeof data.width === "object" ? data.width.value : data.width;
    const height =
      typeof data.height === "object" ? data.height.value : data.height;

    this._dataBindings = data.dataBindings;
    const dom = this.graphView.getView() as HTMLElement;
    dom.style.cssText = `position:fixed;left:0;top:0;width:${width}px;height:${height}px;visibility:hidden`;
    this.graphView.getCanvas().width = width;
    this.graphView.getCanvas().height = height;
    document.body.append(dom);

    if (this.texture) this.texture.update();
    setTimeout(() => {
      dom.remove();
    }, 2000);
  };
}
