import { PBRMaterial } from "@babylonjs/core/Materials/PBR/pbrMaterial";

import { Factory, HCFiler, ObjectId } from "@hc/core";

import { AppStore } from "../app/AppStore";
import { Animatable, Animation, Color3, IAnimationKey } from "../babylon";
import { Entity } from "../entitys";
import { MaterialRecord } from "../record";
import { IAnimation, IMaterialAnimationOption } from "../types/animation.types";
import { removeOne } from "../utils";

@Factory
export class MaterialAnimation implements IAnimation {
  static type = "MaterialAnimation";
  #name = "材质动画";
  private _animatable: Animatable;
  private _animation: Animation;
  constructor(
    private mtl?: string | ObjectId<MaterialRecord>,
    private options?: IMaterialAnimationOption
  ) {
    this.init();
  }
  get Material() {
    if (!this.mtl) return;
    if (typeof this.mtl === "string") {
      return Entity.Scene.getMaterialById(this.mtl) as PBRMaterial;
    } else {
      return this.mtl.Object?.Material;
    }
  }
  get name() {
    return this.#name;
  }
  set name(v: string) {
    this.#name = v;
    if (this._animation) {
      this._animation.name = v;
    }
  }
  get Animatable() {
    return this._animatable;
  }
  get Animation() {
    return this._animation;
  }
  init() {
    const mtl = this.Material;

    if (!mtl) return;

    this.clear();

    const options = this.options;
    if (!options) return;

    const {
      name,
      frame = 30,
      defaultStart = false,
      range = [0, 1],
      dataType,
    } = options;

    if (name) this.#name = name;

    const animate = new Animation(
      this.#name,
      options.targetKey,
      options.frame ?? 30,
      dataType ?? Animation.ANIMATIONTYPE_FLOAT
    );

    const keyFrames: IAnimationKey[] = this.generateKeys();

    animate.setKeys(keyFrames);
    if (!mtl.animations) {
      mtl.animations = [];
    }
    mtl.animations.push(animate);

    this._animation = animate;

    if (defaultStart && AppStore.PreviewMode) {
      this.start();
    }
  }
  start() {
    const { time = 1, frame = 30, loop = true, speed = 1 } = this.options;
    this._animatable = Entity.Scene.beginDirectAnimation(
      this.Material,
      [this.Animation],
      0,
      time * frame,
      loop,
      speed
    );
  }
  pause() {
    if (this._animatable) {
      this._animatable.pause();
    }
  }
  restart() {
    if (this._animatable) {
      this._animatable.restart();
    }
  }
  stop() {
    if (this._animatable) {
      this._animatable.stop();
    }
  }
  reset() {
    if (this._animatable) {
      this._animatable.reset();
    }
  }
  clear() {
    const renderObject = this.Material;
    if (renderObject) {
      if (this._animatable) {
        this._animatable.disposeOnEnd = true;
        this._animatable.stop();
        this._animatable = null;
      }
      if (this._animation) removeOne(renderObject.animations, this._animation);
    }
  }
  writeFile(file: HCFiler) {
    file.write(1);

    file.write(typeof this.mtl === "string");
    if (typeof this.mtl === "string") {
      file.write(this.mtl);
    } else {
      file.writeHardObjectId(this.mtl);
    }
    file.write(this.name);
    file.write(this.options);
  }
  readFile(file: HCFiler) {
    file.read(); // 版本
    let isStr = file.read();
    if (isStr) {
      this.mtl = file.read();
    } else {
      this.mtl = file.readHardObjectId() as any;
    }
    this.name = file.read();
    this.options = file.read();

    requestIdleCallback(() => {
      this.init();
    });
  }
  private getValue() {
    const strs = this.options.targetKey.split(".");
    let object: any = this.Material;
    for (const k of strs) {
      object = object[k];
    }
    return object;
  }
  private generateKeys() {
    const {
      time = 1,
      frame = 30,
      range = [0, 1],
      dataType = Animation.ANIMATIONTYPE_FLOAT,
    } = this.options;
    switch (dataType) {
      case Animation.ANIMATIONTYPE_FLOAT:
        return [
          {
            frame: 0,
            value: range[0],
          },
          {
            frame: time * frame,
            value: range[1],
          },
        ];
      case Animation.ANIMATIONTYPE_COLOR3:
        return [
          {
            frame: 0,
            value: Color3.FromHexString(range[0] as string),
          },
          {
            frame: time * frame,
            value: Color3.FromHexString(range[1] as string),
          },
        ];
      default:
        return [];
    }
  }
}
