import { CubeTexture, HDRCubeTexture, Scene } from "../babylon";

export class LocalStorageHelper {
  public static ReadLocalStorageValue(key: string, defaultValue: number) {
    if (typeof Storage !== "undefined" && localStorage.getItem(key) !== null) {
      return parseInt(localStorage.getItem(key)!);
    }

    return defaultValue;
  }
}

export class EnvironmentTools {
  public static SkyboxPath = "";
  public static Skyboxes = [
    "https://hcwl-cdn.cdn.bcebos.com/hc3d/envs/environmentSpecular.env",
    "https://hcwl-cdn.cdn.bcebos.com/hc3d/envs/studio.env",
  ];

  public static SkyboxesNames = ["Default", "Studio"];

  public static LoadSkyboxPathTexture(scene: Scene) {
    const defaultSkyboxIndex = Math.max(
      0,
      LocalStorageHelper.ReadLocalStorageValue("defaultSkyboxId", 0)
    );
    const path = this.SkyboxPath || this.Skyboxes[defaultSkyboxIndex];
    if (path.indexOf(".hdr") === path.length - 4) {
      return new HDRCubeTexture(path, scene, 256, false, true, false, true);
    }
    return CubeTexture.CreateFromPrefilteredData(path, scene);
  }
}
