import { HCFiler, ISerialize } from "@hc/core";

/**
 * 动作元件对象基类
 * 主要
 * @export
 * @abstract
 * @class BaseWidget
 * @implements {ISerialize}
 */
export abstract class BaseWidget implements ISerialize {
  readonly IsContainer: boolean = false;
  static schema = {};
  public startTime = 0;
  get Type() {
    return this.constructor["type"];
  }
  do(): Promise<unknown> | unknown {
    return null;
  }
  destory() {}
  fromJSON(data: any) {}
  readFile(file: HCFiler): void {}
  writeFile(file: HCFiler): void {}
}
