import { AppStore } from "../../app";
import { Factory, HCFiler } from "@hc/core";
import { BaseWidget } from "../../core/BaseWidget";
import { ISetParam } from "../../types/interaction.types";

@Factory
export class SetVariable extends BaseWidget {
  static type = "SetVariable";
  static schema = {
    label: "设置变量值",
  };
  private list: ISetParam[] = [];
  get List() {
    return [...this.list];
  }
  do() {
    for (const setPar of this.list) {
      const par = AppStore.GlobalVariables.find((p) => p.key === setPar.key);
      if (!par) continue;
      if (setPar.value.type === "value") {
        par.value = setPar.value.value;
      } else if (setPar.value.type === "param") {
        const targetPar = AppStore.GlobalVariables.find(
          (p) => p.key === setPar.value.value
        );
        par.value = targetPar.value;
      } else if (setPar.value.type === "entity") {
        par.value = setPar.value.value.Object[setPar.value.entityKey];
      }
    }
    localStorage.setItem("variables", JSON.stringify(AppStore.GlobalVariables));
    return true;
  }
  add(par: ISetParam) {
    this.list.push(par);
  }
  fromJSON(data: any[]): void {
    this.list = data;
  }
  readFile(filer: HCFiler): void {
    filer.read();
    this.list.length = 0;
    filer.readAnyArray(() => {
      const data: ISetParam = {
        key: "",
        value: { type: null, value: null },
      };
      data.key = filer.read();
      filer.readWidgetValue(data.value);
      this.list.push(data);
    });
  }
  writeFile(filer: HCFiler): void {
    filer.write(1);
    filer.writeAnyArray(this.list, (a) => {
      filer.write(a.key);
      filer.writeWidgetValue(a.value);
    });
  }
}
