import {
  BoundingBox,
  DeepImmutableObject,
  Matrix,
  Size,
  Vector3,
} from "../babylon";

export class Box3 extends BoundingBox {
  static GetCenterFromPoints(points: Vector3[]) {
    if (points.length <= 1) return points[0];
    const min = getMin(points[0], points[1]);
    const max = getMax(points[0], points[1]);
    const box3 = new Box3(min, max);
    if (points.length > 2) {
      box3.setFromPoints(points.slice(2));
    }
    return box3.center;
  }
  constructor(
    min = Vector3.Zero(),
    max = Vector3.Zero(),
    mtx?: DeepImmutableObject<Matrix>
  ) {
    super(min, max, mtx);
  }
  get Size() {
    return this.extendSize.scale(2);
  }
  get SizeWorld() {
    return this.extendSizeWorld.scale(2);
  }
  union(box: BoundingBox) {
    const oldV = this.minimumWorld.clone();
    const newV = box.minimumWorld.clone();

    oldV.set(
      Math.min(oldV.x, newV.x),
      Math.min(oldV.y, newV.y),
      Math.min(oldV.z, newV.z)
    );

    const oldM = this.maximumWorld.clone();
    const newM = box.maximumWorld.clone();

    oldM.set(
      Math.max(oldM.x, newM.x),
      Math.max(oldM.y, newM.y),
      Math.max(oldM.z, newM.z)
    );

    this.reConstruct(oldV, oldM);

    return this;
  }

  expandByPoint(point: Vector3) {
    this.min(this.minimum, point);
    this.max(this.maximum, point);
    this.reConstruct(this.minimum, this.maximum, this.getWorldMatrix());
    return this;
  }
  setFromPoints(points: Vector3[]) {
    for (let i = 0, il = points.length; i < il; i++) {
      this.expandByPoint(points[i]);
    }
    this.reConstruct(this.minimum, this.maximum, this.getWorldMatrix());
    return this;
  }
  private min(sp: Vector3, tp: Vector3) {
    sp.x = Math.min(sp.x, tp.x);
    sp.y = Math.min(sp.y, tp.y);
    sp.z = Math.min(sp.z, tp.z);

    return this;
  }
  private max(sp: Vector3, tp: Vector3) {
    sp.x = Math.max(sp.x, tp.x);
    sp.y = Math.max(sp.y, tp.y);
    sp.z = Math.max(sp.z, tp.z);

    return this;
  }
}

/**联合2包围盒，改变b1包围盒的大小 */
export function unionBoundbox(b1: BoundingBox, b2: BoundingBox) {
  const oldV = b1.minimum;
  const newV = b2.minimum;

  b1.minimum.set(
    Math.min(oldV.x, newV.x),
    Math.min(oldV.y, newV.y),
    Math.min(oldV.z, newV.z)
  );

  const oldM = b1.maximum;
  const newM = b2.maximum;

  b1.maximum.set(
    Math.max(oldM.x, newM.x),
    Math.max(oldM.y, newM.y),
    Math.max(oldM.z, newM.z)
  );
  return b1;
}

export function getMin(sp: Vector3, tp: Vector3) {
  const result = new Vector3();
  result.x = Math.min(sp.x, tp.x);
  result.y = Math.min(sp.y, tp.y);
  result.z = Math.min(sp.z, tp.z);
  return result;
}
export function getMax(sp: Vector3, tp: Vector3) {
  const result = new Vector3();
  result.x = Math.max(sp.x, tp.x);
  result.y = Math.max(sp.y, tp.y);
  result.z = Math.max(sp.z, tp.z);
  return result;
}
