import { Factory, HCFiler } from "@hc/core";
import { BaseWidget } from "../../core/BaseWidget";

@Factory
export class Wait extends BaseWidget {
  static type = "Wait";
  static schema = {
    label: "等待",
  };
  _time = 1000;
  do() {
    return new Promise((res) => setTimeout(res, this._time));
  }
  fromJSON(data: any): void {
    this._time = data.time;
  }
  readFile(filer: HCFiler): void {
    filer.read();
    this._time = filer.read();
  }
  writeFile(filer: HCFiler): void {
    filer.write(1);
    filer.write(this._time);
  }
}
