export type InjectFun = (...arg) => () => void;
export type InjectObj = Record<string | symbol, any>;

enum InjectType {
  begin = "__begin__",
  end = "__end__",
}

/**
 * 初始化注入.
 */
function initInjectFunctionArray(
  obj: InjectObj,
  funcName: symbol
): InjectFun[] {
  if (!obj.hasOwnProperty(funcName)) {
    obj[funcName] = [];
  }
  return obj[funcName];
}

function callInjectFunctionList(
  target: InjectObj,
  funcName: string,
  type: InjectType,
  ...args
) {
  const name = getInjectFunctionArrayName(funcName, type);
  let funcList: InjectFun[] = target[name];
  if (funcList) {
    for (let f of funcList) {
      if (f.call(target, ...args))
        //如果函数返回true 那么结束注入.
        return;
    }
  }
}

function initInjectReplace(target: InjectObj, funcName: string) {
  const key = Symbol.for("__aopinit__" + funcName);
  if (!target.hasOwnProperty(key)) {
    target[key] = true;
    let oldFunction: InjectFun = target[funcName];
    target[funcName] = function (...args) {
      callInjectFunctionList(target, funcName, InjectType.begin, ...args);
      let res = oldFunction.call(target, ...args);
      args.push(res);
      callInjectFunctionList(target, funcName, InjectType.end, ...args);
      return res;
    };
  }
}

/**
 * 注入方法到对象方法
 * @param injectType
 * @returns 移除方法 remove
 */
function inject(injectType: InjectType): InjectFun {
  return function injectObject(
    obj: InjectObj,
    func: InjectFun,
    injectFunction: InjectFun
  ) {
    let name = getFunctionName(obj, func);
    let beginName = getInjectFunctionArrayName(name, injectType);
    initInjectReplace(obj, name);

    let functionArr: InjectFun[] = initInjectFunctionArray(obj, beginName);
    functionArr.unshift(injectFunction);

    if (functionArr.length > 20)
      console.warn("aop注入函数个数超过20", injectFunction);

    return function () {
      let index = functionArr.indexOf(injectFunction);
      if (index !== -1) functionArr.splice(index, 1);
    };
  };
}

export function getFunctionName(target: InjectObj, func: InjectFun): string {
  if (func.name) return func.name;
  for (let key in target) {
    if (target[key] === func) {
      return key;
    }
  }
}

/**
 * 获得注入的函数名称.
 */
function getInjectFunctionArrayName(name: string, type: InjectType): symbol {
  return Symbol.for(type + name);
}

/**往函数体前注入函数 */
export const begin = inject(InjectType.begin);
/**往函数体后注入函数 */
export const end = inject(InjectType.end);
