import { BaseObject } from "../BaseObject";
import { Factory } from "../Factory";
import { HCFiler } from "../filer/HCFiler";
import { CreateObjectData } from "../history/CreateObjectData";
import { HistoryRecord } from "../history/HistoryRecord";
import { RemoveObjectData } from "../history/RemoveObjectData";

export interface CollectionWriteFileOptions<T = unknown> {
  sort?: (a: T, b: T) => number;
}

@Factory
export class ObjectCollection<T extends BaseObject> extends BaseObject {
  static type = "ObjectCollection";
  objects: T[] = [];
  append(object: T) {
    if (this._db && !object.ObjectId) {
      object._SetOwnerDatabase(this._db);
    } else {
      object._SetDatabase(this._db);
    }
    object.Owner = this._Owner;
    this.objects.push(object);

    //加入撤销重做的历史数据
    let undoRec = this.UndoRecord;
    if (undoRec) {
      let hisRec = new HistoryRecord();
      hisRec.redoData = new CreateObjectData(object).save();
      hisRec.undoData = new RemoveObjectData(this.objects.length - 1);

      undoRec.writeObjectHistoryPath(this, hisRec);
    }
    this.appendEvent(object);

    return object.ObjectId;
  }
  appendEvent(object: T) {
    //注入
  }
  remove(obj: T) {
    let index = this.objects.indexOf(obj);
    this.removeIndex(index);
  }

  removeIndex(index: number) {
    if (index === -1) return;
    let obj = this.objects[index];
    this.objects.splice(index, 1);

    if (obj) obj.destory();
    return obj;
  }
  /**实体替换功能
   * # 不允许单独调用
   * 请使用Database的方法进行模型替换
   * @hiddren
   */
  replace(source: T, target: T) {
    const index = this.objects.indexOf(source);
    if (index > -1) {
      this.objects[index] = target;
      source.replace(target);
      this.appendEvent(target);
    }
  }
  destory() {
    super.destory();
    for (const e of this.objects) {
      e.destory(true);
    }
    this.objects.length = 0;
  }
  readFile(file: HCFiler) {
    super.readFile(file);
    const ver = file.read();
    let cout = file.read();
    this.objects = [];
    for (let i = 0; i < cout; i++) {
      let obj = file.readObject() as T;
      if (obj) {
        this.objects.push(obj);
      }
    }
  }
  //对象将自身数据写入到文件.
  writeFile(file: HCFiler, options?: CollectionWriteFileOptions<T>) {
    super.writeFile(file);
    file.write(1);
    file.write(this.objects.length);

    const objects = [...this.objects];

    if (options?.sort) {
      objects.sort(options.sort);
    }

    for (let obj of objects) file.writeObject(obj);
  }
  _applyHistoryRecord(record: BaseObject) {
    if (record instanceof CreateObjectData) {
      let obj = record.getObject(this._db) as T;
      this.objects.push(obj);
      this.appendEvent(obj);
    } else if (record instanceof RemoveObjectData) {
      let obj = this.removeIndex(record.Index);
      record.removeObject = obj;
    }
  }
}
