import "~/extension/HC2DDom";
import "~/extension/Hc2DGui";
import "~/extension/HC2DTexture";

import * as Aop from "@hc/aop";
import * as HCCore from "@hc/core";
import * as HC from "@hc/engine";
import { AppConfig, Application, AppStore } from "@hc/engine";
import React, { useEffect, useRef, useState } from "react";
import { createRoot } from "react-dom/client";
import { registerComponent } from "./utils/component";
import { execScript } from "./utils/loadFile";
Object.assign(HC, HCCore, Aop);

interface PreviewProps {}

console.log(Build_VERSION);

AppConfig.assetURL = "/data/";

AppConfig.Texture_URL = "https://am-img.gkiiot.com/editor/";
AppConfig.SkyBox_URL = "https://am-img.gkiiot.com/editor/";

document.title = "海创可视化平台";

let timeStap = Date.now();

const Preview: React.FC<PreviewProps> = () => {
  const canvasRef = useRef<HTMLCanvasElement>(null);
  const [loading, setLoading] = useState(false);

  let app: Application;

  const getAppJson = (id?: number) => {
    const jsonUrl = `app${id || ""}.json?t=${timeStap}`;
    return fetch("/data/" + jsonUrl).then((res) => res.json());
  };

  const getComponentJson = (id?: number) => {
    const jsonUrl = `components/${id}.json?t=${timeStap}`;
    return fetch("/data/" + jsonUrl).then((res) => res.json());
  };
  const loadVariables = (id?: number) => {
    const jsonUrl = `variable.json?t=${timeStap}`;
    try {
      return fetch("/data/" + jsonUrl)
        .then((res) => res.json())
        .catch((err) => {
          return err;
        });
    } catch (err) {
      return null;
    }
  };

  const loadApp = async (id?: number) => {
    setLoading(true);
    const data = await getAppJson(id);
    if (!data) return;
    const components = data.components ?? [];
    for (const id of components) {
      const data = await getComponentJson(id);
      if (data) registerComponent({ ...data.data, id });
    }
    app.loadFile(data.data);

    const scripts = data.scripts ?? [];
    execScript(scripts, app, HC);
    app.on("loadComplete", () => {
      setLoading(false);
    });
  };

  useEffect(() => {
    (async () => {
      if (canvasRef.current) {
        const canvas = canvasRef.current;
        canvas.width = canvas.parentElement!.clientWidth;
        canvas.height = canvas.parentElement!.clientHeight;

        AppConfig.isEncrypt = false;

        window["app"] = app = new Application({
          canvas,
          editor: false,
          cameraRadius: 100,
        });

        const url = new URLSearchParams(location.search);
        const id = url.get("id");
        try {
          const v = await loadVariables();
          if (v) {
            AppStore.GlobalVariables = v;
          }
        } catch (err) {
          console.log(err);
        }

        loadApp(id ? Number(id) : undefined);
      }
    })();
  }, []);

  return (
    <>
      <canvas style={{ width: "100%", height: "100%" }} ref={canvasRef}>
        预览页面
      </canvas>
      {loading && (
        <div
          style={{
            position: "fixed",
            left: 0,
            right: 0,
            top: 0,
            bottom: 0,
            margin: "auto",
            width: "100%",
            height: "100%",
            background: "#00000073",
            textAlign: "center",
          }}
        >
          <img src="/static/images/loading/loading-mask.gif" alt="" />
          <div style={{ color: "#fff", fontSize: 24 }}>
            资源载入中,请稍后。。。
          </div>
        </div>
      )}
    </>
  );
};

const root = createRoot(document.getElementById("app") as HTMLElement);

root.render(<Preview />);
