import { Factory, HCFiler, IVector3 } from "@hc/core";

import {
  Color3,
  Curve3,
  LinesMesh,
  Mesh,
  MeshBuilder,
  SpotLight as SpotLight2,
  TransformNode,
  UtilityLayerRenderer,
  Vector3,
} from "../../babylon";
import { autoUpdate } from "../../core/autoUpdate";
import { AxisDragGizmo } from "../../editor/gizmo/AxisDragGizmo";
import { CommonMaterials } from "../../materials/commonMaterials";
import { iVector2Vector, vec2Vec3Like } from "./../../utils/vector";
import { Entity } from "./../Entity";
import { Light } from "./Light";

const STEP = 32;
const HELPER_STEP = 100;

/**
 * 射灯
 * @see https://hc3dol.gkiiot.com/#17
 */
@Factory
export class SpotLight extends Light {
  static type = "SpotLight";
  protected _name = "射灯";
  /**范围 */
  private gizmo: AxisDragGizmo;
  private points: Array<Vector3> = [];
  @autoUpdate Range = 150;
  @autoUpdate private Height = 100; //助手高度
  @autoUpdate Direction: IVector3 = { x: 0, y: -1, z: 0 };
  @autoUpdate Angle = Math.PI / 3;

  /**方向 */
  get BDirection() {
    return iVector2Vector(this.Direction);
  }
  get Points() {
    return [new Vector3(0, 0, this.Height / 2), new Vector3(0, 0, this.Range)];
  }

  get Radius() {
    return this.Height * Math.tan(this.Angle / 2);
  }
  initDrawObject() {
    const root = new Mesh(this.UniqueName + "_root", Entity.Scene);
    const light = new SpotLight2(
      this.UniqueName,
      Vector3.Zero(),
      this.BDirection,
      this.Angle,
      2,
      Entity.Scene
    );
    light.parent = root;
    light.diffuse = CommonMaterials.getColor3(this.ColorIndex);
    light.intensity = this.Intensity;
    light.range = this.Range;
    this.initShadowGenerator(light);
    this.createHelper(root);

    return root;
  }
  updateDrawObject(type, obj: Mesh) {
    const nodes = obj.getChildren();
    const light = nodes[0] as SpotLight2;
    light.intensity = this.Intensity;
    light.range = this.Range;
    light.direction.copyFrom(this.BDirection);
    light.diffuse = CommonMaterials.getColor3fromHex(this.Diffuse);
    light.specular = CommonMaterials.getColor3fromHex(this.Specular);
    light.angle = this.Angle;

    this.updateHelper(nodes[1] as TransformNode);
  }

  getDragPoints() {
    const obj = this.RenderObject;
    const helper = obj.getChildren()[1];

    return this.Points.map((p) => {
      let currentPoint = Vector3.TransformCoordinates(
        p,
        helper.getWorldMatrix()
      );
      this.points.push(currentPoint);
      return currentPoint;
    });
  }
  moveDragPoint(vec: Vector3, index: number) {
    const p = this.points[index];
    p.addToRef(vec, p);
    if (index === 0) {
      this.Direction = vec2Vec3Like(p.subtract(this._position).normalize());
    } else {
      this.Range = Vector3.Distance(p, this._position);
    }
  }

  writeFile(file: HCFiler) {
    super.writeFile(file);
    file.write(1);
    file.write(this.Range);
    file.writeVector3(this.Direction);
    file.write(this.Angle);
  }
  readFile(file: HCFiler) {
    super.readFile(file);
    const ver = file.read();
    this.Range = file.read();
    file.readVector3(this.Direction);
    this.Angle = file.read();
    this.update();
  }
  destory() {
    super.destory();
    this.gizmo && this.gizmo.dispose();
  }
  protected getHelper() {
    const root = new TransformNode("root");

    const objects: Mesh[] = [];
    const color = Color3.Green();

    let mainLine = MeshBuilder.CreateLines(
      "lines",
      {
        points: [new Vector3(), new Vector3()],
        updatable: true,
      },
      Entity.Scene
    );
    mainLine.color = color;
    mainLine.isPickable = false;

    // 创建一个箭头
    const arrow = MeshBuilder.CreateSphere(
      "light_helper_range",
      {
        diameter: 0.5,
      },
      Entity.Scene
    );
    let utilLayer = new UtilityLayerRenderer(Entity.Scene);
    let gizmo = new AxisDragGizmo(
      new Vector3(0, 0, 1),
      CommonMaterials.getColor3(2),
      utilLayer
    );

    this.gizmo = gizmo;
    gizmo.updateGizmoPositionToMatchAttachedMesh = true;
    const dragBehavior = gizmo.dragBehavior;
    dragBehavior.dragDeltaRatio = 1;
    dragBehavior.onDragObservable.add((e) => {
      let range = Vector3.Distance(e.dragPlanePoint, this._position);

      range = range > HELPER_STEP ? HELPER_STEP : range;

      this.Height = HELPER_STEP - range;
      let angle = (range / HELPER_STEP) * 180;
      this.Angle = angle * (Math.PI / 180); //角度转弧度
    });
    gizmo.attachedMesh = arrow;

    // 创建圆锥
    const circle = MeshBuilder.CreateLines(
      "lines",
      {
        points: Array.from({ length: STEP + 1 }, () => new Vector3()),
        updatable: true,
      },
      Entity.Scene
    );
    circle.color = color;
    // 创建圆锥的线
    let arrowLine1 = MeshBuilder.CreateLines(
      "lines",
      {
        points: [new Vector3(), new Vector3(), new Vector3()],
        updatable: true,
      },
      Entity.Scene
    );
    arrowLine1.color = color;
    arrowLine1.isPickable = false;

    // 创建圆锥的线
    let arrowLine2 = MeshBuilder.CreateLines(
      "lines",
      {
        points: [new Vector3(), new Vector3(), new Vector3()],
        updatable: true,
      },
      Entity.Scene
    );
    arrowLine2.color = color;
    arrowLine2.isPickable = false;

    objects.push(mainLine, arrow, circle, arrowLine1, arrowLine2);

    objects.forEach((m) => {
      m.parent = root;
    });

    return root;
  }

  protected updateHelper(obj: TransformNode) {
    if (!obj) return;
    obj.setEnabled(this.VisiableHelper);

    const nodes = obj.getChildren() as Mesh[];

    const lines = nodes[0] as LinesMesh;
    MeshBuilder.CreateLines("lines", {
      points: [new Vector3(), new Vector3(0, 0, this.Range)],
      instance: lines,
    });

    //圆锥
    const rangeSphere = nodes[1] as Mesh;
    rangeSphere.position.z = this.Height / 2;

    //圆环
    const rangelines = nodes[2] as LinesMesh;
    rangelines.position.z = this.Height;
    const radius = this.Radius;

    // this.Range * Math.sin(this.Angle / 2);
    const circle = Curve3.ArcThru3Points(
      new Vector3(radius),
      new Vector3(0, radius),
      new Vector3(-radius),
      STEP,
      true,
      true
    );
    MeshBuilder.CreateLines(null, {
      points: circle.getPoints(),
      instance: rangelines,
    });

    //圆锥线1
    const lines3 = nodes[3] as LinesMesh;
    MeshBuilder.CreateLines(null, {
      instance: lines3,
      points: [
        new Vector3(-radius, 0, this.Height),
        new Vector3(),
        new Vector3(radius, 0, this.Height),
      ],
      updatable: true,
    });
    //圆锥线2
    const lines4 = nodes[4] as LinesMesh;
    MeshBuilder.CreateLines(null, {
      instance: lines4,
      points: [
        new Vector3(0, radius, this.Height),
        new Vector3(),
        new Vector3(0, -radius, this.Height),
      ],
      updatable: true,
    });
    obj.lookAt(this.BDirection);
  }
}
