import { IVector3, ObjectId } from "@hc/core";

import { Animation } from "../babylon";
import { Curve } from "../entitys";
import { Entity } from "../entitys/Entity";
import type { Lines } from "../entitys/curves/Lines";

export interface IAnimationOption {
  name?: string;
  loop?: boolean;
  loopCount?: number;
  time?: number; //秒
  frame?: number;
  meshId?: string;
  speed?: number;
  complete?: () => void;
}

export interface IMovePathOption extends IAnimationOption {
  path: IVector3[] | Lines;
}

export type Vec3Array = [number, number, number];

export interface IRotationOption extends IAnimationOption {
  from?: number;
  angle: number;
  angles: number[];
  origin?: IVector3;
  dir?: IVector3;
}
export interface IScaleOption extends IAnimationOption {
  scale?: IVector3;
}
export interface IPathOption extends IAnimationOption {
  path?: IVector3[];
  curve?: ObjectId<Curve>;
  forward?: IVector3;
}

export interface IAnimateReturn {
  restart: () => void;
  stop: () => void;
  pause: () => void;
  reset: () => void;
  clear: () => void;
}
export interface IAnimation {
  name: string;
  defaultStart?: boolean;
  start?: (loop?: boolean, speed?: number) => void;
  restart: () => void;
  stop: () => void;
  pause: () => void;
  reset: () => void;
  clear?: () => void;
}

export interface ICameraAnimateOption extends IAnimationOption {
  target: IVector3 | Entity;
  position?: IVector3;
}

export interface ICameraRotationAnimation extends IAnimationOption {
  angleX?: number;
  angleY?: number;
  target?: IVector3;
}

export enum EDataType {
  FLOAT = Animation.ANIMATIONTYPE_FLOAT,
  COLOR = Animation.ANIMATIONTYPE_COLOR3,
  COLOR4 = Animation.ANIMATIONTYPE_COLOR4,
}

export interface IMaterialAnimationOption extends IAnimationOption {
  targetKey: string;
  dataType: EDataType;
  range: [number, number] | [string, string];
  defaultStart?: boolean;
}
