export function arrayLast<T>(arr: T[]): T {
  return arr[arr.length - 1];
}

export function removeOne<T>(arr: T[], v: T) {
  let j = 0;
  for (let i = 0, l = arr.length; i < l; i++) {
    if (arr[i] !== v) {
      arr[j++] = arr[i];
    }
  }
  arr.length = j;

  return arr;
}

export function arrayRemoveIf<T>(
  arr: Array<T>,
  checkFuntion: (e: T) => boolean
): Array<T> {
  let j = 0;
  for (let i = 0, l = arr.length; i < l; i++) {
    if (!checkFuntion(arr[i])) {
      arr[j++] = arr[i];
    }
  }
  arr.length = j;

  return arr;
}
