import { Entity, StartAnimateOptions } from "./../entitys/Entity";
import { Mesh, TransformNode, Vector3 } from "../babylon";
import { RenderType } from "../types/renderTypes";
import { HCFiler, Factory } from "@hc/core";
import { ComponentSchema } from "../types/schema.types";
import { Box3 } from "../utils/Box3";
import { EventEnum } from "../enums";

export interface Component {
  constructor: typeof Component;
}

/**
 * 自定义组件基类
 * - 子类实现 `getData` `initComponents` `play` 方法
 * @export
 * @class Component
 * @extends {Entity}
 */
@Factory
export class Component extends Entity {
  static type = "Component";
  static readonly component = true;
  static Schema: ComponentSchema;
  data: Record<string, any> = {};
  entitys: Entity[];
  constructor() {
    super();
    this.initData();
    this.entitys = this.initComponents();
  }
  /**
   *
   * @returns 组件自定义属性
   */
  getData() {
    return {};
  }
  get Schema() {
    return this.constructor.Schema;
  }
  get IsComponent() {
    return true;
  }
  get AnimationNames() {
    const as = super.AnimationNames;
    for (const a of this.entitys) {
      as.push(...a.AnimationNames);
    }
    return as;
  }
  get BoundingBox() {
    const { min, max } = this.BoundingVectors;
    return new Box3(min, max);
  }
  startAnimates(name: string, option?: StartAnimateOptions) {
    super.startAnimates(name, option);
    for (const e of this.entitys) {
      e.startAnimates(name, option);
    }
  }
  stopAnimates(names?: string[]) {
    super.stopAnimates(names);
    for (const e of this.entitys) {
      e.stopAnimates(names);
    }
  }
  initData(data?: Record<string, any>) {
    const self = this;
    this.data = new Proxy(data ?? this.getData(), {
      get(target, prop: string) {
        return target[prop];
      },
      set(obj: Record<string, unknown>, prop: string, newval) {
        let needUpdate = obj.hasOwnProperty(prop) && newval !== obj[prop];
        self.writeSnapshoot();
        obj[prop] = newval;
        if (needUpdate) {
          self.play();
          self.update();
          self.trigger({ type: "change", target: this, key: prop });
        }
        // 表示操作成功
        return true;
      },
    });
  }
  /**
   * 初始化组件构成
   */
  initComponents(): Entity[] {
    return [];
  }
  initDrawObject(renderType: RenderType) {
    const node = new Mesh(this.UniqueName, Entity.Scene);
    this.updateDrawObject(renderType, node);
    this.watchLoad()
      .then((r) => {
        this.play();
        this.mounted();
      })
      .catch((err) => {
        console.error("err: ", err);
        this.componentCatch(err);
      })
      .finally(() => {
        this.trigger2({ type: EventEnum.Load, target: this });
      });
    return node;
  }
  updateDrawObject(renderType: RenderType, obj: Mesh) {
    for (let e of this.entitys) {
      e.isPart = true;
      let o = e.GetDrawObjectFromRenderType(renderType);
      if (o) {
        e.updatePart();
        o.parent = obj;
      }
    }
  }
  updateMaterial() {
    this.entitys.forEach((e) => e.updateMaterial());
  }
  /**组件执行动作 */
  play() {
    //子类重载
  }
  /**组件加载完毕后执行 */
  mounted() {}
  componentCatch(err) {}
  /**回收组件 */
  destory(): void {
    super.destory();
    for (const en of this.entitys) {
      en.destory();
    }
  }
  writeFile(filer: HCFiler) {
    super.writeFile(filer);
    filer.write(1);
    filer.write(JSON.stringify({ ...this.data }));
    filer.write(this.entitys.length);
    for (const en of this.entitys) {
      filer.writeObject(en);
    }
  }
  readFile(filer: HCFiler) {
    super.readFile(filer);
    filer.read();
    const dataJSON = filer.read();
    this.initData(JSON.parse(dataJSON));
    const count = filer.read();
    for (const e of this.entitys) {
      e.destory();
    }
    this.entitys.length = 0;

    for (let i = 0; i < count; i++) {
      const e = filer.readObject() as Entity;
      this.entitys.push(e);
    }
    this.update();
    this.watchLoad().then((r) => {
      this.play();
    });
  }
  /**监听组件加载完成 */
  watchLoad() {
    const list = [];
    for (const en of this.entitys) {
      if (en.isAsync) {
        list.push(
          new Promise((res) => {
            en.on("load", () => res(true));
          })
        );
      }
    }
    return Promise.all(list);
  }
  getReady(){
    return this.watchLoad()
  }
}
