import {
  ArcRotateCamera,
  Color3,
  CreateCylinder,
  Engine,
  FreeCamera,
  Frustum,
  Observer,
  Plane,
  Scene,
  StandardMaterial,
  TargetCamera,
  TransformNode,
  Vector3,
  Viewport,
  AdvancedDynamicTexture,
  Ellipse,
} from "../../babylon";
import { CameraManager } from "../cameras";

import { getViewHelperAxes } from "../ViewHelperAxes";

export class SceneHelper {
  private _scene: Scene;
  private _cameraHeight = 3.2;
  private ellipses: Ellipse[] = [];
  private FreeObserver: Observer<TargetCamera>;
  stopRender = false;
  constructor(
    private engine: Engine,
    originScene: Scene,
    private cameraManger: CameraManager
  ) {
    this.init();
    this.initViewHelper();
    const frustumPlanes = Frustum.GetPlanes(this._scene.getTransformMatrix());
    this._scene.registerBeforeRender(() => {
      this.ellipses.forEach((item) => {
        if (
          item.linkedMesh &&
          this.isInFrustum(frustumPlanes, item.linkedMesh.position)
        ) {
          this.changeZIndex(
            item,
            item.linkedMesh,
            Math.round(
              1000 /
                originScene.activeCamera.position
                  .subtract(item.linkedMesh.position)
                  .length()
            )
          );
        }
      });
    });

    originScene.onActiveCameraChanged.add((s) => {
      if (s.activeCamera instanceof FreeCamera && !this.FreeObserver) {
        this.FreeObserver =
          originScene.activeCamera.onViewMatrixChangedObservable.add(
            this.onFreeChange
          );
      }
    });

    if (
      this._scene.activeCamera instanceof ArcRotateCamera &&
      originScene.activeCamera instanceof ArcRotateCamera
    ) {
      this._scene.activeCamera.alpha = originScene.activeCamera.alpha;
      this._scene.activeCamera.beta = originScene.activeCamera.beta;
    }

    originScene.activeCamera.onViewMatrixChangedObservable.add(
      this.onArcRotateChange
    );
  }
  get Scene() {
    return this._scene;
  }
  public render() {
    if (this._scene.activeCamera && !this.stopRender) {
      this._scene.render();
    }
  }
  private init() {
    const scene = new Scene(this.engine);
    scene.autoClear = false;
    scene.ambientColor = Color3.White();

    scene.createDefaultCamera(true, true, true);
    scene.useRightHandedSystem = true;
    const activeCamera = scene.activeCamera as ArcRotateCamera;

    activeCamera.viewport = new Viewport(0.85, 0.85, 0.15, 0.15);

    activeCamera.detachControl();
    activeCamera.target = Vector3.Zero();
    activeCamera.setPosition(new Vector3(0, 0, this._cameraHeight));

    this._scene = scene;
  }
  private initViewHelper() {
    const height = 1;
    const option = { diameter: 0.05, height, tessellation: 96 };

    const root = new TransformNode("viewerHelper", this._scene);

    const Red = "#ff3653";
    const Green = "#8adb00";
    const Blue = "#2c8fff";

    //x-line
    const xl = CreateCylinder("x-line", option, this._scene);

    xl.position.x += height / 2;
    xl.rotation.z = Math.PI / 2;

    xl.parent = root;

    const xMlt = new StandardMaterial("", this._scene);
    xMlt.ambientColor = Color3.FromHexString(Red);
    xl.material = xMlt;

    const yl = CreateCylinder("y-line", option, this._scene);

    const yMlt = new StandardMaterial("", this._scene);
    yMlt.ambientColor = Color3.FromHexString(Green);
    yl.material = yMlt;
    yl.position.y += height / 2;

    const zl = CreateCylinder("z-line", option, this._scene);

    const zMlt = new StandardMaterial("", this._scene);
    zMlt.ambientColor = Color3.FromHexString(Blue);
    zl.material = zMlt;

    zl.rotation.x = Math.PI / 2;
    zl.position.z += height / 2;

    const advancedTexture = AdvancedDynamicTexture.CreateFullscreenUI(
      "UI",
      true,
      this._scene
    );

    const opt = {
      height,
      cameraHeight: this._cameraHeight,
      scene: this._scene,
      cm: this.cameraManger,
    };

    this.ellipses.push(
      getViewHelperAxes(
        {
          ...opt,
          dir: "x",
          color: Red,
        },
        advancedTexture
      ),
      getViewHelperAxes(
        {
          ...opt,
          dir: "x",
          color: Red,
          reverse: true,
        },
        advancedTexture
      ),
      getViewHelperAxes(
        {
          ...opt,
          dir: "y",
          color: Green,
        },
        advancedTexture
      ),
      getViewHelperAxes(
        {
          ...opt,
          dir: "y",
          color: Green,
          reverse: true,
        },
        advancedTexture
      ),
      getViewHelperAxes(
        {
          ...opt,
          dir: "z",
          color: Blue,
        },
        advancedTexture
      ),
      getViewHelperAxes(
        {
          ...opt,
          dir: "z",
          color: Blue,
          reverse: true,
        },
        advancedTexture
      )
    );
  }
  private changeZIndex(label: Ellipse, mesh: TransformNode, zIndex: number) {
    label.zIndex = zIndex;
    // Need to relink mesh
    label.linkWithMesh(mesh);
  }
  private isInFrustum(frustumPlanes: Plane[], center: Vector3): boolean {
    for (let i = 0; i < 6; i++) {
      if (frustumPlanes[i].dotCoordinate(center) < 0) {
        return false;
      }
    }
    return true;
  }
  private onArcRotateChange = (c: ArcRotateCamera) => {
    const activeCamera = this.Scene.activeCamera;
    if (activeCamera instanceof ArcRotateCamera) {
      activeCamera.alpha = c.alpha;
      activeCamera.beta = c.beta;
    }
  };
  private onFreeChange = (c: FreeCamera) => {
    const activeCamera = this.Scene.activeCamera;
    if (activeCamera instanceof ArcRotateCamera) {
      const vec = c.position
        .subtract(c.target)
        .normalize()
        .scale(this._cameraHeight);
      activeCamera.position = vec;
    }
  };
}
