import { HCFiler, ISerialize } from "@hc/core";
import { Vector3 } from "../../babylon";

export type IDoorOption = {
  left?: number;
  width?: number;
  height?: number;
};

export class Door implements ISerialize {
  static type = "Door";
  public left = 0;
  public width = 1;
  public height = 1;
  public position = Vector3.Zero();
  public url = "";
  constructor(option?: IDoorOption) {
    this.left = option?.left ?? 0;
    this.width = option?.width ?? 1;
    this.height = option?.height ?? 1;
  }
  fromJSON(option?: IDoorOption) {
    this.left = option?.left ?? this.left;
    this.width = option?.width ?? this.width;
    this.height = option?.height ?? this.height;
  }
  writeFile(file: HCFiler): void {
    file.write(1);
    file.write(this.left);
    file.write(this.width);
    file.write(this.height);
  }
  readFile(file: HCFiler): void {
    file.read();
    this.left = file.read();
    this.width = file.read();
    this.height = file.read();
  }
}
