export function ImportScript(urls: string[]): Promise<boolean[]> {
  let ps = [];
  urls.forEach((url) => {
    const script = document.createElement("script");
    script.src = url;
    document.body.append(script);
    let p = new Promise((res, rej) => {
      script.onload = function () {
        res(true);
      };
      script.onerror = () => {
        rej(false);
      };
    });
    ps.push(p);
  });
  return Promise.all(ps);
}
