import {
  ArcRotateCamera,
  FreeCamera,
  Scene,
  TransformNode,
  Vector3,
} from "../babylon";
import { Ellipse, TextBlock, AdvancedDynamicTexture } from "../babylon";
import { CameraManager } from "./cameras";

type ViewHelperAxes = {
  color: string;
  height: number;
  dir: "x" | "y" | "z";
  cameraHeight: number;
  scene: Scene;
  cm: CameraManager;
  reverse?: boolean;
};

export function getViewHelperAxes(
  {
    color,
    dir,
    height,
    cameraHeight,
    scene,
    cm,
    reverse = false,
  }: ViewHelperAxes,
  advancedTexture: AdvancedDynamicTexture
) {
  const mark = reverse ? -1 : 1;
  const labelRoot = new TransformNode(
    `${reverse ? "-" : ""}${dir}-label`,
    scene
  );
  labelRoot.position[dir] = height * mark;

  const axesLabel = new Ellipse();
  axesLabel.isPointerBlocker = true;
  axesLabel.width = "120px";
  axesLabel.height = "120px";
  axesLabel.thickness = 4;
  axesLabel.color = color;
  axesLabel.background = color;

  const label = new TextBlock(dir, (reverse ? "-" : "") + dir.toUpperCase());
  label.fontSize = 70;
  label.fontWeight = "400";
  label.color = "#000000";
  axesLabel.addControl(label);

  const activeCamera = scene.activeCamera as ArcRotateCamera;

  axesLabel.onPointerClickObservable.add((info) => {
    activeCamera.restoreState();
    const vec = new Vector3();
    const vec2 = new Vector3();

    vec2[dir] = cameraHeight * mark;

    if (cm.Camera instanceof ArcRotateCamera) {
      const camera = cm.Camera;
      const dist = camera.radius;
      vec[dir] = dist * mark;
      cm.Position = camera.target.add(vec);
    } else if (cm.Camera instanceof FreeCamera) {
      const dist = Vector3.Distance(cm.Camera.target, cm.Camera.position);
      vec[dir] = dist * mark;
      cm.Target = cm.Camera.position.add(vec);
    }
    activeCamera.setPosition(vec2);
  });
  axesLabel.onPointerEnterObservable.add(() => {
    label.color = "#ffffff";
  });
  axesLabel.onPointerOutObservable.add(() => {
    label.color = "#000000";
  });

  advancedTexture.addControl(axesLabel);
  axesLabel.linkWithMesh(labelRoot);

  return axesLabel;
}
