import { Utils } from "@hc/engine";

export function sleep(t: number) {
  return new Promise((res) => setTimeout(res, t));
}

export function equaln(v1: number, v2: number, fuzz = 1e-5) {
  return Math.abs(v1 - v2) <= fuzz;
}

//使用定点表示法来格式化一个数,小数点后面不尾随0. 如 FixedNotZero(1.1 , 3) => 1.1
export function fixedNotZero(v: number | string, fractionDigits = 0) {
  if (typeof v === "string") v = parseFloat(v);
  if (isNaN(v)) return "";
  if (equaln(v, 0)) return "0";
  let str = v.toFixed(fractionDigits);
  let commonIndex = str.indexOf(".");
  if (commonIndex !== -1) {
    let zeroCount = 0;
    let strCount = str.length;
    for (let l = strCount; l--; ) {
      if (str[l] === "0") zeroCount++;
      else break;
    }
    if (zeroCount > 0) {
      if (zeroCount === strCount - commonIndex - 1) zeroCount++;
      return str.slice(0, strCount - zeroCount);
    }
  }
  return str;
}

export function formatDate(value: string, fmt: string) {
  let regPos = /^\d+(\.\d+)?$/;
  if (regPos.test(value)) {
    //如果是数字
    let getDate = new Date(value);
    let o = {
      "M+": getDate.getMonth() + 1,
      "d+": getDate.getDate(),
      "h+": getDate.getHours(),
      "m+": getDate.getMinutes(),
      "s+": getDate.getSeconds(),
      "q+": Math.floor((getDate.getMonth() + 3) / 3),
      S: getDate.getMilliseconds(),
    };
    if (/(y+)/.test(fmt)) {
      fmt = fmt.replace(
        RegExp.$1,
        (getDate.getFullYear() + "").substr(4 - RegExp.$1.length)
      );
    }
    for (let k in o) {
      if (new RegExp("(" + k + ")").test(fmt)) {
        fmt = fmt.replace(
          RegExp.$1,
          RegExp.$1.length === 1
            ? o[k]
            : ("00" + o[k]).substr(("" + o[k]).length)
        );
      }
    }
    return fmt;
  } else {
    //TODO
    value = value.trim();
    return value.substr(0, fmt.length);
  }
}

let copyValue: string | undefined;

export async function copyText(content: string) {
  if (navigator.clipboard) {
    return navigator.clipboard.writeText(content);
  } else {
    const aux = document.createElement("input");
    aux.style.position = "fixed";
    // aux.style.display = "none";
    document.body.appendChild(aux);
    aux.setAttribute("value", content);
    aux.value = content;
    setTimeout(() => {
      aux.select();
      document.execCommand("copy");
      document.body.removeChild(aux);
    }, 0);
    copyValue = content;
    return true;
  }
}

let resolveFun: (txt: string) => void;

document.addEventListener("paste", function (e) {
  if (resolveFun) {
    resolveFun(e.clipboardData?.getData("text/plain") || "");
  }
});

export async function getCopyText() {
  if (navigator.clipboard) {
    return navigator.clipboard.readText();
  } else if (copyValue) {
    const aux = document.getElementById("domClipboard");
    return new Promise<string>((res) => {
      res(copyValue || "");
    });
  } else {
    return new Promise<string>((res) => {
      resolveFun = res;
    });
  }
}

export const getRgba = (c: string) => {
  const color = Utils.Color4.FromHexString(c);
  return { r: color.r * 255, g: color.g * 255, b: color.b * 255, a: color.a };
};

