import { HCFiler, ObjectId, ObjectRecord } from "@hc/core";

import { Material } from "../babylon";
import type { Entity } from "../entitys";
import { firstUpper } from "../utils/utils";
import { TextureRecord } from "./TextureRecord";
import { autoRecordMaterial } from "./autoRecord";

// 新建实体默认的材质
// 导入实体 自带材质
// 用户自定义的材质库
// 用户可以修改自带的材质
// 可以用材质库的材质替换现有的材质
// 材质可快速定位到网格 mesh.showBoundingBox=true
// 保存已有材质修改的值

export class MaterialRecord<
  T extends Material = Material
> extends ObjectRecord {
  @autoRecordMaterial
  BackFaceCulling = false;
  isOwn = false;
  linkEntitys = new Set<Entity>();
  protected material: T;
  protected init = false;
  constructor(mat?: T, protected entity?: Entity) {
    super();
    this.init = true;
    this.material = mat;
    if (mat) {
      this._name = mat.name;
    }
    this.isOwn = !!mat;
  }
  get Material() {
    return this.material;
  }
  get AllTextures(): TextureRecord[] {
    return [];
  }
  get Name() {
    return this._name;
  }
  set Name(v) {
    this._name = v;
    if (this.material) {
      this.material.name = v;
    }
  }
  updateFromDiff() {
    const diff = this.entity.diffMaterials.get(this.material.id);
    if (diff) {
      for (const k in diff) {
        if (k in this) {
          if (k.includes("Texture")) {
            if (typeof diff[k] === "number")
              this[k] = this.entity.DB.getObjectId(diff[k]);
            else {
              this[k] = diff[k];
            }
          } else this[k] = diff[k];
        } else {
          if (k.endsWith("Texture")) {
            const textureID = this[firstUpper(k)] as ObjectId<TextureRecord>;
            if (textureID.Object && diff[k]) {
              for (const k1 in diff[k]) {
                textureID.Object.Texture[k1] = diff[k][k1];
              }
              textureID.Object.readFromTexture();
            }
          }
        }
      }
      this.update();
    }
  }
  updateMaterial() {
    this.material.backFaceCulling = this.BackFaceCulling;
  }
  update() {
    this.updateMaterial();
  }
  async waitRender() {
    return true;
  }
  initByOwnMaterial(mtl: Material) {
    for (let k in this) {
      const recordKey = k.slice(1);
      const propery = firstUpper(recordKey);
      if (k.startsWith("_") && propery in this) {
        this[propery] = mtl[recordKey];
      }
    }
  }
  readFile(filer: HCFiler) {
    super.readFile(filer);
    filer.read();
    this.BackFaceCulling = filer.read();
  }
  writeFile(filer: HCFiler) {
    super.writeFile(filer);
    filer.write(1);
    filer.write(this.BackFaceCulling);
  }
  showMeshBox(status?: boolean) {
    const meshes = this.material.getBindedMeshes();
    meshes.forEach((m) => (m.showBoundingBox = status ?? !m.showBoundingBox));
  }
  destory() {
    super.destory();
    if (this.material) {
      this.material.dispose();
    }
  }
  protected importOwnTextures() {}
}
