import { CreatePlaneVertexData, VertexData } from "../babylon";
import { Factory, HCFiler } from "@hc/core";
import { Solid3D } from "./Solid3D";

@Factory
export class Plane extends Solid3D {
  static type = "Plane";
  protected _name = "平面";
  private width = 10;
  private height = 10;
  constructor(option?: { width: number; height: number }) {
    super();
    this.width = option?.width ?? this.width;
    this.height = option?.height ?? this.height;
  }
  get Width() {
    return this.width;
  }
  set Width(v: number) {
    if (v === this.width) return;
    this.writeSnapshoot();
    this.width = v;
    this.update();
  }
  get Height() {
    return this.height;
  }
  set Height(v: number) {
    if (v === this.height) return;
    this.writeSnapshoot();
    this.height = v;
    this.update();
  }
  get VertextData(): VertexData {
    return CreatePlaneVertexData({
      width: this.width,
      height: this.height,
    });
  }
  writeFile(file: HCFiler) {
    super.writeFile(file);
    file.write(1);
    file.write(this.width);
    file.write(this.height);
  }
  readFile(file: HCFiler) {
    super.readFile(file);
    const ver = file.read();
    this.width = file.read();
    this.height = file.read();
    this.update();
  }
}
