import { BaseObject } from "../BaseObject";
import { HCFiler } from "../filer/HCFiler";
import { ISerialize } from "../types/iSerialize.types";

export class AllObjectData implements ISerialize {
  filer = new HCFiler();
  constructor(obj?: BaseObject) {
    if (obj) {
      this.filer.database = obj.DB;
      obj.writeFile(this.filer);
    }
  }
  get Filer() {
    const file = new HCFiler(this.filer.Data);
    file.database = this.filer.database;
    return file;
  }
  readFile(file: HCFiler) {
    file.read();
    this.filer.Data = file.read();
    this.filer.reset();
  }
  writeFile(file: HCFiler) {
    file.write(1);
    file.write(this.filer.Data);
  }
}
