import {
  Camera,
  Color4,
  DefaultRenderingPipeline,
  Scene,
  Tools,
} from "../../babylon";
import { enableUpdateMaterialDirtyMechanismOnce } from "../../utils/babylonUtils";
import type { SceneControl } from "./Scene";
import { ImageProcessingConfiguration } from "@babylonjs/core/Materials/imageProcessingConfiguration";
import { DepthOfFieldEffectBlurLevel } from "@babylonjs/core/PostProcesses/depthOfFieldEffect";
import { HCFiler, ISerialize } from "@hc/core";

export class SceneEffectManager implements ISerialize {
  #pipeline: DefaultRenderingPipeline;
  #bloomEnabled = false;
  #bloomThreshold = 0.9;
  #bloomWeight = 0.15;
  #bloomKernel = 64;
  #bloomScale = 1;
  #depthOfFieldEnabled = false;
  #depthOfFieldBlurLevel = DepthOfFieldEffectBlurLevel.Low;
  #focusDistance = 2000;
  #focalLength = 50;
  #fStop = 1.4;
  #lensSize = 50;
  #vignetteEnabled = false;
  #vignetteBlendMode = ImageProcessingConfiguration.VIGNETTEMODE_MULTIPLY;
  #vignetteColor = new Color4(0, 0, 0, 0);
  #vignetteWeight = 1.5;
  #imageProcessingEnabled = true;
  #contrast = 1;
  #exposure = 1;
  #sharpenEnabled = false;
  #edgeAmount = 1;
  #colorAmount = 0.3;
  constructor(private scene: Scene, private camera: Camera) {
    this.init();
  }
  get Pipeline() {
    return this.#pipeline;
  }
  /** 开启泛光 */
  get EnableBloom() {
    return this.#bloomEnabled;
  }
  set EnableBloom(v: boolean) {
    this.#bloomEnabled = v;
    this.update();
  }
  /**
   * 阀值,如果场景中物体的亮度超过这个阀值才会发生高光溢出
   *
   * @memberof SceneEffectManager
   */
  get BloomThreshold() {
    return this.#bloomThreshold;
  }
  set BloomThreshold(v: number) {
    this.#bloomThreshold = v;
    this.update();
  }
  /**
   * 发生高光溢出的强度(0~1)
   *
   * @memberof SceneEffectManager
   */
  get BloomWeight() {
    return this.#bloomWeight;
  }
  set BloomWeight(v: number) {
    this.#bloomWeight = v;
    this.update();
  }
  get BloomKernel() {
    return this.#bloomKernel;
  }
  /**
   * 光模糊扩散的范围(1~500)
   *
   * @memberof SceneEffectManager
   */
  set BloomKernel(v: number) {
    this.#bloomKernel = v;
    this.update();
  }
  get BloomScale() {
    return this.#bloomScale;
  }
  /**泛光规模,越低性能越好(0~1) */
  set BloomScale(v: number) {
    if (!v) {
      Tools.Error("值必须大于1");
      return;
    }
    this.#bloomScale = v;
    this.update();
  }
  //-----------------景深-----------------------
  /**开启景深 */
  get DepthOfFieldEnabled() {
    return this.#depthOfFieldEnabled;
  }
  set DepthOfFieldEnabled(v: boolean) {
    this.#depthOfFieldEnabled = v;
    this.update();
  }
  /**
   * 模糊等级,越高性能越低
   *
   * @memberof SceneEffectManager
   */
  get DepthOfFieldBlurLevel() {
    return this.#depthOfFieldBlurLevel;
  }
  set DepthOfFieldBlurLevel(v: DepthOfFieldEffectBlurLevel) {
    this.#depthOfFieldBlurLevel = v;
    this.update();
  }
  /**焦距 */
  get FocusDistance() {
    return this.#focusDistance;
  }
  set FocusDistance(v: number) {
    this.#focusDistance = v;
    this.update();
  }
  /**相机焦距 */
  get FocalLength() {
    return this.#focalLength;
  }
  set FocalLength(v: number) {
    this.#focalLength = v;
    this.update();
  }
  /**光圈測量值 */
  get FStop() {
    return this.#fStop;
  }
  set FStop(v: number) {
    this.#fStop = v;
    this.update();
  }
  /**
   *镜头尺寸
   *
   * @memberof SceneEffectManager
   */
  get LensSize() {
    return this.#lensSize;
  }
  set LensSize(v: number) {
    this.#lensSize = v;
    this.update();
  }
  //--------------晕光-----------------

  get VignetteEnabled() {
    return this.#vignetteEnabled;
  }
  set VignetteEnabled(v: boolean) {
    this.#vignetteEnabled = v;
    this.update();
  }
  get VignetteBlendMode() {
    return this.#vignetteBlendMode as 0 | 1;
  }
  set VignetteBlendMode(v: 0 | 1) {
    this.#vignetteBlendMode = v;
    this.update();
  }
  get VignetteColor() {
    return this.#vignetteColor.toHexString();
  }
  set VignetteColor(v: string) {
    this.#vignetteColor = Color4.FromHexString(v);
    this.update();
  }
  get VignetteWeight() {
    return this.#vignetteWeight;
  }
  set VignetteWeight(v: number) {
    this.#vignetteWeight = v;
    this.update();
  }
  //---------------图像处理--------------------------
  get ImageProcessEnable() {
    return this.#imageProcessingEnabled;
  }
  set ImageProcessEnable(v: boolean) {
    this.#imageProcessingEnabled = v;
    this.update();
  }
  /**
   * 对比度
   *
   * @memberof SceneEffectManager
   */
  get Contrast() {
    return this.#contrast;
  }
  set Contrast(v: number) {
    this.#contrast = v;
    this.update();
  }
  /**
   * 曝光度
   *
   * @memberof SceneEffectManager
   */
  get Exposure() {
    return this.#exposure;
  }
  set Exposure(v: number) {
    this.#exposure = v;
    this.update();
  }
  //---------------锐化-------------------
  get SharpenEnabled() {
    return this.#sharpenEnabled;
  }
  set SharpenEnabled(v: boolean) {
    this.#sharpenEnabled = v;
    this.update();
  }
  /**
   * 边缘强度
   *
   * @memberof SceneEffectManager
   */
  get EdgeAmount() {
    return this.#edgeAmount;
  }
  set EdgeAmount(v: number) {
    this.#edgeAmount = v;
    this.update();
  }
  /**
   * 颜色强度
   *
   * @memberof SceneEffectManager
   */
  get ColorAmount() {
    return this.#colorAmount;
  }
  set ColorAmount(v: number) {
    this.#colorAmount = v;
    this.update();
  }
  init() {
    this.#pipeline = new DefaultRenderingPipeline(
      "默认渲染管线",
      false,
      this.scene,
      [this.camera]
    );
  }
  update() {
    if (this.#pipeline) {
      enableUpdateMaterialDirtyMechanismOnce(this.scene);
      this.#pipeline.bloomEnabled = this.#bloomEnabled;
      this.#pipeline.bloomThreshold = this.#bloomThreshold;
      this.#pipeline.bloomWeight = this.#bloomWeight;
      this.#pipeline.bloomKernel = this.#bloomKernel;
      this.#pipeline.bloomScale = this.#bloomScale;
      this.#pipeline.depthOfFieldEnabled = this.#depthOfFieldEnabled;
      this.#pipeline.depthOfFieldBlurLevel = this.#depthOfFieldBlurLevel;

      this.#pipeline.imageProcessingEnabled = this.#imageProcessingEnabled;
      this.#pipeline.sharpenEnabled = this.#sharpenEnabled;

      if (this.#pipeline.sharpen) {
        this.#pipeline.sharpen.edgeAmount = this.#edgeAmount;
        this.#pipeline.sharpen.colorAmount = this.#colorAmount;
      }

      if (this.#pipeline.depthOfField) {
        this.#pipeline.depthOfField.focusDistance = this.#focusDistance;
        this.#pipeline.depthOfField.focalLength = this.#focalLength;
        this.#pipeline.depthOfField.fStop = this.#fStop;
        this.#pipeline.depthOfField.lensSize = this.#lensSize;
      }

      const updateImageProcessing = () => {
        if (this.#pipeline.imageProcessing) {
          enableUpdateMaterialDirtyMechanismOnce(this.scene);
          this.#pipeline.imageProcessing.contrast = this.#contrast;
          this.#pipeline.imageProcessing.exposure = this.#exposure;

          this.#pipeline.imageProcessing.vignetteEnabled =
            this.#vignetteEnabled;
          this.#pipeline.imageProcessing.vignetteBlendMode =
            this.#vignetteBlendMode;
          this.#pipeline.imageProcessing.vignetteColor = this.#vignetteColor;
          this.#pipeline.imageProcessing.vignetteWeight = this.#vignetteWeight;
        }
      };

      if (!this.#pipeline.imageProcessing) {
        this.#pipeline.onBuildObservable.addOnce(updateImageProcessing);
      } else {
        updateImageProcessing();
      }
    }
  }
  reset() {
    this.#bloomEnabled = false;
    this.#depthOfFieldEnabled = false;
    this.#imageProcessingEnabled = false;
    this.#vignetteEnabled = false;
    this.#sharpenEnabled = false;
    this.update();
  }

  readFile(file: HCFiler): void {
    file.read();
    this.#bloomEnabled = file.read();
    this.#bloomThreshold = file.read();
    this.#bloomWeight = file.read();
    this.#bloomKernel = file.read();
    this.#bloomScale = file.read();

    this.#depthOfFieldEnabled = file.read();
    this.#depthOfFieldBlurLevel = file.read();
    this.#focusDistance = file.read();
    this.#focalLength = file.read();
    this.#fStop = file.read();
    this.#lensSize = file.read();

    this.#imageProcessingEnabled = file.read();
    this.#contrast = file.read();
    this.#exposure = file.read();

    this.#vignetteEnabled = file.read();
    this.#vignetteBlendMode = file.read();
    const hex = file.read();
    this.#vignetteColor = Color4.FromHexString(hex);
    this.#vignetteWeight = file.read();

    this.#sharpenEnabled = file.read();
    this.#edgeAmount = file.read();
    this.#colorAmount = file.read();
    requestIdleCallback(() => {
      this.update();
    });
  }
  writeFile(file: HCFiler): void {
    file.write(1);

    file.write(this.#bloomEnabled);
    file.write(this.#bloomThreshold);
    file.write(this.#bloomWeight);
    file.write(this.#bloomKernel);
    file.write(this.#bloomScale);

    file.write(this.#depthOfFieldEnabled);
    file.write(this.#depthOfFieldBlurLevel);
    file.write(this.#focusDistance);
    file.write(this.#focalLength);
    file.write(this.#fStop);
    file.write(this.#lensSize);

    file.write(this.#imageProcessingEnabled);
    file.write(this.#contrast);
    file.write(this.#exposure);

    file.write(this.#vignetteEnabled);
    file.write(this.#vignetteBlendMode);
    file.write(this.#vignetteColor.toHexString());
    file.write(this.#vignetteWeight);

    file.write(this.#sharpenEnabled);
    file.write(this.#edgeAmount);
    file.write(this.#colorAmount);
  }
}
