import { IScreenshotSize } from "@babylonjs/core/Misc/interfaces/screenshotSize";
import { customLoadingScreen } from "./CustomLoadingScreen";
import {
  ArcRotateCamera,
  Color3,
  Color4,
  Engine,
  FilesInput,
  FilesInputStore,
  FramingBehavior,
  ISceneLoaderProgressEvent,
  Observable,
  SceneLoader,
  StandardMaterial,
  Tools,
  Scene,
} from "../babylon";
import { b64toBlob } from "../utils/utils";
import { EnvironmentTools } from "../utils/sceneEnv";

export class FilePreviewer {
  private filesInput: FilesInput;
  private engine: Engine;
  private scene: Scene;
  private onFileLoadedObserver = new Observable<File>();
  private onFileProgressObserver = new Observable<ISceneLoaderProgressEvent>();
  private onFileErrorObserver = new Observable<{
    file: File;
    scene: any;
    message: string;
  }>();
  private isRendered = false;
  constructor(canvas: HTMLCanvasElement, private defaultEnv = false) {
    this.engine = new Engine(canvas, false, {
      premultipliedAlpha: false,
      preserveDrawingBuffer: true,
      antialias: false,
    });

    this.engine.loadingScreen = customLoadingScreen;

    this.scene = new Scene(this.engine);

    this.scene.clearColor = new Color4(1, 1, 1, 1);

    this.scene.createDefaultCameraOrLight(true, true);
    this.scene.useRightHandedSystem = true;
    this.scene.activeCamera.maxZ = 1e8;

    if (defaultEnv) {
      this.createDefaultEnv();
    }
    this.scene.executeWhenReady(() => {
      this.engine.hideLoadingUI();
      this.engine.runRenderLoop(() => {
        if (this.scene.activeCamera) this.scene.render();
      });
    });

    FilesInputStore.FilesToLoad = new Proxy(FilesInputStore.FilesToLoad, {
      get(obj, prop: string) {
        return obj[prop];
      },
      set(obj, prop: string, value) {
        obj[prop] = value;
        return true;
      },
    });

    let filesInput = new FilesInput(
      this.engine,
      null,
      (sceneFile, scene) => {
        this.scene = scene;
        const isGLTF =
          sceneFile.name.endsWith(".glb") || sceneFile.name.endsWith(".gltf");
        this.prepareCamera(isGLTF);
        (scene.materials as StandardMaterial[]).forEach((m) => {
          m.diffuseColor = Color3.White();
          m.backFaceCulling = false;
          m.emissiveColor = Color3.Black();
        });
        this.onFileLoadedObserver.notifyObservers(sceneFile);
      },
      (e) => {
        this.onFileProgressObserver.notifyObservers(e);
      },
      () => {
        this.isRendered = true;
      },
      (r) => {},
      (fs) => {},
      null,
      (file, scene, message) => {
        this.onFileErrorObserver.notifyObservers({ file, scene, message });
      }
    );

    this.filesInput = filesInput;
  }
  prepareCamera(isGLTF = false) {
    this.scene.createDefaultCameraOrLight(true, true);
    this.scene.activeCamera.maxZ = 1e8;

    const camera = this.scene.activeCamera as ArcRotateCamera;

    if (this.defaultEnv && !this.scene.metadata) {
      this.createDefaultEnv();
    }

    camera.useFramingBehavior = true;

    const framingBehavior = camera.getBehaviorByName(
      "Framing"
    ) as FramingBehavior;
    framingBehavior.framingTime = 0;
    framingBehavior.elevationReturnTime = -1;

    if (this.scene.meshes.length) {
      camera.lowerRadiusLimit = null;

      const worldExtends = this.scene.getWorldExtends(function (mesh) {
        return (
          mesh.isVisible &&
          mesh.isEnabled() &&
          mesh["geometry"] &&
          mesh.name !== "hdrSkyBox"
        );
      });
      framingBehavior.zoomOnBoundingInfo(worldExtends.min, worldExtends.max);
      camera.useFramingBehavior = false;
    }

    camera.pinchPrecision = 200 / camera.radius;
    camera.upperRadiusLimit = 5 * camera.radius;

    camera.wheelDeltaPercentage = 0.01;
    camera.pinchDeltaPercentage = 0.01;

    camera.attachControl();

    camera.alpha = Math.PI / 4;
    camera.beta = Math.PI / 3;
  }
  loadLocalFile(evt: any) {
    this.isRendered = false;
    this.filesInput.loadFiles(evt);
  }
  async loadFile(url: string) {
    this.disposeImportMeshes();
    try {
      this.isRendered = false;
      const assets = await SceneLoader.LoadAssetContainerAsync(
        url,
        "",
        this.scene
      );
      assets.addAllToScene();

      this.prepareCamera();
      (assets.materials as StandardMaterial[]).forEach((m) => {
        m.diffuseColor = Color3.White();
        m.backFaceCulling = false;
        m.emissiveColor = Color3.Black();
      });
      return true;
    } catch (err) {
      return false;
    } finally {
      this.isRendered = true;
    }
  }
  async getPreviewUrl(size: number | IScreenshotSize = 400): Promise<string> {
    await this.waitRendered();
    return Tools.CreateScreenshotAsync(
      this.engine,
      this.scene.activeCamera,
      size
    );
  }
  async getBlob() {
    this.scene.clearColor = new Color4(1, 1, 1, 1);
    await this.waitRendered();
    const data = await this.getPreviewUrl();
    return b64toBlob(data);
  }
  async waitRendered() {
    return new Promise((res) => {
      let timer = setInterval(() => {
        if (this.isRendered) {
          clearInterval(timer);
          res(null);
        }
      }, 100);
    });
  }
  onLoaded(callback: (file: File) => void) {
    const observer = this.onFileLoadedObserver.add(callback);
    return () => {
      this.onFileLoadedObserver.remove(observer);
    };
  }
  onError(callback: ({ file, message }) => void) {
    const observer = this.onFileErrorObserver.add(callback);
    return () => {
      this.onFileErrorObserver.remove(observer);
    };
  }
  onProgress(callback: (e) => void) {
    const observer = this.onFileProgressObserver.add(callback);
    return () => {
      this.onFileProgressObserver.remove(observer);
    };
  }
  destory() {
    this.filesInput.dispose();
    this.onFileLoadedObserver.clear();
    this.engine.dispose();
  }
  disposeImportMeshes() {
    for (const n of this.scene.rootNodes) {
      if (n.name === "__root__") {
        n.dispose(false, true);
      }
    }
  }
  createDefaultEnv() {
    this.scene.environmentTexture = EnvironmentTools.LoadSkyboxPathTexture(
      this.scene
    );
    if (this.scene.activeCamera)
      this.scene.createDefaultSkybox(
        this.scene.environmentTexture,
        true,
        1e8,
        0.3,
        false
      );
    this.scene.metadata = "ok";
  }
}
