import { BaseObject } from "../BaseObject";
import { CommandHistoryRecord } from "./CommandHistoryRecord";
import { HCFiler } from "../filer/HCFiler";
import { ObjectAllDataHistoryRecord } from "./ObjectAllDataHistoryRecord";
import { arrayLast } from "../utils/array";

/**
 * 历史记录管理
 * 创建实体记录
 * 删除实体记录
 * 更新实体记录
 * */
export class HistoricManager extends BaseObject {
  public historyRecordList: CommandHistoryRecord[] = [];
  public doing = false;
  public cursor = -1;
  public isEnable = true;
  public startCmd(cmdName: string) {
    this.historyRecordList.splice(
      this.cursor + 1,
      this.historyRecordList.length - (this.cursor + 1)
    );
    this.historyRecordList.push(new CommandHistoryRecord(cmdName));
    this.cursor = this.historyRecordList.length - 1;
  }
  public endCmd() {
    let lastRec = this.historyRecordList[this.cursor];

    if (lastRec) {
      for (const [, hs] of lastRec.HistoryList) {
        for (const h of hs) {
          if (h instanceof ObjectAllDataHistoryRecord) {
            h.writeRedo();
          }
        }
      }
      this.endCmdEvent(true);
      return true;
    }
    this.endCmdEvent(false);
    return false;
  }
  get HasUndo() {
    return !!this.historyRecordList[this.cursor];
  }
  get HasRedo() {
    return !!this.historyRecordList[this.cursor + 1];
  }
  endCmdEvent(res) {}
  undo() {
    let historyRec = this.historyRecordList[this.cursor];
    if (!historyRec) return false;
    this.doing = true;
    for (let [id, recList] of historyRec.HistoryList) {
      for (let i = recList.length; i--; ) {
        id.Object._applyHistoryRecord(recList[i].undoData);
      }
    }
    this.cursor--;
    this.doing = false;
    this.undoEvent(historyRec);
    return true;
  }
  undoEvent(record: CommandHistoryRecord) {}
  public redo() {
    let historyRec = this.historyRecordList[this.cursor + 1];
    if (!historyRec) return false;
    this.doing = true;
    for (let [id, recList] of historyRec.HistoryList) {
      for (let i = recList.length; i--; ) {
        id.Object._applyHistoryRecord(recList[i].redoData);
      }
    }
    this.cursor++;
    this.doing = false;
    this.redoEvent(historyRec);
    return true;
  }
  redoEvent(record: CommandHistoryRecord) {}
  public clear() {
    this.historyRecordList.length = 0;
    this.cursor = -1;
  }
  public destory() {
    super.destory();
    for (const h of this.historyRecordList) {
      h.destory();
    }
    this.historyRecordList.length = 0;
    this.cursor = -1;
  }
  get LastCmdRecord() {
    if (this.doing || !this.isEnable) return;
    return arrayLast(this.historyRecordList);
  }
  writeFile(file: HCFiler) {
    file.write(1);
    file.write(this.historyRecordList.length);
    for (const h of this.historyRecordList) {
      h.writeFile(file);
    }
  }
  readFile(file: HCFiler) {
    file.read();
    const count = file.read();
    this.historyRecordList.length = 0;
    for (let i = 0; i < count; i++) {
      const h = new CommandHistoryRecord();
      h.readFile(file);
      this.historyRecordList.push(h);
    }
  }
}
