import { equaln } from "../utils";
import { ICanvasRenderingContext, Vector3 } from "../babylon";
import { MultiLine, MultiLinePoint } from "../babylon";

export class MultiLine2 extends MultiLine {
  public _draw(context: ICanvasRenderingContext): void {
    context.save();

    if (this.shadowBlur || this.shadowOffsetX || this.shadowOffsetY) {
      context.shadowColor = this.shadowColor;
      context.shadowBlur = this.shadowBlur;
      context.shadowOffsetX = this.shadowOffsetX;
      context.shadowOffsetY = this.shadowOffsetY;
    }

    this._applyStates(context);

    context.strokeStyle = this.color;
    context.lineWidth = this.lineWidth;
    context.setLineDash(this.dash);

    context.beginPath();

    let first: boolean = true; //first index is not necessarily 0
    let previousPoint: Vector3;

    //@ts-ignore
    let points = this._points.slice() as MultiLinePoint[];

    if (points.length === 2) {
      if (points[0].control) {
        points[0]._point._isDirty = true;
        points[0]._point.y += points[0].control._currentMeasure.height / 3;
        points[0]._point._isDirty = false;
      }

      const start = points[0]._point;
      const end = points[1]._point;

      if (!equaln(start.x, end.x) && !equaln(start.y, end.y)) {
        const center = Vector3.Center(start, end);

        points = [
          points[0],
          {
            //@ts-ignore
            _point: {
              x: start.x,
              y: center.y,
              z: center.z,
            },
          },
          {
            //@ts-ignore
            _point: {
              x: end.x,
              y: center.y,
              z: center.z,
            },
          },
          points[1],
        ];
      }
    }

    points.forEach((point) => {
      if (!point) {
        return;
      }

      if (first) {
        context.moveTo(point._point.x, point._point.y);

        first = false;
      } else {
        if (point._point.z < 1 && previousPoint.z < 1) {
          context.lineTo(point._point.x, point._point.y);
        } else {
          context.moveTo(point._point.x, point._point.y);
        }
      }
      previousPoint = point._point;
    });

    context.stroke();

    context.restore();
  }
}
