import "@babylonjs/loaders/glTF/2.0";
import "@babylonjs/loaders/OBJ";
import "@babylonjs/loaders/STL";
import "../loaders/hc3d";
import { BaseDom } from "./../core/BaseDom";
import { GUISelectTool } from "./../editor/GuiSelectTool";
import { ImportEntity } from "./../entitys/ImportEntity";
import { BaseGui } from "./../guis/Gui";
import { ParticleSystem } from "./../particleSystem/ParticleSystem";

import { end } from "@hc/aop";
import { HCFactory, HCFiler } from "@hc/core";
import { AppEvent } from "../core/AppEvent";
import { Editor } from "../editor/Editor";
import { Entity } from "../entitys/Entity";
import { EventCallback } from "../types/event";
import { RenderType } from "../types/renderTypes";
import { Viewer } from "../viewer/Viewer";
import { Database } from "./../database/Database";
import { HemisphericLight } from "./../entitys/lights/HemisphericLight";
import { PBRMaterialRecord } from "./../record/PBRMaterialRecord";
import { TextureRecord } from "./../record/TextureRecord";

import { ParticleSystemGroup } from "..";
import { SceneLoader } from "../babylon";
import { Light } from "../entitys/lights/Light";
import { EventEnum } from "../enums";
import { CommonMaterials } from "../materials/commonMaterials";
import { Particle } from "../particleSystem/Particle";
import type { IAppOption, ICreateOption } from "../types/application.types";
import { appCache } from "./AppCache";
import { AppConfig } from "./appConfig";
import { AppStore } from "./AppStore";
import { FileToolsOptions } from "@babylonjs/core/Misc/fileTools";

SceneLoader.ShowLoadingScreen = false;

FileToolsOptions.PreprocessUrl = (url: string) => {
  if (url.startsWith("http") && AppConfig.clearCacheKeys.length) {
    const cacheData = AppConfig.clearCacheKeys.find((d) => url.includes(d.key));
    if (cacheData) {
      const Url = new URL(url);
      Url.searchParams.append("t", cacheData.timestamp);
      return Url.href;
    }
  }
  return url;
};

/**
 * 引擎入口
 * @see https://hc3dol.gkiiot.com/#1
 */
export class Application {
  static Intance: Application;
  Editor: Editor;
  Viewer: Viewer;
  Database: Database;
  /**应用是否准备好 */
  isReady = true;
  private _domContainer: HTMLElement;
  private _IsShadow = false;
  private cleaners: (() => void)[] = [];
  private guiSelectTool: GUISelectTool;
  /**
   * 创建一个引擎实例
   * @param option 创建实例配置项 {@link IAppOption}
   */
  constructor(option: IAppOption) {
    if (option.lowPerformance !== undefined) {
      AppConfig.lowPerformance = option.lowPerformance;
    }

    //是否需要单例？？
    if (!Application.Intance) {
      AppStore.Application = this;

      this.Viewer = new Viewer(option.canvas, !option.editor);

      //GUI工作在编辑器相关事件前注册
      this.guiSelectTool = new GUISelectTool(this.Viewer);

      this.Editor = new Editor(this.Viewer, option.editor);

      if (option.domContainer) {
        this._domContainer = option.domContainer;
      } else {
        this._domContainer =
          document.getElementById("2d") || option.canvas.parentElement;
      }

      AppStore.PreviewMode = !option.editor;

      this.Database = new Database(!option.editor);
      AppStore.MainViewer = this.Viewer;

      this._register();

      //初始化渲染灯光
      this.initLight();

      if (option.skybox) {
        this.Viewer.sceneControl.Mode = option.skybox;
      }

      if (option.cameraRadius) {
        this.Viewer.cameraManager.Radius = option.cameraRadius;
      }

      AppConfig.debugger = option.debug;

      Application.Intance = this;
    }
    return Application.Intance;
  }
  /**
   * 是否开启阴影
   */
  get IsShadow() {
    return this.Viewer.sceneControl.IsShadow;
  }
  set IsShadow(v: boolean) {
    this.Viewer.sceneControl.IsShadow = v;
  }
  /**
   * 创建实体
   * @param option {@link ICreateOption}
   * @returns
   */
  create(option: ICreateOption) {
    const entity = HCFactory.CreateObject<Entity>(option.type, option);
    if (!entity) {
      console.error(option.type + "类型实体不存在");
      return;
    }

    if (option.name) {
      entity.Name = option.name;
    }

    if (option.position) {
      entity.Position = option.position;

      if (entity instanceof ImportEntity) {
        entity.isRead = true;
      }
    }
    if (option.colorIndex !== undefined) {
      entity.ColorIndex = option.colorIndex;
    }

    if (entity instanceof BaseGui) {
      this.Database.GuiSpace.append(entity);
    } else if (entity instanceof BaseDom) {
      this.Database.DomSpace.append(entity);
    } else if (entity instanceof Entity) {
      if (entity.isLight) {
        this.Database.LightSpace.append(entity as Light);
      } else this.Database.ModelSpace.append(entity);
    }

    if (option.loading) {
      entity.on("loading", option.loading);
    }

    if (option.complete) {
      if (entity.isAsync) {
        entity.once("load", option.complete);
      } else {
        option.complete({ type: "load", target: entity });
      }
    }

    return entity;
  }
  createGui(type: string, option) {
    const gui = HCFactory.CreateObject<BaseGui>(type, option);
    if (!gui) {
      console.error(option.type + "类型实体不存在");
      return;
    }
    this.Database.GuiSpace.append(gui);
    return gui;
  }
  createParticle(type: string, option) {
    const part = HCFactory.CreateObject<Particle>(type, option);
    if (!part) {
      console.error(option.type + "特效不存在");
      return;
    }
    this.Database.ParticleSystemSpace.append(part);

    return part;
  }
  /**
   * 创建一个PBR材质
   * @returns 创建PBR材质
   */
  createPBR() {
    const mtl = new PBRMaterialRecord();
    this.Database.MaterialTable.append(mtl);
    return mtl;
  }
  /**
   * 创建贴图
   * @param url 贴图地址
   * @param onload //仅会触发一次
   * @returns texture
   */
  createTexture(url: string, onload?: (e) => void) {
    const tx = new TextureRecord(url);
    if (onload) tx.once("load", onload);
    this.Database.TextureTable.append(tx);
    return tx;
  }

  get SelectObjects(): (Entity | BaseGui | BaseDom | Particle)[] {
    return [
      ...this.SelectEntitys,
      ...this.SelectGuis,
      ...this.SelectDoms,
      ...this.SelectParticles,
    ];
  }
  get SelectEntitys() {
    return this.Editor?.selectControl.selectSet.SelectEntitys;
  }
  get SelectParticles() {
    const particle = this.Editor?.selectControl?.SelectParticle;
    return particle ? [particle] : [];
  }
  get SelectGuis() {
    const gui = this.Editor?.selectControl?.SelectGui;
    return gui ? [gui] : [];
  }
  get SelectDoms() {
    const dom = this.Editor?.selectControl?.SelectDOM;
    return dom ? [dom] : [];
  }
  /**
   * 场景实体最大化
   */
  zoomAll() {
    this.Viewer.cameraManager.zoomAll();
  }
  changeRenderType(type: RenderType) {
    if (type === undefined) return;
    this.Database.ModelSpace.Entitys.forEach((e) => e.updateRenderType(type));
  }
  /**
   * 新建一个场景
   * @param trigger 是否触发事件
   */
  new(trigger = true) {
    if (this.Editor?.transformControl)
      this.Editor.transformControl.detachMeshs();
    this.Viewer.reset();
    this.Database.readFile(new Database().writeFile());
    this.initLight();
    if (trigger) AppEvent.trigger({ type: EventEnum.NewFile, target: null });
  }
  /**
   * 保存场景数据
   * @returns 场景数据
   */
  saveFile() {
    const filer = this.Database.writeFile();
    this.Viewer.writeFile(filer);
    return filer.Data;
  }
  /**
   * 读取场景数据
   * @param data 场景数据
   */
  loadFile(data: any[]) {
    this.isReady = false;
    AppStore.StartWait();
    this.Viewer.Scene.blockfreeActiveMeshesAndRenderingGroups = true;
    this.Viewer.stopRender();
    this.Viewer.reset();
    if (this.Editor?.transformControl)
      this.Editor.transformControl.detachMeshs();
    appCache.clear();
    AppEvent.trigger({ type: EventEnum.StartLoad, target: null });
    if (AppConfig.loadPlugins.size > 0) {
      AppConfig.loadPlugins.forEach((p: any) => p?.dispose?.());
      AppConfig.loadPlugins.clear();
    }
    this.Viewer.clearScene();

    if (data) {
      AppStore.Reading = true;
      const filer = new HCFiler(data);
      this.Database.readFile(filer);
      this.Viewer.readFile(filer);
      this.Viewer.hemisphericLight = this.Database.LightSpace.Entitys.find(
        (e) => e instanceof HemisphericLight
      ) as HemisphericLight;
      AppStore.Reading = false;
    } else {
      this.new(false);
    }
    this.Viewer.Scene.blockfreeActiveMeshesAndRenderingGroups = false;
    this.Viewer.render();
    AppEvent.trigger({ type: EventEnum.LoadFile, target: null });
    AppStore.WaitComplete().then((res) => {
      AppEvent.trigger({ type: EventEnum.LoadComplete, target: null });
      this.isReady = true;
      /**加载完成通知阴影切换 */
      this.Viewer.sceneControl.ToggleShadowObserable.notifyObservers(
        this.IsShadow
      );
    });
  }
  /**回收场景 */
  destory() {
    this.Viewer.Scene.blockfreeActiveMeshesAndRenderingGroups = true;
    this.Database.destory();
    this.Viewer.Scene.blockfreeActiveMeshesAndRenderingGroups = false;
    this.Viewer.destory();
    Application.Intance = null;
    CommonMaterials.clear();
    this.cleaners.forEach((c) => c());
    this.cleaners.length = 0;
  }
  /**
   * 清理资源的缓存
   */
  clearCache() {
    appCache.clear();
  }
  /**创建一个视图 */
  createView(canvas: HTMLCanvasElement, option?: any) {
    return new Viewer(canvas, true, option);
  }
  /*@hidden */
  openShadow() {
    const lights = this.Database.LightSpace.Entitys as Light[];
    const entitys = this.Database.ModelSpace.Entitys;

    for (const l of lights) {
      if (l.HasShadowGenerator) {
        l.addShadowCaster(entitys);
      }
    }
  }
  /**
   * @hidden
   */
  closeShadow() {
    const lights = this.Database.LightSpace.Entitys as Light[];

    for (const l of lights) {
      if (l.HasShadowGenerator) {
        l.removeShadowCaster();
      }
    }
  }
  /**
   * 全局事件监听
   * @param type 事件类型
   * @param callback 事件回调
   * @returns 回收回调
   */
  on(type: string, callback: EventCallback) {
    return AppEvent.on(type, callback);
  }
  /**
   * 全局事件监听注销
   * @param type 事件类型
   * @param callback 事件回调
   * @returns
   */
  off(type: string, callback: EventCallback) {
    AppEvent.off(type, callback);
  }
  private initLight() {
    const light = new HemisphericLight();
    this.Viewer.hemisphericLight = light;
    this.Database.LightSpace.append(light);
  }
  private _register() {
    const callback = () => {
      this.Viewer.setSize();
      this.Database.DomSpace.Entitys.forEach((e) => e.update());
      this.Database.GuiSpace.Entitys.forEach((e) => e.update());
    };
    window.addEventListener("resize", callback);

    const observer = this.Viewer.sceneControl.ToggleShadowObserable.add((v) => {
      if (!this.isReady) return;
      if (v) {
        this.openShadow();
      } else {
        this.closeShadow();
      }
    });

    this.cleaners.push(
      end(
        this.Database.ModelSpace,
        this.Database.ModelSpace.appendEvent,
        (e: Entity) => {
          this.Viewer.Scene.addTransformNode(e.DrawObject);
        }
      ),
      end(
        this.Database.LightSpace,
        this.Database.LightSpace.appendEvent,
        (e: Light) => {
          this.Viewer.Scene.addTransformNode(e.DrawObject);
          if (this._IsShadow && e.EnableShadow) {
            e.addShadowCaster(this.Database.ModelSpace.Entitys);
          }
        }
      ),
      end(
        this.Database.GuiSpace,
        this.Database.GuiSpace.appendEvent,
        (e: BaseGui) => {
          this.Viewer.GuiContainer.addControl(e.DrawObject);
        }
      ),
      end(
        this.Database.DomSpace,
        this.Database.DomSpace.appendEvent,
        (e: BaseDom) => {
          this._domContainer.appendChild(e.DrawObject);
        }
      ),
      end(
        this.Database.ParticleSystemSpace,
        this.Database.ParticleSystemSpace.appendEvent,
        (e: ParticleSystem | ParticleSystemGroup) => {
          const oldScene = Entity.Scene;
          Entity.Scene = AppStore.MainScene;
          if (!e.IsErase) e.Intance;
          Entity.Scene = oldScene;
        }
      ),
      end(this.Database, this.Database.readFile, async () => {
        for (const en of this.Database.ModelSpace.Entitys) {
          if (!en.IsErase) {
            this.Viewer.Scene.addTransformNode(en.DrawObject);
          }
        }
        for (const en of this.Database.LightSpace.Entitys) {
          if (!en.IsErase) {
            this.Viewer.Scene.addTransformNode(en.DrawObject);
          }
        }
        for (const en of this.Database.GuiSpace.Entitys) {
          if (!en.IsErase) this.Viewer.GuiContainer.addControl(en.DrawObject);
        }
        for (const en of this.Database.DomSpace.Entitys) {
          if (!en.IsErase) this._domContainer.appendChild(en.DrawObject);
        }

        const oldScene = Entity.Scene;
        Entity.Scene = AppStore.MainScene;
        for (const en of this.Database.ParticleSystemSpace.Entitys as (
          | ParticleSystem
          | ParticleSystemGroup
        )[]) {
          if (!en.IsErase) en.Intance;
        }
        Entity.Scene = oldScene;
      }),
      this.on("update-shadow", (event) => {
        const en = event.target as Entity;
        const lights = this.Database.LightSpace.Entitys as Light[];

        for (const l of lights) {
          if (l.HasShadowGenerator) {
            if (en.EnableShadow) {
              l.addShadowCaster([en]);
            } else l.removeShadowCaster([en]);
          }
        }
      }),

      this.on("remove", (event) => {
        if (
          this.Editor.selectControl &&
          this.Editor.selectControl.SelectObjects.includes(event.target as any)
        )
          this.Editor.selectControl.clearSelect(true);
      }),
      () => {
        window.removeEventListener("resize", callback);
      },
      () => {
        this.Viewer.sceneControl.ToggleShadowObserable.remove(observer);
      }
    );
  }
}
