import {
  Animation,
  AnimationGroup,
  BaseCameraPointersInput,
  Camera,
  FramingBehavior,
  IAnimationKey,
  IWheelEvent,
  Observable,
  Observer,
  PointerEventTypes,
  PointerInfo,
  Scene,
  TargetCamera,
  Vector3,
  Mesh,
} from "../../babylon";
import { HCFiler, ISerialize, IVector3 } from "@hc/core";
import { Entity } from "../../entitys/Entity";
import {
  IAnimateReturn,
  ICameraAnimateOption,
} from "../../types/animation.types";
import { CameraEnable } from "../../types/camera.types";
import { UndefinedType } from "../../types/types";
import { iVector2Vector, setVecLike2Vector } from "../../utils/vector";
import { ORTH_SIZE } from "../../contants";
import { CustomFramingBehavior } from "./framingBehavior";

export class BaseCamera implements ISerialize {
  protected _enable = CameraEnable.All;
  protected _camera: TargetCamera;
  protected _position = new Vector3(0, 0, 10);
  protected _target = Vector3.Zero();
  protected _mode = Camera.PERSPECTIVE_CAMERA;
  protected wheelSpeed = 20;
  protected wheelDeltaPercentage = 0.05;
  protected _flying = false;
  protected size = ORTH_SIZE;
  protected customWheel: Observer<PointerInfo>;
  protected _pointerInput: BaseCameraPointersInput;
  protected _animateGroup: AnimationGroup;
  private customFramingBehavior: CustomFramingBehavior;
  public onRadiusChangeObserver = new Observable<Camera>();
  public onZommScaleChangeObserver = new Observable<number>();
  zoomScale = 1;
  constructor(protected _scene: Scene, protected _canvas: HTMLCanvasElement) {}
  get CurrentCamera() {
    return this._camera;
  }
  get Position() {
    return { x: this._position.x, y: this._position.y, z: this._position.z };
  }
  set Position(v: IVector3) {
    setVecLike2Vector(this._position, v);
  }
  get Target(): IVector3 {
    return this._target;
  }
  set Target(v: IVector3) {
    setVecLike2Vector(this._target, v);
  }
  get Alpha() {
    return 0;
  }
  set Alpha(v) {}
  get Beta() {
    return 0;
  }
  set Beta(v) {}
  get Radius() {
    return Vector3.Distance(this._target, this._position);
  }
  set Radius(v: number) {
    const vec = this._position.subtract(this._target).normalize().scale(v);
    this._position = this._position.add(vec);
    this._camera.position.copyFrom(this._position);
  }
  get Aspect() {
    return this._canvas.width / this._canvas.height;
  }
  get Enable() {
    return this._enable !== CameraEnable.None;
  }
  set Enable(v: boolean) {
    this._enable = v ? CameraEnable.All : CameraEnable.None;
  }
  get EnableZoom() {
    return !!(this._enable & CameraEnable.Zoom);
  }
  set EnableZoom(v: boolean) {
    if (v === this.EnableZoom) return;
    if (v) {
      this._enable |= CameraEnable.Zoom;
    } else {
      this._enable &= ~CameraEnable.Zoom;
    }
    if (this._mode === Camera.PERSPECTIVE_CAMERA) {
      if (v) {
        this._camera.inputs.attached.mousewheel.attachControl();
      } else this._camera.inputs.attached.mousewheel.detachControl();
    }
  }
  get Mode() {
    return this._mode;
  }
  set Mode(v) {
    this._mode = v;
  }
  set Size(v: number) {
    if (!v) return;

    this.size = v;
    this._camera.orthoTop = this.size * this.zoomScale;
    this._camera.orthoBottom = -this.size * this.zoomScale;
    this._camera.orthoLeft = -this.size * this.zoomScale * this.Aspect;
    this._camera.orthoRight = this.size * this.zoomScale * this.Aspect;
  }
  active() {
    this._scene.activeCamera = this._camera;
    this.attachControl();
  }
  attachControl() {}
  detachControl() {
    this._camera.detachControl();
  }
  zoomByBoundBox(min: Vector3, max: Vector3, autoLimit = false) {
    const camera = this._camera;

    const framingBehavior = camera.getBehaviorByName(
      "Framing"
    ) as FramingBehavior;
    if (framingBehavior) {
      framingBehavior.framingTime = 10;
      framingBehavior.elevationReturnTime = -1;
      framingBehavior.autoCorrectCameraLimitsAndSensibility = autoLimit;

      framingBehavior.zoomOnBoundingInfo(min, max, false, () => {
        if (autoLimit && "radius" in camera) {
          camera.minZ = camera["radius"] * 0.01;
        }
      });
    } else {
      if (!this.customFramingBehavior) {
        this.customFramingBehavior = new CustomFramingBehavior(this._camera);
      }
      this.customFramingBehavior.zoomOnBoundingInfo(min, max);
    }
  }
  zoomAll(autoLimit = false) {
    const filterFun = (mesh: Mesh) =>
      mesh.isVisible &&
      mesh.isEnabled() &&
      mesh.name !== "WORLD_GROUND" &&
      !mesh
        .getBoundingInfo()
        .boundingBox.extendSize.equalsWithEpsilon(Vector3.Zero());

    if (this._scene.meshes.filter(filterFun).length) {
      const worldExtends = this._scene.getWorldExtends(filterFun);
      const { max, min } = worldExtends;
      if (max.equalsWithEpsilon(min, 1e-3)) {
        return;
      }

      this.zoomByBoundBox(min, max, autoLimit);
    }
  }
  zoomToEntity(en: Entity) {
    const { extendSize, minimumWorld, maximumWorld } = en.BoundingBox;
    if (extendSize.equalsWithEpsilon(Vector3.Zero())) {
      console.log("空包围盒");
      return;
    }

    this.zoomByBoundBox(minimumWorld, maximumWorld);
  }
  initOrthCamera() {
    this.updateOrthCamera();
    this._camera.inputs.attached.mousewheel.detachControl();
    this.customWheel = this._scene.onPointerObservable.add((p) => {
      if (!this.EnableZoom) return;
      const event = p.event as IWheelEvent;

      let wheelDelta = 0;

      if (event.wheelDelta) {
        wheelDelta = -event.wheelDelta;
      } else {
        wheelDelta = -(event.deltaY || event.detail) * 60;
      }

      this.zoomScale += wheelDelta / (this.wheelSpeed * 100);

      if (this.zoomScale < 0) {
        this.zoomScale = 0.001;
        return;
      }

      this.updateOrthCamera();
      this.onZommScaleChangeObserver.notifyObservers(this.zoomScale);
    }, PointerEventTypes.POINTERWHEEL);
  }
  updateOrthCamera() {
    this._camera.orthoTop = this.Radius * this.zoomScale;
    this._camera.orthoBottom = -this.Radius * this.zoomScale;
    this._camera.orthoLeft = -this.Radius * this.zoomScale * this.Aspect;
    this._camera.orthoRight = this.Radius * this.zoomScale * this.Aspect;
    this._camera.metadata = {
      zoomScale: this.zoomScale,
      size: this.Radius * this.zoomScale,
    };
  }
  flyTo(option: ICameraAnimateOption): UndefinedType<IAnimateReturn> {
    if (this._animateGroup) {
      // this._animateGroup.reset();
      this._animateGroup.dispose();
      this._animateGroup = null;
    }

    let { target, position, time = 1, complete, loop = false } = option;

    if (!target) {
      console.error("必须传入目标点");
      return;
    }

    const targetVector =
      target instanceof Entity ? target.Position : iVector2Vector(target);

    const currentGroup = new AnimationGroup("camera-fly-group");

    const targetAninmate = new Animation(
      "camera-fly-target",
      "target",
      30,
      Animation.ANIMATIONTYPE_VECTOR3
    );

    targetAninmate.setKeys([
      {
        frame: 0,
        value: this._camera.target.clone(),
      },
      {
        frame: time * 30,
        value: targetVector,
      },
    ]);

    currentGroup.addTargetedAnimation(targetAninmate, this._camera);

    if (position) {
      const positionAninmate = new Animation(
        "camera-fly-position",
        "position",
        30,
        Animation.ANIMATIONTYPE_VECTOR3
      );

      const posKeyFrames: IAnimationKey[] = [
        {
          frame: 0,
          value: this._camera.position.clone(),
        },
        {
          frame: time * 30,
          value: iVector2Vector(position),
        },
      ];

      positionAninmate.setKeys(posKeyFrames);
      currentGroup.addTargetedAnimation(positionAninmate, this._camera);
    }

    currentGroup.normalize(0, 100);

    currentGroup.play(loop);

    currentGroup.onAnimationGroupPlayObservable.add(() => {
      this._flying = true;
    });
    currentGroup.onAnimationEndObservable.add(() => {
      complete?.();
      this._flying = false;
    });

    this._animateGroup = currentGroup;

    return {
      clear: () => {
        currentGroup.dispose();
        this._animateGroup = null;
      },
      stop: () => currentGroup.stop(),
      pause: () => currentGroup.pause(),
      reset: () => currentGroup.reset(),
      restart: () => currentGroup.restart(),
    };
  }
  moveTo(option: {
    alpha?: number;
    beta?: number;
    radius?: number;
    target?: IVector3;
    entity?: Entity;
    position?: IVector3;
    time?: number;
    type?: string;
  }) {
    return null;
  }
  moveByPath(option: { path: (Vector3 | IVector3)[]; time: number }) {}
  destory() {
    if (this._camera) this._camera.dispose();
  }
  writeFile(filer: HCFiler): void {
    filer.write(2);
    filer.write(this._target.x);
    filer.write(this._target.y);
    filer.write(this._target.z);
    filer.write(this.zoomScale);
    filer.write(this.size);
    filer.write(this._mode);
    filer.write(this._enable);
  }
  readFile(file: HCFiler): void {
    const ver = file.read();
    this._target.x = file.read();
    this._target.y = file.read();
    this._target.z = file.read();
    this.zoomScale = file.read();
    this.size = file.read();
    this._mode = file.read();
    if (ver > 1) {
      this._enable = file.read();
    }
  }
}
