import {
  Animation,
  AnimationGroup,
  Camera,
  FreeCamera,
  IWheelEvent,
  Observer,
  PointerEventTypes,
  PointerInfo,
  Scene,
  Vector3,
} from "../../babylon";

import { AppEvent } from "../../core/AppEvent";
import { HCFiler, IVector3 } from "@hc/core";
import { CameraEnable, CameraViewMode } from "../../types/camera.types";
import { KeyBoard } from "../../utils";
import { iVector2Vector, setVecLike2Vector } from "../../utils/vector";
import { SKYBOX_SIZE } from "../../contants";
import { BaseCamera } from "./BaseCamera";
import { PathAnimation } from "../../animates/PathAnimation";

export class FreeCameraControl extends BaseCamera {
  protected _camera: FreeCamera;
  protected customWheel: Observer<PointerInfo>;

  constructor(protected _scene: Scene, protected _canvas: HTMLCanvasElement) {
    super(_scene, _canvas);
    if (!_scene || !_canvas) return;
    this.init();
    this._camera.onViewMatrixChangedObservable.add((c: FreeCamera) => {
      this._position.copyFrom(c.position);
      this._target.copyFrom(c.target);
      AppEvent.trigger({ type: "CameraUpdate", target: this as any });
    });
  }
  get EnableZoom() {
    return !!(this._enable & CameraEnable.Zoom);
  }
  set EnableZoom(v: boolean) {
    if (v === this.EnableZoom) return;
    if (v) {
      this._enable |= CameraEnable.Zoom;
    } else {
      this._enable &= ~CameraEnable.Zoom;
    }
    if (this._mode === Camera.PERSPECTIVE_CAMERA) {
      if (v) {
        this._camera.inputs.attached.mousewheel.attachControl();
      } else this._camera.inputs.attached.mousewheel.detachControl();
    }
  }
  get IsFlying() {
    return this._flying;
  }

  get Position() {
    return {
      x: this._camera.position.x,
      y: this._camera.position.y,
      z: this._camera.position.z,
    };
  }
  set Position(v: IVector3) {
    const position = new Vector3();
    setVecLike2Vector(position, v);
    this._camera.position.copyFromFloats(position.x, position.y, position.z);
  }
  get Target() {
    return { x: this._target.x, y: this._target.y, z: this._target.z };
  }
  set Target(v: IVector3) {
    setVecLike2Vector(this._target, v);
    this._camera.setTarget(this._target);
  }
  zoomTo(target: Vector3, size: Vector3) {
    this._camera.setTarget(new Vector3(target.x, target.y));
  }
  zoomByBoundBox(min: Vector3, max: Vector3) {
    super.zoomByBoundBox(min, max);
  }

  init() {
    const camera = new FreeCamera(
      "MainFreeCamera",
      this._position,
      this._scene
    );
    camera.inputs._mouseInput.buttons = [1];
    camera.keysDown = [KeyBoard.KeyS];
    camera.keysDownward = [KeyBoard.ArrowDown];
    camera.keysUp = [KeyBoard.KeyW];
    camera.keysUpward = [KeyBoard.ArrowUp];
    camera.keysLeft = [KeyBoard.KeyA];
    camera.keysRight = [KeyBoard.KeyD];
    camera.inputs.addMouseWheel();
    camera.maxZ = SKYBOX_SIZE;

    this._camera = camera;

    if (this._mode === Camera.ORTHOGRAPHIC_CAMERA) {
      this.initOrthCamera();
    } else {
      this.initPerCamera();
    }
  }
  set ViewMode(v: CameraViewMode) {
    switch (v) {
      case CameraViewMode.Up:
        this._camera.position = this._camera.target
          .clone()
          .add(new Vector3(0, 0, 10));
        break;
      case CameraViewMode.Down:
        this._camera.position = this._camera.target
          .clone()
          .add(new Vector3(0, 0, -10));
        break;
      case CameraViewMode.Left:
        this._camera.position = this._camera.target
          .clone()
          .add(new Vector3(-10));
        break;
      case CameraViewMode.Right:
        this._camera.position = this._camera.target
          .clone()
          .add(new Vector3(10));
        break;
      case CameraViewMode.Front:
        this._camera.position = this._camera.target
          .clone()
          .add(new Vector3(0, 10));
        break;
      case CameraViewMode.Back:
        this._camera.position = this._camera.target
          .clone()
          .add(new Vector3(0, -10));
        break;
      case CameraViewMode.SW:
        this._camera.position = this._camera.target
          .clone()
          .add(new Vector3(10, 10, 10));
        break;
      default:
        break;
    }
  }
  initOrthCamera() {
    this._camera.orthoTop = this.size;
    this._camera.orthoBottom = -this.size;
    this._camera.orthoLeft = -this.size * this.Aspect;
    this._camera.orthoRight = this.size * this.Aspect;
    this._camera.inputs.attached.mousewheel.detachControl();
    this.customWheel = this._scene.onPointerObservable.add((p) => {
      if (!this.EnableZoom) return;
      const event = p.event as IWheelEvent;

      let wheelDelta = 0;

      if (event.wheelDelta) {
        wheelDelta = -event.wheelDelta;
      } else {
        wheelDelta = -(event.deltaY || event.detail) * 60;
      }

      this.zoomScale += wheelDelta / (this.wheelSpeed * 100);

      if (this.zoomScale < 0) {
        this.zoomScale = 0.001;
        return;
      }

      this.updateOrthCamera();
    }, PointerEventTypes.POINTERWHEEL);
  }
  initPerCamera() {
    if (this.customWheel) {
      this._scene.onPointerObservable.remove(this.customWheel);
      this.customWheel = null;
      if (this.EnableZoom)
        this._camera.inputs.attached.mousewheel.attachControl();
    }
  }
  attachControl() {
    this._camera.attachControl(this._canvas, true);
  }
  detachControl() {
    this._camera.detachControl();
  }
  lookAt(target: IVector3) {
    this._camera.setTarget(iVector2Vector(target));
  }
  moveTo(option: {
    position?: IVector3;
    target?: IVector3;
    time?: number;
    type?: string;
  }) {
    const { target, time = 1, position } = option;
    if (position === undefined && target === undefined) return;

    const group = new AnimationGroup("camera-move-to", this._scene);
    const frame = 30;

    if (target !== undefined) {
      const btAnimation = new Animation(
        "target-a",
        "target",
        frame,
        Animation.ANIMATIONTYPE_VECTOR3
      );
      btAnimation.setKeys([
        {
          frame: 0,
          value: this._camera.target.clone(),
        },
        {
          frame: time * frame,
          value: iVector2Vector(target),
        },
      ]);

      group.addTargetedAnimation(btAnimation, this._camera);
    }
    if (position !== undefined) {
      const btAnimation = new Animation(
        "position",
        "position",
        frame,
        Animation.ANIMATIONTYPE_VECTOR3
      );
      btAnimation.setKeys([
        {
          frame: 0,
          value: this._camera.position.clone(),
        },
        {
          frame: time * frame,
          value: iVector2Vector(position),
        },
      ]);

      group.addTargetedAnimation(btAnimation, this._camera);
    }
    return {
      start: () => {
        group.start(false, 1, 0, frame * time);
        return new Promise((res) =>
          group.onAnimationGroupEndObservable.add(res)
        );
      },
      clear: () => {
        group.dispose();
        this._animateGroup = null;
      },
      stop: () => group.stop(),
      pause: () => group.pause(),
      reset: () => group.reset(),
      restart: () => group.restart(),
    };
  }
  moveByPath(option: { path: (Vector3 | IVector3)[]; time: number }) {
    if (option.path.length === 0) return Promise.resolve(false);
    const time = option.time;
    const points = option.path.map(iVector2Vector);
    const an = new PathAnimation(this._camera, {
      path: points,
      time,
    });

    return an;
  }
  update() {
    this._camera.mode = this._mode;
    this._camera.setTarget(this._target);
    this._camera.position = this._position;

    if (this._mode === Camera.ORTHOGRAPHIC_CAMERA) {
      this.updateOrthCamera();
    }
  }
  reset() {
    this._target.copyFrom(Vector3.Zero());
    this.update();
  }
  updateOrthCamera() {
    this._camera.orthoTop = this.size * this.zoomScale;
    this._camera.orthoBottom = -this.size * this.zoomScale;
    this._camera.orthoLeft = -this.size * this.zoomScale * this.Aspect;
    this._camera.orthoRight = this.size * this.zoomScale * this.Aspect;
  }
  writeFile(filer: HCFiler) {
    filer.write(2);
    super.writeFile(filer);
    filer.write(this._position.x);
    filer.write(this._position.y);
    filer.write(this._position.z);
  }
  readFile(filer: HCFiler) {
    const ver = filer.read();
    if (ver > 1) {
      super.readFile(filer);
    }
    this._position.x = filer.read();
    this._position.y = filer.read();
    this._position.z = filer.read();

    this.update();
  }
}
