import {
  AssetsManager,
  Color4,
  CreateLines,
  CreateLineSystem,
} from "../../babylon";
import {
  IBlock,
  IDxf,
  IEntity,
  IInsertEntity,
  IPoint,
  IPolylineEntity,
  ILineEntity,
  ILayer,
} from "dxf-parser";
import DxfParser from "dxf-parser";
import {
  Color3,
  LinesMesh,
  Matrix,
  Scene,
  Tools,
  TransformNode,
  Vector3,
} from "../../babylon";
import { asVector3, toRgba } from "./utils";
import createArcForLWPolyline from "./createArcForLWPolyline";
import { sleep } from "../../utils/utils";
import { EventDispatcher } from "@hc/core";
import { ImportEntity } from "../../entitys";
import {
  arrayLast,
  Box3,
  deg2rad,
  getAreaByPoints,
  getPointToLineDistance,
} from "../../utils";
import { IBlockRecord } from "dxf-parser/esm/DxfParser";

export type ModelConfig = {
  name?: string;
  length?: number;
  width?: number;
  depth?: number;
  rotationOffset?: number;
  isOuterWall?: boolean;
};

export type IDXFLoaderConfig = {
  destoryBlock: boolean;
  ignoreLayers: Set<string>;
  ignoreBlocks: Set<string>;
  scale: number;
  destoryDXF: boolean;
  modelMap: Record<string, ModelConfig>;
  clearLines?: boolean;
};
export type { IBlock, IDxf } from "dxf-parser";

declare module "dxf-parser" {
  export interface IBlock {
    used?: boolean;
  }
}

export class DXFLoader extends EventDispatcher {
  static Parser = new DxfParser();
  private BlockMap = new Map<string, LinesMesh>();
  private material = new Map();
  private matrix4 = new Matrix();
  public name: string;
  public result: IDxf;
  public entitys: LinesMesh[] = [];
  public ModelList: ImportEntity[] = [];
  public root: TransformNode;
  public center: Vector3;
  public config: IDXFLoaderConfig = {
    destoryBlock: true,
    ignoreLayers: new Set<string>(),
    ignoreBlocks: new Set<string>(),
    scale: 1e3,
    destoryDXF: false,
    modelMap: {},
  };
  public size = new Vector3();
  public outerWalls: { thickness: number; outline: Vector3[] }[] = [];
  constructor(content: string, private scene: Scene) {
    super();
    console.time("分析dxf");
    this.result = DXFLoader.Parser.parse(content);
    console.timeEnd("分析dxf");

    Matrix.FromXYZAxesToRef(
      new Vector3(1),
      new Vector3(0, 0, 1),
      new Vector3(0, 1),
      this.matrix4
    );
    window["dxf"] = this;

    const max = this.asVector3(this.result.header["$EXTMAX"] as IPoint);
    const min = this.asVector3(this.result.header["$EXTMIN"] as IPoint);
    const center = Vector3.Center(max, min);
    const box = new Box3(min, max);
    center.z = 0;
    this.center = center;
    this.size = box.extendSizeWorld.scale(2 * this.config.scale);

    for (const i in this.result.blocks) {
      if (this.result.blocks.hasOwnProperty(i)) {
        const block = this.result.blocks[i];
        if (block.type === 1) {
          const records = this.result.tables.blockRecord.blockRecord;
          const record = records[block.name] as IBlockRecord;
          const dbHandle = record.dbHandle;
          if (dbHandle) {
            for (const k in records) {
              if (records.hasOwnProperty(k)) {
                if (records[k].handle === dbHandle) {
                  block.name = records[k].name;
                  block["used"] = true;
                  break;
                }
              }
            }
          }
        }
      }
    }
  }
  get UsedBlocksLayers(): [IBlock[], ILayer[]] {
    const blocks = new Set<IBlock>();
    const layers = new Set<ILayer>();

    for (const en of this.result.entities) {
      if (en.type === "INSERT") {
        const name = (en as IInsertEntity).name;
        const block = this.result.blocks[name];
        blocks.add(block);
      }
      const ls = this.result.tables.layer.layers;
      layers.add(ls[en.layer]);
    }

    return [Array.from(blocks), Array.from(layers)];
  }
  async parse() {
    console.time("加载DXF");
    const lines = await this.parseEntitys(this.result.entities, null, true);
    const root = new TransformNode("root-position", this.scene);
    for (const l of lines) {
      l.parent = root;
    }

    root.position = this.center.negate();
    const r = new TransformNode("dxf-root", this.scene);
    root.parent = r;
    r.rotation.x = -Math.PI / 2;

    this.root = r;

    this.root.metadata = {
      needDispose: true,
    };

    this.entitys = lines;
    console.timeEnd("加载DXF");

    this.trigger({
      type: "load",
      target: null,
    });
  }
  async parseEntitys(ens: IEntity[], block?: IBlock, root?: boolean) {
    let i = 0;
    let lines: LinesMesh[] = [];
    if (block) {
      const points: Vector3[][] = [];
      const ls: LinesMesh[] = [];
      for (const en of ens) {
        const ps = await this.parseEntity(en);
        if (ps.length > 0) {
          if (ps[0] instanceof LinesMesh) {
            ls.push(...(ps as LinesMesh[]));
          } else {
            points.push(ps as Vector3[]);
          }
        }
        i++;
        if (i > 500) {
          await sleep(0);
          i = 0;
        }
      }
      let wall: { thickness: number; outline: Vector3[] };
      if (this.config.modelMap[block.name]?.isOuterWall) {
        wall = this.parseWall(points);
      }

      const line = CreateLineSystem(
        this.config.modelMap[block.name]?.name || block.name,
        { lines: points },
        this.scene
      );
      line.metadata = {
        block: true,
        layer: block.layer,
        originName: block.name,
        wall,
      };
      for (const l of ls) {
        l.parent = line;
      }
      lines.push(line);
    } else {
      for (let current = 0; current < ens.length; current++) {
        const en = ens[current];
        if (root) {
          if (this.config.ignoreLayers.has(en.layer)) {
            continue;
          }
          if (en.type === "INSERT") {
            const name = (en as IInsertEntity).name;
            if (this.config.ignoreBlocks.has(name)) continue;
          }
        }
        const ps = await this.parseEntity(en);
        if (ps.length > 0) {
          if (ps[0] instanceof Vector3) {
            const line = CreateLines(
              en.layer,
              { points: ps as Vector3[] },
              this.scene
            );
            line.metadata = {
              block: false,
              layer: en.layer,
              id: en.handle,
            };
            this.setColor(en, line);
            lines.push(line);
          } else {
            lines.push(...(ps as LinesMesh[]));
          }
        }
        i++;
        if (i > 500) {
          await sleep(0);
          i = 0;
        }
        if (root) {
          this.trigger({
            type: "loading",
            target: null,
            total: this.result.entities.length,
            current: current,
          });
        }
      }
    }
    return lines;
  }
  async parseEntity(en: IEntity) {
    switch (en.type) {
      case "LINE":
        return this.parseLine(en as ILineEntity);
      case "POLYLINE":
      case "LWPOLYLINE":
        return this.parsePolyline(en as IPolylineEntity);
      case "INSERT": {
        const { xScale, yScale, zScale, position, rotation, name } =
          en as IInsertEntity;

        const block = this.result.blocks[name];
        let lines: LinesMesh[];

        if (block.entities) {
          if (this.BlockMap.has(name)) {
            const origin = this.BlockMap.get(name);
            const mesh = origin.clone(origin.name);
            mesh.metadata = { ...origin.metadata };
            lines = [mesh];
          } else {
            lines = await this.parseEntitys(block.entities, block);
            this.BlockMap.set(name, lines[0]);
          }

          let roOffset = this.config.modelMap[block.name]?.rotationOffset ?? 0;
          roOffset = deg2rad(roOffset);

          for (const l of lines) {
            l.scaling = new Vector3(xScale ?? 1, yScale ?? 1, zScale ?? 1);
            l.position = this.asVector3(position);
            if (!l.metadata.size) {
              l.rotation.z = roOffset;
              l.computeWorldMatrix(true);
              const size = l
                .getBoundingInfo()
                .boundingBox.extendSizeWorld.scale(2);
              l.metadata.size = size;
            }
            const ro = Tools.ToRadians(rotation ?? 0);
            l.rotation.z = ro;
            l.metadata.rotation = ro;
            l.metadata.rotationOffset = roOffset;

            l.computeWorldMatrix(true);
            const center = l.getBoundingInfo().boundingBox.centerWorld.clone();
            center.addToRef(this.center.negate(), center);
            l.metadata.center = center;
            l.metadata.id = en.handle;
            l.metadata.scale = l.scaling;

            if (l.metadata.wall) {
              l.metadata.wall.outline = l.metadata.wall.outline.map((p) =>
                p.add(l.position).add(this.center.negate())
              );
            }
          }
        }
        return lines;
      }
      default:
        return [];
    }
  }
  parseLine(en: ILineEntity) {
    const vertices = en.vertices;
    if (vertices?.length) {
      const ps = vertices.map((p) => this.asVector3(p));
      return ps;
    }
    return [];
  }
  parsePolyline(entity: IPolylineEntity) {
    const vertices = entity.vertices;
    if (!vertices?.length) return [];

    const ps: Vector3[] = [];
    for (let i = 0; i < vertices.length - 1; i++) {
      const from = this.asVector3(vertices[i], false);
      const to = this.asVector3(vertices[i + 1], false);
      ps.push(from);
      if (vertices[i].bulge) {
        const points = createArcForLWPolyline(from, to, vertices[i].bulge, 10);
        ps.push(...points.map((p) => this.asVector3(new Vector3(p.x, p.y))));
      }
      if (i === vertices.length - 2) {
        ps.push(to);
      }
    }

    if (entity.shape) {
      ps.push(ps[0].clone());
    }

    return ps;
  }
  asVector3(p: IPoint, isRot = true) {
    //   if (isRot)
    //   return Vector3.TransformCoordinates(
    //     asVector3(p, this.scale),
    //     this.matrix4
    //   );
    // else
    return asVector3(p, this.config.scale);
  }
  setColor(entity: IEntity, line: LinesMesh) {
    const layerTable = this.result.tables.layer.layers;
    let rbg = [];
    if (layerTable) {
      rbg = toRgba(layerTable[entity.layer].color);
    } else {
      rbg = [255, 255, 255];
    }
    const key = rbg.join("-");
    // if (this.material.has(key)) {
    //   console.log("key: ", key);
    //   line.material = this.material.get(key);
    // } else {
    line.color = Color3.FromArray(rbg);
    this.material.set(key, line.material);
    // }
  }
  parseWall(points: Vector3[][]) {
    //TODO:当前算法仅识别 特定的轮廓作为外墙轮廓，无法识别其他场景，需要扩展
    const closestPoints: Vector3[][] = [];
    const areaMap = new WeakMap<Vector3[], number>();
    for (const ps of points) {
      if (ps.length > 2 && ps[0].equalsWithEpsilon(arrayLast(ps))) {
        closestPoints.push(ps);
      }
      areaMap;
    }

    if (closestPoints.length === 1) {
      return {
        thickness: 10,
        outline: closestPoints[0],
      };
    } else {
      closestPoints.sort((p, p2) => getAreaByPoints(p) - getAreaByPoints(p2));
      const basePt = closestPoints[0][0];
      let thickness = Infinity;
      for (let i = 0; i < closestPoints[1].length - 1; i++) {
        const p1 = closestPoints[1][i];
        const p2 = closestPoints[1][i + 1];
        const dist = getPointToLineDistance([p1, p2], basePt);
        thickness = Math.min(thickness, dist);
      }
      return {
        thickness: thickness,
        outline: closestPoints[1],
      };
    }
  }
}
