import { HCFiler, ObjectId, Factory } from "@hc/core";
import { Entity } from "../entitys";
import { CollectionRecord } from "./CollectionRecord";

@Factory
export class GroupRecord extends CollectionRecord {
  static type = "GroupRecord";
  constructor() {
    super();
    const self = this;
    this.entitys = new Proxy([], {
      get(target, key, receiver) {
        return Reflect.get(target, key, receiver);
      },
      set(target, key, value, receiver) {
        if (Reflect.get(target, key, receiver) !== value) {
          if (self.UndoRecord) {
            if (value instanceof ObjectId && value.Object instanceof Entity) {
              if (!self.Id) console.warn("请先添加到Database后在进行操作!");
              else
                value.Object.GroupId = self.ObjectId as ObjectId<GroupRecord>;
            }
          }
        }
        return Reflect.set(target, key, value, receiver);
      },
    });
  }
  readFile(filer: HCFiler): void {
    super.readFile(filer);
    filer.read();
  }
  writeFile(filer: HCFiler): void {
    super.writeFile(filer);
    filer.write(1);
  }
}
