import { HCFiler } from "@hc/core";
import { ParticleSystem } from "../ParticleSystem";
import { Emiter } from "./Emiter";

export class RadiusEmiter extends Emiter {
  protected _radius = 1;
  protected _radiusRange = 1;
  constructor(
    protected attachSystem: ParticleSystem,
    option?: { radius?: number; radiusRange?: number }
  ) {
    super(attachSystem);
    if (option?.radius) {
      this._radius = option.radius;
    }
    if (option?.radiusRange) {
      this._radiusRange = option.radiusRange;
    }
    if (attachSystem?.HasIntance) this.setEmiter();
  }
  setEmiter() {}
  get AttachSystem() {
    return this.attachSystem;
  }
  set AttachSystem(v: ParticleSystem) {
    this.attachSystem = v;
    this.setEmiter();
  }
  get Radius() {
    return this._radius;
  }
  set Radius(v: number) {
    if (v === this._radius) return;
    this._radius = v;
    this.update();
  }

  get RadiusRange() {
    return this._radiusRange;
  }
  set RadiusRange(v: number) {
    if (v === this._radiusRange) return;
    this._radiusRange = v;
    this.update();
  }

  update(): void {
    if (!this._emiter) {
      // if (this.attachSystem) {
      //   this._emiter = this.attachSystem.Intance.createSphereEmitter(
      //     this._radius,
      //     this._radiusRange
      //   );
      // }
      return;
    }
    this._emiter["radius"] = this._radius;
    this._emiter["radiusRange"] = this._radiusRange;
  }
  toJSON() {
    return {
      Radius: this.Radius,
      RadiusRange: this.RadiusRange,
    };
  }
  fromJSON(json): this {
    this._radius = json.Radius;
    this._radiusRange = json.RadiusRange;
    return this;
  }
  writeFile(file: HCFiler): void {
    super.writeFile(file);
    file.write(1);
    file.write(this._radius);
    file.write(this._radiusRange);
  }
  readFile(file: HCFiler): void {
    super.readFile(file);
    file.read();
    this._radius = file.read();
    this._radiusRange = file.read();
  }
}
