import { HCFiler, BaseObject, IEvent } from "@hc/core";
import { AppEvent } from "../core/AppEvent";
import { Interaction } from "../interaction/Interaction";
import { arrayRemoveIf } from "../utils/array";
import { sleep } from "../utils";
import { AppStore } from "../app/AppStore";

export class InteractionTable extends BaseObject {
  objects: Interaction[] = [];
  private cleaners: (() => void)[] = [];
  doing = false;
  private _pause = false;
  public queues = [];
  private preventDbTimeId: NodeJS.Timeout;
  private lastClickTimeStap: number;
  private resolveFun: ((v: unknown) => void)[] = [];
  constructor() {
    super();
  }
  get Pause() {
    return this._pause;
  }
  start() {
    this._pause = false;
    if (this.resolveFun.length > 0) {
      this.resolveFun.forEach((r) => r(true));
      this.resolveFun.length = 0;
    }
    AppStore.MainScene.animatables.forEach((a) => a.restart());
    if (this.queues.length > 0) {
      const func = this.queues.pop();
      func();
    }
  }
  stop() {
    this._pause = true;
    AppStore.MainScene.animatables.forEach((a) => a.pause());
  }
  wait() {
    return new Promise((res) => this.resolveFun.push(res));
  }
  append(interaction: Interaction) {
    interaction._SetOwnerDatabase(this._db);
    this.objects.push(interaction);
  }
  removeAll() {
    this.objects.forEach((o) => o.destory());
    this.objects.length = 0;
  }
  remove(obj: Interaction | Interaction[]) {
    let arr: Interaction[];
    if (Array.isArray(obj)) arr = obj;
    else arr = [obj];
    for (const a of arr) a.destory();
    arrayRemoveIf(this.objects, (o) => arr.includes(o));
  }
  destory(): void {
    super.destory();
    // this.cleaners.forEach((c) => c());
    // this.cleaners.length = 0;
    this.objects.forEach((o) => o.destory());
    this.objects.length = 0;
    this.doing = false;
  }
  private callback = async (
    event: IEvent,
    filter: (o: Interaction) => boolean
  ) => {
    if (this.doing) {
      if (event.isChild) {
        if (event.isEnd) {
          this.queues.length = 0;
        } else this.queues.push(() => this.callback(event, filter));
      }
      return;
    }
    this.doing = true;
    for (const o of this.objects) {
      if (o.Target) continue;
      if (filter(o)) {
        await o.do();
      }
    }
    this.doing = false;
    if (this.queues.length > 0 && !this.Pause) {
      const func = this.queues.pop();
      func();
    }
  };
  private pointerCallback = (event: IEvent) => {
    if (!this.lastClickTimeStap) {
      this.lastClickTimeStap = event.event.timeStamp;
    } else {
      let diff = event.event.timeStamp - this.lastClickTimeStap;
      this.lastClickTimeStap = null;
      if (diff < 200) {
        if (this.preventDbTimeId) {
          clearTimeout(this.preventDbTimeId);
          return;
        }
      }
    }

    const doIt = () => {
      const IsRight = event.event.button === 2;
      this.lastClickTimeStap = null;
      this.callback(event, (o) => {
        if (o.TriggerType === "RightClick" && IsRight) return true;
        else if (o.TriggerType === "Click") {
          if (event.event.button === 0) return true;
        }
      });
    };

    if (event.isChild) {
      doIt();
      return;
    }

    this.preventDbTimeId = setTimeout(() => {
      doIt();
    }, 200);
  };
  private pointerDbClick = async (event: IEvent) => {
    console.log("db");
    await this.callback(event, (o) => o.TriggerType === "DoubleClick");
  };
  private onKeydown = async (event: IEvent) => {
    await this.callback(event, (o) => o.TriggerType === "KeyDown");
  };
  private onKeyup = async (event: IEvent) => {
    await this.callback(event, (o) => o.TriggerType === "KeyUp");
  };
  private onLoad = async (event: IEvent) => {
    await sleep(100);
    await this.callback(event, (o) => o.TriggerType === "Load");
  };
  private onUpdate = async (event: IEvent) => {
    await this.callback(event, (o) => o.TriggerType === "Update");
  };
  public watch() {
    this.cleaners.push(
      AppEvent.on("pointerTap", this.pointerCallback),
      AppEvent.on("pointerDoubleTap", this.pointerDbClick),
      AppEvent.on("keydown", this.onKeydown),
      AppEvent.on("keyup", this.onKeyup),
      AppEvent.on("loadComplete", this.onLoad),
      AppEvent.on("update", this.onUpdate)
    );

    document.addEventListener("visibilitychange", () => {
      if (document.visibilityState === "visible") {
        this.start();
      }
      if (document.visibilityState === "hidden") {
        this.stop();
      }
    });
  }
  writeFile(filer: HCFiler): void {
    super.writeFile(filer);
    filer.write(1);
    filer.writeAnyArray(this.objects, filer.writeObject);
  }
  readFile(filer: HCFiler): void {
    super.readFile(filer);
    filer.read();
    filer.readAnyArrayAndPush(this.objects, filer.readObject);
  }
}
