import { HCFiler, BaseObject } from "@hc/core";
import { Mesh, Vector3 } from "../babylon";
import { Entity } from "../entitys/Entity";
import { Box3 } from "../utils/Box3";

export class Particle extends BaseObject {
  protected _name: string = "粒子特效";
  protected _isSelect = false;
  protected _isStart = true;
  constructor() {
    super();
  }
  get IsSelect() {
    return this._isSelect;
  }
  set IsSelect(v: boolean) {
    this._isSelect = v;
  }
  get Name() {
    return this._name;
  }
  set Name(v: string) {
    this._name = v;
  }
  get UniupeName() {
    return this.Name + this.Id;
  }
  get IsStart() {
    return this._isStart;
  }
  set IsStart(v: boolean) {
    this.writeSnapshoot();
    if (v === this._isStart) return;
    this._isStart = v;
    this.updateStatus();
  }
  get SceneSize() {
    const filterFun = (mesh: Mesh) =>
      mesh.isVisible &&
      mesh.isEnabled() &&
      mesh.name !== "WORLD_GROUND" &&
      !mesh
        .getBoundingInfo()
        .boundingBox.extendSize.equalsWithEpsilon(Vector3.Zero());

    if (Entity.Scene.meshes.filter(filterFun).length) {
      const worldExtends = Entity.Scene.getWorldExtends(filterFun);
      const { max, min } = worldExtends;
      if (max.equalsWithEpsilon(min, 1e-3)) {
        return null;
      }
      return new Box3(min, max);
    }
    return null;
  }
  start() {}
  stop() {}
  destory() {}
  /**填充粒子到场景 */
  fillScene(box?: Box3) {}
  updateStatus() {
    this._isStart ? this.start() : this.stop();
  }
  update() {}
  fromJSON(json: Record<string, any>) {
    return this;
  }
  writeFile(filer: HCFiler): void {
    super.writeFile(filer);
    filer.write(2);
    filer.write(this._name);
    filer.write(this._isStart);
  }
  readFile(filer: HCFiler): void {
    super.readFile(filer);
    const v = filer.read();
    this._name = filer.read();
    if (v > 1) {
      this._isStart = filer.read();
    }
  }
}
