import { BaseObject } from "../BaseObject";
import { HCFactory } from "../Factory";
import { ObjectId } from "../ObjectId";
import { BaseDatabase } from "../database/BaseDatabase";
import { ISerialize } from "../types/iSerialize.types";
import { IVector3 } from "../types/math.types";

/**海创文件数据 */
export class HCFiler {
  database?: BaseDatabase;
  private readIndex = 0;
  constructor(protected _datas: any[] = []) {}
  get Data(): any[] {
    return this._datas;
  }
  set Data(data: any[]) {
    this._datas = data;
    this.reset();
  }
  clear() {
    this._datas.length = 0;
    return this.reset();
  }
  reset() {
    this.readIndex = 0;
    return this;
  }
  write(data: any) {
    if (data instanceof ObjectId) this._datas.push(data.Index);
    else this._datas.push(data);

    return this;
  }

  read(): any {
    return this._datas[this.readIndex++];
  }

  //------------------------ID序列化------------------------
  /*
     Id关联分为三种情况:
      1.普通关联:关联对象未被拷贝时,关联到空对象.
      2.软关联  :关联对象未被拷贝时,关联到原先的对象.
      3.硬关联  :对象被拷贝时,被关联的对象必须也被拷贝.
    */

  // 普通关联
  writeObjectId(id: ObjectId): this {
    if (id) this.write(id.Index);
    else this.write(0);
    return this;
  }
  readObjectId<T extends BaseObject = BaseObject>(): ObjectId<T> | undefined {
    let index = this.read();
    if (this.database) {
      return this.database.getObjectId(index) as ObjectId<T>;
    }
  }

  //-------2.软关联
  writeSoftObjectId(id: ObjectId): this {
    return this.writeObjectId(id);
  }
  readSoftObjectId(): ObjectId | undefined {
    return this.readObjectId();
  }

  /**硬关联 */
  writeHardObjectId(id: ObjectId): this {
    return this.writeObjectId(id);
  }
  readHardObjectId() {
    return this.readObjectId();
  }

  writeObject(obj: ISerialize) {
    if (!obj) {
      this.write("");
      return;
    }
    this.writeString(obj.constructor["type"] || obj.constructor.name);
    obj.writeFile(this);

    return this;
  }

  readObject(obj?) {
    let className = this.readString();
    if (className) {
      if (obj === undefined) {
        obj = HCFactory.CreateObject(className);

        if (!obj && className.startsWith("CustomComponent")) {
          obj = HCFactory.CreateObject("Component");
          console.error(className + "已丢失");
        }

        if (this.database !== undefined && "_SetDefaultDb" in obj)
          obj._SetDefaultDb(this.database);
      }
      obj.readFile(this);
      return obj;
    }
  }
  writeString(str: string) {
    this._datas.push(str);
    return this;
  }

  readString(): string {
    return this._datas[this.readIndex++] as string;
  }
  writeArray(arr: (string | number)[]) {
    this.write(arr.length);
    for (const a of arr) {
      this.write(a);
    }
  }
  readArray(count: number) {
    let arr = this._datas.slice(this.readIndex, this.readIndex + count);
    this.readIndex += count;
    return arr;
  }
  writeAnyArray<T>(arr: T[], callback: (a: T) => void) {
    this.write(arr.length);
    for (const a of arr) {
      callback.call(this, a);
    }
  }
  readAnyArray<T>(callback: () => void) {
    const count = this.read();
    for (let i = 0; i < count; i++) {
      callback.call(this);
    }
  }
  readAnyArrayAndPush<T>(arr: T[], read: () => T) {
    arr.length = 0;
    const count = this.read();
    for (let i = 0; i < count; i++) {
      const en = read.call(this);
      arr.push(en);
    }
  }
  writeVector3(v: IVector3) {
    this.write(v?.x);
    this.write(v?.y);
    this.write(v?.z);
  }
  readVector3(source: IVector3) {
    const x = this.read();
    const y = this.read();
    const z = this.read();
    source.x = x;
    source.y = y;
    source.z = z;
  }
}
