import { IPointerEvent } from "./../types/event";
import { Viewer } from "./../viewer/Viewer";
import { SelectControl } from "./SelectControl";
import { TransformControl } from "./TransformControl";
import { KeyboardControl } from "./KeyboardControl";
import { MouseControl } from "./MouseControl";
import { GetPointService } from "./services/GetPointService";
import { EditorService, EditorStatus, ServiceOption } from "./interface";
import { KeyCode } from "../utils";
import { DragControlService } from "./DragControl";
import { Entity } from "../entitys/Entity";

export class Editor {
  selectControl: SelectControl;
  transformControl: TransformControl;
  keyboardCtrl: KeyboardControl;
  mouseCtrl: MouseControl;
  dragCtrlService: DragControlService;
  getPointService: GetPointService;
  private status: EditorStatus = EditorStatus.None;
  private services: EditorService[] = [];
  constructor(private _viewer: Viewer, private editorMode = false) {
    this.keyboardCtrl = new KeyboardControl(_viewer);
    this.mouseCtrl = new MouseControl(_viewer);

    this.getPointService = new GetPointService(this);

    if (editorMode) {
      this.initEditor();
    }

    this.services.push(
      this.getPointService,
      this.dragCtrlService,
      this.selectControl
    );
  }
  get Status() {
    return this.status;
  }
  set Status(v) {
    this.status = v;
    switch (v) {
      case EditorStatus.GetPoint:
        this._viewer.setCursor("progress");
        break;
      case EditorStatus.Select:
        this._viewer.setCursor("crosshair");
        break;
      case EditorStatus.Drag:
        this._viewer.setCursor("pointer");
        break;
      default:
        this._viewer.setCursor("default");
    }
  }

  initEditor() {
    this._viewer.initEditor();
    this.selectControl = new SelectControl(this._viewer, this);
    this.transformControl = new TransformControl(this._viewer);
    this.dragCtrlService = new DragControlService(this);
    this._register();
  }
  /**
   * 开始拾取点
   * 运行此方法编辑器会进入拾取点状态，此时程序被promise挂起，无法执行其他动作
   * @param option {@link ServiceOption}
   * @returns `ServiceResult`
   * @see https://hc3dol.gkiiot.com/#21
   */
  getPoint(option?: ServiceOption) {
    return this.getPointService.start(option);
  }
  focus() {
    this._viewer.focus();
  }
  private _register() {
    this.selectControl.on(this.onSelect);

    this.mouseCtrl.on("pointerDown", this.onPointerDown);
    this.keyboardCtrl.on("keydown", this.onKeyDownCtrl);

    //控制 变换回调
    this.transformControl.positionGizmo.onDragStartObservable.add(
      this.onTransfromDragStart
    );
    this.transformControl.positionGizmo.onDragEndObservable.add(
      this.onTransfromDragEnd
    );
    this.transformControl.rotationGizmo.onDragStartObservable.add(
      this.onTransfromDragStart
    );
    this.transformControl.rotationGizmo.onDragEndObservable.add(
      this.onTransfromDragEnd
    );
    this.transformControl.scaleGizmo.onDragStartObservable.add(
      this.onTransfromDragStart
    );
    this.transformControl.scaleGizmo.onDragEndObservable.add(
      this.onTransfromDragEnd
    );
  }
  private onTransfromDragStart = () => {
    this.dragCtrlService.manager.hide();
  };
  private onTransfromDragEnd = () => {
    this.dragCtrlService.manager.update();
  };
  /**键盘按下回调 */
  private onKeyDownCtrl = (event: KeyboardEvent) => {
    switch (event.code) {
      case KeyCode.Escape: {
        for (const sv of this.services) {
          sv.cancel();
        }
        break;
      }
      default:
        break;
    }
  };
  /**选择实体回调 */
  private onSelect = (data) => {
    if (data.type === "select") {
      this.transformControl.attachMeshs(data.meshs);
      this.dragCtrlService.attach(
        data.entitys.filter((e) => e instanceof Entity)
      );
    }
  };
  /**鼠标按下回调 */
  private onPointerDown = async (event: IPointerEvent) => {
    for (const sv of this.services) {
      if (sv.isReady) {
        const status = await sv.do(event);
        if (status) {
          break;
        }
      }
    }
  };
}
