import { AppStore } from "../app/AppStore";
import { Color3, Color4, Scene, StandardMaterial } from "../babylon";
import { ColorPalette } from "../utils/color";

StandardMaterial.AmbientTextureEnabled = false;

export namespace CommonMaterials {
  const _BasicMaterialMap = new Map<number, StandardMaterial>();
  const _BasicWireframeMaterialMap = new Map<number, StandardMaterial>();
  let DefaultStandardMaterial: StandardMaterial;
  export function getColor3(index: number) {
    return Color3.FromArray(ColorPalette[index].map((c) => c / 255));
  }
  export function getColor4(index: number) {
    return Color4.FromArray(ColorPalette[index].map((c) => c / 255));
  }
  export function getColor3fromHex(index: string) {
    return Color3.FromHexString(index);
  }
  export function getColorArray(index: number) {
    return ColorPalette[index].map((c) => c / 255);
  }
  export function getStandMaterial(colorIndex: number, scene: Scene) {
    if (_BasicMaterialMap.has(colorIndex))
      return _BasicMaterialMap.get(colorIndex);
    let mtl = new StandardMaterial("__common_color_" + colorIndex, scene);
    mtl.backFaceCulling = false;
    mtl.diffuseColor = getColor3(colorIndex);
    _BasicMaterialMap.set(colorIndex, mtl);
    return mtl;
  }
  export function getWireframeStandMaterial(colorIndex: number, scene: Scene) {
    if (_BasicWireframeMaterialMap.has(colorIndex))
      return _BasicWireframeMaterialMap.get(colorIndex);
    let mtl = new StandardMaterial("__common__wf_color_" + colorIndex, scene);
    mtl.diffuseColor = getColor3(colorIndex);
    mtl.alpha = 0.0;
    _BasicWireframeMaterialMap.set(colorIndex, mtl);
    return mtl;
  }
  export function clear() {
    _BasicMaterialMap.clear();
    _BasicWireframeMaterialMap.clear();
  }
  export function getDefaultStandardMaterial() {
    if (!DefaultStandardMaterial) {
      DefaultStandardMaterial = getStandMaterial(9, AppStore.Scene);
    }
    return DefaultStandardMaterial;
  }
}
