import { SkyboxMode } from "../../types/viewer.types";
import {
  Engine,
  CubeTexture,
  HDRCubeTexture,
  Mesh,
  GlowLayer,
  Color4,
  Color3,
  StandardMaterial,
  Texture,
  BaseTexture,
  ReflectionProbe,
  PBRMaterial,
  Vector3,
  Material,
  Layer,
  CreateBox,
  Observable,
  AnimationGroup,
  SpotLight,
  Scene,
  SkyMaterial,
} from "../../babylon";
import { AppConfig } from "../../app/appConfig";
import { HCFiler, ISerialize, IVector3 } from "@hc/core";
import { setVecLike2Vector } from "../../utils/vector";
import { sleep } from "../../utils/utils";
import { SKYBOX_SIZE } from "../../contants";
import { EnvironmentTools } from "../../utils/sceneEnv";
import { AppEvent } from "../../core/AppEvent";
import { getAnimation } from "../../utils/antimationUtils";
import { hexToRgba, parseRgbaColor } from "../../utils";
import { AppStore } from "../../app/AppStore";
import { EventEnum } from "../../enums";

//https://forum.babylonjs.com/t/gradient-for-smooth-scene-limitations/20751 场景渐变
//https://playground.babylonjs.com/#9VC8L#2 渐变色

const ExcludesReflect = [
  "groundMaterial",
  "skyMaterial",
  "skyBox",
  "default material",
];

const extensions = ["px.jpg", "py.jpg", "pz.jpg", "nx.jpg", "ny.jpg", "nz.jpg"];
const DefaultClearColor = new Color4(0, 0, 0);

export enum UpdateSceneFlag {
  None = 0b0000000,
  Mode = 0b00001,
  Env = 0b00010,
  Param = 0b00100,
  All = 0b1111111,
}

export class SceneControl implements ISerialize {
  private _scene: Scene;
  private _skybox = "";
  private _image = "";

  private _skyboxMesh: Mesh;
  private _standMaterial: StandardMaterial;
  private _pbrMaterial: PBRMaterial;
  private _skyMaterial: SkyMaterial;
  private _glowLayer: GlowLayer;
  private _mode = SkyboxMode.None;
  private _reflectionProbe: ReflectionProbe;
  //浊度
  private _turbidity = 10;
  //亮度 0-1,190
  private _luminance = 1;
  private _rayleigh = 2;
  private _sunPosition = new Vector3(0, 100, 0);
  private _inclination = 0;
  private _azimuth = 0.25;
  private _mieDirectionalG = 0.8;
  private _mieCoefficient = 0.005;
  private _isReflect = false;
  private _layer: Layer;
  private reslove: (...arg) => void;
  private _env: string;
  private _iblIntensity = 1;
  private _cacheEnv = "";
  /** 环境纹理配置 */
  private _envTextureOption = {
    rotationY: 0,
    level: 1,
  };
  /**天空盒纹理配置,预留 */
  private _skyTextureOption = {
    rotationY: 0,
    level: 1,
  };
  private _isShadow = false;
  LoadObserable = new Observable<boolean>();
  ToggleShadowObserable = new Observable<boolean>();
  constructor(engine: Engine) {
    if (!engine) return;
    this._scene = new Scene(engine, {
      useMaterialMeshMap: !AppConfig.lowPerformance,
      useClonedMeshMap: !AppConfig.lowPerformance,
      useGeometryUniqueIdsMap: !AppConfig.lowPerformance,
    });
    this._scene.ambientColor = Color3.FromHexString("#222222");
    this._scene.useRightHandedSystem = true;
    this.BlockMaterialUpdate = true;
    this._scene.clearColor = DefaultClearColor;

    this.initReflection();

    this.init();
  }
  init() {
    const gl = new GlowLayer("glow", this._scene);
    gl.intensity = 0.5;
    this._glowLayer = gl;
  }
  initReflection() {
    if (this._reflectionProbe) {
      this._reflectionProbe.dispose();
    }
    const rp = new ReflectionProbe("ref", 512, this._scene);

    this._reflectionProbe = rp;
  }
  /**场景是否存在环境 */
  get IsExistEnv() {
    return this._env || this._cacheEnv;
  }
  get Scene() {
    return this._scene;
  }
  get Mode() {
    return this._mode;
  }
  set Mode(v: SkyboxMode) {
    if (v === this._mode) return;
    this._mode = v;
    this.update(UpdateSceneFlag.Mode);
  }
  get Skybox() {
    return this._skybox;
  }
  set Skybox(url: string) {
    if (this._skybox === url) return;
    this._skybox = url;
    this.showSkyBox();
  }

  set BackgroundUrl(url: string) {
    if (this.isImageType(url)) {
      this.Image = url;
      this.Mode = SkyboxMode.Image;
    } else {
      this.Skybox = url;
      this.Mode = SkyboxMode.Skybox;
    }
  }
  get Image() {
    return this._image;
  }
  set Image(url: string) {
    if (this._image === url) return;
    this._image = url;
    this.showImage();
  }

  get AmbientColor() {
    return this._scene.ambientColor.toHexString();
  }
  set AmbientColor(v: string) {
    this.Scene.ambientColor = Color3.FromHexString(v);
  }

  get EmissiveIntensity() {
    return this._glowLayer.intensity;
  }
  set EmissiveIntensity(v: number) {
    this._glowLayer.intensity = v;
  }

  get Background(): string {
    const string = this.Scene.clearColor.toHexString();
    if (string.endsWith("FF")) {
      return string.slice(0, 7);
    } else {
      return hexToRgba(string);
    }
  }
  set Background(v: Color4 | string | [number, number, number, number?]) {
    if (!v) return;
    if (typeof v === "string") {
      if (v.startsWith("#")) {
        this.Scene.clearColor = Color4.FromHexString(v);
      } else if (v.startsWith("rgb")) {
        const color = parseRgbaColor(v);
        this.Scene.clearColor = new Color4(
          color.r / 255,
          color.g / 255,
          color.b / 255,
          color.a
        );
      }
    } else {
      if (Array.isArray(v))
        this.Scene.clearColor = new Color4(
          v[0] / 255,
          v[1] / 255,
          v[2] / 255,
          v[3] ?? 1
        );
      else this.Scene.clearColor = v;
    }
  }
  get Turbidity() {
    return this._turbidity;
  }
  set Turbidity(v: number) {
    if (v === this._turbidity) return;
    this._turbidity = v;
    this.update(UpdateSceneFlag.Mode);
  }
  get Rayleigh() {
    return this._rayleigh;
  }
  set Rayleigh(v: number) {
    if (v === this._rayleigh) return;
    this._rayleigh = v;
    this.update(UpdateSceneFlag.Mode);
  }
  get Luminance() {
    return this._luminance;
  }
  set Luminance(v: number) {
    if (v === this._luminance) return;
    this._luminance = v;
    this.update(UpdateSceneFlag.Mode);
  }
  get SunPosition() {
    return {
      x: this._sunPosition.x,
      y: this._sunPosition.y,
      z: this._sunPosition.z,
    };
  }
  set SunPosition(v: IVector3) {
    setVecLike2Vector(this._sunPosition, v);
    this.update(UpdateSceneFlag.Mode);
  }
  get Inclination() {
    return this._inclination;
  }
  set Inclination(v: number) {
    if (v === this._inclination) return;
    this._inclination = v;
    this.update(UpdateSceneFlag.Mode);
  }
  get Azimuth() {
    return this._azimuth;
  }
  set Azimuth(v: number) {
    if (v === this._azimuth) return;
    this._azimuth = v;
    this.update(UpdateSceneFlag.Mode);
  }
  get IsReflect() {
    return this._isReflect;
  }
  set IsReflect(v: boolean) {
    if (v === this._isReflect) return;
    this._isReflect = v;
    if (v) {
      this._scene.onNewMaterialAddedObservable.add(this.watchMaterial);
      this.reflect();
    } else {
      this._scene.onNewMaterialAddedObservable.removeCallback(
        this.watchMaterial
      );
      this.cancelReflect();
    }
  }
  get BlockMaterialUpdate() {
    return this._scene.blockMaterialDirtyMechanism;
  }
  set BlockMaterialUpdate(v: boolean) {
    this._scene.blockMaterialDirtyMechanism = v;
  }
  get Env() {
    return this._env;
  }
  set Env(v: string) {
    if (v === this._env) return;
    this._env = v;
    this.update(UpdateSceneFlag.Env);
  }
  get IBLIntensity() {
    return this._iblIntensity;
  }
  set IBLIntensity(v) {
    this._iblIntensity = v;
    this.update(UpdateSceneFlag.Param);
  }
  get EnvRotation() {
    return this._envTextureOption.rotationY;
  }
  set EnvRotation(v: number) {
    this._envTextureOption.rotationY = v;
    this.update(UpdateSceneFlag.Param);
  }
  /**
   * 是否开启阴影
   */
  get IsShadow() {
    return this._isShadow;
  }
  set IsShadow(v: boolean) {
    if (this._isShadow === v) return;
    this._isShadow = v;
    /**通知阴影状态以及切换 */
    this.ToggleShadowObserable.notifyObservers(v);
  }
  debugger(config: any = {}) {
    if (AppConfig.debugger) {
      this._scene.debugLayer.show({
        embedMode: true,
        ...config,
      });
    }
  }
  toggleShowEnv() {
    if (this._env) {
      this._cacheEnv = this._env;
      this._env = "";
    } else {
      this._env = this._cacheEnv;
    }
    this.loadEnv();
  }
  render() {
    if (this._scene.activeCamera) {
      this._scene.render();
    }
  }
  update(flag = UpdateSceneFlag.All) {
    if (flag & UpdateSceneFlag.Mode) {
      switch (this._mode) {
        case SkyboxMode.Image:
          this.showImage();
          break;
        case SkyboxMode.Skybox:
          this.showSkyBox();
          break;
        case SkyboxMode.Sun:
          this.getSkyBox();
          this._skyMaterial.inclination = this._inclination;
          this._skyMaterial.azimuth = this._azimuth;
          // this._skyMaterial.sunPosition = this._sunPosition;
          this._skyMaterial.rayleigh = this._rayleigh;
          this._skyMaterial.turbidity = this._turbidity;
          this._skyMaterial.luminance = this._luminance;
          this._skyMaterial.mieDirectionalG = this._mieDirectionalG;
          this._skyMaterial.mieCoefficient = this._mieCoefficient;
          break;
        case SkyboxMode.Env:
          break;
        default:
          this.hiddenSkyBox();
      }
    }

    if (flag & UpdateSceneFlag.Env) this.loadEnv();

    if (flag & UpdateSceneFlag.Param) {
      this._scene.environmentIntensity = this._iblIntensity;
      if (this._scene.environmentTexture) {
        (this._scene.environmentTexture as CubeTexture).rotationY =
          this._envTextureOption.rotationY;
      }
    }
    AppEvent.trigger({ type: EventEnum.UpdateScene, target: null });
  }
  async reflect() {
    this.BlockMaterialUpdate = false;
    let i = 0;
    for (const material of this._scene.materials) {
      if (ExcludesReflect.includes(material.name)) continue;
      this.reflectMaterial(material);
      if (i === 5) {
        await sleep(100);
        i = 0;
      } else i++;
    }
    this.BlockMaterialUpdate = true;
  }
  async cancelReflect() {
    this.BlockMaterialUpdate = false;
    let i = 0;
    for (const material of this._scene.materials) {
      if (ExcludesReflect.includes(material.name)) continue;
      this.cancelReflectMaterial(material);
      if (i === 5) {
        await sleep(100);
        i = 0;
      } else i++;
    }
    this.BlockMaterialUpdate = true;
  }
  createDefaultEnv() {
    this._scene.environmentTexture = EnvironmentTools.LoadSkyboxPathTexture(
      this._scene
    );
    this._scene.createDefaultSkybox(
      this._scene.environmentTexture,
      true,
      (this._scene.activeCamera!.maxZ - this._scene.activeCamera!.minZ) / 2,
      0.3,
      false
    );
  }
  loadEnv(path: string = this._env) {
    setTimeout(() => {
      this.BlockMaterialUpdate = true;
    }, 100);
    this.BlockMaterialUpdate = false;
    if (path) {
      const texture = CubeTexture.CreateFromPrefilteredData(
        AppConfig.SkyBox_URL + path,
        this._scene
      );
      texture.onLoadObservable.add(this.onLoad);

      this._scene.environmentTexture = texture;
      return texture;
    } else {
      this._scene.environmentTexture = null;
      return null;
    }
  }
  reset() {
    this._skybox = "";
    this._env = "";
    this._cacheEnv = "";
    this._image = "";
    this._isReflect = false;
    this._isShadow = false;
    this._mode = SkyboxMode.None;
    this._scene.clearColor = DefaultClearColor;
    this._scene.ambientColor = Color3.FromHexString("#222222");
    this._reflectionProbe.dispose();
    this._reflectionProbe = new ReflectionProbe("ref", 512, this._scene);
    this.update();
  }
  writeFile(file: HCFiler): void {
    file.write(6);
    file.write(this._skybox);
    file.write(this.EmissiveIntensity);
    file.write(this.Background);
    file.write(this.AmbientColor);

    file.write(this._mode);
    file.write(this._turbidity);
    file.write(this._luminance);
    file.write(this._rayleigh);
    file.write(this._sunPosition.x);
    file.write(this._sunPosition.y);
    file.write(this._sunPosition.z);
    file.write(this._isReflect);

    file.write(this._inclination);
    file.write(this._azimuth);

    file.write(this._image);

    //v4
    file.write(this._env);
    file.write(this._envTextureOption.rotationY);

    //v5
    file.write(this._iblIntensity);

    //v6
    file.write(this._isShadow);
  }
  readFile(file: HCFiler): void {
    this.initReflection();

    const v = file.read();
    this._skybox = file.read();
    this.EmissiveIntensity = file.read();
    this.Background = file.read();

    this.AmbientColor = file.read();

    this._mode = file.read();
    this._turbidity = file.read();
    this._luminance = file.read();
    this._rayleigh = file.read();
    this._sunPosition.x = file.read();
    this._sunPosition.y = file.read();
    this._sunPosition.z = file.read();
    this.IsReflect = file.read();

    if (v > 1) {
      this._inclination = file.read();
      this._azimuth = file.read();
    }
    if (v > 2) {
      this._image = file.read();
    }

    //兼容旧数据
    if (!this._image && this.isImageType(this._skybox)) {
      this._image = this._skybox;
      this._skybox = "";
      this._mode = SkyboxMode.Image;
    }

    if (v > 3) {
      this._env = file.read();
      this._envTextureOption.rotationY = file.read();
    }
    if (v > 4) {
      this._iblIntensity = file.read();
    }
    if (v > 5) {
      this.IsShadow = file.read();
    }

    this.update();
  }
  /**TODO:港口项目临时用的脚本，需要移除 */
  testLights() {
    let [x, y, z] = [275, 30, 180];
    let [x1, z1] = [225, 146];
    let [x2, z2] = [80, 148];
    let [x3, z3] = [-71, 190];

    const points = [
      // new Vector3(x, y, z),
      new Vector3(x, y, z - 36),
      // new Vector3(x, y, z - 36 * 2),

      // new Vector3(x1, y, z1),
      new Vector3(x1 - 43, y, z1),
      // new Vector3(x1 - 43 * 2, y, z1),
      // new Vector3(x1, y, z1 - 30),
      new Vector3(x1 - 43, y, z1 - 30),
      // new Vector3(x1 - 43 * 2, y, z1 - 30),

      // new Vector3(x2, y, z2),
      new Vector3(x2 - 43, y, z2),
      // new Vector3(x2 - 43 * 2, y, z2),
      // new Vector3(x2, y, z2 - 30),
      // new Vector3(x2 - 43, y, z2 - 30),
      // new Vector3(x2 - 43 * 2, y, z2 - 30),

      // new Vector3(x3, y, z3),
      new Vector3(x3, y, z3 - 43),
      // new Vector3(x3, y, z3 + 43 * 2),
      // new Vector3(x3 - 30, y, z3),
      // new Vector3(x3 - 30, y, z3 - 43),
      // new Vector3(x3 - 30, y, z3 + 43 * 2),
    ];
    let index = 1;
    let intensity = 3000;
    let range = 1000;

    this._scene.materials.forEach(
      //@ts-ignore
      (m) => (m.maxSimultaneousLights = points.length + 1)
    );

    for (const pos of points) {
      const spot = new SpotLight(
        "s" + index++,
        pos,
        new Vector3(0, -1),
        Math.PI * 2,
        2,
        this._scene
      );
      spot.intensity = intensity;
      spot.range = range;
    }
  }
  /**TODO:港口项目临时用的脚本，需要移除 */
  day2Evening(isRevert = false) {
    if (!this._skyMaterial) return;
    let time = 6;

    const TV = [this._turbidity, 1];
    const RV = [this._rayleigh, 0];
    const LV = [this._luminance, 0.8];
    const IV = [this._inclination, -0.48];
    const AV = [this._inclination, -0.48];
    const LIV = [1, -0.1];

    if (isRevert) {
      TV.reverse();
      RV.reverse();
      LV.reverse();
      IV.reverse();
      AV.reverse();
      LIV.reverse();
    }

    const group = new AnimationGroup("d2e");

    const turAn = getAnimation(TV[0], TV[1], "material.turbidity", time);

    const RayleighAn = getAnimation(RV[0], RV[1], "material.rayleigh", time);

    const lumAn = getAnimation(LV[0], LV[1], "material.luminance", time);

    const intAn = getAnimation(IV[0], IV[1], "material.inclination", time);
    const AzAn = getAnimation(AV[0], AV[1], "material.azimuth", time);

    const light = this._scene.lights[0];
    const SceneAn = getAnimation(LIV[0], LIV[1], "intensity", time);

    this._scene.stopAnimation(this._skyboxMesh);
    this._scene.stopAnimation(light);

    for (const a of [turAn, RayleighAn, lumAn, intAn, AzAn])
      group.addTargetedAnimation(a, this._skyboxMesh);

    group.addTargetedAnimation(SceneAn, light);

    group.start(false, 1, 0, 30 * time);

    // this._scene.beginDirectAnimation(
    //   this._skyboxMesh,
    //   [turAn, RayleighAn, lumAn, intAn, AzAn],
    //   0,
    //   30 * time,
    //   false,
    //   1
    // );
    // this._scene.beginDirectAnimation(light, [SceneAn], 0, 30 * time, false, 1);
  }
  showFog(start = 200, end = 1000) {
    this._scene.fogMode = Scene.FOGMODE_LINEAR;
    this._scene.fogColor = new Color3(0.9, 0.9, 0.85);
    this._scene.fogDensity = 0.01;
    this._scene.fogStart = start;
    this._scene.fogEnd = end;
  }
  private isImageType(url: string) {
    return ["png", "jpg", "jpeg"].some((a) => url.endsWith(a));
  }
  private showSkyBox() {
    if (this._skybox) {
      this.hiddenLayer();
      if (this._skybox.endsWith("hdr")) {
        this.setSkyboxByHdr(AppConfig.SkyBox_URL + this._skybox);
      } else {
        this.reslove = AppStore.Wait();
        AppEvent.trigger({ type: "loadingBackground", target: null });
        this.setEnvTexture(
          new CubeTexture(
            AppConfig.SkyBox_URL + this._skybox,
            this._scene,
            extensions,
            undefined,
            null,
            this.onLoad
          )
        );
      }
    } else {
      this.hiddenSkyBox();
    }
  }
  private showImage() {
    if (this._image) {
      this.hiddenBox();
      if (!this._layer) {
        this._layer = new Layer(
          "GlobalBackground",
          AppConfig.SkyBox_URL + this._image,
          this._scene,
          true
        );
      } else {
        this.reslove = AppStore.Wait();
        AppEvent.trigger({ type: "loadingBackground", target: null });
        this._layer.isEnabled = true;
        (this._layer.texture as Texture).updateURL(
          AppConfig.SkyBox_URL + this._image,
          null,
          this.onLoad
        );
      }
    } else {
      this.hiddenSkyBox();
    }
  }
  private hiddenLayer() {
    if (this._layer) this._layer.isEnabled = false;
  }
  private hiddenBox() {
    if (this._skyboxMesh) {
      this._skyboxMesh.setEnabled(false);
    }
  }
  private hiddenSkyBox() {
    this.hiddenBox();
    this.hiddenLayer();
  }
  private setSkyboxByHdr(url: string) {
    const cubeTexture = new HDRCubeTexture(url, this._scene, 512);
    this.setEnvTexture(cubeTexture);
  }
  private getSkyBox() {
    const box = this.createSkybox();
    box.setEnabled(true);

    if (this._mode === SkyboxMode.Sun) {
      box.material = this.getSkyMaterial();
    } else if (this._mode === SkyboxMode.Env) {
      box.material = this.getPBRMaterial();
    } else {
      box.material = this.getStandMaterial();
    }

    return box;
  }
  private createSkybox() {
    if (this._skyboxMesh) return this._skyboxMesh;

    const skybox = CreateBox("skybox", { size: SKYBOX_SIZE }, this._scene);
    skybox.isPickable = false;
    skybox.infiniteDistance = true;
    skybox.ignoreCameraMaxZ = true;

    this._reflectionProbe.renderList.push(skybox);

    this._skyboxMesh = skybox;

    return this._skyboxMesh;
  }
  private getStandMaterial() {
    if (this._standMaterial) return this._standMaterial;
    const skyboxMaterial = new StandardMaterial("skyBox", this._scene);

    this._standMaterial = skyboxMaterial;

    skyboxMaterial.backFaceCulling = false;

    skyboxMaterial.disableLighting = true;
    this._standMaterial = skyboxMaterial;

    return this._standMaterial;
  }
  private getPBRMaterial() {
    if (this._pbrMaterial) return this._pbrMaterial;
    const hdrSkyboxMaterial = new PBRMaterial("skyBox", this._scene);
    hdrSkyboxMaterial.backFaceCulling = false;
    if (hdrSkyboxMaterial.reflectionTexture) {
      hdrSkyboxMaterial.reflectionTexture.coordinatesMode = Texture.SKYBOX_MODE;
    }
    hdrSkyboxMaterial.microSurface = 1.0;
    hdrSkyboxMaterial.disableLighting = true;
    hdrSkyboxMaterial.twoSidedLighting = true;
    this._pbrMaterial = hdrSkyboxMaterial;
    return this._pbrMaterial;
  }
  private getSkyMaterial() {
    if (this._skyMaterial) return this._skyMaterial;

    const material = new SkyMaterial("skyMaterial", this._scene);
    // material.useSunPosition = true;
    material.backFaceCulling = false;
    this._skyMaterial = material;

    return this._skyMaterial;
  }
  private setEnvTexture(environmentTexture: BaseTexture) {
    const skybox = this.getSkyBox();
    const skyboxMaterial = skybox.material as StandardMaterial | PBRMaterial;
    skyboxMaterial.reflectionTexture = environmentTexture;
    if (skyboxMaterial.reflectionTexture) {
      skyboxMaterial.reflectionTexture.coordinatesMode = Texture.SKYBOX_MODE;
    }
  }
  private watchMaterial = (material: Material) => {
    setTimeout(() => {
      this.reflectMaterial(material);
    }, 0);
  };
  private reflectMaterial(material: Material) {
    if (ExcludesReflect.includes(material.name)) return;
    if (
      material instanceof StandardMaterial ||
      material instanceof PBRMaterial
    ) {
      this.BlockMaterialUpdate = false;
      // material.unfreeze();
      const oldRefl = (<StandardMaterial | PBRMaterial>material)
        .reflectionTexture;

      material.metadata = oldRefl;

      (<StandardMaterial | PBRMaterial>material).reflectionTexture =
        this._reflectionProbe.cubeTexture;

      if (material instanceof PBRMaterial) material.realTimeFiltering = true;
      // setTimeout(() => {
      //   material.freeze();
      // }, 0);
      this.BlockMaterialUpdate = true;
    }
  }
  private cancelReflectMaterial(material: Material) {
    if (ExcludesReflect.includes(material.name)) return;
    if (
      material instanceof StandardMaterial ||
      material instanceof PBRMaterial
    ) {
      // material.unfreeze();

      (<StandardMaterial | PBRMaterial>material).reflectionTexture =
        material.metadata;

      material.metadata = null;

      //@ts-ignore
      material.realTimeFiltering = false;

      // setTimeout(() => {
      //   material.freeze();
      // }, 0);
    }
  }
  private onLoad = () => {
    AppEvent.trigger({ type: "loadBackground", target: null });
    this.LoadObserable.notifyObservers(true);
    if (this.reslove) {
      this.reslove();
      this.reslove = null;
    }
  };
}
