import {
  ArcRotateCameraPointersInput,
  Camera,
  IPointerEvent,
  Nullable,
  PointerTouch,
} from "../../babylon";

export class CustomArcRotateCameraPointersInput extends ArcRotateCameraPointersInput {
  public panningSensibility = 1000;
  private _button = -1;

  private twoFingerActivityCount: number = 0;
  private isPinching: boolean = false;

  enablePan = false;
  enableRotate = true;
  panPercentage = 0.1;
  /**
   * 使用单指触控手势来控制摄像头的旋转
   */
  useTouchForRotating = false;
  /**
   * 使用自然平移模式
   *
   * 表现为摄像头的移动更为跟手
   */
  useNaturalPan = true;
  /**
   * 指定一个两指之间距离，用来判断当前对摄像头进行平移还是缩放
   *
   * 当初始状态下的手指间距离大于这个值的时候判定为缩放
   *
   * 注意：两点距离在 iPad 上测得的值会比华为平板上的小
   */
  pinchToPanMaxDistance = 125;

  private getPanSpeedPercent() {
    if (this.camera.mode === Camera.PERSPECTIVE_CAMERA)
      return 1000 / (this.panPercentage * this.camera.radius);
    else return this.panPercentage * 500;
  }
  onButtonDown(evt: IPointerEvent) {
    this._button = evt.button;
  }
  public onTouch(
    point: Nullable<PointerTouch>,
    offsetX: number,
    offsetY: number
  ) {
    if (
      this.panningSensibility !== 0 &&
      (this._ctrlKey || this._altKey) &&
      this._button === 1
    ) {
      this.pan(offsetX, offsetY);
    } else {
      if (this._button === 1) {
        this.rotate(offsetX, offsetY);
      } else if (this.useTouchForRotating && point.type === "touch") {
        this.rotate(offsetX, offsetY);
      }
    }
  }

  /**
   * Called on pointer POINTERMOVE event if multiple touches are active.
   *
   * 在`ArcRotateCameraPointersInput` 基础上修改了一些内容
   * 修改内容
   * - 根据初始的两指间距离来判断当前是平移手势还是缩放手势
   * @param pointA
   * @param pointB
   * @param previousPinchSquaredDistance
   * @param pinchSquaredDistance
   * @param previousMultiTouchPanPosition
   * @param multiTouchPanPosition
   */
  public onMultiTouch(
    pointA: Nullable<PointerTouch>,
    pointB: Nullable<PointerTouch>,
    previousPinchSquaredDistance: number,
    pinchSquaredDistance: number,
    previousMultiTouchPanPosition: Nullable<PointerTouch>,
    multiTouchPanPosition: Nullable<PointerTouch>
  ): void {
    if (
      previousPinchSquaredDistance === 0 &&
      previousMultiTouchPanPosition === null
    ) {
      // First time this method is called for new pinch.
      // Next time this is called there will be a
      // previousPinchSquaredDistance and pinchSquaredDistance to compare.
      return;
    }
    if (pinchSquaredDistance === 0 && multiTouchPanPosition === null) {
      // Last time this method is called at the end of a pinch.
      return;
    }

    // Zoom and panning enabled together
    if (this.multiTouchPanAndZoom) {
      this.computePinchZoom(previousPinchSquaredDistance, pinchSquaredDistance);
      this.computeMultiTouchPanning(
        previousMultiTouchPanPosition,
        multiTouchPanPosition
      );

      // Zoom and panning enabled but only one at a time
    } else if (this.multiTouchPanning && this.pinchZoom) {
      this.twoFingerActivityCount++;

      if (this.twoFingerActivityCount === 1) {
        this.isPinching =
          this.getDistanceBetweenFingers(pointA, pointB) >=
          this.pinchToPanMaxDistance;
      }

      if (this.isPinching) {
        // Since pinch has not been active long, assume we intend to zoom.
        this.computePinchZoom(
          previousPinchSquaredDistance,
          pinchSquaredDistance
        );

        // Since we are pinching, remain pinching on next iteration.
        this.isPinching = true;
      } else {
        // Pause between pinch starting and moving implies not a zoom event. Pan instead.
        this.computeMultiTouchPanning(
          previousMultiTouchPanPosition,
          multiTouchPanPosition
        );
      }

      // Panning enabled, zoom disabled
    } else if (this.multiTouchPanning) {
      this.computeMultiTouchPanning(
        previousMultiTouchPanPosition,
        multiTouchPanPosition
      );

      // Zoom enabled, panning disabled
    } else if (this.pinchZoom) {
      this.computePinchZoom(previousPinchSquaredDistance, pinchSquaredDistance);
    }
  }

  /**
   * Called each time a new POINTERUP event occurs. Ie, for each button
   * release.
   */
  public onButtonUp(): void {
    super.onButtonUp();
    this.twoFingerActivityCount = 0;
    this.isPinching = false;
  }

  /**
   * Called when window becomes inactive.
   */
  public onLostFocus(): void {
    super.onLostFocus();
    this.twoFingerActivityCount = 0;
    this.isPinching = false;
  }

  private pan(offsetX: number, offsetY: number) {
    if (!this.enablePan) return;
    const speed = this.getPanSpeedPercent();
    this.camera.inertialPanningX += -offsetX / speed;
    this.camera.inertialPanningY += offsetY / speed;
  }
  private rotate(offsetX: number, offsetY: number) {
    if (!this.enableRotate) return;
    this.camera.inertialAlphaOffset -= offsetX / this.angularSensibilityX;
    this.camera.inertialBetaOffset -= offsetY / this.angularSensibilityY;
  }

  /**
   * Move camera from multi touch panning positions.
   *
   * 在`ArcRotateCameraPointersInput` 基础上新增了一些内容
   * 新增内容
   * - 在原算法的基础上增加了自然平移的算法
   * @param previousMultiTouchPanPosition
   * @param multiTouchPanPosition
   */
  private computeMultiTouchPanning(
    previousMultiTouchPanPosition: Nullable<PointerTouch>,
    multiTouchPanPosition: Nullable<PointerTouch>
  ): void {
    if (
      this.panningSensibility !== 0 &&
      previousMultiTouchPanPosition &&
      multiTouchPanPosition
    ) {
      const moveDeltaX =
        multiTouchPanPosition.x - previousMultiTouchPanPosition.x;

      const moveDeltaY =
        multiTouchPanPosition.y - previousMultiTouchPanPosition.y;

      // 增加自然平移模式的计算方法
      if (this.useNaturalPan) {
        const radius = Math.max(this.camera.radius, -Number.MAX_SAFE_INTEGER);
        this.camera.inertialPanningX += -(moveDeltaX * radius) / 1750;
        this.camera.inertialPanningY += (moveDeltaY * radius) / 1750;
      } else {
        this.camera.inertialPanningX += -moveDeltaX / this.panningSensibility;
        this.camera.inertialPanningY += moveDeltaY / this.panningSensibility;
      }
    }
  }

  /**
   * Move camera from pinch zoom distances.
   * @param previousPinchSquaredDistance
   * @param pinchSquaredDistance
   */
  private computePinchZoom(
    previousPinchSquaredDistance: number,
    pinchSquaredDistance: number
  ): void {
    const radius =
      this.camera.radius || ArcRotateCameraPointersInput.MinimumRadiusForPinch;

    if (this.useNaturalPinchZoom) {
      this.camera.radius =
        (radius * Math.sqrt(previousPinchSquaredDistance)) /
        Math.sqrt(pinchSquaredDistance);
    } else if (this.pinchDeltaPercentage) {
      this.camera.inertialRadiusOffset +=
        (pinchSquaredDistance - previousPinchSquaredDistance) *
        0.001 *
        radius *
        this.pinchDeltaPercentage;
    } else {
      this.camera.inertialRadiusOffset +=
        (pinchSquaredDistance - previousPinchSquaredDistance) /
        ((this.pinchPrecision *
          (this.pinchInwards ? 1 : -1) *
          (this.angularSensibilityX + this.angularSensibilityY)) /
          2);
    }
  }

  private getDistanceBetweenFingers(
    pointA: Nullable<PointerTouch>,
    pointB: Nullable<PointerTouch>
  ) {
    return Math.sqrt((pointA.x - pointB.x) ** 2 + (pointA.y - pointB.y) ** 2);
  }
}
