/**
 * 判断是否为触控板事件
 * @param e 滚轮事件
 */
export function isWheelByTrackpad(e: WheelEvent): boolean {
  // 这里使用已经废弃的 `wheelDeltaX` 字段来辅助判断当前的滚轮滚动事件是否由触摸板触发
  const wheelDeltaX: number | undefined = (e as any).wheelDeltaX;
  const wheelDeltaY: number | undefined = (e as any).wheelDeltaY;

  // 苹果触控板触发的滚轮的事件中 `wheelDeltaX / deltaX === -3` 恒成立，`wheelDeltaY` 和 `delta` 同理
  // wheelDeltaX 一般不等于 120 的倍数
  // 通过检测当前的输入是否满足该条件来判断是否为触控板事件
  const predicate = (delta?: number) =>
    delta != null &&
    delta !== 0 &&
    delta / e.deltaX === -3 &&
    delta % 120 !== 0;

  if (predicate(wheelDeltaX)) {
    return true;
  }

  if (predicate(wheelDeltaY)) {
    return true;
  }

  return false;
}
