import { IAnimation } from "./../types/animation.types";
import { HCFiler } from "@hc/core";
import {
  Animatable,
  Animation as BAnimation,
  AnimationGroup,
  Observer,
} from "../babylon";
import { Entity } from "../entitys/Entity";
import { removeOne } from "../utils";
import { IAnimationOption } from "../types";

export class Animation implements IAnimation {
  protected _name = "未命名";
  protected _animation: BAnimation | AnimationGroup;
  protected _animatable: Animatable;
  protected _entity?: Entity;
  protected _speedRadio = 1;
  protected _time = 1;
  protected _loop = false;
  protected _frame = 30;
  protected _loopCount: number;
  protected _speed;
  protected completeCallback: () => void;
  protected _meshId?: string;
  constructor(entity: Entity, option: IAnimationOption) {
    let { time = 1, loop = false, frame = 30, loopCount } = option;
    this.completeCallback = option?.complete;
    this._meshId = option?.meshId;
    this._entity = entity;
    this._time = time;
    this._loop = loop;
    this._frame = frame;
    this._loopCount = loopCount;
  }
  get Animation() {
    return this._animation;
  }
  get name() {
    return this._name;
  }
  set name(v: string) {
    this._name = v;
  }
  getNodeByMeshId(meshId) {
    const meshs = this._entity.DrawObject.getChildMeshes();
    const mesh = meshs.find((m) => m.name.includes(meshId));
    return mesh;
  }
  get Target() {
    if (this._meshId) {
      return this.getNodeByMeshId(this._meshId);
    } else {
      return this._entity?.isPart
        ? this._entity.RenderObject
        : this._entity.DrawObject;
    }
  }
  init() {}
  start() {
    if (!this.Target?.getScene?.()) return;
    this._animatable = this.Target.getScene().beginAnimation(
      this.Target,
      0,
      this._time * this._frame,
      this._loop,
      this._speedRadio
    );

    if (this.completeCallback) {
      this._animatable.onAnimationEndObservable.add(this.completeCallback);
    }

    if (this._loopCount && this._loop) {
      this.setLoopCount(this._loopCount);
    }
  }
  pause() {
    if (this._animatable) {
      this._animatable.pause();
    }
  }
  restart() {
    if (this._animatable) {
      this._animatable.restart();
    }
  }
  stop() {
    if (this._animatable) {
      this._animatable.stop();
    }
  }
  reset() {
    if (this._animatable) {
      this._animatable.reset();
    }
  }
  clear() {
    const renderObject = this._entity?.RenderObject;
    if (renderObject) {
      removeOne(renderObject.animations, this._animation);
    }
  }

  writeFile(file: HCFiler) {
    file.write(1);
    file.write(this._name);
    file.write(this._time);
    file.write(this._loop);
    file.write(this._frame);
    file.write(this._speedRadio);
    file.write(this._loopCount);
  }
  readFile(file: HCFiler) {
    file.read(); // 版本
    this._name = file.read();
    this._time = file.read();
    this._loop = file.read();
    this._frame = file.read();
    this._speedRadio = file.read();
    this._loopCount = file.read();
  }
  private setLoopCount(loopCount: number) {
    let loopObserable: Observer<Animatable>;
    if (loopCount) {
      let count = 0;
      loopObserable = this._animatable.onAnimationLoopObservable.add((data) => {
        count++;
        if (count === loopCount) {
          data.stop();
          this._animatable.onAnimationEndObservable.remove(loopObserable);
        }
      });
    }
    return () => {
      loopObserable &&
        this._animatable.onAnimationEndObservable.remove(loopObserable);
    };
  }
}
