import { Entity } from "../..";
import { BaseWidget } from "../../core/BaseWidget";
import { Factory, HCFiler, ObjectId } from "@hc/core";

@Factory
export class Highlight extends BaseWidget {
  static type = "Highlight";
  static schema = {
    label: "显示高亮边框",
  };
  private _list: { color: string; objectId: ObjectId<Entity> }[] = [];

  get List() {
    return [...this._list];
  }
  async do() {
    for (const data of this._list) {
      const en = data.objectId.Object;
      en.highlight(data.color);
    }
    return true;
  }
  fromJSON(data: any[]): void {
    this._list = data;
  }
  readFile(filer: HCFiler): void {
    filer.read();
    this._list.length = 0;
    filer.readAnyArray(() => {
      const d = { color: "", objectId: null };
      d.color = filer.read();
      d.objectId = filer.readObjectId();
      this._list.push(d);
    });
  }
  writeFile(filer: HCFiler): void {
    filer.write(1);
    filer.writeAnyArray(this._list, (d) => {
      filer.write(d.color);
      filer.writeObjectId(d.objectId);
    });
  }
}
