import { LinesMesh, Vector3 } from "../../babylon";
import { CommonMaterials } from "../../materials/commonMaterials";
import { Entity } from "../Entity";

export abstract class Curve extends Entity {
  updateMaterial() {
    const l = this.RenderObject as LinesMesh;
    l.color = CommonMaterials.getColor3(this.ColorIndex);
  }
  /**
   * 曲线点的原始位置
   *
   * @readonly
   * @memberof Lines
   */
  get Points(): Vector3[] {
    return [];
  }
  /**
   * 曲线点的实际位置
   *
   * @readonly
   * @type {Vector3[]}
   * @memberof Curve
   */
  get Shape(): Vector3[] {
    return this.Points;
  }
  get AllPoints() {
    return this.Points;
  }
  get StartPoint() {
    return new Vector3();
  }
  get EndPoint() {
    return new Vector3();
  }
  get MidPoint() {
    return new Vector3();
  }
  get Length() {
    return 0;
  }
  reverse() {
    return this;
  }
}
