const tempEval = eval;

//TODO:表达式合法性验证

export function evalExpr(
  expr: string,
  params?: Record<string, string | number>
): number {
  if (!expr) return 0;

  let code = "";
  if (params) for (let name in params) code += `let ${name} = ${params[name]};`;

  code += expr;

  let result = tempEval(code);

  if (typeof result === "function") return result();

  return Number(result);
}
