export function setNodeMetaData(
  node: { metadata?: any },
  key: string,
  value: any
) {
  if (!node.metadata) {
    node.metadata = { [key]: value };
  } else {
    if (typeof node.metadata === "object") {
      node.metadata[key] = value;
    }
  }
}
export function getNodeMetaData(node: { metadata?: any }, key: string) {
  if (!node.metadata) {
    return null;
  } else {
    if (typeof node.metadata === "object") {
      return node.metadata[key];
    }
  }
  return null;
}
