import { IVector2 } from "@hc/core";
import { Vector2, Vector3 } from "@babylonjs/core/Maths/math.vector";
import { equaln } from "./utils";

export function getPolylineCenter(
  p1: Vector2,
  p2: Vector2,
  bulge: number
): Vector2 {
  const arc2d = new Arc2d(p1, p2, bulge);
  return arc2d.MidPoint;
}

export class Arc2d {
  StartAngle: number;
  EndAngle: number;
  StartPoint: Vector2;
  EndPoint: Vector2;
  Center: Vector2;
  Radius: number;
  MidPoint: Vector2;
  constructor(p1: Vector2, p2: Vector2, public bul: number) {
    this.StartPoint = p1.clone();
    this.EndPoint = p2.clone();

    let vec: Vector2 = p2.subtract(p1);
    let len = vec.length();
    let an = angle(vec);

    this.Radius = Math.abs(len / Math.sin(2 * Math.atan(bul)) / 2);
    let delDis = Math.abs((bul * len) / 2);
    let toDis = this.Radius - delDis;
    an += Math.PI * 0.5 * Math.sign(bul);

    this.Center = Vector2.Center(p1, p2);
    this.MidPoint = this.Center.clone();
    polar(this.Center, an, toDis);
    polar(this.MidPoint, an + Math.PI, delDis);

    this.StartAngle = angle(p1.clone().subtract(this.Center));
    this.EndAngle = angle(p2.clone().subtract(this.Center));
    if (bul < 0) {
      this.StartAngle -= Math.PI;
      this.EndAngle -= Math.PI;
    }
  }
  // get MidPoint() {
  //   const vec1 = asVector3(this.StartPoint.subtract(this.Center)).normalize();
  //   const vec2 = asVector3(this.EndPoint.subtract(this.Center)).normalize();

  //   if (isParallelTo(vec1, vec2)) {
  //     const angle = this.AllAngle / 2;
  //     vec1.applyRotationQuaternionInPlace(
  //       Quaternion.RotationAxis(Axis.Z, angle)
  //     );

  //     return this.Center.add(asVector2(vec1).scale(this.Radius));
  //   } else {
  //     const vec3 = vec1.add(vec2).normalize().scale(this.Radius);
  //     const v1 = vec1.cross(vec3).normalize();

  //     if (v1.z === Math.sign(this.bul)) {
  //       return this.Center.add(asVector2(vec3));
  //     } else {
  //       return this.Center.add(asVector2(vec3.negate()));
  //     }
  //   }
  // }
  get AllAngle() {
    return Math.atan(this.bul) * 4;
  }
}

/**
 * 按照极坐标的方式移动一个点
 *
 * @export
 * @template
 * @param {T} v 向量(2d,3d)
 * @param {number} an 角度
 * @param {number} dis 距离
 * @returns {T}
 */
export function polar<T extends Vector2 | Vector3>(
  v: T,
  an: number,
  dis: number
): T {
  v.x += Math.cos(an) * dis;
  v.y += Math.sin(an) * dis;
  return v;
}

export function angle(vec: Vector2 | Vector3) {
  return Math.atan2(-vec.y, -vec.x) + Math.PI;
}

export function isParallelTo(v1: Vector3, v2: Vector3, fuzz = 1e-8): boolean {
  return v1.clone().cross(v2).lengthSquared() < fuzz;
}

export function asVector3(vec: Vector2) {
  return new Vector3(vec.x, vec.y);
}
export function asVector2(vec: Vector3) {
  return new Vector2(vec.x, vec.y);
}

export function getOrthPoint(basePt: IVector2, targetPt: IVector2) {
  const p1 = new Vector2(basePt.x, basePt.y);
  const p2 = new Vector2(targetPt.x, targetPt.y);

  const vec = p2.subtract(p1);

  const an = angle(vec);

  if (an <= Math.PI / 4 || an >= (Math.PI * 7) / 4) {
    //修正到X
    targetPt.y = basePt.y;
  } else if (an > Math.PI / 4 && an <= (Math.PI * 3) / 4) {
    //修正到正Y
    targetPt.x = basePt.x;
  } else if (an > (Math.PI * 3) / 4 && an <= (Math.PI * 5) / 4) {
    //修正到正-X
    targetPt.y = basePt.y;
  } else {
    //修正到正-Y
    targetPt.x = basePt.x;
  }

  return targetPt;
}

//  获取点到直线的距离
export function getPointToLineDistance(
  list: [Vector2 | Vector3, Vector2 | Vector3],
  point: Vector2 | Vector3
) {
  let [p1, p2] = list;
  if (p1.x === p2.x) {
    return Math.abs(point.x - p1.x);
  } else if (p1.y === p2.y) {
    return Math.abs(point.y - p1.y);
  }

  return (
    Math.abs(
      (p1.y - p2.y) * point.x +
        (p2.x - p1.x) * point.y +
        p1.x * p2.y -
        p1.y * p2.x
    ) / Math.sqrt((p1.y - p2.y) * (p1.y - p2.y) + (p1.x - p2.x) * (p1.x - p2.x))
  );
}

/**获取最近点信息 */
export function getClosestPoint(sp: Vector2, ep: Vector2, target: Vector2) {
  if (sp.equalsWithEpsilon(target) || ep.equalsWithEpsilon(target)) {
    return {
      closestPt: target,
      dist: 0,
    };
  }

  const dir = ep.subtract(sp);

  let length = dir.length();

  if (equaln(length, 0)) {
    return {
      closestPt: sp,
      dist: 0,
    };
  }

  dir.scaleInPlace(1 / length);

  let diff = target.subtract(sp);
  let param = Vector2.Dot(dir, diff);

  const closestPt = sp.add(dir.scale(param));
  return {
    closestPt,
    dist: Vector2.Distance(target, closestPt),
  };
}

/**
 * 将角度调整为0-2pi之间
 */
export function clampRad(an: number) {
  an = an % (Math.PI * 2);
  if (an < 0) an += Math.PI * 2;
  return an;
}

export function getDeterminantFor2V(
  v1: Vector3 | Vector2,
  v2: Vector2 | Vector3
) {
  return v1.x * v2.y - v1.y * v2.x;
}

/**计算平面点区域的面积，不考虑圆弧的情况 */
export function getAreaByPoints(points: Vector3[]) {
  let area = 0;
  for (let i = 0; i < points.length - 1; i++) {
    let startV = points[i];
    let endV = points[i + 1];

    area += getDeterminantFor2V(startV, endV);
  }

  return area;
}
