import { CylinderParticleEmitter, IParticleEmitterType } from "../../babylon";
import { RadiusEmiter } from "./RadiusEmiter";
import type { ParticleSystem } from "../ParticleSystem";
import { HCFiler, Factory } from "@hc/core";

@Factory
export class CylinderEmitter extends RadiusEmiter {
  static type = "CylinderEmitter";
  protected height = 1;
  protected _emiter: CylinderParticleEmitter;
  constructor(
    protected attachSystem: ParticleSystem,
    option?: { radius?: number; radiusRange?: number; height?: number }
  ) {
    super(attachSystem, option);

    if (option?.height) {
      this.height = option.height;
    }
    if (attachSystem?.HasIntance) this.setEmiter();
  }
  setEmiter() {
    this._emiter = this.attachSystem.Intance.createCylinderEmitter(
      this._radius,
      this.height,
      this._radiusRange
    );
  }
  writeFile(file: HCFiler): void {
    super.writeFile(file);
    file.write(1);
    file.write(this.height);
  }
  readFile(file: HCFiler): void {
    super.readFile(file);
    file.read();
    this.height = file.read();
  }
}
