import type { WebVRFreeCamera } from "@babylonjs/core/Cameras/VR/webVRCamera";
import {
  Gizmo,
  Nullable,
  Node,
  Vector3,
  Camera,
  TransformNode,
  Space,
  Matrix,
} from "../../babylon";
import { Box3 } from "../../utils";

export class Gizmos extends Gizmo {
  protected _attachedNodes: Nullable<Node[]> = null;
  protected _attachedNodesChanged(value: Nullable<Node[]>) {}
  protected _tempVector_ = new Vector3();
  public gizmoPosition: Vector3 = null;

  get attachedNode() {
    return super.attachedNode;
  }
  set attachedNode(b) {
    this._attachedNodes = null;
    super.attachedNode = b;
  }
  get attachedMesh() {
    return super.attachedMesh;
  }
  set attachedMesh(b) {
    this._attachedNodes = null;
    super.attachedMesh = b;
  }
  get AttachedNodes() {
    let nodes: Node[];
    if (this.attachedNode) {
      nodes = [this.attachedNode];
    } else if (this.attachedNodes) {
      nodes = this.attachedNodes;
    }
    return nodes;
  }
  public get attachedNodes() {
    return this._attachedNodes;
  }
  public set attachedNodes(value) {
    //@ts-ignore
    this._attachedNode = null;
    //@ts-ignore
    this._attachedMesh = null;

    this._attachedNodes = value;

    this._rootMesh.setEnabled(!!value);
    this._attachedNodesChanged(value);
    this.setGizmosPosition();
    if (this.gizmoPosition)
      this._rootMesh.position.copyFrom(this.gizmoPosition);
  }
  protected setGizmosPosition() {
    if (this.attachedNodes) {
      let effectiveNodes = this.attachedNodes;
      const rows = effectiveNodes.map((n) => n.getWorldMatrix().getRow(3));

      const positions = rows.map((row) =>
        row ? row.toVector3() : new Vector3(0, 0, 0)
      );

      if (positions.length === 1) {
        this.gizmoPosition = null;
      } else {
        this.gizmoPosition = Box3.GetCenterFromPoints(positions);
      }
    } else {
      this.gizmoPosition = null;
    }
  }
  protected _nodeMatrixChanged(node: Node) {
    //@ts-ignore
    let oldNode = this._attachedNode;
    //@ts-ignore
    this._attachedNode = node;
    this._matrixChanged();
    //@ts-ignore
    this._attachedNode = oldNode;
  }
  protected _handlePivots() {
    const attachedNodeTransforms = this._attachedNodes as any;

    for (const attachedNodeTransform of attachedNodeTransforms) {
      // check there is an active pivot for the TransformNode attached
      if (
        attachedNodeTransform.isUsingPivotMatrix &&
        attachedNodeTransform.isUsingPivotMatrix()
      ) {
        // When a TransformNode has an active pivot, even without parenting,
        // translation from the world matrix is different from TransformNode.position.
        // Pivot works like a virtual parent that's using the node orientation.
        // As the world matrix is transformed by the gizmo and then decomposed to TRS
        // its translation part must be set to the Node's position.
        attachedNodeTransform
          .getWorldMatrix()
          .setTranslation(attachedNodeTransform.position);
      }
    }
  }
  protected _update() {
    super._update();
    if (this.attachedNodes) {
      let effectiveNodes = this.attachedNodes;
      if (this.updateGizmoPositionToMatchAttachedMesh) {
        this.setGizmosPosition();
        if (this.gizmoPosition)
          this._rootMesh.position.copyFrom(this.gizmoPosition);
      }

      // Rotation
      if (this.updateGizmoRotationToMatchAttachedMesh) {
        this._rootMesh.rotationQuaternion!.set(0, 0, 0, 1);
      }

      // Scale
      if (this.updateScale) {
        const activeCamera = this.gizmoLayer.utilityLayerScene.activeCamera!;
        let cameraPosition = activeCamera.globalPosition;
        if ((<WebVRFreeCamera>activeCamera).devicePosition) {
          cameraPosition = (<WebVRFreeCamera>activeCamera).devicePosition;
        }
        this._rootMesh.position.subtractToRef(
          cameraPosition,
          this._tempVector_
        );
        const dist = this._tempVector_.length() * this.scaleRatio;
        this._rootMesh.scaling.set(dist, dist, dist);

        // Account for handedness, similar to Matrix.decompose
        if (
          effectiveNodes.some((n) => n._getWorldMatrixDeterminant() < 0) &&
          !Gizmo.PreserveScaling
        ) {
          this._rootMesh.scaling.y *= -1;
        }
      } else {
        this._rootMesh.scaling.setAll(this.scaleRatio);
      }
    } else {
      this.gizmoPosition = null;
    }

    if (this.updateScale) {
      const activeCamera = this.gizmoLayer.utilityLayerScene.activeCamera!;
      if (activeCamera.mode === Camera.ORTHOGRAPHIC_CAMERA) {
        const size = activeCamera.metadata?.size;
        if (!size) return;
        this._rootMesh.scaling.set(size * 2, size * 2, size * 2);
      }
    }
  }
  protected setNodesPovits(nodes: Node[]) {
    if (nodes?.length > 1 && this.gizmoPosition) {
      const center = this.gizmoPosition;
      for (const n of nodes) {
        if (n instanceof TransformNode) {
          const ap = n.absolutePosition.clone();
          n.setPivotPoint(center, Space.WORLD);
          n.computeWorldMatrix(true);
          const delta = n.absolutePosition.subtract(ap);
          n.setAbsolutePosition(ap.subtract(delta));
        }
      }
    }
  }
  protected resetNodesPovits(nodes: Node[]) {
    if (nodes?.length > 1) {
      for (const n of nodes) {
        if (n instanceof TransformNode) {
          const ap = n.absolutePosition.clone();
          n.setPivotMatrix(Matrix.Identity());
          n.setAbsolutePosition(ap);
        }
      }
    }
  }
}
