import { Action } from "./Actions";
import { ObjectId, IVector3 } from "@hc/core";
import { Entity } from "./../entitys/Entity";

export class MoveAction extends Action {
  animateType = "";
  isOffset = true;
  time = 1;
  constructor(
    private entitys: ObjectId[] = [],
    private target: IVector3 = { x: 0, y: 0, z: 0 }
  ) {
    super();
  }
  do(): Promise<unknown[]> {
    const fs: Promise<unknown>[] = [];
    for (const entity of this.entitys) {
      const en = entity.Object as Entity;
      fs.push(en.moveTo(this.target, this.isOffset, { time: 1 }));
    }
    return Promise.all(fs);
  }
}
