import { Factory, HCFiler } from "@hc/core";
import { Light } from "./Light";
import { RenderType } from "../../types/renderTypes";
import {
  Mesh,
  MeshBuilder,
  PointLight as BPointLight,
  StandardMaterial,
  TransformNode,
  Vector3,
} from "../../babylon";
import { CommonMaterials } from "../../materials/commonMaterials";
import { autoUpdate } from "../../core/autoUpdate";
import { Entity } from "../Entity";

@Factory
export class PointLight extends Light {
  static type = "PointLight";
  protected _name = "点光源";
  /**范围 */
  @autoUpdate Range = 100;
  initDrawObject(renderType = RenderType.Conceptual) {
    const root = new Mesh(this.UniqueName + "_root", Entity.Scene);
    const light = new BPointLight(
      this.UniqueName,
      Vector3.Zero(),
      Entity.Scene
    );
    light.parent = root;

    this.createHelper(root);

    this.initShadowGenerator(light);
    return root;
  }

  updateDrawObject(renderType, obj: Mesh) {
    if (!obj || obj.getChildren().length === 0) return;
    const light = obj.getChildren()[0] as BPointLight;
    light.diffuse = CommonMaterials.getColor3fromHex(this.Diffuse);
    light.specular = CommonMaterials.getColor3fromHex(this.Specular);
    light.intensity = this.Intensity;
    light.range = this.Range;
    this.updateHelper(obj.getChildren()[1] as Mesh);
  }
  writeFile(file: HCFiler) {
    super.writeFile(file);
    file.write(2);
    file.write(this.Range);
  }
  readFile(file: HCFiler) {
    super.readFile(file);
    const ver = file.read();
    this.Range = file.read();
    this.update();
  }
  protected getHelper() {
    const mesh = MeshBuilder.CreateSphere(
      "helper_" + this.UniqueName,
      {
        diameter: 0.5,
      },
      Entity.Scene
    );
    return mesh;
  }
  protected updateHelper(obj: Mesh) {
    if (!obj) return;
    obj.setEnabled(this.VisiableHelper);
  }
}
