import { Factory, HCFiler, IColor, ObjectId, RecordTable } from "@hc/core";

import { Color3, PBRMaterial, Texture } from "../babylon";
import type { Entity } from "../entitys/Entity";
import { firstLower } from "../utils";
import { getColor3 } from "../utils/color";
import { AppStore } from "./../app/AppStore";
import { MaterialRecord } from "./MaterialRecord";
import { TextureRecord } from "./TextureRecord";
import { autoRecordMaterial, autoRecordMaterialFilter } from "./autoRecord";
import { AlphaMode, TransparentMode } from "./types";
import { initTextureISNull, setTexture } from "./utils";

@Factory
export class PBRMaterialRecord extends MaterialRecord<PBRMaterial> {
  static type = "PBRMaterialRecord";
  /**反照颜色 */
  @autoRecordMaterialFilter((v: Color3) => v.toHexString())
  AlbedoColor: IColor = "#ffffff";

  @autoRecordMaterialFilter((v: Color3) => v.toHexString())
  ReflectivityColor: IColor = "#000000";

  @autoRecordMaterialFilter((v: Color3) => v.toHexString())
  EmissiveColor: IColor = "#000000";
  @autoRecordMaterialFilter((v: Color3) => v.toHexString())
  AmbientColor: IColor = "#000000";

  //金属度
  @autoRecordMaterial
  Metallic = 0;

  //镜面度
  @autoRecordMaterial
  Roughness = 0;

  @autoRecordMaterial
  Alpha = 1;
  /**光泽度 */
  @autoRecordMaterial
  MicroSurface = 1;

  /**从显示纹理获取alpha信息 */
  @autoRecordMaterial
  UseAlphaFromAlbedoTexture = false;

  /***透明模式 */
  @autoRecordMaterial
  TransparencyMode: TransparentMode = null;

  /**混合模式 */
  @autoRecordMaterial
  AlphaMode: AlphaMode = AlphaMode.ALPHA_COMBINE;

  //漫反射纹理
  @autoRecordMaterialFilter(initTextureISNull, setTexture)
  AlbedoTexture: ObjectId;
  /**质感贴图 */
  @autoRecordMaterialFilter(initTextureISNull, setTexture)
  MetallicTexture: ObjectId;

  //凹凸贴图
  @autoRecordMaterialFilter(initTextureISNull, setTexture)
  BumpTexture: ObjectId;

  /**自发光贴图 */
  @autoRecordMaterialFilter(initTextureISNull, setTexture)
  EmissiveTexture: ObjectId;

  /**光照贴图 */
  @autoRecordMaterialFilter(initTextureISNull, setTexture)
  LightmapTexture: ObjectId;

  //反射贴图
  @autoRecordMaterialFilter((v) => null)
  ReflectionTexture: ObjectId;

  constructor(mtl?: PBRMaterial, entity?: Entity) {
    super(mtl, entity);

    if (!this._name) {
      this._name = "物理材质";
    }

    if (!mtl) {
      this.material = new PBRMaterial(this._name, AppStore.Scene);
      this.updateMaterial();
    } else {
      this.importOwnTextures();
    }
    this.init = false;
    if (!this.material.metadata) this.material.metadata = {};
    this.material.metadata.record = this;
  }
  get AllTextures() {
    const textures: TextureRecord[] = [];
    for (const k of this.AllTextureKeys) {
      if (this[k]) {
        const oid = this[k] as ObjectId<TextureRecord>;
        if (oid?.Object) {
          textures.push(oid.Object);
        }
      }
    }

    return textures;
  }
  copyFrom(obj: this) {
    let idBak = this._id;
    let ownerBak = this._Owner;
    const keys = this.AllTextureKeys;
    let texturesBaks = keys.map((k) => this[k]) as ObjectId<TextureRecord>[];
    this.writeSnapshoot();
    let f = new HCFiler();
    obj.writeFile(f);
    this.readFile(f);
    this._id = idBak;
    this._Owner = ownerBak;
    const IdRecord = new Map<ObjectId, ObjectId<TextureRecord>>();
    for (let i = 0; i < texturesBaks.length; i++) {
      const key = keys[i];
      const txt = texturesBaks[i];
      if (txt) {
        //目标没有对应的纹理
        if (!obj[key]) {
          this[key] = null;
        } else {
          //有的话直接拷贝过来
          if (obj[key].Object) txt.Object.copyFrom(obj[key].Object);
          this[key] = txt;
        }
      } else {
        if (obj[key]?.Object) {
          if (obj._db === this._db) {
            this[key] = obj[key];
          } else {
            let newId: ObjectId<TextureRecord>;
            if (IdRecord.has(obj[key])) {
              newId = IdRecord.get(obj[key]);
            } else {
              const c = obj[key].Object.clone() as TextureRecord;
              let newOwnerId = this.DB.getObjectId(c.Owner.Index);
              (newOwnerId.Object as RecordTable).append(c);
              IdRecord.set(obj[key], c.ObjectId);
              newId = c.ObjectId;
            }
            this[key] = newId;
          }
        }
      }
    }

    this.update();

    return this;
  }
  updateMaterial() {
    super.updateMaterial();
    this.material.name = this._name;
    this.material.albedoColor = getColor3(this.AlbedoColor);
    this.material.reflectivityColor = getColor3(this.ReflectivityColor);
    this.material.emissiveColor = getColor3(this.EmissiveColor);
    this.material.ambientColor = getColor3(this.AmbientColor);

    this.material.alpha = this.Alpha;
    this.material.metallic = this.Metallic;
    this.material.roughness = this.Roughness;
    this.material.microSurface = this.MicroSurface;

    this.material.transparencyMode = this.TransparencyMode;
    this.material.alphaMode = this.AlphaMode;

    this.material.useAlphaFromAlbedoTexture = this.UseAlphaFromAlbedoTexture;
  }
  updateTextures() {
    //TODO: 自带材质不需要 这样更新纹理？
    if (this.isOwn) return;
    {
      const txr = this.AlbedoTexture?.Object as TextureRecord;
      this.material.albedoTexture = txr?.Texture;
    }
    {
      const txr = this.BumpTexture?.Object as TextureRecord;
      this.material.bumpTexture = txr?.Texture;
    }
    {
      const txr = this.MetallicTexture?.Object as TextureRecord;
      this.material.metallicTexture = txr?.Texture;
    }
    {
      const txr = this.EmissiveTexture?.Object as TextureRecord;
      this.material.emissiveTexture = txr?.Texture;
    }
    {
      const txr = this.LightmapTexture?.Object as TextureRecord;
      this.material.lightmapTexture = txr?.Texture;
    }
    {
      const txr = this.ReflectionTexture?.Object as TextureRecord;
      this.material.reflectionTexture = txr?.Texture;
    }
  }
  update() {
    if (!this.material) return;
    this.material.getScene().blockMaterialDirtyMechanism = false;
    super.update();
    this.updateTextures();
    this.material.getScene().blockMaterialDirtyMechanism = true;
  }
  async waitRender(): Promise<boolean> {
    const promises: Promise<any>[] = [];
    for (const k of this.AllTextureKeys) {
      if (this[k]) {
        const textureID = this[k] as ObjectId<TextureRecord>;
        promises.push(textureID.Object.waitUpdate());
      }
    }
    await Promise.all(promises);

    return new Promise((res) => {
      this.material.onBind = (evt) => {
        this.material.onBind = null;
        res(true);
      };
      this.material.onError = (evt) => {
        this.material.onError = null;
        res(false);
      };
    });
  }
  writeFile(file: HCFiler) {
    super.writeFile(file);
    file.write(1);

    file.write(this.AlbedoColor);
    file.write(this.ReflectivityColor);
    file.write(this.EmissiveColor);
    file.write(this.AmbientColor);

    file.write(this.Alpha);
    file.write(this.UseAlphaFromAlbedoTexture);
    file.write(this.TransparencyMode);
    file.write(this.AlphaMode);

    file.write(this.Metallic);
    file.write(this.Roughness);
    file.write(this.MicroSurface);

    file.writeHardObjectId(this.AlbedoTexture);
    file.writeHardObjectId(this.BumpTexture);
    file.writeHardObjectId(this.MetallicTexture);
    file.writeHardObjectId(this.EmissiveTexture);
    file.writeHardObjectId(this.LightmapTexture);
    file.writeHardObjectId(this.ReflectionTexture);
  }
  readFile(file: HCFiler) {
    super.readFile(file);
    const ver = file.read();

    this.AlbedoColor = file.read();
    this.ReflectivityColor = file.read();
    this.EmissiveColor = file.read();
    this.AmbientColor = file.read();

    this.Alpha = file.read();
    this.UseAlphaFromAlbedoTexture = file.read();
    this.TransparencyMode = file.read();
    this.AlphaMode = file.read();

    this.Metallic = file.read();
    this.Roughness = file.read();
    this.MicroSurface = file.read();

    this.AlbedoTexture = file.readObjectId();
    this.BumpTexture = file.readObjectId();
    this.MetallicTexture = file.readObjectId();
    this.EmissiveTexture = file.readObjectId();
    this.LightmapTexture = file.readObjectId();
    this.ReflectionTexture = file.readObjectId();

    this.update();
  }
  _SetOwnerDatabase(db) {
    super._SetOwnerDatabase(db);
    this.material.name = `pbr-${this.Id}`;
    this.material.id = `pbr-${this.Id}`;
    return this;
  }
  private get AllTextureKeys() {
    return [
      "AlbedoTexture",
      "BumpTexture",
      "MetallicTexture",
      "EmissiveTexture",
      "LightmapTexture",
      "ReflectionTexture",
    ];
  }
  protected importOwnTextures() {
    if (this.isOwn) {
      const IngoreKeys = ["name"];
      const entity = this.entity;
      const material = this.material;
      this.AllTextureKeys.forEach((k) => {
        const k1 = firstLower(k);
        const texture = this.material[k1] as Texture;
        if (texture) {
          let url = texture.url;
          let isInit = true;
          if (url.startsWith("data:")) {
            url = url.replace("data:", "");
          }
          texture.url = url;
          const proxyTexture = new Proxy(texture, {
            get(target, key, receiver) {
              return Reflect.get(target, key, receiver);
            },
            set(target, key: string, value, receiver) {
              if (
                isInit ||
                IngoreKeys.includes(key) ||
                typeof value === "function" ||
                target[key] === value
              ) {
                return Reflect.set(target, key, value, receiver);
              }

              let diff = entity.diffMaterials.get(material.id);
              if (!diff) {
                diff = {};
                entity.diffMaterials.set(material.id, diff);
              }
              if (!diff[k1]) {
                diff[k1] = {};
              }

              diff[k1][key] = value;

              return Reflect.set(target, key, value, receiver);
            },
          });

          const record = new TextureRecord(url, proxyTexture);

          this["_" + k1] = new ObjectId(-texture.uniqueId, record);

          isInit = false;
        }
      });
    }
  }
}
