import {
  Color3,
  CreateDisc,
  CreateGround,
  DefaultRenderingPipeline,
  Effect,
  Mesh,
  MeshBuilder,
  NodeMaterial,
  Scene,
  ShaderMaterial,
  Texture,
  Vector2,
  Vector3,
  WaterMaterial,
} from "../babylon";

export async function generateOcean(scene, size) {
  // Our built-in 'ground' shape.
  const ground = MeshBuilder.CreateGround(
    "ground",
    { width: 1, height: 1, subdivisions: 128 },
    scene
  );
  ground.scaling = new Vector3(size, size, size);
  ground.position.y = -size / 4;

  const material = await NodeMaterial.ParseFromFileAsync(
    "waterMaterial",
    "https://hcwl-cdn.cdn.bcebos.com/hc3d/envs/water.json",
    scene
  );
  console.log("material: ", material);

  ground.material = material;

  const pipeline = new DefaultRenderingPipeline(
    "defaultPipeline", // The name of the pipeline
    false, // Do you want the pipeline to use HDR texture?
    scene, // The scene instance
    [scene.activeCamera] // The list of cameras to be attached to
  );

  pipeline.bloomEnabled = true;
  pipeline.bloomThreshold = 0;
  pipeline.bloomKernel = 0.35;
  pipeline.bloomScale = 0.5;

  pipeline.grainEnabled = true;
  pipeline.grain.intensity = 8;
  pipeline.grain.animated = true;

  pipeline.chromaticAberrationEnabled = true;
  pipeline.chromaticAberration.aberrationAmount = 65.1;
  pipeline.chromaticAberration.radialIntensity = 2;

  pipeline.sharpenEnabled = true;
  pipeline.sharpen.edgeAmount = 0.15;

  return ground;
}

Effect.ShadersStore["myshaderVertexShader"] = `
         attribute vec3 position;                
         uniform mat4 world;                    
         uniform mat4 view;                    
         uniform mat4 viewProjection;          
         varying float fFogDistance;
         void main(void) {
                 vec4 worldPosition = world * vec4(position, 1.0);
                 fFogDistance = (view * worldPosition).z;
                 gl_Position =  viewProjection * worldPosition;
         }`;

Effect.ShadersStore["myshaderFragmentShader"] = `
      #ifdef GL_ES
  precision mediump float;
  #endif
  
  uniform float time;
  uniform vec2 mouse;
  uniform vec2 resolution;
  
  // "Seascape" by Alexander Alekseev aka TDM - 2014
  // License Creative Commons Attribution-NonCommercial-ShareAlike 3.0 Unported License.
  
  const int NUM_STEPS = 12;
  const float PI	 	= 3.1415;
  const float EPSILON	= 1e-3;
  float EPSILON_NRM	= 0.;
  
  // sea
  const int ITER_GEOMETRY = 3;
  const int ITER_FRAGMENT = 5;
  const float SEA_HEIGHT = 0.6;
  const float SEA_CHOPPY = 4.0;
  const float SEA_SPEED = 0.8;
  const float SEA_FREQ = 0.16;
  const vec3 SEA_BASE = vec3(0.1,0.19,0.22);
  const vec3 SEA_WATER_COLOR = vec3(0.8,0.9,0.6);
  float SEA_TIME = 0.;
  mat2 octave_m = mat2(1.6,1.2,-1.2,1.6);
  
  // math
  mat3 fromEuler(vec3 ang) {
      vec2 a1 = vec2(sin(ang.x),cos(ang.x));
      vec2 a2 = vec2(sin(ang.y),cos(ang.y));
      vec2 a3 = vec2(sin(ang.z),cos(ang.z));
      mat3 m;
      m[0] = vec3(a1.y*a3.y+a1.x*a2.x*a3.x,a1.y*a2.x*a3.x+a3.y*a1.x,-a2.y*a3.x);
      m[1] = vec3(-a2.y*a1.x,a1.y*a2.y,a2.x);
      m[2] = vec3(a3.y*a1.x*a2.x+a1.y*a3.x,a1.x*a3.x-a1.y*a3.y*a2.x,a2.y*a3.y);
      return m;
  }
  float hash( vec2 p ) {
      float h = dot(p,vec2(127.1,311.7));	
      return fract(sin(h)*43758.5453123);
  }
  float noise( in vec2 p ) {
      vec2 i = floor( p );
      vec2 f = fract( p );	
      vec2 u = f*f*(3.0-2.0*f);
      return -1.0+2.0*mix( mix( hash( i + vec2(0.0,0.0) ), 
                       hash( i + vec2(1.0,0.0) ), u.x),
                  mix( hash( i + vec2(0.0,1.0) ), 
                       hash( i + vec2(1.0,1.0) ), u.x), u.y);
  }
  
  // lighting
  float diffuse(vec3 n,vec3 l,float p) {
      return pow(dot(n,l) * 0.4 + 0.6,p);
  }
  float specular(vec3 n,vec3 l,vec3 e,float s) {    
      float nrm = (s + 8.0) / (3.1415 * 8.0);
      return pow(max(dot(reflect(e,n),l),0.0),s) * nrm;
  }
  
  // sky
  vec3 getSkyColor(vec3 e) {
      e.y = max(e.y,0.0);
      vec3 ret;
      ret.x = pow(1.0-e.y,2.0);
      ret.y = 1.0-e.y;
      ret.z = 0.6+(1.0-e.y)*0.4;
      return ret;
  }
  
  // sea
  float sea_octave(vec2 uv, float choppy) {
      uv += noise(uv);        
      vec2 wv = 1.0-abs(sin(uv));
      vec2 swv = abs(cos(uv));    
      wv = mix(wv,swv,wv);
      return pow(1.0-pow(wv.x * wv.y,0.65),choppy);
  }
  
  float map(vec3 p) {
      float freq = SEA_FREQ;
      float amp = SEA_HEIGHT;
      float choppy = SEA_CHOPPY;
      vec2 uv = p.xz; uv.x *= 0.75;
      
      float d, h = 0.0;    
      for(int i = 0; i < ITER_GEOMETRY; i++) {        
          d = sea_octave((uv+SEA_TIME)*freq,choppy);
          d += sea_octave((uv-SEA_TIME)*freq,choppy);
          h += d * amp;        
          uv *= octave_m; freq *= 1.9; amp *= 0.22;
          choppy = mix(choppy,1.0,0.2);
      }
      return p.y - h;
  }
  
  float map_detailed(vec3 p) {
      float freq = SEA_FREQ;
      float amp = SEA_HEIGHT;
      float choppy = SEA_CHOPPY;
      vec2 uv = p.xz; uv.x *= 0.75;
      
      float d, h = 0.0;    
      for(int i = 0; i < ITER_FRAGMENT; i++) {        
          d = sea_octave((uv+SEA_TIME)*freq,choppy);
          d += sea_octave((uv-SEA_TIME)*freq,choppy);
          h += d * amp;        
          uv *= octave_m; freq *= 1.9; amp *= 0.22;
          choppy = mix(choppy,1.0,0.2);
      }
      return p.y - h;
  }
  
  vec3 getSeaColor(vec3 p, vec3 n, vec3 l, vec3 eye, vec3 dist) {  
      float fresnel = 1.0 - max(dot(n,-eye),0.0);
      fresnel = pow(fresnel,3.0) * 0.65;
          
      vec3 reflected = getSkyColor(reflect(eye,n));    
      vec3 refracted = SEA_BASE + diffuse(n,l,80.0) * SEA_WATER_COLOR * 0.12; 
      
      vec3 color = mix(refracted,reflected,fresnel);
      
      float atten = max(1.0 - dot(dist,dist) * 0.001, 0.0);
      color += SEA_WATER_COLOR * (p.y - SEA_HEIGHT) * 0.18 * atten;
      
      color += vec3(specular(n,l,eye,60.0));
      
      return color;
  }
  
  // tracing
  vec3 getNormal(vec3 p, float eps) {
      vec3 n;
      n.y = map_detailed(p);    
      n.x = map_detailed(vec3(p.x+eps,p.y,p.z)) - n.y;
      n.z = map_detailed(vec3(p.x,p.y,p.z+eps)) - n.y;
      n.y = eps;
      return normalize(n);
  }
  
  float heightMapTracing(vec3 ori, vec3 dir, out vec3 p) {  
      float tm = 0.0;
      float tx = 1000.0;    
      float hx = map(ori + dir * tx);
      if(hx > 0.0) return tx;   
      float hm = map(ori + dir * tm);    
      float tmid = 0.0;
      for(int i = 0; i < NUM_STEPS; i++) {
          tmid = mix(tm,tx, hm/(hm-hx));                   
          p = ori + dir * tmid;                   
          float hmid = map(p);
          if(hmid < 0.0) {
              tx = tmid;
              hx = hmid;
          } else {
              tm = tmid;
              hm = hmid;
          }
      }
      return tmid;
  }
  
  // main
  void main() {
      EPSILON_NRM = 0.1 / resolution.x;
      SEA_TIME = time * SEA_SPEED;
      
      vec2 uv = gl_FragCoord.xy / resolution.xy;
      uv = uv * 2.0 - 1.0;
      uv.x *= resolution.x / resolution.y;  
      float time2 = 0.0;
      time2 = time * 0.1 + (mouse.x*10.0);
          
      // ray
      vec3 ang = vec3(sin(time2*3.0)*0.1,sin(time2)*0.2+0.3,time2);    
      vec3 ori = vec3(0.0,3.5,time*5.0);
      vec3 dir = normalize(vec3(uv.xy,-2.0)); 
      dir.z += length(uv) * 0.15;
      dir = normalize(dir) * fromEuler(ang);
      
      // tracing
      vec3 p;
      heightMapTracing(ori,dir,p);
      vec3 dist = p - ori;
      vec3 n = getNormal(p, dot(dist,dist) * EPSILON_NRM);
      vec3 light = normalize(vec3(0.0,1.0,0.8)); 
               
      // color
      vec3 color = mix(
          getSkyColor(dir),
          getSeaColor(p,n,light,dir,dist),
          pow(smoothstep(0.0,-0.05,dir.y),0.3));
          
      // post
      gl_FragColor = vec4(pow(color,vec3(0.75)), 1.0);
  }`;

export function getOcean(size, scene) {
  let ground = MeshBuilder.CreateGround(
    "ground",
    { width: size, height: size },
    scene
  );
  let boxMaterial = new ShaderMaterial(
    "test",
    scene,
    {
      vertex: "myshader",
      fragment: "myshader",
    },
    {
      needAlphaBlending: true,
      attributes: ["position"],
      uniforms: [
        "world",
        "view",
        "viewProjection",
        "vFogInfos",
        "vFogColor",
        "time",
        "mouse",
        "resolution",
      ],
      samplers: [],
    }
  );

  ground.material = boxMaterial;

  let t = 0.1;
  boxMaterial.onBind = function (mesh) {
    let effect = boxMaterial.getEffect();
    effect.setFloat("time", (t += 0.001));
    effect.setVector2("mouse", new Vector2(0.8, 0.5));
    effect.setVector2("resolution", new Vector2(1024, 1024));
  };
}

export function getWaterPlane(
  scene: Scene,
  radius: number,
  nodes: Mesh[] = [],
  baseColor?: [string, string?]
) {
  const waterMesh = CreateDisc("waterMesh", { radius: radius }, scene);
  waterMesh.rotation.x = Math.PI / 2;
  const water = new WaterMaterial("water", scene, new Vector2(512, 512));
  water.backFaceCulling = true;
  water.bumpTexture = new Texture(
    "https://hcwl-cdn.cdn.bcebos.com/hc3d/textures/waterbump.png",
    scene
  );
  water.windForce = -10;
  water.waveHeight = 2.0;
  water.bumpHeight = 0.2;
  water.windDirection = new Vector2(1, 0);
  water.waterColor = new Color3(0.1, 0.1, 0.6);
  water.colorBlendFactor = 0.0;
  nodes.forEach((n) => water.addToRenderList(n));

  if (baseColor?.[0]) {
    water.waterColor = Color3.FromHexString(baseColor[0]);
  }
  if (baseColor?.[1]) {
    water.waterColor2 = Color3.FromHexString(baseColor[1]);
  }

  waterMesh.material = water;

  return [waterMesh, water];
}
