import {
  CompareType,
  IVariableOption,
  WidgetValue,
} from "./../../types/interaction.types";
import { Factory, HCFiler, ISerialize } from "@hc/core";
import { getWidgetValue } from "../../utils/interactionUtils";

@Factory
export class Compare implements ISerialize {
  static type = "Compare";
  _Params: IVariableOption[];
  type: CompareType = CompareType.Equal;
  value1: WidgetValue;
  value2: WidgetValue;
  public check() {
    return this.compare();
  }
  writeFile(file: HCFiler): void {
    file.write(1);
    file.writeWidgetValue(this.value1);
    file.writeWidgetValue(this.value2);
    file.write(this.type);
  }
  readFile(file: HCFiler): void {
    file.read();
    if (!this.value1) this.value1 = {} as any;
    file.readWidgetValue(this.value1);

    if (!this.value2) this.value2 = {} as any;

    file.readWidgetValue(this.value2);

    this.type = file.read();
  }
  private compare() {
    const v1 = getWidgetValue(this.value1, this._Params);
    const v2 = getWidgetValue(this.value2, this._Params);
    switch (this.type) {
      case CompareType.Equal:
        return v1 === v2;
      case CompareType.UnEqual:
        return v1 !== v2;
      case CompareType.Great:
        return v1 > v2;
      case CompareType.Less:
        return v1 < v2;
      case CompareType.GtEqual:
        return v1 >= v2;
      case CompareType.LessEqual:
        return v1 <= v2;
      case CompareType.Include:
      case CompareType.UnInclude:
      case CompareType.Is:
      case CompareType.IsNot:
      default:
        return false;
    }
  }
}
